import { useMemo }          from "react";
import { TreePicker }       from "rsuite";
import PropTypes            from "prop-types";
import { FormGroup, Label } from "reactstrap";
import { Controller }       from "react-hook-form";

import { isValidArray } from "Helpers";
import "./FormTreePicker.scss";

//this styles was taken from the rsuite, it has almost all the styles of rsuite, if you want the entire styles, you can go to rsuite and take it
//Note: rember that rsuite use a css for their styles, so if you import the css from rsuite, your current styles will be overrided,
// that is why I took the styles from the rsuite and modified it.
import "./styles.css";


//This project has rsuite as a dependency, because we need a treepicker, but I encourage you to use your own library, and supply this one to don't have to use rsuite
const FormTreePicker = ({
	name,
	//If we initialize rules on defaultValues of PropTypes, sometimes it doesn't work, that is why we initialize it here.
	rules = {
		required : {
			value   : true,
			message : "Este campo no puede estar vaco.",
		},
	},
	hookForm,
	className,
	treeData,
	...rest
}) => {

	const handleDisabledTitles = useMemo(() => {
		if (!isValidArray(treeData)) {
			return false;
		}

		const disabledValues = treeData.map((item) => item.value);

		return disabledValues;
	}, [treeData]);


	return (
		<FormGroup id="FormTreePicker" className={className}>
			<Label>Clasificacin de la Zona:</Label>
			<Controller
				name={name}
				rules={rules}
				control={hookForm.control}
				render={({ field: { onChange, value }, formState : { errors : controllerErrors } },) => (
					<>
						<TreePicker
							{...rest}
							name={name}
							data={treeData}
							value={value}
							onChange={onChange}
							disabledItemValues={handleDisabledTitles}
							placeholder="Selecciona una opcin"
							className={controllerErrors?.[name] ? "w-100 invalid" : "w-100"}
						/>
						{ controllerErrors?.[name]?.message && (
							<span className="text-error mb-1">{controllerErrors[name].message}</span>
						)}
					</>
				)}
			/>
		</FormGroup>
	);
};

FormTreePicker.propTypes = {
	className : PropTypes.string,
	rules     : PropTypes.object,
	hookForm  : PropTypes.any.isRequired,
	treeData  : PropTypes.array.isRequired,
	name      : PropTypes.string.isRequired,
};

FormTreePicker.defaultProps = {
	name     : "",
	treeData : [],
};

export default FormTreePicker;
