import { Button, Table }            from "reactstrap";
import { FilePlus, Grid, Trash, X } from "react-feather";

import apprasialOptions                                           from "Resources/data/Appraisal";
import { FormSelectGroup, HelpToolTip }                           from "Components/Global";
import TdInput                                                    from "Components/Appraisal/TdInput";
import ConstructionMatrix                                         from "../ConstructionMatrix";
import ModalAditionalInfo                                         from "../ModalAditionalInfo";
import TdWatcherHOC                                               from "Components/Appraisal/TdWatcherHOC";
import { getAgeF, getLandPartialValue, getVrNet, sumSetOfValues } from "Helpers/formulas";
import { Controller, useFormContext }                             from "react-hook-form";
import { isInvalidNumber, MaskedStyledInput }                     from "Components/Global/InputMaskNumber/InputMaskNumber";
import { isValidArray }                                           from "Helpers";

const genericUseOptions = [
	{
		label : "Habitacional",
		value : 0,
	},
	{
		label : "No habitacional",
		value : 1,
	},
];

const typeConstructionOptions = [
	{
		value : 0,
		label : "Habitacional",
	},
	{
		value : 1,
		label : "Comercio",
	},
	{
		value : 2,
		label : "Oficinas",
	},
	{
		value : 3,
		label : "Industria",
	},
	{
		value : 4,
		label : "Comunicaciones",
	},
];

const { conservationOptions }  = apprasialOptions;

const TableConstruction = ({
	delegations : {
		isOpen,
		calcFRe,
		hookForm,
		removeRow,
		handleOpen,
		appendNewRow,
		constructionFields,
	},
}) => {
	return (
		<div className="mb-1">
			<div className="row align-items-end mb-1">
				<h5 className="col-md-2">Construcciones</h5>
				<FormSelectGroup
					size="sm"
					name="generic_use"
					label="Uso Generico:"
					className="col-md-4"
					hookForm={hookForm}
					options={genericUseOptions}
					menuPortalTarget={document.body}
					rules={{ required : false }}
				/>
			</div>
			<Table striped bordered responsive>
				<thead>
					<tr>
						<th rowSpan="3" className="text-center align-middle">Nm.</th>
						<th rowSpan="3" className="text-center align-middle  w-150">Tipo de Construccin</th>
						<th rowSpan="3" className="text-center align-middle">Nmero de Niveles</th>
						<th rowSpan="3" className="text-center align-middle w-150">Clase</th>
						<th rowSpan="3" className="text-center align-middle">rea m<sup>2</sup></th>
						<th rowSpan="3" className="text-center align-middle">VR nuevo</th>
						<th rowSpan="1" className="text-center align-middle" colSpan="5" scope="colgroup">Factores</th>
						<th rowSpan="3" className="text-center align-middle">
							<span>F.Re </span>
							<HelpToolTip target="help-table-construction-f-re">
								<p className="text-start">Factor Resultante = (Multiplicatoria de los factores empleados)</p>
							</HelpToolTip>
						</th>
						<th rowSpan="3" className="text-center align-middle">
							<span>V.R. Neto </span>
							<HelpToolTip target="help-table-construction-vr-net">
								<p className="text-start">Valor Neto = (VR Nuevo) x (Factor Resultante))</p>
							</HelpToolTip>
						</th>
						<th rowSpan="3" className="text-center align-middle">Info. Adicional</th>
						<th rowSpan="3" className="text-center align-middle">
							<span>Valor Parcial del terreno </span>
							<HelpToolTip target="help-table-construction-partial-land-val">
								<p className="text-start">Valor Parcial Construcciones = (Area) x (Valor Neto)</p>
							</HelpToolTip>
						</th>
						<th rowSpan="3" className="text-center align-middle"><Trash size={14} /></th>
					</tr>

					<tr className="section-table">
						<th colSpan="3" rowSpan="1" scope="colgroup" className="text-center align-middle">Demrito por Edad</th>
						<th colSpan="1" rowSpan="2" scope="colgroup" className="text-center align-middle w-200">Conservacin</th>
						<th colSpan="1" rowSpan="2" scope="colgroup" className="text-center align-middle">Otro (Avance)</th>
					</tr>

					<tr>
						<th scope="col" className="text-center align-middle">Edad</th>
						<th scope="col" className="text-center align-middle">VUT</th>
						<th scope="col" className="text-center align-middle">
							<span>Factor Edad </span>
							<HelpToolTip target="help-table-construction-age-factor">
								<p className="text-start">Demerito = (0.1) VUT + (0.9) (VUT - Edad) / VUT</p>
								<p className="text-start">No puede ser menor de .60</p>
							</HelpToolTip>
						</th>
					</tr>
				</thead>
				<tbody>
					{
						constructionFields.map( (constructionFields, index) => (
							<tr key={constructionFields.id}>
								<td className="text-center">{index + 1}</td>
								<TdInput hookForm={hookForm} name={`construction.${index}.type_construction`} options={typeConstructionOptions} />
								<TdInput hookForm={hookForm} name={`construction.${index}.level_num`} />
								<TdInput
									disabled
									hookForm={hookForm}
									name={`construction.${index}.class.class_type`}
									tdProps={{ className : "d-flex align-items-center"}}
								>
									<div className="pr-1">
										<Button.Ripple
											active={isOpen.matrix === index}
											type="button"
											color="flat-secondary"
											onClick={handleOpen("matrix", index)}
											className="btn-icon rounded-circle"
										>
											<Grid  size={14} />
										</Button.Ripple>
									</div>
								</TdInput>
								<TdAm2  name={`construction.${index}.area_m2`} length={constructionFields.length} />
								<TdInput hookForm={hookForm} name={`construction.${index}.new_vr`} />
								<TdInput hookForm={hookForm} name={`construction.${index}.age_demerit`} />
								<TdInput hookForm={hookForm} name={`construction.${index}.vut`} />
								<TdWatcherHOC
									name={`construction.${index}.age_factor`}
									deps={[
										`construction.${index}.vut`,
										`construction.${index}.age_demerit`,
									]}
									formula={getAgeF}
								/>
								<TdInput
									name={`construction.${index}.conservation`}
									hookForm={hookForm}
									tdProps={{ colSpan : "1" }}
									options={conservationOptions}
								/>
								<TdInput hookForm={hookForm} name={`construction.${index}.other_av`} />
								<TdWatcherHOC
									isArrayArgs={true}
									name={`construction.${index}.f_re`}
									deps={[
										`construction.${index}.age_factor`,
										`construction.${index}.conservation`,
										`construction.${index}.other_av`,
									]}
									callback={calcFRe}
								/>
								<TdWatcherHOC
									name={`construction.${index}.net_vr`}
									deps={[
										`construction.${index}.new_vr`,
										`construction.${index}.f_re`,
									]}
									formula={getVrNet}
								/>
								<td className="text-center align-middle">
									<Button.Ripple
										type="button"
										color="flat-secondary"
										onClick={handleOpen("modalIndex", index)}
										className="btn-icon rounded-circle"
									>
										<FilePlus  size={15} />
									</Button.Ripple>
								</td>
								<TdWatcherHOC
									isArrayArgs={true}
									name={`construction.${index}.partial_val`}
									deps={[
										`construction.${index}.area_m2`,
										`construction.${index}.net_vr`,
									]}
									formula={getLandPartialValue}
								/>
								<td className="text-center align-middle">
									<Button.Ripple
										type="button"
										color="flat-danger"
										onClick={removeRow(index)}
										className="btn-icon rounded-circle"
									>
										<X  size={14} className="color-danger" />
									</Button.Ripple>
								</td>
							</tr>
						))
					}
					<tr>
						<td colSpan="4" align="end">Superficie Total:</td>
						<TdInput hookForm={hookForm} name="construction_partial_total_val" disabled />
						<td colSpan="9" align="end">Valor Total de Construcciones:	</td>
						<TdInput hookForm={hookForm} name="construction_total_val" disabled />
						<td colSpan="1"></td>
					</tr>
				</tbody>
			</Table>
			<Button
				size="sm"
				type="button"
				color="primary"
				className="mt-1"
				onClick={appendNewRow}
			>
				Agregar Tipo
			</Button>
			{
				typeof(isOpen.matrix) === "number" &&
				<ConstructionMatrix
					tableIndex={isOpen.matrix}
					info={hookForm.getValues(`construction.${isOpen.matrix}.class`)}
					toggle={handleOpen("matrix", null)}
				/>
			}
			<ModalAditionalInfo
				tableIndex={isOpen?.modalIndex}
				setData={hookForm.setValue}
				info={hookForm.getValues(`construction.${isOpen.modalIndex}.additional_info`)}
				toggle={handleOpen("modalIndex", null)}
				isOpen={typeof(isOpen.modalIndex) === "number"}
			/>
		</div>
	);
};

const TdAm2 = ({ name }) => {

	const { control, setValue, getValues } = useFormContext();

	const options = {
		mask               : Number,
		thousandsSeparator : ",",
		radix              : ".",
		unmask             : true,
	};

	const length = getValues("construction").length;

	const keys = [...Array(length).keys()].map((index) => `construction.${index}.area_m2`);

	const handleOnchange = () => {
		if (!isValidArray(keys)) return "0";

		const vals = keys.map(key => getValues(key));

		const totalSum = sumSetOfValues(vals);
		setValue("construction_partial_total_val", totalSum.toString());
	};

	return (
		<td>
			<Controller
				name={name}
				control={control}
				rules={{ required : false }}
				render={({ field : { value, onChange}, formState : { errors : controllerErrors } },) => (
					<MaskedStyledInput
						{...options}
						type="text"
						bsSize="sm"
						className="w-100"
						value={isInvalidNumber(value) ? "" : value?.toString() }
						onAccept={(value) => {
							onChange(value);
							handleOnchange();
						}}

					/>
				)}
			/>
		</td>
	);
};

export default TableConstruction;
