import { useFormContext, useWatch } from "react-hook-form";

import LivingTypeResults from "./LivingTypeResults";


const LivingTypeResultsContainer = ({ onSubmit, isSubmitingData }) => {
	const { control  } = useFormContext();

	const livingType = useWatch({ name : "housing_type", control});

	return (
		<LivingTypeResults
			delegations={{
				onSubmit,
				livingType,
				isSubmitingData,
			}}
		/>
	);
};

export default LivingTypeResultsContainer;
