import { Card }     from "reactstrap";
import { Equation } from "react-equation";

import { HelpToolTip, InputMaskNumber } from "Components/Global";
import { roundFixed }                   from "Helpers/formulas";

const formatNumber = new Intl.NumberFormat();

const CalcByCapt = ({
	delegations : {
		hookForm,
		deduction = 0,
		referenceAverage = 0,
		incomeNetMonthly = 0,
		grossMensualRent = 0,
	},
}) => {


	return (
		<Card className="p-1">
			<Equation value="VC = P M T [1 - (1 + i )^(-n)] / i" />
			<div className="row my-1">
				<div className="col-md-3 d-flex">
					<p className="fw-bolder">Renta mensual bruta: {formatNumber.format(grossMensualRent)}</p>
					<HelpToolTip target="calc-by-capt-income-montly-gross">
						Renta Mensual Bruta = Sumatoria de Rentas Mensuales
					</HelpToolTip>
				</div>
				<div className="col-md-3 d-flex">
					<p className="fw-bolder">Deducciones mensuales estimadas: {formatNumber.format(deduction)}</p>
					<HelpToolTip target="calc-by-capt-deductions">
						Deducciones = (Renta Mensual Bruta) x (Tabla Deducciones [En numeros redondos]) % )
					</HelpToolTip>
				</div>
				<div className="col-md-3 d-flex">
					<p className="fw-bolder">PMT renta neta mensual: {formatNumber.format(incomeNetMonthly)}</p>
					<HelpToolTip target="calc-by-capt-pmt">
						PMT (Pago Total - ingresos menos egresos) = Renta Mensual Bruta - Deducciones
					</HelpToolTip>
				</div>
				<div className="col-md-3 d-flex">
					<p className="fw-bolder">Tasa de capitalizacin: {formatNumber.format(roundFixed(referenceAverage))} %</p>
					<HelpToolTip target="calc-by-capt-rate">
						<p>Tasa de Capitalizacin - Seccin Obtencin de la taza de capitalizacin</p>
						<p>(A utilizar numero redondos)</p>
					</HelpToolTip>
				</div>
				<InputMaskNumber
					bsSize="sm"
					className="mt-1 col-md-3"
					name="num_periods"
					label="(n) Numero de periodos"
					hookForm={hookForm}
					rules={{ required : false }}
				/>
			</div>
		</Card>
	);
};

export default CalcByCapt;
