import { HelpToolTip } from "Components/Global";
import Divider         from "Components/Appraisal/Divider";

export const ValuesIncidencesToolTip = () => {
	return (
		<div  className="d-flex col-12">
			<h5>Incidencias de Valores</h5>
			<HelpToolTip target="help-values-incidences">
				<div className="text-start">
					<h3 className="text-white">Incidencia de Valores</h3>
					<p className="mb-1">Este mtodo refleja resultados sustentados en los enfoques; de mercado, fsico y de capitalizacin de rentas, tomando diferentes porcentajes en los que cada mtodo influir en el valor comercial del inmueble.</p>

					<h5 className="text-white mb-1">Procedimiento</h5>

					<Divider title="Porcentaje de incidencia" />
					<p className="mb-1">
						Considerndose aplicables al caso, dos o tres enfoques de los mencionados anteriormente;
						se realiza el clculo del porcentaje de incidencia de cada enfoque, mediante la siguiente frmula:
					</p>
					<p className="fw-bolder mb-1">
						Porcentaje de Incidencia = (Resultado del Enfoque / Sumatoria de los Enfoques Aplicables) x 100
					</p>
					<Divider title="Incidencia del Enfoque" />
					<p className="mb-1">
						La incidencia de cada enfoque corresponde al Resultado del enfoque, multiplicado por su porcentaje correspondiente de incidencia:
					</p>
					<p className="fw-bolder mb-1"> Incidencia de Enfoque =  Resultado del Enfoque x Porcentaje de Incidencia</p>
					<Divider title="Resultado por Incidencia de Valores" />
					<p>El resultado equivale a la suma de las incidencias de cada enfoque aplicable.</p>
				</div>
			</HelpToolTip>
		</div>
	);
};
