import { useFieldArray, useFormContext } from "react-hook-form";
import InvesHousing                      from "./InvesHousing";

const InvesHousingContainer = () => {

	const hookForm = useFormContext();

	const { control } = hookForm;

	const { fields : housingFields } = useFieldArray({
		control,
		name : "inves_housing",
	});

	return (
		<InvesHousing
			delegations={{
				hookForm,
				housingFields,
			}}
		/>
	);
};

export default InvesHousingContainer;
