import { useEffect, useState }      from "react";
import { useFormContext, useWatch } from "react-hook-form";

import federalEntities from "Resources/data/EstadosMunicipiosMx";

import { isValidArray, removeDiacriticalMark } from "Helpers";
import toaster                                 from "Components/Global/Toaster";
import GeoForm                                 from "./GeoForm";

const GeoFormContainer = ({ onSubmit, isSubmitingData, data }) => {

	const hookForm = useFormContext();

	const [watchState, watchMunicipality] = useWatch({ name : ["state", "municipality"] });

	const [localities, setLocalities] = useState([]);
	const [municipalities, setMunicipalities] = useState([]);

	const sortOptionsByLabel = (options) => {
		if (!isValidArray(options.default, 1)) {
			return  [{ label : "No hay localidades", isDisabled : true }];
		}
		const optionsSorted = options.default.sort((a, b) => {
			if (removeDiacriticalMark(a.label) < removeDiacriticalMark(b.label)) {
				return -1;
			}
			if (removeDiacriticalMark(a.label) > removeDiacriticalMark(b.label)) {
				return 1;
			}
			return 0;
		});
		return optionsSorted;
	};

	const getLocalitiesOptions = async (state, municipality) => {
		try {
			console.log(state, municipality);
			const options = await import(`Resources/data/LocalidadesMx/${state}/${municipality}-options.json`);

			return options;
		} catch (error) {
			return [{ label : "No hay localidades", isDisabled : true }];
		}
	};

	const getStates = () => {
		return Object.keys(federalEntities).map((state, index) => {
			return {
				label : state,
				value : state,
				index,
			};
		});
	};

	const getMunicipalities = () => {
		const hookState = hookForm.getValues("state");
		const state        = (typeof hookState === "object") ? ((hookState && hookState.value) ? hookState.value : null) : hookState;
		const municipalities = federalEntities[state];

		setMunicipalities(municipalities);
	};

	const getLocalities = async () => {
		const hookState = hookForm.getValues("state");
		const hookMunicipality = hookForm.getValues("municipality");
		const state        = (typeof hookState === "object") ? ((hookState && hookState.value) ? hookState.value : null) : hookState;
		const municipality = (typeof hookMunicipality === "object") ? ((hookMunicipality && hookMunicipality.label) ? hookMunicipality.label : null) : hookMunicipality;

		if (!(state && municipality)) {
			return;
		}
		const options = await getLocalitiesOptions(removeDiacriticalMark(state), removeDiacriticalMark(municipality));

		const optionsSorted = sortOptionsByLabel(options);

		setLocalities(optionsSorted);

		if (!isValidArray(options, 1)) {
			return;
		}
		toaster.success({
			title : "Localidades.",
			body  : "Las opciones de localidades se han cargado correctamente.",
		});
	};

	useEffect(() => {
		getLocalities();
	}, [watchState, watchMunicipality]);

	useEffect(() => {
		getMunicipalities();
	}, [watchState]);

	useEffect(() => {
		const info = (data && data.geo_address) ? data.geo_address : null;
		hookForm.setValue("state", (info && info.state) ? { label : info.state, value : info.state } : null);
		hookForm.setValue("municipality", (info && info.municipality) ? { label : info.municipality, value : info.municipality } : null);
	}, [data]);

	return (
		<GeoForm
			delegations={{
				onSubmit,
				hookForm,
				localities,
				getStates,
				municipalities,
				isSubmitingData,
			}}
		/>
	);
};

export default GeoFormContainer;
