import qs from "qs";

// Import Own Components
import { api } from ".";

export const searcherApi = api.injectEndpoints({
	endpoints : builder => ({
		search : builder.query({
			query : argsOrString => typeof argsOrString === "string"
				? argsOrString
				: `${argsOrString.moduleName}?${qs.stringify({
					search : argsOrString.search,
					page   : 1,
					...argsOrString.extraQuery,
				})}`,
		}),
	}),
});

