import dayjs                        from "dayjs";
import { useEffect, useState }      from "react";
import { useFormContext, useWatch } from "react-hook-form";
import LifeSpan                     from "./LifeSpan";

const LifeSpanContainer = () => {
	const hookForm = useFormContext();

	const { setValue, getValues } = hookForm;

	const [optionalYear, setOptionalYear] = useState(null);

	const lifespanYears   = useWatch({ name : "description_general.lifespan_years" });

	const yearsToMonths = (years) => {
		return years * 12;
	};

	const setFinishYear = (ageYears) => {
		const currentYear = dayjs().year();

		if (ageYears === undefined) {
			setValue("description_general.finish_year", currentYear);
			setOptionalYear(null);
			return;
		}

		const substractYears = currentYear - ageYears;
		setValue("description_general.finish_year", substractYears);

		setOptionalYear(substractYears - 1);
	};

	const setRemainderLifespanYears = (ageYears) => {
		const numAgeYears      = Number(ageYears) ?? 0;
		const numLifespanYears = Number(lifespanYears) ?? 0;

		const result = numLifespanYears - numAgeYears;

		setValue("description_general.remainder_lifespan_years", result);

		const months = yearsToMonths(result);

		setValue("description_general.remainder_lifespan_months", months);
	};

	const handleSeteOptionalYear = () => {
		setValue("description_general.finish_year", optionalYear);
		setOptionalYear(null);
	};


	const handleChange = ({ target }) => {
		const ageYears = target.value;

		const months = yearsToMonths(ageYears);

		setRemainderLifespanYears(ageYears);

		setFinishYear(ageYears);

		setValue("description_general.age_months", months);
	};

	useEffect(() => {
		const months = yearsToMonths(lifespanYears);

		const ageYears = getValues("description_general.age_years");

		setRemainderLifespanYears(ageYears);

		setValue("description_general.lifespan_months", months);

	}, [lifespanYears]);

	return (
		<LifeSpan
			delegations={{
				hookForm,
				optionalYear,
				handleChange,
				handleSeteOptionalYear,
			}}
		/>
	);
};

export default LifeSpanContainer;
