import { useFieldArray, useFormContext } from "react-hook-form";

import RepetearForm from "./RepetearForm";

const ADD_FIELD = {
	address     : "",
	distance    : "",
	description : "",
};

const RepetearFormContainer = ({
	collectionName,
}) => {
	const hookForm = useFormContext();

	const { fields, append, remove } = useFieldArray({
		control : hookForm.control,
		name    : collectionName,
	});


	const handleAddTitle = () => append({ title : ""});

	const handleAdd = () => append({...ADD_FIELD});

	const removeItem = (index) => () => remove(index);

	return (
		<RepetearForm
			delegations={{
				fields,
				hookForm,
				handleAdd,
				removeItem,
				collectionName,
				handleAddTitle,
			}}
		/>
	);
};

export default RepetearFormContainer;
