import PropTypes from "prop-types";
import Avatar    from "@components/avatar";

// Import Own Components
import "./UserImage.scss";
import classNames from "classnames";

const UserImage = ({
	name,
	size,
	image,
	rounded,
	subIcon,
	subIconColor,
	delegations : {
		imageError,
		setImageError,
	},
}) => {

	return (
		<div id="UserImage">
			{ subIcon && size >= 30 && (
				<div className="subIcon-container">
					<span style={{ color : subIconColor }}>{ subIcon }</span>
					<div className="background" style={{ backgroundColor : subIconColor }} />
				</div>
			)}

			{ image && !imageError ? (
				<Avatar
					img={ image }
					className={ classNames( "user-avatar image", {
						xs : size <= 20,
						sm : size <= 35 && size > 20,
						mm : size <= 60 && size > 35,
						lg : size <= 90 && size > 60,
						xl : size > 90,
						rounded,
					}) }
					style={{ width : `${ size }px`, height : `${ size }px` }}
					onError={ () => setImageError( true ) }
				/>
			) : (
				<Avatar
					initials
					content={ name }
					className={ classNames( "user-avatar initials", {
						xs : size <= 20,
						sm : size <= 35 && size > 20,
						mm : size <= 60 && size > 35,
						lg : size <= 90 && size > 60,
						xl : size > 90,
						rounded,
					}) }
					style={{ width : `${ size }px`, height : `${ size }px` }}
				/>
			) }
		</div>
	);
};

UserImage.propTypes = {
	image        : PropTypes.string,
	name         : PropTypes.string.isRequired,
	size         : PropTypes.number,
	subIcon      : PropTypes.any,
	subIconColor : PropTypes.string,
	rounded      : PropTypes.bool,
};

UserImage.defaultProps = {
	image        : null,
	name         : "?",
	size         : 45,
	subIcon      : null,
	subIconColor : "white",
	rounded      : false,
};

export default UserImage;
