import ApproachIndex from "./ApproachIndex";

const NAVBAR_SIZE = 110;

const ApproachIndexContainer = ({ sections, hookForm, id }) => {

	const hanldeClick = (ref) => () => {
		//this is just for testing puporse
		// hookForm.trigger();
		document.getElementById(id).scrollTo(0, ref.current.offsetTop - NAVBAR_SIZE);
	};

	return (
		<ApproachIndex
			delegations={{
				sections,
				hanldeClick,
			}}
		/>
	);
};

export default ApproachIndexContainer;
