import qs from "qs";

// Import Own Components
import { api } from ".";

const section1Api = api.injectEndpoints({
	endpoints : builder => ({
		getSection1 : builder.query({
			query : (id) => {
				const params = { select_fields :
					[
						"page_one",
						"applicant",
						"createdAt",
						"documentation.visit_date",
					],
				};

				return `appraisals/${id}/?${qs.stringify(params)}`;
			},
			providesTags : () => ["apprasial_details"],
		}),
	}),
});

export default section1Api;
