import PropTypes from "prop-types";

//Our Imports
import { Button, Spinner } from "reactstrap";
import "./BtnsActions.scss";

const BtnsActions = ({
	loading,
	primaryBtnIcon,
	showPrimaryBtn,
	primaryBtnLabel,
	handlePrimaryBtn,
	secundaryBtnLabel,
	secundaryBtnIcon,
	showSecundaryBtn,
	handleSecundaryBtn,
	tertiaryBtnLabel,
	tertiaryBtnIcon,
	showTertiaryBtn,
	handletertiaryBtn,
}) => {
	return (
		<div id="BtnsActions" className="card">
			<div className="card-body">
				{
					showPrimaryBtn &&
					<Button.Ripple
						block
						type="submit"
						color="primary"
						disabled={loading}
						className="primary-btn text"
						onClick={handlePrimaryBtn}
					>
						{
							loading ?
								<>
									<Spinner color="white" size="sm" type="grow" />
									<span>{primaryBtnLabel}...</span>
								</>
								:
								<div className="btn-content">
									{ primaryBtnIcon && (primaryBtnIcon) }
									<p>{primaryBtnLabel}</p>
								</div>
						}
					</Button.Ripple>
				}
				{
					showSecundaryBtn &&
					<Button.Ripple
						block
						disabled={loading}
						color="outline-primary"
						className="primary-btn text mt-1"
						onClick={handleSecundaryBtn}
					>
						{
							loading ?
								<>
									<Spinner color="white" size="sm" type="grow" />
									<span>{secundaryBtnLabel}...</span>
								</>
								:
								<div className="btn-content">
									{ secundaryBtnIcon && (secundaryBtnIcon) }
									<p>{secundaryBtnLabel}</p>
								</div>
						}
					</Button.Ripple>
				}
				{
					showTertiaryBtn &&
					<Button.Ripple
						block
						disabled={loading}
						color="danger"
						className="primary-btn text mt-1"
						onClick={handletertiaryBtn}
					>
						{
							loading ?
								<>
									<Spinner color="white" size="sm" type="grow" />
									<span>{tertiaryBtnLabel}...</span>
								</>
								:
								<div className="btn-content">
									{ tertiaryBtnIcon && (tertiaryBtnIcon) }
									<p>{tertiaryBtnLabel}</p>
								</div>
						}
					</Button.Ripple>
				}
			</div>
		</div>
	);
};

BtnsActions.propTypes = {
	loading : PropTypes.bool,

	handlePrimaryBtn : PropTypes.func,
	showPrimaryBtn   : PropTypes.bool,
	primaryBtnLabel  : PropTypes.string,
	primaryBtnIcon   : PropTypes.oneOfType([PropTypes.element, PropTypes.string]),

	handleSecundaryBtn : PropTypes.func,
	showSecundaryBtn   : PropTypes.bool,
	secundaryBtnLabel  : PropTypes.string,
	secundaryBtnIcon   : PropTypes.oneOfType([PropTypes.element, PropTypes.string]),

	handletertiaryBtn : PropTypes.func,
	showTertiaryBtn   : PropTypes.bool,
	tertiaryBtnLabel  : PropTypes.string,
	tertiaryBtnIcon   : PropTypes.oneOfType([PropTypes.element, PropTypes.string]),
};

BtnsActions.defaultProps = {
	loading : false,

	primaryBtnIcon   : "",
	primaryBtnLabel  : "",
	showPrimaryBtn   : true,
	handlePrimaryBtn : () => {},

	secundaryBtnLabel  : "",
	secundaryBtnIcon   : "",
	showSecundaryBtn   : true,
	handleSecundaryBtn : () => {},

	tertiaryBtnLabel  : "",
	tertiaryBtnIcon   : "",
	showTertiaryBtn   : true,
	handletertiaryBtn : () => {},
};

export default BtnsActions;
