import { useEffect, useState } from "react";
import { connect }             from "react-redux";
import { useForm }             from "react-hook-form";

//Our Imports
import { bindAll }         from "Helpers";
import { ModalSlice }      from "Store/Slices";
import GenericSerivices    from "Store/Services/GenericSerivices";
import toaster             from "Components/Global/Toaster";
import AddPreferencesModal from "./AddPreferencesModal";

const AddPreferencesModalContainer = ({
	page,
	isOpen,
	creditToEdit,
	genericSerivices,
	modalSliceActions,
}) => {
	const isEditing = Boolean(creditToEdit?._id);

	const [image, setImage]     = useState(creditToEdit?.image ?? null);
	const [loading, setLoading] = useState(false);

	const defaultValues = {
		name        : creditToEdit?.name        ?? "",
		key         : creditToEdit?.key         ?? "",
		description : creditToEdit?.description ?? "",
	};

	const {handleSubmit, reset,  ...hookForm} = useForm(defaultValues);

	const handleCloseModal = () => modalSliceActions.closeModal("addPreferencesModal");

	const handleResponse = ({ response }) => {
		setLoading(false);

		if (response.ok) {
			genericSerivices.getAll({ module : "credit-grantors", actionType : "getAllCredit", query : { page } });

			handleCloseModal();

			toaster.success({
				title : `Otorgantes de Crdito ${isEditing ? "editado" : "creado"}`,
				body  : `El otorgante de crdito se ha  ${isEditing ? "editado" : "creado"} con exito.`,
			});
			return;
		}

		if (response.status === 409) {
			toaster.warning({
				title : "Otorgante de crdito duplicado",
				body  : "El Otorgante de Crdito ya existe",
			});
			return;
		}

		toaster.error({
			title : "Error",
			body  : "Ocurri un error, favor de revisar su conexin a internet.",
		});
	};

	const submitForm = (...args) => {

		handleSubmit((data) => {
			setLoading(true);

			const dataToSubmit = {
				...data,
				image,
				is_delete_image : !image ? true : false,
			};

			if (isEditing) {
				genericSerivices.update({ module : `credit-grantors/${creditToEdit._id}`, dataToSubmit, callback : handleResponse });
			} else {
				genericSerivices.add({ module : "credit-grantors", dataToSubmit, callback : handleResponse });
			}


		})(...args);
	};

	useEffect(() => {
		reset({...defaultValues}, {   keepErrors : true });
	}, [isOpen]);

	return (
		<AddPreferencesModal
			delegations={{
				image,
				loading,
				setImage,
				hookForm,
				submitForm,
				handleCloseModal,
			}}
		/>
	);
};

const mapDispatchToState = ({ _modalSlice, _creditgrantorsSlice  }) => ({
	page         : _creditgrantorsSlice?.page              ?? 1,
	isOpen       : _modalSlice?.addPreferencesModal        ?? false,
	creditToEdit : _modalSlice?.props?.addPreferencesModal ?? {},

});

const mapDispatchToProps = bindAll({
	GenericSerivices,
	ModalSliceActions : ModalSlice.actions,
});

export default connect(mapDispatchToState, mapDispatchToProps)(AddPreferencesModalContainer);
