import { connect } from "react-redux";
import {
	Button,
	ModalBody,
} from "reactstrap";

//Import OwnComponets
import { bindAll } from "Helpers";
import SubmitBtn   from "Components/Global/SubmitBtn";
import UserImage   from "Components/Global/UserImage";
import ModalSlice  from "Store/Slices/ModalSlice";
import "./ConfirmationModal.scss";

const ConfirmationModal = ({
	_id,
	title,
	name,
	image,
	bodyText,
	identifier,
	replacePage,
	confirmLabel,
	actionToDispatch,
	modalSliceActions,
	confirmationQuestion,
	delegations : {
		loading,
		handleConfirm,
	},
}) => {
	return (
		<ModalBody id="ConfirmationModal">
			<div className="wrapper">
				<UserImage
					name={name}
					image={image}
				/>
				<h4 className="title">{title}</h4>
				<div className="text-wrapper text-center">
					<p className="text">{confirmationQuestion}</p>
					<p className="text txt-body mt-1">{bodyText}</p>
				</div>
				<div>
					<SubmitBtn
						loading={loading}
						label={confirmLabel}
						handleOnclick={() => handleConfirm(actionToDispatch, _id, identifier, replacePage)}
					/>
					<Button.Ripple
						color="flat-danger"
						className="mt-1 w-100"
						onClick={() => modalSliceActions.closeModal("confirmationModal")}
					>
						Cancelar
					</Button.Ripple>
				</div>
			</div>
		</ModalBody>
	);
};

const mapStateToProps = ({ _modalSlice }) => ({
	_id                  : _modalSlice?.props?.confirmationModal?._id ?? "",
	title                : _modalSlice?.props?.confirmationModal?.title ?? "",
	name                 : _modalSlice?.props?.confirmationModal?.name ?? "",
	image                : _modalSlice?.props?.confirmationModal?.image ?? "",
	bodyText             : _modalSlice?.props?.confirmationModal?.bodyText ?? "",
	identifier           : _modalSlice?.props?.confirmationModal?.identifier ?? "",
	replacePage          : _modalSlice?.props?.confirmationModal?.replacePage ?? "",
	confirmLabel         : _modalSlice?.props?.confirmationModal?.confirmLabel ?? "",
	actionToDispatch     : _modalSlice?.props?.confirmationModal?.actionToDispatch ?? "",
	confirmationQuestion : _modalSlice?.props?.confirmationModal?.confirmationQuestion ?? "",
});

const mapDispatchToProps = bindAll({ ModalSliceActions : ModalSlice.actions });

export default connect(mapStateToProps, mapDispatchToProps)(ConfirmationModal);
