import { Button, Card } from "reactstrap";

const ApproachIndex = ({
	delegations : {
		sections,
		hanldeClick,
	},
}) => {

	const NUM_COLS = Math.round(12 / sections.length);

	return (
		<Card className="overflow-hidden mb-2">
			<div className="row justify-content-around">
				{
					sections.map( ({ title, ref }, index) => (
						<Button
							key={title + index }
							size="sm"
							color="flat-primary"
							className={`col-md-${ NUM_COLS }`}
							onClick={hanldeClick(ref)}
						>
							{title}
						</Button>
					))
				}
			</div>
		</Card>
	);
};

export default ApproachIndex;
