const ConstructionMatrix = ({
	delegations : {
		onSubmit,
		getScore,
		classType,
		totalScore,
		tableIndex,
		checkedMatrix,
		handleOnchange,
	},
}) => {
	return (
		<>
			<h5 className="subTitle-text text-center my-2">
				Matriz de Caractersticas de Construccin
				<b>{ typeof(tableIndex) === "number" && ` T : ${tableIndex + 1}`}</b>
			</h5>
			<Table striped bordered responsive>
				<thead>
					<tr>
						<th rowSpan="2" colSpan="1" className="text-center align-middle">Clase</th>
						<th rowSpan="1" colSpan="1" className="text-center align-middle">ESPACIOS</th>
						<th rowSpan="1" colSpan="4" className="text-center align-middle">ESTRUCTURA	</th>
						<th rowSpan="1" colSpan="5" className="text-center align-middle">ACABADOS</th>
						<th rowSpan="1" colSpan="1" className="text-center align-middle">SERVICIOS</th>
					</tr>
					<tr>
						<th rowSpan="1" className="text-center align-middle">Superficie de Construccin</th>
						<th rowSpan="1" className="text-center align-middle">Tipo</th>
						<th rowSpan="1" className="text-center align-middle">Muros</th>
						<th rowSpan="1" className="text-center align-middle">Entrepisos</th>
						<th rowSpan="1" className="text-center align-middle">Cubiertas</th>

						<th rowSpan="1" className="text-center align-middle">Muros</th>
						<th rowSpan="1" className="text-center align-middle">Pisos</th>
						<th rowSpan="1" className="text-center align-middle">Fachadas</th>
						<th rowSpan="1" className="text-center align-middle">Ventanera</th>
						<th rowSpan="1" className="text-center align-middle">Recubrimiento en baos y cocina</th>
						<th rowSpan="1" className="text-center align-middle">Muebles de bao</th>
					</tr>
				</thead>
				<tbody>
					{
						[...Array(7).keys()].map(i => (
							<tr key={`construction-matrix-${i}`}>
								<td  align="center">#{i + 1}</td>
								<TdInput
									tdProps={{ id : `construction-matrix-${i}-0`}}
									inpt_type="check-box"
									onChange={handleOnchange("surface", String(i))}
									checkValue={checkedMatrix?.["surface"] === String(i)}
								>
									<UncontrolledTooltip target={`construction-matrix-${i}-0`}>
										{TD_TOOLTIPS[`${i}-0`]}
									</UncontrolledTooltip>
								</TdInput>
								<TdInput
									tdProps={{ id : `construction-matrix-${i}-1`}}
									inpt_type="check-box"
									onChange={handleOnchange("type", String(i))}
									checkValue={checkedMatrix?.["type"] === String(i)}
								>
									<UncontrolledTooltip target={`construction-matrix-${i}-1`}>
										{TD_TOOLTIPS[`${i}-1`]}
									</UncontrolledTooltip>
								</TdInput>
								<TdInput
									tdProps={{ id : `construction-matrix-${i}-2`}}
									inpt_type="check-box"
									onChange={handleOnchange("structure_walls", String(i))}
									checkValue={checkedMatrix?.["structure_walls"] === String(i)}
								>
									<UncontrolledTooltip target={`construction-matrix-${i}-2`}>
										{TD_TOOLTIPS[`${i}-2`]}
									</UncontrolledTooltip>
								</TdInput>
								<TdInput
									tdProps={{ id : `construction-matrix-${i}-3`}}
									inpt_type="check-box"
									onChange={handleOnchange("mezzanine", String(i))}
									checkValue={checkedMatrix?.["mezzanine"] === String(i)}
								>
									<UncontrolledTooltip target={`construction-matrix-${i}-3`}>
										{TD_TOOLTIPS[`${i}-3`]}
									</UncontrolledTooltip>
								</TdInput>
								<TdInput
									tdProps={{ id : `construction-matrix-${i}-4`}}
									inpt_type="check-box"
									onChange={handleOnchange("covers", String(i))}
									checkValue={checkedMatrix?.["covers"] === String(i)}
								>
									<UncontrolledTooltip target={`construction-matrix-${i}-4`}>
										{TD_TOOLTIPS[`${i}-4`]}
									</UncontrolledTooltip>
								</TdInput>
								<TdInput
									tdProps={{ id : `construction-matrix-${i}-5`}}
									inpt_type="check-box"
									onChange={handleOnchange("finishes_walls", String(i))}
									checkValue={checkedMatrix?.["finishes_walls"] === String(i)}
								>
									<UncontrolledTooltip target={`construction-matrix-${i}-5`}>
										{TD_TOOLTIPS[`${i}-5`]}
									</UncontrolledTooltip>
								</TdInput>
								<TdInput
									tdProps={{ id : `construction-matrix-${i}-6`}}
									inpt_type="check-box"
									onChange={handleOnchange("floors", String(i))}
									checkValue={checkedMatrix?.["floors"] === String(i)}
								>
									<UncontrolledTooltip target={`construction-matrix-${i}-6`}>
										{TD_TOOLTIPS[`${i}-6`]}
									</UncontrolledTooltip>
								</TdInput>
								<TdInput
									tdProps={{ id : `construction-matrix-${i}-7`}}
									inpt_type="check-box"
									onChange={handleOnchange("facade", String(i))}
									checkValue={checkedMatrix?.["facade"] === String(i)}
								>
									<UncontrolledTooltip target={`construction-matrix-${i}-7`}>
										{TD_TOOLTIPS[`${i}-7`]}
									</UncontrolledTooltip>
								</TdInput>
								<TdInput
									tdProps={{ id : `construction-matrix-${i}-8`}}
									inpt_type="check-box"
									onChange={handleOnchange("windows", String(i))}
									checkValue={checkedMatrix?.["windows"] === String(i)}
								>
									<UncontrolledTooltip target={`construction-matrix-${i}-8`}>
										{TD_TOOLTIPS[`${i}-8`]}
									</UncontrolledTooltip>
								</TdInput>
								<TdInput
									tdProps={{ id : `construction-matrix-${i}-9`}}
									inpt_type="check-box"
									onChange={handleOnchange("bath_kitchen_covers", String(i))}
									checkValue={checkedMatrix?.["bath_kitchen_covers"] === String(i)}
								>
									<UncontrolledTooltip target={`construction-matrix-${i}-9`}>
										{TD_TOOLTIPS[`${i}-9`]}
									</UncontrolledTooltip>
								</TdInput>
								<TdInput
									tdProps={{ id : `construction-matrix-${i}-10`}}
									inpt_type="check-box"
									onChange={handleOnchange("bath_furniture", String(i))}
									checkValue={checkedMatrix?.["bath_furniture"] === String(i)}
								>
									<UncontrolledTooltip target={`construction-matrix-${i}-10`}>
										{TD_TOOLTIPS[`${i}-10`]}
									</UncontrolledTooltip>
								</TdInput>
							</tr>
						))
					}
					<tr className="text-center fw-bolder">
						<td className="fw-normal">Puntuacin:</td>
						<td>{getScore("surface")}</td>
						<td>{getScore("type")}</td>
						<td>{getScore("structure_walls")}</td>
						<td>{getScore("mezzanine")}</td>
						<td>{getScore("covers")}</td>
						<td>{getScore("finishes_walls")}</td>
						<td>{getScore("floors")}</td>
						<td>{getScore("facade")}</td>
						<td>{getScore("windows")}</td>
						<td>{getScore("bath_kitchen_covers")}</td>
						<td>{getScore("bath_furniture")}</td>
					</tr>
					<tr>
						<td colSpan="4">Total de puntos:</td>
						<td colSpan="3" className="fw-bolder">{totalScore}</td>
						<td colSpan="2">Clase:</td>
						<td colSpan="2" className="fw-bolder">{classType.val} - {classType.label}</td>
						<td colSpan="1" align="center">
							<Button
								size="sm"
								type="button"
								onClick={onSubmit}
							>
								Confirmar
							</Button>
						</td>
					</tr>
				</tbody>
			</Table>
		</>
	);
};

export default ConstructionMatrix;
