import { ModalBody, ModalFooter, ModalHeader } from "reactstrap";

//Our Imports
import defaultImage   from "Resources/images/default-img.png";
import SubmitBtn      from "Components/Global/SubmitBtn";
import DropZoneImg    from "Components/Global/DropZoneImg";
import FormInputGroup from "Components/Global/FormInputGroup";
import "./AddPreferencesModal.scss";

const AddPreferencesModal = ({
	delegations : {
		image,
		loading,
		setImage,
		hookForm,
		submitForm,
		handleCloseModal,
	},
}) => {
	return (
		<div id="AddPreferencesModal">
			<ModalHeader toggle={handleCloseModal}>
				Otorgantes de Crdito
			</ModalHeader>
			<form onSubmit={submitForm}>
				<ModalBody>
					<div className="img-inpts-wrapper">
						<DropZoneImg
							image={image}
							setImage={setImage}
							defaultImageW="50%"
							defaultImageH="50%"
							backgroundSize="contain"
							defaultImage={defaultImage}
						/>
						<div className="w-100">
							<FormInputGroup
								name="name"
								placeholder="Nombre"
								register={hookForm.register}
								errors={hookForm.formState.errors}
							/>
							<FormInputGroup
								name="key"
								type="number"
								bsSize="sm"
								placeholder="Clave"
								register={hookForm.register}
								errors={hookForm.formState.errors}
								options={{
									required : {
										value   : true,
										message : "Este campo no puede estar vaco.",
									},
									minLength : {
										value   : 6,
										message : "Este campo debe de tener 6 carcteres.",
									},
									maxLength : {
										value   : 6,
										message : "Este campo debe de tener 6 carcteres.",
									},
								}}
							/>
						</div>
					</div>
					<FormInputGroup
						type="textarea"
						name="description"
						placeholder="Descripcin"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						options={{ require : false}}
					/>
				</ModalBody>
				<ModalFooter>
					<SubmitBtn
						loading={loading}
						label="Registrar Otorgante"
						onClick={submitForm}
					/>
				</ModalFooter>
			</form>
		</div>
	);
};

export default AddPreferencesModal;
