import { useParams } from "react-router-dom";

import { getTime } from "Helpers";
import section1Api from "Store/api/section1Api";

const dateFormat = { year : "numeric", month : "long", day : "numeric" };

const Section1Info = () => {

	const { id : appraiseId } = useParams();

	const { data } = section1Api.useGetSection1Query(appraiseId);

	const { applicant, createdAt, documentation } = data.appraise;

	const getVistDate = () => {
		const { visit_date } = documentation;
		const date = getTime(visit_date);
		return date ? date : "No se ha agregado ninguna fecha de visita en la seccin de documentacin!";
	};

	const getAdress = () => {
		if (!applicant?.street || !applicant?.num_int || !applicant?.colony) {
			return "No se ha agregado un domicilio!";
		}
		return `${applicant?.street} ${applicant?.num_int}, ${applicant?.colony}`;
	};

	return (
		<div className="info-wrapper">
			<div className="row">
				<p className="duo-title col-md-3">Fecha de Entrada al Sistema:</p>
				<p className="col-md-9">{getTime(createdAt, dateFormat)}</p>
			</div>
			<div className="row">
				<p className="duo-title col-md-3">Fecha de Visita:</p>
				<p className="col-md-9">{getVistDate()}</p>
			</div>
			<div className="row">
				<p  className="duo-title col-md-3">Solicitante:</p>
				<p  className="col-md-9">{applicant?.name} {applicant?.paternal_last_name} {applicant?.maternal_last_name}</p>
			</div>
			<div className="row">
				<p className="duo-title col-md-3">Unidad de Valuacin:</p>
				{/* TODO: Define this */}
				<p className="col-md-9">15001 INGENIERIA, ASESORIA Y VALUACIONES, S.A. de C.V.</p>
			</div>
			<div className="row">
				<p  className="duo-title col-md-3">Domicilio:</p>
				<p  className="col-md-9">{getAdress()}</p>
			</div>
		</div>
	);
};

export default Section1Info;
