import { useColumnAverage }              from "Helpers/Hooks";
import { useFieldArray, useFormContext } from "react-hook-form";

import TableConstructionPrice from "./TableConstructionPrice";

const TableConstructionPriceContainer = () => {

	const hookForm = useFormContext();

	const { control } = hookForm;

	const { fields : construictionPricesFields } = useFieldArray({
		control,
		name : "construction_price",
	});

	useColumnAverage({
		length   : 3,
		arrayKey : "construction_price",
		fieldKey : "unit_price",
		name     : "average_construction_price",
	});

	return (
		<TableConstructionPrice
			delegations={{
				hookForm,
				construictionPricesFields,
			}}
		/>
	);
};

export default TableConstructionPriceContainer;
