import { ModalBody, ModalHeader } from "reactstrap";
import { connect }                from "react-redux";

//Our Imports
import { Menu } from "Components/Appraisal";

const ApprasialMenuModal = ({
	id,
	key_sofia,
	delegations : {
		handleCloseModal,
	},
}) => {
	return (
		<div id="ApprasialMenuModal">
			<ModalHeader
				toggle={handleCloseModal}
			>
				{ key_sofia ? key_sofia : id }
			</ModalHeader>
			<ModalBody>
				<Menu id={id} />
			</ModalBody>
		</div>
	);
};

const mapStateToProps = ({ _modalSlice }) => ({
	id        : _modalSlice?.props?.apprasialMenuModal?.id ?? "",
	key_sofia : _modalSlice?.props?.apprasialMenuModal?.key_sofia ?? "",
});

export default connect(mapStateToProps)(ApprasialMenuModal);
