
import Form         from "Components/Appraisal/Form";
import RepetearForm from "../RepetearForm";
import UploadButton from "../UploadButton";

const LandBoundaries = ({
	...rest
}) => {
	return (
		<Form {...rest}>
			<RepetearForm collectionName="land_boundaries" />
			<UploadButton />
		</Form>

	);
};

export default LandBoundaries;
