import { useFormContext } from "react-hook-form";

import { MapFormInpts } from "Components/Global";
import { Form }         from "Components/Appraisal";
import Section1Info     from "../Section1Info";

const BackgroundForm = ({ onSubmit, isSubmitingData }) => {

	const hookForm = useFormContext();

	const appraisalObjectOptions = [
		{
			value : 0,
			label : "Estimar el Valor Comercial del inmueble en estudio",
		},
	];

	const apprasialIntentionOptions  = [
		{
			value : 1,
			label : " Originacin de Crdito ",
		},
		{
			value : 2,
			label : " Recuperacin ",
		},
		// the value "3" is of "Other option, that means the new option or the option that the user added"
		{
			value : 4,
			label : "  Originacin de Crdito Para Vivienda con Mejoras  ",
		},
	];

	const inpts = [
		{
			//TODO: Define this logic
			disabled : true,
			name     : "background.infonavit_number",
			label    : "Nmero de Registro Infonavit (Contrato):",
			options  : { required : false },
		},
		{
			//TODO: Define this logic
			disabled : true,
			name     : "background.infonavit_set_number",
			label    : "Nmero de Registro Conjunto Infonavit:",
			options  : { required : false },
		},
		{
			name    : "background.nomenclature",
			label   : "Nomenclatura:",
			options : { required : false },
		},
		{
			name    : "background.set_name",
			label   : "Nombre del Conjunto:",
			options : { required : false },
		},
		{
			name    : "background.house_number",
			type    : "number",
			label   : "Nmero de vivienda:",
			options : { required : false },
		},
		{
			name    : "background.cuv",
			label   : "Clave nica de Vivienda(CUV):",
			options : { required : false },
		},
		{
			name    : "background.builder_promoter",
			label   : "Constructor / Promotor:",
			options : { required : false },
		},
		{
			name    : "background.appraisal_object",
			label   : "Objeto del Avalo:",
			options : appraisalObjectOptions,
			rules   : { required : false },
		},
		{
			name        : "background.appraisal_intention",
			label       : "Propsito del Avalo:",
			options     : apprasialIntentionOptions,
			inpt_type   : "creatable",
			newOptionId : 3,
			rules       : { required : false },
		},
		{
			module : "credit-grantors",
			name   : "background.credit_provider",
			label  : "Otorgante del Crdito:",
			rules  : { required : false },
		},
	];

	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<Section1Info />
			<hr />
			<MapFormInpts inpts={inpts} hookForm={hookForm} />
		</Form>

	);
};

export default BackgroundForm;
