import { useEffect }                               from "react";
import { useFieldArray, useFormContext, useWatch } from "react-hook-form";

import { roundWholeNumber } from "Helpers/formulas";
import { useColumnAverage } from "Helpers/Hooks";

import TableHomologation from "./TableHomologation";

const TableHomologationContainer = () => {

	const hookForm = useFormContext();

	const { control, setValue } = hookForm;

	const { fields : homologationFields } = useFieldArray({
		control,
		name : "homologation",
	});

	const average = useWatch({ name : "homologation_average"});


	const handleSetFactorValues = () => homologationFields.forEach((_, index) => {
		setValue(`homologation.${index}.zone`,  1);
		setValue(`homologation.${index}.ubication`, 1);
		setValue(`homologation.${index}.front`, 1);
		setValue(`homologation.${index}.shape`, 1);
	});


	useColumnAverage({
		length   : 6,
		arrayKey : "homologation",
		fieldKey : "unit_val_homologation",
		name     : "homologation_average",
	});

	useEffect(() => {
		const result = roundWholeNumber(average);

		setValue("homologation_unit_val", result);
	}, [average]);

	return (
		<TableHomologation
			delegations={{
				hookForm,
				homologationFields,
				handleSetFactorValues,
			}}
		/>
	);
};

export default TableHomologationContainer;
