import PropTypes from "prop-types";


import FormInputGroup       from "../FormInputGroup";
import FormSelectGroup      from "../FormSelectGroup";
import FormFlatPickrGroup   from "../FormFlatPickrGroup";
import FormAsyncSelectGroup from "../FormAsyncSelectGroup";
import CheckboxInpt         from "../CheckboxInpt";
import FormCreatable        from "../FormCreatable";
import { isValidArray }     from "Helpers";

const MapFormInpts = ({ inpts, hookForm, className, title}) => {
	return (
		<>
			{
				title &&
				<>
					<hr className="mb-1" />
					<h5 className="subTitle-text">{title}</h5>
				</>
			}
			{
				inpts.map( props => {
					return (
						<div className={className} key={props.name}>
							{
								props?.module ?
									<FormAsyncSelectGroup
										{...props}
										size={props.size ? props.size : "sm"}
										hookForm={hookForm}
										menuPortalTarget={document.body}
									/>
									:
									props?.inpt_type === "row-select" ?
										<div className="row align-items-center mt-1">
											<p className="col-md-8 primary-color">{props?.text}</p>
											<FormSelectGroup
												{...props}
												size={props.size ? props.size : "sm"}
												className="col-md-4"
												hookForm={hookForm}
												menuPortalTarget={document.body}
											/>
										</div>
										:
										props?.inpt_type === "date" ?
											<FormFlatPickrGroup
												{...props}
												control={hookForm.control}
												errors={hookForm.formState.errors}
											/>
											:
											isValidArray(props?.options) && props?.inpt_type !== "creatable"  ?
												<FormSelectGroup
													{...props}
													size={props.size ? props.size : "sm"}
													hookForm={hookForm}
													menuPortalTarget={document.body}
												/>
												:
												props?.inpt_type === "creatable" ?
													<FormCreatable
														{...props}
														label={props.label + " (Creable)"}
														size={props.size ? props.size : "sm"}
														hookForm={hookForm}
														menuPortalTarget={document.body}
													/>
													:
													props?.inpt_type === "check-box" ?
														<CheckboxInpt
															{...props}
															rules={{
																...props?.rules ?? {},
															}}
															control={hookForm.control}
														/>
														:
														props?.inpt_type === "check-box-group" ?
															<div className="row mt-1">
																<label className="text-chechbox col-md-8" htmlFor={props.name}>
																	{props.text}
																</label>
																<CheckboxInpt
																	{...props}
																	rules={{
																		// validate : validateCheckBox,
																		required : false,
																		...props?.rules ?? {},
																	}}
																	label={props?.label ? props.label : "Existe" }
																	falseLabel={props?.falseLabel ? props.falseLabel :"No Existe"}
																	control={hookForm.control}
																	className={props?.className ? props.className : "col-md-4"}
																/>
															</div>
															:
															<FormInputGroup
																{...props}
																bsSize={props.size ? props.size : "sm"}
																register={hookForm.register}
																errors={hookForm.formState.errors}
															/>

							}
						</div>
					);
				})
			}
		</>
	);
};

MapFormInpts.propTypes = {
	inpts    : PropTypes.array.isRequired,
	hookForm : PropTypes.any.isRequired,
};

FormInputGroup.defaultProps = {
	inpts     : [],
	className : "",
};


export default MapFormInpts;
