import { useFormContext } from "react-hook-form";

import { FormSelectGroup } from "Components/Global";
import Form                from "Components/Appraisal/Form";

const funcionalityOptions = [
	{
		value : 0,
		label : "Las caractersticas de la construccin permiten el desarrollo ptimo de habitantes",
	},
	{
		value : 1,
		label : "Las caractersticas de la construccin permiten el desarrollo suficiente de los habitantes",
	},
	{
		value : 2,
		label : "Las caractersticas de la construccin permiten el desarrollo insuficiente de los habitantes",
	},
];

const conservationStateOptions = [
	{
		value : 0,
		label : " Presenta condiciones ptimas de conservacin",
	},
	{
		value : 1,
		label : " Presenta condiciones suficientes de conservacin",
	},
	{
		value : 2,
		label : " Presenta condiciones insuficientes de conservacin",
	},
];

const lifespanOptions = [
	{
		value : 0,
		label : " Con una vida til estimada en ms de 40 aos",
	},
	{
		value : 1,
		label : " Con una vida til estimada entre los 30 y 40 aos",
	},
	{
		value : 2,
		label : " Con una vida estimada en menos de 30 aos",
	},
];


const Quality = ({ onSubmit, isSubmitingData }) => {

	const hookForm = useFormContext();

	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<FormSelectGroup
				name="funcionality"
				label="Funcionalidad:"
				hookForm={hookForm}
				options={funcionalityOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="conservation_state"
				label="Estado de Conservacin:"
				hookForm={hookForm}
				options={conservationStateOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="lifespan"
				label="Vida til Remanente:"
				hookForm={hookForm}
				options={lifespanOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
		</Form>
	);
};

export default Quality;
