import React              from "react";
import { useFormContext } from "react-hook-form";

import { useColumnAverage } from "Helpers/Hooks";
import { InputMaskNumber }  from "Components/Global";

const Avergaes = ({
	name,
	label,
	className = "col-md-3",
	columnAverage,
	...rest
}) => {

	const hookForm = useFormContext();

	useColumnAverage(columnAverage);

	return (
		<InputMaskNumber
			{...rest}
			disabled
			bsSize="sm"
			className={className}
			label={label}
			name={name}
			inputGroupClassName="mb-0"
			hookForm={hookForm}
			rules={{ required : false }}
		/>

	);
};

export default Avergaes;
