import { useRef }            from "react";
import { useForm, useWatch } from "react-hook-form";
import { useParams }         from "react-router-dom";

import { getDefaultArrVals }       from "Helpers";
import { useHandlerMutation }      from "Helpers/Hooks";
import { AppraisalHOCPageFetcher } from "Components/Appraisal";
import CapitalizationApproach      from "./CapitalizationApproach";

import {
	PARAMS,
	HOMOLOGATION,
	INVES_HOUSING,
	RENT_ESTIMATE,
	DEFAULT_FETCH_DATA,
} from "./Capitalization.constants";
import { capitalizationResult } from "Helpers/formulas";

const CapitalizationApproachContainer = ({ appraise = DEFAULT_FETCH_DATA }) => {

	const { id : appraiseId } = useParams();

	const invesHousingRef      = useRef(null);
	const tableHomologationRef = useRef(null);
	const rentEstimateRef      = useRef(null);
	const anualDeductionRef    = useRef(null);
	const incomeCapRentRef     = useRef(null);
	const calcCaptRef          = useRef(null);
	const ResultRef            = useRef(null);

	const [ editApprasial, { isLoading  : isLoadingMutation } ] = useHandlerMutation({ toastName : "apprasialCRUD" });

	const {  capitalization_approach, page_four, business_approach, physical_approach } = appraise;

	const {
		homologation,
		inves_housing,
		rent_estimate,
		rate_capitalization,
	 } = capitalization_approach;

	 const { unit_amount_value } = business_approach;

	const defaultValues = {
		...capitalization_approach,
		physic_rate : {
			...capitalization_approach.physic_rate,
			physic_value : physical_approach.physic_result,
		},
		business_rate : {
			...capitalization_approach.business_rate,
			bussiness_value : unit_amount_value,
		},
		cus_subject         : page_four.surface.cus,
		surface_subject     : page_four.surface.superficie_total_construction,
		age_subject         : page_four.description_general.age_years,
		inves_housing       : getDefaultArrVals(inves_housing, INVES_HOUSING),
		homologation        : getDefaultArrVals(homologation, HOMOLOGATION),
		rent_estimate       : getDefaultArrVals(rent_estimate, RENT_ESTIMATE),
		rate_capitalization : {
			...rate_capitalization,
			rate_sums     : [...Array(6).keys()].map((i) => ({ value : rate_capitalization?.rate_sums?.[i] ?? "" })),
			rate_partials : [...Array(6).keys()].map((i) => ({ value : rate_capitalization?.rate_partials?.[i] ?? "" })),
		},
	};

	const hookForm = useForm({ defaultValues });

	const { control, handleSubmit } = hookForm;

	const approachStatus = useWatch({ control, name : "approach_status" });

	const parseValsObjectsToStrings = (arr) =>  arr.map(({ value }) => value);

	const getValidColonyToSubmit = (colony) => {
		delete colony["d_asenta"];
		delete colony["d_tipo_asenta"];
		delete colony["d_estado"];
		delete colony["id_asenta_cpcons"];

		return colony;
	};

	const getValidUbicationToSubmit = (ubication) => {
		const copyUbication = { ...ubication };

		const { col } = copyUbication;

		const validUbicationToSubmit = {
			...copyUbication,
			col : getValidColonyToSubmit({ ...col }),
		};
		return validUbicationToSubmit;
	};

	const buildValidInvesHousing = (inves_housing = []) => {
		const dataBuilded = inves_housing.map((element, index) => {
			const ubication = getValidUbicationToSubmit(element.ubication);
			return {
				...element,
				ubication,
			};
		});
		return dataBuilded;
	};

	const buildDataToSubmit = (formData) => {
		const { rate_capitalization } = formData;

		const rate_sums     = parseValsObjectsToStrings(rate_capitalization.rate_sums);
		const rate_partials = parseValsObjectsToStrings(rate_capitalization.rate_partials);

		const inves_housing = buildValidInvesHousing(formData.inves_housing);

		const { physic_rate : { income_net_monthly }, reference_average } = formData;

		const result = capitalizationResult([income_net_monthly, reference_average]);

		const dataToSend = {
			...formData,
			inves_housing,
			rate_capitalization : {
				...rate_capitalization,
				rate_sums,
				rate_partials,
			},
			capitalization_appoach_result : result,
		};
		return dataToSend;
	};

	const onSubmit =  handleSubmit(async (formData) => {
		const dataToSubmit = buildDataToSubmit(formData);

		await editApprasial({
			module : `/appraisals/capitalization-approach/${appraiseId}`,
			method : "PUT",
			data   : dataToSubmit,
			tags   : ["appraisals", "apprasial_details"],
		});
	});

	return (
		<CapitalizationApproach
			delegations={{
				hookForm,
				onSubmit,
				ResultRef,
				calcCaptRef,
				approachStatus,
				invesHousingRef,
				rentEstimateRef,
				incomeCapRentRef,
				anualDeductionRef,
				tableHomologationRef,
				isLoading : isLoadingMutation,
			}}
		/>
	);
};

export default AppraisalHOCPageFetcher(CapitalizationApproachContainer, PARAMS);
