import { useFormContext, useWatch } from "react-hook-form";

import ConclusionSection from "./ConclusionSection";

const ConclusionSectionContainer = ({ onSubmit, isSubmitingData }) => {

	const hookForm = useFormContext();

	const referredValue      = useWatch({ control : hookForm.control, name : "refered_val"});
	const referredValueCheck = useWatch({ control : hookForm.control, name : "referred_value_check"});

	return (
		<ConclusionSection
			delegations={{
				onSubmit,
				hookForm,
				referredValue,
				isSubmitingData,
				referredValueCheck,
			}}
		/>
	);
};

export default ConclusionSectionContainer;
