import { connect } from "react-redux";

// ** Vertical Menu Components
import VerticalNavMenuLink          from "./VerticalNavMenuLink";
import VerticalNavMenuGroup         from "./VerticalNavMenuGroup";
import VerticalNavMenuSectionHeader from "./VerticalNavMenuSectionHeader";

// ** Utils
import {
	// canViewMenuItem,
	// canViewMenuGroup,
	resolveVerticalNavMenuItemComponent as resolveNavItemComponent,
} from "@layouts/utils";

const VerticalMenuNavItems = props => {
	// ** Components Object
	const Components = {
		VerticalNavMenuLink,
		VerticalNavMenuGroup,
		VerticalNavMenuSectionHeader,
	};

	// ** Render Nav Menu Items
	const RenderNavItems = props.items.map((item, index) => {
		const TagName = Components[resolveNavItemComponent(item)];

		return item?.roles?.includes(props?.role ?? "") && (<TagName key={item.id || item.header} item={item} {...props} />);
	});

	return RenderNavItems;
};

const mapStateToProps = ({ _authSlice }) => ({
	role : _authSlice?.user.role ?? "",
});

export default connect(mapStateToProps)(VerticalMenuNavItems);
