import { deepAccessObj, reactSelectStyles } from "Helpers";
import { Controller }                       from "react-hook-form";
import AsyncSelect                          from "react-select/async";
import {
	Label,
	FormGroup,
} from "reactstrap";

import "./FormAsyncSelectGroup.scss";

const FormAsyncSelectGroup = ({
	name,
	size,
	value,
	label,
	errors,
	hookForm,
	className,
	handleSearch,
	placeholder = "Seleccione una opcin",
	rules= {
		required : {
			value   : true,
			message : "Este campo no puede estar vaco.",
		},
	},
	isClearable = true,
	...rest
}) => {
	return (
		<FormGroup  id="FormAsyncSelectGroup" className={className}>
			{ label && (
				<Label>{label}</Label>
			)}
			{
				hookForm ?
					<Controller
						name={name}
						control={hookForm.control}
						rules={rules}
						render={({ field: { onChange, value }, formState : { errors : controllerErrors }}) => (
							<>
								<AsyncSelect
									{...rest}
									cacheOptions
									defaultOptions
									name={name}
									value={value}
									onChange={onChange}
									placeholder={placeholder}
									className="react-select"
									classNamePrefix="select"
									isClearable={isClearable}
									loadOptions={handleSearch}
									styles={reactSelectStyles(deepAccessObj(name, controllerErrors), size)}
								/>
								{ !!deepAccessObj(name, controllerErrors) && (
									<span className="text-error mb-1">{  deepAccessObj(name, controllerErrors)?.message }</span>
								)}
							</>
						)}
					/>
					:
					<>
						<AsyncSelect
							{...rest}
							cacheOptions
							defaultOptions
							name={name}
							value={value}
							placeholder={placeholder}
							className="react-select"
							classNamePrefix="select"
							isClearable={isClearable}
							loadOptions={handleSearch}
							styles={reactSelectStyles(deepAccessObj(name, errors), size)}
						/>
						{ !!deepAccessObj(name, errors) && (
							<span className="text-error mb-1">{ deepAccessObj(name, errors)?.message }</span>
						)}
					</>
			}

		</FormGroup>
	);
};

export default FormAsyncSelectGroup;
