import { useFormContext } from "react-hook-form";

import { MapFormInpts } from "Components/Global";

const Floor = () => {

	const hookForm = useFormContext();

	const inpts = [
		{
			label   : "Densidad habitacional; Habitantes por Hectrea (Hab-Ha). (numrico):",
			name    : "housing_density",
			type    : "number",
			options : { required : false },
		},
		{
			label   : "Densidad habitacional; Viviendas por Hectrea (Viv-Ha). (numrico):",
			name    : "housing_density_viv",
			type    : "number",
			options : { required : false },
		},
	];

	return (
		<div className="row mt-1">
			<hr />
			<h4>Suelo</h4>
			<MapFormInpts className="col-md-6" inpts={inpts} hookForm={hookForm} />
		</div>
	);
};

export default Floor;
