import classNames      from "classnames";
import { useDropzone } from "react-dropzone";
import { UploadCloud } from "react-feather";

import { useSetURLFileToRemove }                   from "Helpers/Hooks";
import { handleGetName, handleGetType, RemoveBtn } from "../DropZoneRHF";
import "./styles.scss";
import { EyeBtn }                                  from "../DropZoneRHF/DropZoneRHF";


const UploadSingleFile = ({
	name,
	file,
	label,
	accept,
	onDrop,
	onChange,
	className,
	errors = false,
}) => {

	const setURLFileToRemove = useSetURLFileToRemove();

	const { getRootProps, getInputProps } = useDropzone({
		accept,
		onDrop,
		multiple : false,
	});

	const handleRemoveFile = (e) => {
		e.stopPropagation();
		onChange(null);
		// we need to store all the files that were uploaded on the server, because later we're going to use them to delete from the server on the custom hook "useSendFiles/handleRemoveFiles"
		setURLFileToRemove({...file, inpt_name : name});
	};

	const handleOpen = (file) => () => {
		if (file?.preview) {
			window.open(file.preview);
			return;
		}
		window.open(file.location);
	};

	return (
		<div
			id="UploadSingleFile"
			className={className}
		>
			{label && <p className="label">{label}</p>}
			<div
				className={classNames("file-container", {
					"file-uploaded"            : file,
					"content-format-not-file " : !file,
					"invalid"                  : errors,
				})}
				{...getRootProps()}
			>
				<input  {...getInputProps()} />
				<RemoveBtn
					render={!!file}
					onClick={handleRemoveFile}
				/>
				<EyeBtn
					render={!!file}
					className="show-file"
					onClick={handleOpen(file)}
				/>

				{
					file ?
						<div className="card">
							<div className="card-body">
								<img className="img" src={ handleGetType( handleGetName(file) ) } width="42" height="50" />
								<p className="text-name">{ handleGetName(file) }</p>
							</div>
						</div>
						:
						<>
							<UploadCloud size={16} />
							<span className={classNames("text customStylesText", {
								"invalid" : errors,
							})}>
								Arrastra o da click para agregar un archivo
								{"(PDF)"}
							</span>
						</>
				}
			</div>
		</div>
	);
};


export default UploadSingleFile;
