import { genericApi } from "Store/api/genericApi";

import { useParams } from "react-router-dom";

const useIsCDMX = () => {
	const { id : appraiseId } = useParams();

	const { data } = genericApi.useGetDataQuery({
		params : { select_fields : ["general_information.state"] },
		module : `appraisals/${appraiseId}`,
		tags   : ["apprasial_details"],
	});

	const appraise = data?.appraise;

	const handleValidateCDMX = () => {
		if (!appraise?.general_information?.state?.label) {
			return false;
		}

		const CDMX_LABEL =  "Ciudad de Mxico";
		return appraise?.general_information?.state?.label === CDMX_LABEL;
	};

	return handleValidateCDMX;
};

export default useIsCDMX;
