import { Button, Card } from "reactstrap";

import { FormInputGroup, InputMaskNumber } from "Components/Global";
import TableCUS                            from "../TableCUS";
import TableFactors                        from "../TableFactors";

const HomogolationAnalysis = ({
	delegations : {
		hookForm,
		justifications,
		setFactorValues,
	},
}) => {

	const handleCusChange = (e) => {
		const value = parseFloat(e.target.value);
		const roundedValue = isNaN(value) ? "" : value.toFixed(3);
		hookForm.setValue("cus_property_subj", roundedValue);
	};

	return (
		<Card className="p-1">
			<div className="mb-2">
				<div className="row ">
					<InputMaskNumber
						bsSize="sm"
						className="col-md-6"
						inputGroupClassName="mb-1"
						label="Valor Unitario Aplicado al Suelo:"
						name="unit_value_applied_land"
						hookForm={hookForm}
						rules={{ required : false }}
					/>
					{/* <InputMaskNumber
						disabled
						className="col-md-6"
						inputGroupClassName="mb-1"
						bsSize="sm"
						label="CUS Inmueble Sujeto:"
						name="cus_property_subj"
						hookForm={hookForm}
						rules={{ required : false }}
					/> */}
					<FormInputGroup
						bsSize="sm"
						type="number"
						className="col-md-6 mb-1"
						name="cus_property_subj"
						label="CUS Inmueble Sujeto:"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						options={{ required : false }}
						onBlur={(e) => handleCusChange(e)}
					/>
					<InputMaskNumber
						className="col-md-6"
						inputGroupClassName="mb-1"
						bsSize="sm"
						label="Superficie de Construccin Sujeto:"
						name="building_surface_subj"
						hookForm={hookForm}
						rules={{ required : false }}
					/>
					<InputMaskNumber
						className="col-md-6"
						inputGroupClassName="mb-1"
						bsSize="sm"
						label="Superficie de Terreno Sujeto:"
						name="land_surface_subj"
						hookForm={hookForm}
						rules={{ required : false }}
					/>
				</div>
				<h5>Homologacin por CUS</h5>
				<TableCUS />
			</div>
			<div>
				<h5>Anlisis y justificacin de los factores empleados en la comparacin de inmuebles similares</h5>
				<div className="row">
					<div className="col-md-4 align-self-center">
						<Button
							outline
							size="sm"
							type="button"
							color="primary"
							onClick={setFactorValues}
						>
							Llenar Factores con 1
						</Button>
					</div>
					<FormInputGroup
						bsSize="sm"
						className="col-md-4"
						name="homologation_factors_age_subj"
						label="Edad del Sujeto:"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						options={{ required : false }}
					/>
				</div>
				<TableFactors />
			</div>
			<div className=" row mt-1">
				<h5>Justificaciones en el caso de utilizar factores de Homologacin diferentes a la unidad.</h5>
				{
					justifications.map(({ id }, index) => (
						<FormInputGroup
							bsSize="sm"
							key={id}
							className="col-md-4"
							name={`justifications.${index}.value`}
							label={`Justificacin ${index + 1}:`}
							register={hookForm.register}
							errors={hookForm.formState.errors}
							options={{ required : false }}
						/>
					))

				}
			</div>
		</Card>
	);
};

export default HomogolationAnalysis;
