import { useFormContext } from "react-hook-form";

import InfanavitSectionOptions from "Resources/data/InfanavitSection";
import { MapFormInpts }        from "Components/Global";

const InfonavitNewFields = () => {

	const hookForm = useFormContext();

	const {
		refrigeratorOptions,
		washerOptions,
		stoveOptions,
		photovoltaicOptions,
		waterCaptureOptions,
		electricSaverOptions,
	} = InfanavitSectionOptions;

	const inpts = [
		{
			label   : "Refrigerador:",
			name    : "refrigerator",
			options : refrigeratorOptions,
			rules   : { required : false },
		},
		{
			label   : "Lavadora:",
			name    : "washer",
			options : washerOptions,
			rules   : { required : false },
		},
		{
			label   : "Estufa:",
			name    : "stove",
			options : stoveOptions,
			rules   : { required : false },
		},
		{
			label   : "Fotovoltaicos:",
			name    : "photovoltaic",
			options : photovoltaicOptions,
			rules   : { required : false },
		},
		{
			label   : "Captacin de Agua:",
			name    : "water_capture",
			options : waterCaptureOptions,
			rules   : { required : false },
		},
		{
			label   : "Ahorrador de Energa Elctrica:",
			name    : "electric_saver",
			options : electricSaverOptions,
			rules   : { required : false },
		},
	];

	return (
		<div className="row mt-1">
			<hr />
			<h4>Nuevos Campos Infonavit (2015)</h4>
			<MapFormInpts className="col-md-4" inpts={inpts} hookForm={hookForm} />
		</div>
	);
};

export default InfonavitNewFields;
