import { useFormContext, useWatch } from "react-hook-form";

import federalEntities from "Resources/data/EstadosMunicipiosMx";
import GeneralInfo     from "./GeneralInfo";
const GeneralInfoContainer = ({ isSubmitingData, onSubmit }) => {

	const hookForm = useFormContext();

	const state = useWatch({ name : "general_information.state" });

	const getMunicipalities = () => {
		const municipalities = federalEntities[state];

		if (!municipalities) return [ { label : "Selecciona un estado" }];

		return municipalities;
	};

	return (
		<GeneralInfo
			delegations={{
				onSubmit,
				hookForm,
				isSubmitingData,
				getMunicipalities,
			}}
		/>
	);
};

export default GeneralInfoContainer;
