import { useFormContext } from "react-hook-form";
import PBRates            from "./PBRates";

const PBRatesContainer = () => {

	const hookForm = useFormContext();

	const { setValue } = hookForm;

	const handleSetValue = ([ val ], key) => setValue(key, val);

	return (
		<PBRates
			delegations={{
				handleSetValue,
			}}
		/>
	);
};

export default PBRatesContainer;
