import { useFormContext } from "react-hook-form";

import SurfcesCalcs from "./SurfcesCalcs";

const SurfcesCalcsContainer = () => {
	const hookForm = useFormContext();

	const { setValue, getValues } = hookForm;

	const setLot = () => setValue("surface_f.type_lot", getValues("surface_f.lot"));

	return (
		<SurfcesCalcs
			delegations={{
				setLot,
				hookForm,
			}}
		/>
	);
};

export default SurfcesCalcsContainer;
