import { useEffect }                from "react";
import { useFormContext, useWatch } from "react-hook-form";

import { roudnTwoDecimals, simpleSubtractionWithOutRounded } from "Helpers/formulas";
import TdInput                                               from "../TdInput";

//the reason of this componet is because there is a bug that araise when we use an array at name deps, that causes an infinity loop.
//So, the solution of this is that we need to state independent watchers.
const TdCalcAgeHomologation = ({ name, compAgeKey, subjAgeKey }) => {
	const hookForm     = useFormContext();
	const { setValue } = hookForm;

	const compAge = useWatch({ name : compAgeKey });
	const subjAge = useWatch({ name : subjAgeKey });

	const calcAge = () => {
		const difference = Number(subjAge) > Number(compAge) ? simpleSubtractionWithOutRounded(subjAge, compAge) : simpleSubtractionWithOutRounded(compAge, subjAge);

		const porcentage = difference <= 0 ? 0 : difference / 100;

		const result =  Number(subjAge) > Number(compAge) ? 1 - porcentage  : 1 + porcentage;

		setValue(name, roudnTwoDecimals(result));
	};

	useEffect(() => {
		calcAge();
	}, [compAge, subjAge]);

	return (
		<TdInput disabled hookForm={hookForm} name={name} />
	);

};

export default TdCalcAgeHomologation;
