import { Card }     from "reactstrap";
import { useWatch } from "react-hook-form";

import { numberFormat } from "Helpers";
import { HelpToolTip }  from "Components/Global";


const ResultApproach = ({
	deps,
	name,
	title,
	tooltip,
	fromula,
}) => {

	const vals = useWatch({ name : deps});

	return (
		<Card>
			<div className="text-center p-3">
				<div className="d-flex justify-content-center">
					<h4>{title}</h4>
					<HelpToolTip target={name}>
						<p>{tooltip}</p>
					</HelpToolTip>
				</div>
				<h3>{numberFormat(fromula(vals))}</h3>
			</div>
		</Card>
	);
};

export default ResultApproach;
