
const CreableMsg = ({ className }) => {
	return (
		<div className={className}>
			<p>Los campos que contengan la leyenda <b>(Creable)</b>, indica que son campos seleccionables con opciones por defecto.</p>
			<p>Adems, se puede crear una nueva opcin, para crear una nueva opcin, solo deber escribir en el campo y dar <b>enter</b> o <b>seleccionar</b>  cuando en el panel de las opciones lo indique.</p>
		</div>
	);
};

export default CreableMsg;
