import { Button } from "reactstrap";

import { amountAnualDeduction }         from "Helpers/formulas";
import { HelpToolTip, InputMaskNumber } from "Components/Global";
import InptWatcherHOC                   from "Components/Appraisal/InptWatcherHOC";

export const InptsRow = ({ hookForm, prefixName, label, onClick, InptPorcentage }) => (
	<div className="row align-items-center gy-1">
		<p className="fw-bolder col-md-3">{label}</p>
		{/* Amoutn Inpt with tooltip included */}
		<div className="col-md-3 d-flex">
			<InptWatcherHOC
				className="w-100"
				label="Cantidad $"
				name={`${prefixName}_amount`}
				formula={amountAnualDeduction}
				deps={[
					"gross_mensual_rent",
					`${prefixName}_porcentage`,
				]}
			/>
			<HelpToolTip target={"help-anual-deduction" + prefixName}>
				Cantidad = (Renta Mensual Bruta) x (Porcentaje)
			</HelpToolTip>
		</div>
		{/* The porcentage inpt can be passed by the props */}
		{
			InptPorcentage ?
				<InptPorcentage />
				:
				<InputMaskNumber
					bsSize="sm"
					className="col-md-3"
					label="Porcentaje%"
					disabled={typeof(onClick) === "function"}
					name={`${prefixName}_porcentage`}
					hookForm={hookForm}
					rules={{ required : false }}
				/>

		}
		{
			typeof(onClick) === "function" &&
			<Button
				onClick={onClick}
				color="flat-primary"
				className="col-md-3"
			>
				Ver tabla de eqivalencia
			</Button>
		}
	</div>
);
