import { Table as TableRS } from "reactstrap";

import { isValidArray } from "Helpers";
import "./styles.scss";


const Table = ({ children, loading,  ...rest }) => {
	return (
		<>
			{
				loading ?
					<TableSkeleton />
					:
					<TableRS {...rest}>
						{
							isValidArray(children) ?
								children.map(element => element)
								:
								children
						}
					</TableRS>
			}
		</>
	);
};

const TableSkeleton = () => (
	<div id="Table">
		<table>
			<tbody id="skeleton">
				<tr>
					<td className="td-1"><span></span></td>
					<td className="td-2"><span></span></td>
					<td className="td-3"><span></span></td>
					<td className="td-4"></td>
					<td className="td-5"><span></span></td>
				</tr>
				<tr>
					<td className="td-1"><span></span></td>
					<td className="td-2"><span></span></td>
					<td className="td-3"><span></span></td>
					<td className="td-4"></td>
					<td className="td-5"><span></span></td>
				</tr>
				<tr>
					<td className="td-1"><span></span></td>
					<td className="td-2"><span></span></td>
					<td className="td-3"><span></span></td>
					<td className="td-4"></td>
					<td className="td-5"><span></span></td>
				</tr>
				<tr>
					<td className="td-1"><span></span></td>
					<td className="td-2"><span></span></td>
					<td className="td-3"><span></span></td>
					<td className="td-4"></td>
					<td className="td-5"><span></span></td>
				</tr>
				<tr>
					<td className="td-1"><span></span></td>
					<td className="td-2"><span></span></td>
					<td className="td-3"><span></span></td>
					<td className="td-4"></td>
					<td className="td-5"><span></span></td>
				</tr><tr>
					<td className="td-1"><span></span></td>
					<td className="td-2"><span></span></td>
					<td className="td-3"><span></span></td>
					<td className="td-4"></td>
					<td className="td-5"><span></span></td>
				</tr>
			</tbody>
		</table>
	</div>
);

export default Table;
