import React              from "react";
import { Table }          from "reactstrap";
import { useFormContext } from "react-hook-form";

import TdInput                   from "Components/Appraisal/TdInput";
import { setOnlyPositiveNumber } from "Helpers";

const TableUbication = ({
	delegations : {
		index,
		settlementOptions,
	},
}) => {

	const hookForm = useFormContext();

	return (
		<Table className="mt-1" striped bordered responsive>
			<thead>
				<tr>
					<th className="text-center">Calle</th>
					<th className="text-center">Numero</th>
					<th className="text-center">Numero interior</th>
					<th className="text-center">C.P.</th>
					<th className="text-center">Clave colonia</th>
					<th className="text-center">Colonia</th>
					<th className="text-center">Clave delegacin</th>
					<th className="text-center">Delegacin</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<TdInput
						type="text"
						hookForm={hookForm}
						name={`inves_housing.${index}.ubication.street`}
					/>
					<TdInput
						hookForm={hookForm}
						name={`inves_housing.${index}.ubication.num`}
					/>
					<TdInput
						hookForm={hookForm}
						name={`inves_housing.${index}.ubication.inner_num`}
					/>
					<TdInput
						type="text"
						maxLength={5}
						hookForm={hookForm}
						onKeyPress={setOnlyPositiveNumber}
						name={`inves_housing.${index}.ubication.zipe_code`}
					/>
					<TdInput
						type="text"
						hookForm={hookForm}
						name={`inves_housing.${index}.ubication.key_colony`}
					/>
					<TdInput
						type="text"
						hookForm={hookForm}
						name={`inves_housing.${index}.ubication.col`}
						options={settlementOptions}
					/>
					<TdInput
						type="text"
						hookForm={hookForm}
						name={`inves_housing.${index}.ubication.key_delegation`}
					/>
					<TdInput
						type="text"
						hookForm={hookForm}
						name={`inves_housing.${index}.ubication.delegation`}
					/>
				</tr>
			</tbody>
		</Table>
	);
};

export default TableUbication;
