import { Table }          from "reactstrap";
import { useFormContext } from "react-hook-form";

import TdInput                   from "Components/Appraisal/TdInput";
import { setOnlyPositiveNumber } from "Helpers";

const TableUbication = ({
	delegations : {
		index,
		settlementOptions,
	},
}) => {

	const hookForm = useFormContext();

	return (
		<>
			<h5>Ubicacin</h5>
			<Table striped bordered responsive>
				<thead>
					<tr>
						<th className="text-center w-250">Calle</th>
						<th className="text-center">Nmero</th>
						<th className="text-center">Nm. Int.</th>
						<th className="text-center">C.P.</th>
						<th className="text-center">Clave colonia</th>
						<th className="text-center w-200">Colonia</th>
						<th className="text-center">Clave municipio/delegacin</th>
						<th className="text-center">Municipio/Delegacin</th>
					</tr>
				</thead>
				<tbody>
					<tr>
						<TdInput
							type="text"
							hookForm={hookForm}
							name={`unit_value_land.${index}.ubication.street`}
						/>
						<TdInput
							type="text"
							hookForm={hookForm}
							name={`unit_value_land.${index}.ubication.number`}
						/>
						<TdInput
							type="text"
							hookForm={hookForm}
							name={`unit_value_land.${index}.ubication.inner_number`}
						/>
						<TdInput
							type="text"
							maxLength={5}
							hookForm={hookForm}
							onKeyPress={setOnlyPositiveNumber}
							name={`unit_value_land.${index}.ubication.zipe_code`}
						/>
						<TdInput
							hookForm={hookForm}
							type="text"
							name={`unit_value_land.${index}.ubication.colony_ke`}
						/>
						<TdInput
							hookForm={hookForm}
							type="text"
							name={`unit_value_land.${index}.ubication.colony`}
							options={settlementOptions}
						/>
						<TdInput
							hookForm={hookForm}
							type="text"
							name={`unit_value_land.${index}.ubication.delegation_key`}
						/>
						<TdInput
							hookForm={hookForm}
							type="text"
							name={`unit_value_land.${index}.ubication.delegation`}
						/>
					</tr>
				</tbody>
			</Table>
		</>
	);
};

export default TableUbication;
