import { Table }          from "reactstrap";
import { useFormContext } from "react-hook-form";

import TdInput from "Components/Appraisal/TdInput";

const TableSources = ({ index }) => {

	const hookForm = useFormContext();

	const handleChangeInputNumber = (event) => {
		const val = event.target.value;
		const message = val.slice(0, 12);

		hookForm.setValue(`unit_value_land.${index}.source.phone`, message);
	};

	return (
		<>
			<h5>Fuentes</h5>
			<Table striped bordered responsive>
				<thead>
					<tr>
						<th className="text-center">Nombre</th>
						<th className="text-center">Telfono</th>
						<th className="text-center">Fecha</th>
						<th className="text-center">LInk / Url</th>
					</tr>
				</thead>
				<tbody>
					<tr>
						<TdInput hookForm={hookForm} name={`unit_value_land.${index}.source.name`} type="text" />
						<TdInput hookForm={hookForm} name={`unit_value_land.${index}.source.phone`} inpt_type="isNotMasked" onChange={handleChangeInputNumber} />
						<TdInput hookForm={hookForm} name={`unit_value_land.${index}.source.date`}  inpt_type="date" />
						<TdInput hookForm={hookForm} name={`unit_value_land.${index}.source.url`}  type="text" />
					</tr>
				</tbody>
			</Table>
		</>
	);
};

export default TableSources;
