import { Card }     from "reactstrap";
import { useWatch } from "react-hook-form";

import { numberFormat }           from "Helpers";
import { physicalApproachResult } from "Helpers/formulas";
import { HelpToolTip }            from "Components/Global";

const ResultPA = () => {

	const vals = useWatch({ name : [
		"land_partial_total_val",
		"construction_total_val",
		"total_val_commun_area",
		"total_val_element",
	]});

	return (
		<Card>
			<div className="text-center p-3">
				<div className="d-flex justify-content-center">
					<h4>Resultado</h4>
					<HelpToolTip target="result-ap">
						<p>Resultado del Enfoque Fsico = Valor Total del Terreno + Valor Total Construcciones + Valor Total Areas e Instalaciones + Valor Total Elementos Adicionales</p>
					</HelpToolTip>
				</div>
				<h3>{numberFormat(physicalApproachResult(vals))}</h3>
			</div>
		</Card>
	);
};

export default ResultPA;
