import { useFormContext, useWatch } from "react-hook-form";
import { Button, Table }            from "reactstrap";

import { HelpToolTip } from "Components/Global";
import TdInput         from "Components/Appraisal/TdInput";
import TdWatcherHOC    from "Components/Appraisal/TdWatcherHOC";

import {
	simpleDivison,
	sumSetOfValues,
	getLandSurfaceN,
	getExcessMissing,
	productSetOfValues,
	getUnitValueHomologatin,
} from "Helpers/formulas";
import { useEffect } from "react";

const TableCUS = ({
	delegations : {
		hookForm,
		cusFields,
		setFactorValues,
	},
}) => {
	return (
		<Table striped bordered responsive>
			<thead>
				<tr>
					<th className="text-center" scope="col">Nm.</th>
					<th className="text-center" scope="col">
						<span>Factor de Eficiencia</span>
						<Button
							outline
							onClick={setFactorValues}
							size="sm"
							type="button"
							color="primary"
						>
							Llenar con 1
						</Button>
					</th>
					<th className="text-center" scope="col">Valor Unitario del Suelo (VUS)</th>
					<th className="text-center" scope="col">
						<span>Valor Unitario del Suelo Neto (compar) </span>
						<HelpToolTip target={"unit-value-net"}>
							<p>Valor Unitario del Suelo Neto = (VUS) x (Factor de Eficiencia)</p>
						</HelpToolTip>
					</th>
					<th className="text-center" scope="col">
						<span>Superficie de Terreno Necesaria </span>
						<HelpToolTip target={"help-surface-land"}>
							<p>Superficie de Terreno Necesaria = (Superficie de Construccin Comparable) / (CUS Sujeto)</p>
						</HelpToolTip>
					</th>
					<th className="text-center" scope="col">
						<span>Excedente o Faltante </span>
						<HelpToolTip target={"help-missing"}>
							<p>Exedente o Faltante = (Superficie de Terreno Necesaria) - (Superficie de Terreno Comparable)</p>
						</HelpToolTip>
					</th>
					<th className="text-center" scope="col">
						<span>Importe Excedente o Faltante </span>
						<HelpToolTip target={"help-missing-tax"}>
							<p>Importe Excedente o Faltante = (VUS Neto) x (Excedente o Faltante)</p>
						</HelpToolTip>
					</th>
					<th className="text-center w-offer-price" scope="col">
						<span>Ajuste % / m<sup>2</sup> Const. </span>
						<HelpToolTip target={"help-adjustment"}>
							<p>Ajuste % / m<sup>2</sup> Const. = (Importe Excedente o Faltante) / (Superficie de Construccin Comparable)</p>
						</HelpToolTip>
					</th>
					<th className="text-center w-unit-price" scope="col">
						<span>Valor Unitario($ /m<sup>2</sup>) </span>
						<HelpToolTip target={"unit-price-table"}>
							<p>Valor Unitario = (Ajuste) + (Precio Unitario [Investigacin])</p>
						</HelpToolTip>
					</th>
				</tr>
			</thead>
			<tbody>
				{
					cusFields.map(({ id }, index) => (
						<tr key={id}>
							<td className="text-center">{index + 1}</td>
							<TdInput hookForm={hookForm} name={`cus.${index}.efficiency_factor`} />
							{/* TODO: retrive thies value from page 4 */}
							<TdInput hookForm={hookForm} name={`cus.${index}.vus`} />
							<TdWatcherHOC
								name={`cus.${index}.unit_value_land_net`}
								deps={[
									`cus.${index}.vus`,
									`cus.${index}.efficiency_factor`,
								]}
								formula={getUnitValueHomologatin}
							/>
							<TdWatcherHOC
								name={`cus.${index}.land_surface`}
								deps={[
									`comparable_property.${index}.inv_calcs.building`,
									"cus_property_subj",
								]}
								formula={getLandSurfaceN}
							/>
							<TdWatcherHOC
								name={`cus.${index}.excess_missing`}
								deps={[
									`cus.${index}.land_surface`,
									`comparable_property.${index}.inv_calcs.land`,
								]}
								formula={getExcessMissing}
							/>
							<TdWatcherHOC
								isArrayArgs={true}
								name={`cus.${index}.tax`}
								deps={[
									`cus.${index}.unit_value_land_net`,
									`cus.${index}.excess_missing`,
								]}
								formula={productSetOfValues}
							/>
							<TdWatcherHOC
								name={`cus.${index}.adj`}
								deps={[
									`cus.${index}.tax`,
									`comparable_property.${index}.inv_calcs.building`,
								]}
								formula={simpleDivison}
							/>
							<TdCalcUnitValue
								name={`cus.${index}.unit_val`}
								index={index}
							/>
						</tr>
					))
				}
				<tr>
					<td colSpan="8" align="right">
						Promedio Absoluto:
					</td>
					<TdInput disabled hookForm={hookForm} name="cus_average" />
				</tr>
			</tbody>
		</Table>
	);
};

//the reason of this componet is because there is a bug that araise when we use an array at name deps, that causes an infinity loop.
//So, the solution of this is that we need to state independent watchers.
const TdCalcUnitValue = ({ name, index }) => {
	const hookForm = useFormContext();

	const adj      = useWatch({ name : `cus.${index}.adj` });
	const unit_price = useWatch({ name : `comparable_property.${index}.inv_calcs.unit_price` });

	useEffect(() => {
		const result = sumSetOfValues([adj, unit_price]);
		hookForm.setValue(name, result);
	}, [adj, unit_price]);


	return (
		<TdInput disabled hookForm={hookForm} name={name} />
	);

};

export default TableCUS;
