import { useFormContext, useWatch } from "react-hook-form";

import { FormInputGroup, FormSelectGroup } from "Components/Global";
import Form                                from "Components/Appraisal/Form";
import Divider                             from "Components/Appraisal/Divider";
import ValuesIncidences                    from "../ValuesIncidences";

import {
	ubicationOptions,
	socialRiskOptions,
	adjoinsLandOptions,
	riskCommercialOptions,
	typesOfValuationOptions,
} from "./ValueResume.constants";

import ApproachesResults from "../ApproachesResults";

const ValueResume = ({
	onSubmit,
	isSubmitingData,
}) => {
	const hookForm = useFormContext();

	const { control } = hookForm;

	const typeOfValuation = useWatch({ control, name : "type_of_valuation"});


	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<ApproachesResults />
			<div className="row mb-1">
				<FormInputGroup
					bsSize="sm"
					className="col-md-6"
					name="other_fiscal_value"
					label="Revisin del valor por parmetros fiscales (Otro valor):"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ required : false }}
				/>
				<FormSelectGroup
					size="sm"
					options={typesOfValuationOptions}
					hookForm={hookForm}
					className="col-md-6"
					name="type_of_valuation"
					label="Mtodo de Valuacin:"
					menuPortalTarget={document.body}
					rules={{ required : false }}
				/>
			</div>
			<ValuesIncidences />
			<h4>Consideraciones previas a la conclusin</h4>
			<p>
				El metodo aplicado en el presente avalo es distinto al marcado en el manual de procedimientos y lineamientos tcnicos de valuacin inmobiliaria
				y de la autorizacin de sociedades y registros de peritos valuadores de acuerdo al articulo 19 y 20, de los anlisis de los valores obtenidos
				y tomando en cuenta las caractersticas fsicas y funcionales del inmueble y los factores de oferta y demanda en la zona, se concluye que el valor
				del inmueble corresponde al calculado en
				<b> {typeOfValuation?.value === 4 ? "la" : "el"} {typeOfValuation?.label}</b>.
			</p>
			<FormInputGroup
				type="textarea"
				className="mt-1"
				label="Completar (opcional):"
				name="additional_info"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				options={{ required : false }}
			/>
			<Divider title="Otros Valores" />
			<div className="row">
				<FormInputGroup
					bsSize="sm"
					className="col-md-6"
					name="hipotetic_val"
					label="Valor Hipottico:"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ required : false }}
				/>
				<FormInputGroup
					bsSize="sm"
					className="col-md-6"
					name="residual_val"
					label="Valor Residual:"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ required : false }}
				/>
				<FormInputGroup
					bsSize="sm"
					className="col-md-6"
					name="capitalization_annual"
					label="Producto Lquido Anual (Capitalizacin):"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ required : false }}
				/>
				<FormInputGroup
					bsSize="sm"
					className="col-md-6"
					name="capitalization_monthly"
					label="Producto Lquido Mensual (Capitalizacin):"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ required : false }}
				/>
			</div>
			<Divider title="Anexo Riesgo / Ubicacin" />
			<div className="row gy-1">
				<FormSelectGroup
					size="sm"
					options={riskCommercialOptions}
					hookForm={hookForm}
					className="col-md-6"
					name="commercial_risk"
					label="Riesgo Comercial:"
					menuPortalTarget={document.body}
					rules={{ required : false }}
				/>
				<FormSelectGroup
					size="sm"
					options={socialRiskOptions}
					hookForm={hookForm}
					className="col-md-6"
					name="social_risk"
					label="Riesgo Social:"
					menuPortalTarget={document.body}
					rules={{ required : false }}
				/>
				<FormSelectGroup
					size="sm"
					options={adjoinsLandOptions}
					hookForm={hookForm}
					className="col-md-6"
					name="adjoins_lands"
					label="Se observa terreno que colinda con:"
					menuPortalTarget={document.body}
					rules={{ required : false }}
				/>
				<FormSelectGroup
					size="sm"
					options={ubicationOptions}
					hookForm={hookForm}
					className="col-md-6"
					name="ubication"
					label="Se observa terreno ubicado sobre:"
					menuPortalTarget={document.body}
					rules={{ required : false }}
				/>
			</div>
		</Form>
	);
};

export default ValueResume;
