import isValidArray from "./isValidArray";
//global functions
export const roudnTwoDecimals = (num) =>  Math.round(num * 100) / 100;

export const roundFixed = (num) => {
	const parsedNum = parseFloat(num);
	return Number(parsedNum) ? parsedNum.toFixed() : 0;
};

export const getAverage = (values = []) => {
	if (!isValidArray(values)) return 0;

	const validNumbers = values.filter( num => Number(num));

	const n   = validNumbers.length < 1 ? 1 : validNumbers.length;
	const sum = values.reduce((prev, current) => prev + (isNaN(current) ? 0 : Number(current)), 0);

	const average = sum / n;

	return roudnTwoDecimals(average);
};

export const getAverageAge = (values = [], unitPrices = []) => {
	const validNumbers = values.map( num => Number(num));
	let n = validNumbers.length < 1 ? 1 : validNumbers.length;

	if (unitPrices.length>0) {
		values.forEach((item, index) => {
			const isValid = (Number(unitPrices[index]) > 0);

			if (!isValid) {
				n--;
				delete (values[index]);
			}
		});
	}

	const sum = values.reduce((prev, current) => prev + (isNaN(current) ? 0 : Number(current)), 0);

	const average = sum / n;

	return roudnTwoDecimals(average);
};

export const simpleDivison = (dividend = 0, divisor = 0) => {
	if (divisor === 0) return 0;
	const result = dividend / divisor;
	return roudnTwoDecimals(result);
};

export const simpleDivisonForFactors = (dividend = 0, divisor = 0) => {
	if (dividend === "" || divisor === "") return 1;


	if (divisor === 0) return 1;

	const result = dividend / divisor;
	return roudnTwoDecimals(result);
};

export const simpleDivisonWithOutRounded = (dividend = 0, divisor = 0) => {
	if (!Number(dividend)) return 0;

	if (divisor === 0 || !Number(divisor)) return 0;
	const result = dividend / divisor;
	return result;
};

export const productSetOfValues = (values) => {
	if (!isValidArray(values)) return 1;

	const isEmpty = values.every(val => val === "");

	if (isEmpty) return 1;

	const result = values.reduce((prev, current) =>{
		const num = (isNaN(current) || current === "") ? 1 : current;

		return  prev * num;
	}, 1);

	return roudnTwoDecimals(result);
};

export const simpleSubtraction = (minuend = 0, substrahend = 0) => {
	const result = minuend - substrahend;

	return roudnTwoDecimals(result);
};

export const simpleSubtractionWithOutRounded = (minuend = 0, substrahend = 0) => minuend - substrahend;

export const sumSetOfValues = (values) => {
	return roudnTwoDecimals(values.reduce((prev, current) => prev + (isNaN(current) ? 0 : Number(current)), 0));
};

// --------------Formulas--------------
export const getAdjustedPrice = (offer = 0, negotation = 0) => {
	const adjuestedPrice = offer * negotation;
	return roudnTwoDecimals(adjuestedPrice);
};

export const getUnitPrice = (adjustedPrice = 0, surface = 0 ) => {
	if (surface <= 0) return 0;
	const unitPrice = adjustedPrice / surface;
	return roudnTwoDecimals(unitPrice);
};

export const getDemerit = (vut = 0, age = 0) => {
	const demerit = ( (0.1 * vut) + ( 0.9 * (vut - age) ) ) / vut;
	return roudnTwoDecimals(demerit);
};

export const getVi = (adjutedPrice, b) => {
	const vi = adjutedPrice * (1 - b);
	return roudnTwoDecimals(vi);
};

export const getNetPayments = (payments = 0, ageDemerit = 0, conservation = 0) => {
	const netPayments = payments * ageDemerit * conservation;
	return roudnTwoDecimals(netPayments);
};

export const getTotalPayments = ( constructionSurface = 0, netPayments = 0) => {
	const totalPayment = constructionSurface * netPayments;
	return roudnTwoDecimals(totalPayment);
};

export const getFLandValue = (vi = 0, totalPayments = 0) => {
	const f = vi - totalPayments;
	return roudnTwoDecimals(f);
};

export const getUnitValueConstruction = (f = 0, ground = 0) => {
	if (ground <= 0) return 0;
	const unitValue = f / ground;
	return roudnTwoDecimals(unitValue);
};

export const getFResult = (values) => productSetOfValues(values);

export const getUnitValueHomologatin = (unitValueInves = 0, fResult = 0) => {
	const unitValueH = unitValueInves * fResult;
	return roudnTwoDecimals(unitValueH);
};

export const surfaceHomologation = (compSurface = 0,  subjectSurface = 0) => {
	if (compSurface === "" || subjectSurface === "") return 1;

	if (subjectSurface <= 0) return 1;

	const result = compSurface / subjectSurface;
	//raiz sexta
	const surfaceHomlogation = Math.pow(result, 1/6);

	return  roudnTwoDecimals(surfaceHomlogation);
};

export const roundWholeNumber = (num = 0) => {
	const rounded = Math.round(num);

	// const base = Math.round(temp / 10);
	const base = Math.log10(rounded);

	const trunc = Math.trunc(base);

	const size = trunc - 1;

	const div = Math.pow(10, size === 0 ? 1 : size);

	const total = rounded / div;

	const result = Math.round(total) * div;

	return result;
};

export const getLandPartialValue = (vals) => productSetOfValues(vals);

export const getAgeF = (vut = 0, age = 0) => {
	const result = getDemerit(vut, age);

	return result < 0.6 ? 0.6 : result;
};

export const getVrNet = (vr = 0, fr = 0) => {
	const validVr = vr === "" ? 0 : vr;
	const validFr = fr === "" ? 0 : fr;

	const result = productSetOfValues([validVr, validFr]);
	return result.toFixed(1);
};

export const getFResultTwoFactors = (age = 1, conservation = 1) => {
	const labelSplited = conservation?.label ? conservation.label.split("-") : [];

	const conservationVal = labelSplited?.[0] ?? 0;

	const fResult = age * conservationVal;
	return roudnTwoDecimals(fResult);
};

export const getCUS = (buildingSurface =  0, landSurface = 0) => {
	if (landSurface <= 0) return 0;
	const cus = buildingSurface / landSurface;

	return roudnTwoDecimals(cus);
};

export const getLandSurfaceN = (surfacesBuildingComp = 0, cusSub = 0) => {
	if (cusSub <= 0) return 0;
	const landSurface = surfacesBuildingComp / cusSub;
	return roudnTwoDecimals(landSurface);
};

export const getExcessMissing = (surfaceNeeded = 0, surfaceComp = 0) => {
	const excessMissing = surfaceNeeded - surfaceComp;
	return roudnTwoDecimals(excessMissing);
};

//900000, 126, 72, 179.00, 69.95, 1400 = 1.09
//790000, 140, 69, 179.00, 69.95, 1400 = 1.06
export const getFic = (pp = 0, stc = 0, scc = 0, stiv = 0, sciv = 0, vus = 0) => {
	if (pp === "" || stc === "" || scc === "" || stiv === "" || sciv === "" || vus === "") return 1;

	const vua = getVua(pp, stc, scc, stiv, sciv, vus);
	const vu  = simpleDivisonWithOutRounded(pp, scc);
	const fic = simpleDivisonWithOutRounded(vua, vu);

	return roudnTwoDecimals(fic);
};

export const getVua = (pp = 0, stc = 0,  scc = 0, stiv = 0, sciv = 0, vus = 0) => {
	const vua =  (Number(pp) + ( ( ( Number(scc) / (Number(sciv) / Number(stiv)) ) - Number(stc) ) * Number(vus))) / Number(scc);

	return vua;
};

export const amountAnualDeduction = (gross_rent, porcentage) => {
	const parsePorcentage = porcentage / 100;
	const amount = gross_rent * parsePorcentage;
	return roudnTwoDecimals(amount);
};

export const getFiscalDepreciation = (constructionVal = 0, grossRent = 0) => {
	if (grossRent <= 0 || !Number(grossRent)) return 0;
	const result = ( (constructionVal * 0.05 ) / 12 ) / grossRent;
	return roudnTwoDecimals(result);
};

export const getDeductions = (monthlyIncome = 0, decutionsTable = 0) => {
	const porcentage = decutionsTable / 100;
	const deductions = monthlyIncome * porcentage;
	return roudnTwoDecimals(deductions);
};

export const getAnualIncomeNet = (monthlyIncome = 0) => {
	const anualIncome = monthlyIncome * 12;
	return roudnTwoDecimals(anualIncome);
};

export const getNetRate = (AnualIncomeNet = 0, physicValue = 0) => {
	const result = simpleDivisonWithOutRounded(AnualIncomeNet, physicValue);
	const porcentage = result * 100;
	return roudnTwoDecimals(porcentage);
};

export const capitalizationResult = (values) => {
	const [incomeNetmonthly, capitalizationRate] = values;
	const product = incomeNetmonthly * 12;

	const porcentage = roundFixed(capitalizationRate) / 100;

	const result  = simpleDivisonWithOutRounded(product, porcentage);

	return roudnTwoDecimals(result);
};

export const physicalApproachResult = (vals) => roudnTwoDecimals(sumSetOfValues(vals));
