import React             from "react";
import { AlertTriangle } from "react-feather";
const CostMessagge = () => {
	return (
		<>
			<AlertTriangle style={{ color : "#ff9f43"}} />
			<p className="mt-1">
				El costo del avalo no puede ser calculado, ya que no se han definido la superficie  el valor comercial.
				Concluya primero el avalo y posteriormente entre a esta seccin para definir el costo.
			</p>
		</>
	);
};

export default CostMessagge;
