import { useState }                           from "react";
import { useHistory, useLocation, useParams } from "react-router-dom";

import { apprasialNavigation } from "Helpers";
import HeaderPage              from "./HeaderPage";

const MENU_LABELS_TRANSLATION = {
	"documentation"                  : "Documentacin",
	"request"                        : "Solicitud de Avalo",
	"geolocation"                    : "Domicilio Geogrfico",
	"section-1"                      : "1.1 Antecedentes",
	"section-2"                      : "1.4 Entorno",
	"section-3"                      : "2.1 Terreno",
	"section-4"                      : "2.2 Descripcin General de las Construcciones",
	"section-5"                      : "2.4 Elementos de Construccin",
	"section-infonavit"              : "Infonavit",
	"physical-approach"              : "Enfoque Fsico",
	"business-approach"              : "Enfoque de Mercado",
	"income-capitalization-approach" : "Enfoque de Capitalizacin de Rentas",
	"pictures"                       : "Cargar Fotografas",
	"appraisal-cost"                 : "Costos",
	"conclusion"                     : "Conclusiones",
	"dtc"                            : "DTC",
};

const HeaderPageContainer = ({
	isDirty,
	section,
	onSubmit,
	currentIndex,
	isSubmitingData,
}) => {

	const { id } = useParams();

	const { pathname } = useLocation();

	const history = useHistory();

	const [isOpenMenu, setIsOpenMenu] = useState(true);

	const toggleMenu = () => setIsOpenMenu(!isOpenMenu);

	const handleSaveBtnClick = async () => {
		const nextPageRoute = getNextPageRoute();
		await onSubmit();
		history.push(nextPageRoute);
	};

	const getNextPageRoute = (key = section) => {

		//for this working correctly, we need to keep the correct ordern on apprasialNavigation
		const { navigationHeader, navigationBody, navigationFooter } = apprasialNavigation;

		//beacause apprasialNavigation is splited in three different section, these section have a different structure, so we need to identify wich section is and acces into it in a proper way
		switch (key) {
			case "header": {
				if (currentIndex + 1 < navigationHeader.length) {
					return getNextRoute(navigationHeader, currentIndex);
				}

				return `${navigationBody[0].options[0].to}/${id}`;
			}

			case "body": {
				const [ subSectionIndex, optionIndex ] = currentIndex;

				const { options } = navigationBody[subSectionIndex];

				const route = walkThroughSubSections(optionIndex, options, subSectionIndex, navigationBody);

				if (route) return route;

				return  `${navigationFooter[0].options[0].to}/${id}`;
			}
			case "footer": {
				const [ subSectionIndex, optionIndex ] = currentIndex;

				const { options } = navigationFooter[subSectionIndex];

				const route = walkThroughSubSections(optionIndex, options, subSectionIndex, navigationFooter);

				if (route) return route;

				//TODO: we need to ask what happen, when we are in the last section. Where should we redirect?
				return "";

			}
			default:
				return "";
		}
	};

	const getNextRoute = (routes = [], currentIndex) => {
		const length    = routes.length;

		const nextIndex = currentIndex + 1;

		const route = nextIndex < length ? `${routes[nextIndex].to}/${id}` : "";
		return route;
	};

	const walkThroughSubSections = (optionIndex, options, subSectionIndex, navOptions) => {
		//go to next in the same subSection
		if (optionIndex < options.length -1) {
			return getNextRoute(options, optionIndex);
		}

		//if we are in the last item of the subsection options, we need to go to the next subsection options
		//validate if the next subsection exist
		if (subSectionIndex + 1 < navOptions.length) {
			const { options : nextSectionOpt } = navOptions[subSectionIndex + 1];

			return `${nextSectionOpt[0].to}/${id}`;
		}

		return "";
	};

	const getCurrentSection = () => {
		const path = pathname.split("/");
		// if the length of path is lees than 3, that means the path dosen't have a module name, so we put a default label.
		if (path.length < 3) {
			return "*";
		}

		const moduleName = path[3];

		return MENU_LABELS_TRANSLATION[moduleName] || "*";
	};

	return (
		<HeaderPage
			delegations={{
				isDirty,
				isOpenMenu,
				toggleMenu,
				isSubmitingData,
				getCurrentSection,
				handleSaveBtnClick,
			}}
		/>
	);
};

export default HeaderPageContainer;
