import { useEffect, useState }                                from "react";
import { useForm }                                            from "react-hook-form";
import { Button, Modal, ModalBody, ModalFooter, ModalHeader } from "reactstrap";

import { validateCheckBox }                              from "Helpers";
import { CheckboxInpt, FormInputGroup, FormSelectGroup } from "Components/Global";

const useOptions = [
	{
		value : "A",
		label : "Abasto (Bodega o Cuarto de Servicio)",
	},
	{
		value : "C",
		label : "Comercio",
	},
	{
		value : "D",
		label : "Deporte",
	},
	{
		value : "E",
		label : "Educacin",
	},
	{
		value : "F",
		label : "Educacin",
	},
	{
		value : "H",
		label : "Habitacional",
	},
	{
		value : "I",
		label : "Industria",
	},
	{
		value : "J",
		label : "Jardines",
	},
	{
		value : "K",
		label : "Comunicaciones",
	},
	{
		value : "L",
		label : "Hotel",
	},
	{
		value : "O",
		label : "Oficina",
	},
	{
		value : "P",
		label : "Panteones",
	},
	{
		value : "PC",
		label : "Canchas deportivas",
	},
	{
		value : "PE",
		label : "Estacionamientos, patios y plazuelas",
	},
	{
		value : "Q",
		label : "Cultura",
	},
	{
		value : "S",
		label : "Salud",
	},
	{
		value : "W",
		label : "Baldo",
	},
	{
		value : "X",
		label : "Agrcola",
	},
];

const classOptions = [
	{
		value : 1,
		label : "Mnima",
	},
	{
		value : 2,
		label : "Econmica",
	},
	{
		value : 3,
		label : "Inters Social",
	},
	{
		value : 4,
		label : "Media",
	},
	{
		value : 5,
		label : "Semilujo",
	},
	{
		value : 6,
		label : "Residencial",
	},
	{
		value : 7,
		label : "Residencial Plus",
	},
	{
		value : 8,
		label : "nica",
	},
];

const ModalAditionalInfo = ({ isOpen, toggle, tableIndex, info, setData }) => {

	const [rank, setRank] = useState();

	const defaultValues = {
		type       : info?.type       ?? true,
		undivided  : info?.undivided  ?? "",
		level_rank : info?.level_rank ?? "0",
		use        : info?.use        ?? undefined,
		class      : info?.class      ?? undefined,
	};

	const hookForm = useForm({
		defaultValues,
	});

	const { setValue, handleSubmit, reset } = hookForm;

	const handleOnchange = (val) => () => {
		setRank(val);
		setValue("level_rank", val);
	};

	const onSubmit  = (data) => {
		setData(`construction.${tableIndex}.additional_info`, data);
		toggle();
	};

	useEffect(() => {
		if (isOpen) {
			reset({...defaultValues});
			const val = defaultValues.level_rank;
			setRank(val);
		}
	}, [isOpen]);

	return (
		<Modal
			isOpen={isOpen}
			toggle={toggle}
			className="modal-dialog-centered"
		>
			<ModalHeader toggle={toggle}>
				Informacin Complementara de la Construccin: {isOpen && <b>#{tableIndex + 1}</b>}
			</ModalHeader>
			<ModalBody>
				<CheckboxInpt
					name={"type"}
					inpt_type="check-box"
					label="Comn"
					falseLabel="Privativa"
					control={hookForm.control}
					rules={{
						validate : validateCheckBox,
					}}
				/>
				<FormInputGroup
					className="mt-1"
					bsSize="sm"
					type="number"
					name={"undivided"}
					label="Indiviso"
					register={hookForm.register}
					errors={hookForm.formState.errors}
				/>
				<h5>Rango Nivel:</h5>
				<CheckboxInpt
					name={"level_rank_0"}
					inpt_type="check-box"
					label="No Aplica"
					checkValue={rank === "0"}
					onChange={handleOnchange("0")}
				/>
				<CheckboxInpt
					name={"level_rank_1"}
					inpt_type="check-box"
					label="Superficies construidas descubiertas."
					checkValue={rank === "01"}
					onChange={handleOnchange("01")}
				/>
				<CheckboxInpt
					name={"level_rank_2"}
					inpt_type="check-box"
					label="De 1 a 2 niveles o bien, si no existe una clara distincin de ellos y la construccin tenga una altura hasta de 6.00 metros."
					checkValue={rank === "02"}
					onChange={handleOnchange("02")}
				/>
				<CheckboxInpt
					name={"level_rank_3"}
					inpt_type="check-box"
					label=" De 3 a 5 niveles o bien, si no existe una clara distincin de ellos y la construccin tenga una altura de 6.01 metros a 15.00 metros."
					checkValue={rank === "05"}
					onChange={handleOnchange("05")}
				/>
				<CheckboxInpt
					name={"level_rank_4"}
					inpt_type="check-box"
					label=" De 6 a 10 niveles."
					checkValue={rank === "10"}
					onChange={handleOnchange("10")}
				/>
				<CheckboxInpt
					name={"level_rank_5"}
					inpt_type="check-box"
					label="De 11 a 15 niveles."
					checkValue={rank === "15"}
					onChange={handleOnchange("15")}
				/>
				<CheckboxInpt
					name={"level_rank_6"}
					inpt_type="check-box"
					label="De 16 a 20 niveles."
					checkValue={rank === "20"}
					onChange={handleOnchange("20")}
				/>
				<CheckboxInpt
					name={"level_rank_7"}
					inpt_type="check-box"
					label="De 21 a ms niveles."
					checkValue={rank === "99"}
					onChange={handleOnchange("99")}
				/>
				<CheckboxInpt
					name={"level_rank_8"}
					inpt_type="check-box"
					label="Rango nico. Se aplica a edificaciones sin una clara distincin de niveles tales como naves industriales, bodegas galerones, centros comerciales, restaurantes y estructuras semejantes que excedan una altura de 15.00 metros."
					checkValue={rank === "RU"}
					onChange={handleOnchange("RU")}
				/>
				<FormSelectGroup
					size="sm"
					name={"use"}
					label="Uso:"
					hookForm={hookForm}
					options={useOptions}
				/>
				<FormSelectGroup
					size="sm"
					name={"class"}
					label="Clase:"
					hookForm={hookForm}
					options={classOptions}
				/>
			</ModalBody>
			<ModalFooter>
				<Button type="button" onClick={handleSubmit(onSubmit)}>
					Guardar y Cerrar
				</Button>
			</ModalFooter>
		</Modal>
	);
};

export default ModalAditionalInfo;
