import { useFormContext } from "react-hook-form";

import { MapFormInpts }     from "Components/Global";
import {Form, CreableMsg}   from "Components/Appraisal";
import InstallationsOptions from "Resources/data/Installations";

const Installations = ({ onSubmit, isSubmitingData }) => {

	const hookForm = useFormContext();

	const {
		installationsOptions,
		hydraulicMaterialOptions,
		sanitaryMaterialOptions,
		campertyOptions,
		smithyOptions,
		glasseryOptions,
		glasseryFeaturesOptions,
	} = InstallationsOptions;

	const inptsHydraulic = [
		{
			label       : "Instalaciones Hidrulicas:",
			name        : "installation.hydraulic_installations",
			inpt_type   : "creatable",
			options     : installationsOptions,
			rules       : { required : false },
			newOptionId : installationsOptions.length,
		},
		{
			label       : "Material de Instalaciones Hidrulicas:",
			name        : "installation.hydraulic_material",
			inpt_type   : "creatable",
			options     : hydraulicMaterialOptions,
			rules       : { required : false },
			newOptionId : hydraulicMaterialOptions.length,

		},
	];

	const inptsSanitary = [
		{
			label       : "Instalaciones Sanitarias:",
			name        : "installation.sanitary_installations",
			inpt_type   : "creatable",
			options     : installationsOptions,
			rules       : { required : false },
			newOptionId : installationsOptions.length,
		},
		{
			label       : "Material de las Instalaciones Sanitarias:",
			name        : "installation.sanitary_material",
			inpt_type   : "creatable",
			options     : sanitaryMaterialOptions,
			rules       : { required : false },
			newOptionId : sanitaryMaterialOptions.length,

		},
	];

	const inptsElectrical = [
		{
			label       : "Instalaciones Elctricas:",
			name        : "installation.electrical_installations",
			inpt_type   : "creatable",
			options     : installationsOptions,
			rules       : { required : false },
			newOptionId : installationsOptions.length,

		},
		{
			label   : "Material de las Instalaciones Elctricas:(Nuevo Campo)",
			name    : "installation.electrical_material",
			options : { required : false },
		},
	];

	const inptsMaterials = [
		{
			label       : "Carpintera:",
			name        : "installation.carpentry",
			inpt_type   : "creatable",
			options     : campertyOptions,
			rules       : { required : false },
			newOptionId : campertyOptions.length,
		},
		{
			label       : "Herrera:",
			name        : "installation.smithy",
			inpt_type   : "creatable",
			options     : smithyOptions,
			rules       : { required : false },
			newOptionId : smithyOptions.length,
		},
		{
			label   : "Vidrera:",
			name    : "installation.glassery",
			options : glasseryOptions,
			rules   : { required : false },
		},
		{
			label   : "Caractersticas Vidrera:",
			name    : "installation.glassery_features",
			options : glasseryFeaturesOptions,
			rules   : { required : false },
		},
	];

	const inptsFinishings = [
		{
			label   : "Muros en Escalera:",
			name    : "installation.stairs_wall",
			options : { required : false },
		},
		{
			label   : "Plafones en Escalera:",
			name    : "installation.stairs_roof",
			options : { required : false },
		},
	];

	const inptsConstruction = [
		{
			label   : "Azoteas:",
			name    : "installation.rooftops",
			options : { required : false },
		},
		{
			label   : "Bardas:",
			name    : "installation.fences",
			options : { required : false },
		},
		{
			label   : "Cimentacin:",
			name    : "installation.foundation",
			options : { required : false },
		},
		{
			label   : "Entrepisos:",
			name    : "installation.mezzanines",
			options : { required : false },
		},
		{
			label   : "Estructura:",
			name    : "installation.structure",
			options : { required : false },
		},
		{
			label   : "Muros:",
			name    : "installation.walls",
			options : { required : false },
		},
		{
			label   : "Techos:",
			name    : "installation.roofs",
			options : { required : false },
		},
	];

	const inptsCarpentry = [
		{
			label   : "Guardarropas:",
			name    : "installation.wardrobes",
			options : { required : false },
		},
		{
			label   : "Muebles Empotrados Fijos:",
			name    : "installation.fixed_furniture",
			options : { required : false },
		},
		{
			label   : "Puertas Interiores:",
			name    : "installation.internal_doors",
			options : { required : false },
		},
	];

	const intpsCoating =[
		{
			label   : "Lambrines:",
			name    : "installation.lambrines",
			options : { required : false },
		},
		{
			label   : "Pintura:",
			name    : "installation.paint",
			options : { required : false },
		},
		{
			label   : "Recubrimientos Especiales:",
			name    : "installation.special_coating",
			options : { required : false },
		},
		{
			label   : "Zoclos:",
			name    : "installation.zoclos",
			options : { required : false },
		},
		{
			label   : "Muebles de Bao:",
			name    : "installation.bathroom_furniture",
			options : { required : false },
		},
		{
			label   : "Ventanera:",
			name    : "installation.window_covering",
			options : { required : false },
		},
		{
			label   : "Cerrajera:",
			name    : "installation.locksmithing",
			options : { required : false },
		},
	];
	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<CreableMsg />
			<div className="row">
				<div className="col-md-4">
					<MapFormInpts  inpts={inptsHydraulic} hookForm={hookForm} title="Hidrulicas" />
				</div>
				<div className="col-md-4">
					<MapFormInpts inpts={inptsSanitary} hookForm={hookForm} title="Sanitarias" />
				</div>
				<div className="col-md-4">
					<MapFormInpts inpts={inptsElectrical} hookForm={hookForm} title="Elctricas" />
				</div>
			</div>
			<hr />
			<div className="row">
				<MapFormInpts className="col-md-3" inpts={inptsMaterials} hookForm={hookForm} />
			</div>
			<div className="row">
				<MapFormInpts className="col-md-6" inpts={inptsFinishings} hookForm={hookForm}  title="Otros Acabados" />
			</div>
			<div className="row">
				<MapFormInpts className="col-md-6" inpts={inptsConstruction} hookForm={hookForm}  title="Obra Negra" />
			</div>
			<div className="row">
				<MapFormInpts className="col-md-4" inpts={inptsCarpentry} hookForm={hookForm}  title="Carpintera" />
			</div>
			<div className="row">
				<MapFormInpts className="col-md-6" inpts={intpsCoating} hookForm={hookForm}  title="Revestimientos y Otros Acabados" />
			</div>
		</Form>
	);
};

export default Installations;
