import { useMemo }                  from "react";
import { useFormContext, useWatch } from "react-hook-form";

import { numberFormat }               from "Helpers";
import { roudnTwoDecimals }           from "Helpers/formulas";
import { CheckboxInpt }               from "Components/Global";
import AppraisalHOCPageFetcher        from "Components/Appraisal/AppraisalHOCPageFetcher";
import { PARAMS, DEFAULT_FETCH_DATA } from "Pages/app/Appraisal/Conclusion/ConclusionConstants";
import { ValuesIncidencesToolTip }    from "./ValuesIncidencesChild";

const ValuesIncidences = ({ appraise = DEFAULT_FETCH_DATA }) => {
	const {
		business_approach : { business_result },
		physical_approach : { physic_result },
		capitalization_approach : { approach_status, capitalization_appoach_result },
	} = appraise;

	const hookForm = useFormContext();

	const { setValue } = hookForm;

	const typeOfValuation = useWatch({ name : "type_of_valuation"});

	const physicApproach         = useWatch({ name : "physic_approach"});
	const businessApproach       = useWatch({ name : "business_approach"});
	const capitalizationApproach = useWatch({ name : "capitalization_approach"});

	const getApproachesSummation = () => {
		const approach_status      = physicApproach ? physic_result : 0;
		const businessResult       = businessApproach ? business_result : 0;
		const capitalizationResult = capitalizationApproach ? capitalization_appoach_result : 0;

		const summation = Number(approach_status) + Number(businessResult) + Number(capitalizationResult);

		return summation;
	};

	const getIncidencePercentage = (approachResult) => {
		const result = (approachResult * 100) / getApproachesSummation();

		return roudnTwoDecimals(result);
	};

	const  getIncidenceProduct = (approachResult, incidencePercentage) => {
		const product = Number(approachResult) * (Number(incidencePercentage) / 100);
		return product >= .01 ? roudnTwoDecimals(product) : product;
	};

	const getIncidence = (approachCheck, apprasialResult, percentage) => approachCheck ? getIncidenceProduct(apprasialResult, percentage) : "-";

	const getPerncentage = (approachCheck, apprasialResult) => approachCheck ? getIncidencePercentage(apprasialResult) : "-";

	const getIncidenceLabel = (incidence) => incidence === "-" ? incidence : numberFormat(incidence);

	const businessPercentage = useMemo( () =>
		getPerncentage(businessApproach, business_result),
	 [ businessApproach, business_result]);

	const physicPercentage = useMemo( () =>
		getPerncentage(physicApproach, physic_result),
		 [ physicApproach, physic_result]);

	const capitalizationPercentage = useMemo( () =>
		getPerncentage(capitalizationApproach, capitalization_appoach_result),
	[ capitalizationApproach, capitalization_appoach_result]);
	//Incidence
	const businessIncidence = useMemo(() =>
		getIncidence(businessApproach, business_result, businessPercentage),
	[businessApproach, business_result, businessPercentage]);

	const physicIncidence = useMemo(() =>
		getIncidence(physicApproach, physic_result, physicPercentage),
	[physicApproach, physic_result, physicPercentage]);

	const capitalizationIncidence = useMemo(() =>
		getIncidence(capitalizationApproach, capitalization_appoach_result, capitalizationPercentage),
	[capitalizationApproach, capitalization_appoach_result, capitalizationPercentage]);

	const result = useMemo(() => {
		const businesseNum = isNaN(businessIncidence) ? 0 : Number(businessIncidence);
		const physicNum = isNaN(physicIncidence) ? 0 : Number(physicIncidence);
		const capitalizationNum = isNaN(capitalizationIncidence) ? 0 : Number(capitalizationIncidence);

		const sum = businesseNum + physicNum + capitalizationNum;

		setValue("comercial_value", sum);

		return numberFormat(roudnTwoDecimals(sum));
	}, [businessIncidence, physicIncidence, capitalizationIncidence]);

	return (
		<>
			{
				Number(typeOfValuation?.value ) === 4 &&
				<div className="row border rounded p-md-2 p-sm-1">
					<ValuesIncidencesToolTip />
					<div className="row col-12">
						<p className="text-md-end  text-sm-center col-md-6">Sumatoria de enfoques aplicados:</p>
						<p className="text-md-start text-sm-center col-md-6 fw-bolder">{numberFormat(getApproachesSummation())}</p>
					</div>
					<div className="row col-md-4 gy-1">
						<h5 className="text-center fw-bolder">Enfoque</h5>
						<CheckboxInpt
							id="sa"
							className="d-flex justify-content-md-start justify-content-center"
							name="business_approach"
							label="Enfoque de Mercado:"
							control={hookForm.control}
							rules={{ required : false }}
						/>
						<CheckboxInpt
							className="d-flex justify-content-md-start justify-content-center"
							name="physic_approach"
							label="Enfoque Fsico:"
							control={hookForm.control}
							rules={{ required : false }}
						/>
						<CheckboxInpt
							disabled={approach_status}
							className="d-flex justify-content-md-start justify-content-center"
							name="capitalization_approach"
							label="Enfoque de Capitalizacin:"
							control={hookForm.control}
							rules={{ required : false }}
						/>
					</div>
					<div className="row col-md-4 gy-1">
						<h5 className="text-center fw-bolder">%</h5>
						<p className="text-center">{ businessPercentage }</p>
						<p className="text-center">{ physicPercentage }</p>
						<p className="text-center">{ capitalizationPercentage }</p>
					</div>
					<div className="row col-md-4 gy-1">
						<h5 className="text-center fw-bolder">Incidencia</h5>
						<p className="text-center">{ getIncidenceLabel(businessIncidence) }</p>
						<p className="text-center">{ getIncidenceLabel(physicIncidence) }</p>
						<p className="text-center">{ getIncidenceLabel(capitalizationIncidence) }</p>
					</div>
					<hr className="my-1" />
					<h4 className="col-md-8 text-md-end    text-center">Resultado:</h4>
					<h4 className="col-md-4 text-md-center text-center text-muted">{ result }</h4>
				</div>
			}
		</>
	);
};

export default AppraisalHOCPageFetcher(ValuesIncidences, PARAMS);
