import { useFormContext, useWatch } from "react-hook-form";

import { numberFormat }               from "Helpers";
import AppraisalHOCPageFetcher        from "Components/Appraisal/AppraisalHOCPageFetcher";
import { PARAMS, DEFAULT_FETCH_DATA } from "Pages/app/Appraisal/Conclusion/ConclusionConstants";
import { useEffect }                  from "react";

const ApproachesResults = ({ appraise = DEFAULT_FETCH_DATA })  => {

	const {
		business_approach : { business_result },
		physical_approach : { physic_result },
		capitalization_approach : { approach_status, capitalization_appoach_result },
	} = appraise;

	const { setValue } = useFormContext();

	const typeOfValuation = useWatch({ name : "type_of_valuation"});

	const getApproachLabel = (approach_result) => {
		const NOT_EXIST = "-";
		if (!approach_result || isNaN(approach_result)) {
			return NOT_EXIST;
		}
		return numberFormat(approach_result);
	};

	const getCpitalization = () => {
		if (approach_status) {
			const NOT_APPLICABLE = "No Aplica";
			return NOT_APPLICABLE;
		}
		return numberFormat(capitalization_appoach_result);
	};

	useEffect(() => {
		const BUSINESS_VAL = 1;
		const PHYSIC_VAL = 2;
		const CAPITALIZATION_VAL = 3;

		if (typeOfValuation?.value === BUSINESS_VAL) {
			setValue("comercial_value", business_result);
		}
		if (typeOfValuation?.value === PHYSIC_VAL) {
			setValue("comercial_value", physic_result);
		}
		if (typeOfValuation?.value === CAPITALIZATION_VAL) {
			setValue("comercial_value", capitalization_appoach_result);
		}
	}, [typeOfValuation]);

	return (
		<div className="row gy-1 mb-1">
			<div className="col-md-4">
				<p>Enfoque de Mercado:</p>
				<p className="fw-bolder">{getApproachLabel(business_result)}</p>
			</div>
			<div className="col-md-4">
				<p>Enfoque Fsico:</p>
				<p className="fw-bolder">{getApproachLabel(physic_result)}</p>
			</div>
			<div className="col-md-4">
				<p>Enfoque de capitalizacin de rentas:</p>
				<p className="fw-bolder">{getApproachLabel(getCpitalization())}</p>
			</div>
		</div>
	);
};

export default AppraisalHOCPageFetcher(ApproachesResults, PARAMS);
