import { UncontrolledTooltip } from "reactstrap";
import { HelpCircle }          from "react-feather";

import { FormCreatable, MapFormInpts } from "Components/Global";
import Form                            from "Components/Appraisal/Form";
import LandOptions                     from "Resources/data/Land";
import CreableMsg                      from "Components/Appraisal/CreableMsg";
const Land = ({
	delegations :{
		hookForm,
		onSubmit,
		isSubmitingData,
	},
}) => {

	const  {
		locationsOptions,
		topographyOptions,
		urbanGroundOptions,
		configurationOptions,
		panoramicFeaturesOptions,
		constructionIntensityOptions,
	} = LandOptions;

	const inpts = [
		{
			type        : "textarea",
			placeholder : "Especificar si el predio esta en esquina, si es cabecera de manzana o manzana completa",
			label       : "Tramo de calle, calles transversales limtrofes y orientacin:",
			name        : "land.street_boundaries",
			options     : { required : false },
		},
		{
			label       : "Caractersticas Panoramicas:",
			name        : "land.panoramic_features",
			inpt_type   : "creatable",
			options     : panoramicFeaturesOptions,
			rules       : { required : false },
			newOptionId : panoramicFeaturesOptions.length + 1,
		},
		{
			label   : "Topografa:",
			name    : "land.topography",
			options : topographyOptions,
			rules   : { required : false },
		},
		{
			label   : "Configuracin:",
			name    : "land.configuration",
			options : configurationOptions,
			rules   : { required : false },
		},
		{
			label   : "Numero de Frentes:",
			name    : "land.number_fronts",
			type    : "number",
			options : { required : false },
		},
		{
			label   : "Ubicacin:",
			name    : "land.location",
			options : locationsOptions,
			rules   : { required : false },
		},
		{
			label   : "Servidumbres y/o Restricciones:",
			name    : "land.restrictions",
			type    : "textarea",
			options : { required : false },
		},
		{
			label   : "Densidad Habitacional:",
			name    : "land.housing_density",
			options : { required : false },
		},

	];

	const inptsSurfaceUse = [
		{
			label     : "Suelo urbano:",
			name      : "land.urban_ground",
			options   : urbanGroundOptions,
			rules     : { required : false },
			inpt_type : "creatable",
		},
		{
			label   : "Nmero de niveles permitidos:",
			name    : "land.num_levels_allowed",
			type    : "number",
			options : { required : false },
		},
		{
			label   : "rea libre minima:",
			name    : "land.free_area",
			type    : "number",
			options : { required : false },
		},
		{
			label   : "rea de vivienda minima:",
			name    : "land.housing_area",
			type    : "number",
			options : { required : false },
		},
	];
	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<CreableMsg />
			<div className="row mt-1">
				<MapFormInpts className="col-md-6" inpts={inpts} hookForm={hookForm}  />
			</div>
			<div className="row">
				<MapFormInpts className="col-md-3" inpts={inptsSurfaceUse} hookForm={hookForm} title="Uso de Suelo" />
			</div>
			<div className="d-flex g-1">
				<FormCreatable
					size="sm"
					className="w-100"
					hookForm={hookForm}
					label="Intensidad de la Construccin:(Creable)"
					name="land.construction_intensity"
					newOptionId={constructionIntensityOptions.length + 1}
					menuPortalTarget={document.body}
					options={constructionIntensityOptions}
					rules={{ required : false }}
				/>
				<HelpCircle id="help-construction_intensity" size={14} />
				<UncontrolledTooltip className="custom-style" placement="bottom" target="help-construction_intensity">
					<p><b>Para los estados que aplica:</b></p>
					<p>Intensidad de construccin = (No. niveles permitidos) * (Porcentaje de reas construidas)</p>
					<p>Porcentaje de reas construidas = 100 - Porcentaje de reas libres</p>
					<p className="mt-1"><b>Para los estados que aplica:</b></p>
					<p>Selecciona una opcin</p>
				</UncontrolledTooltip>
			</div>
		</Form>
	);
};

export default Land;
