// ** React Imports
import { Link } from "react-router-dom";

// ** Custom Components
import Avatar from "@components/avatar";

// ** Utils
// import { isUserLoggedIn } from '@utils'

// ** Third Party Components
import { Power } from "react-feather";

// ** Reactstrap Imports
import { UncontrolledDropdown, DropdownMenu, DropdownToggle, DropdownItem } from "reactstrap";

// ** Default Avatar Image

//Own components
import { dicRoles, bindAll } from "Helpers";
import { connect }           from "react-redux";
import AuthSlice             from "Store/Slices/AuthSlice";

const UserDropdown = ({ userData, authSlice }) => {


	return (
		<UncontrolledDropdown tag='li' className='dropdown-user nav-item'>
			<DropdownToggle href='/' tag='a' className='nav-link dropdown-user-link' onClick={e => e.preventDefault()}>
				<div className='user-nav d-sm-flex d-none'>
					<span className='user-name font-weight-bold'>{ userData?.name } { userData?.role !== "client" ? userData?.lastname : userData?.short_name || ""}</span>
					<span className='user-status text-uppercase'>{userData && dicRoles[userData?.role] }</span>
				</div>
				{
					userData?.image ? (
						<Avatar img={userData?.image} imgHeight='40' imgWidth='40' status='online' />
					) : (
						<Avatar color='light-primary' content={`${userData?.name}` } initials />
					)
				}
			</DropdownToggle>
			<DropdownMenu end>
				<DropdownItem tag={Link} to='#' onClick={authSlice.clearUserData}>
					<Power size={14} className='me-75' />
					<span className='align-middle'>Logout</span>
				</DropdownItem>
			</DropdownMenu>
		</UncontrolledDropdown>
	);
};

const mapStateToProps = ({ _authSlice }) => ({
	userData : _authSlice?.user ?? {},
});

const mapDispatchToProps = bindAll({ AuthSlice : AuthSlice.actions});

export default connect(mapStateToProps, mapDispatchToProps) (UserDropdown);
