import { isValidArray } from "Helpers";
import React            from "react";
import { useWatch }     from "react-hook-form";

const TdOptionalWtch = ({ children, name, handleShowField = () => {} }) => {

	const watcher = useWatch({ name });

	return (
		<>
			{
				handleShowField(watcher) ?
					React.Children.map(children, child => {
						const defaultRHFRules = {
							required         : false,
							shouldUnregister : true,
						};

						const props = {
							...child.props,
							options : isValidArray(child.props?.options) ? child.props?.options : {...defaultRHFRules, ...(child.props?.options ?? {})},
							rules   : isValidArray(child.props?.options) ?  {...defaultRHFRules, ...(child.props?.rules ?? {})} : undefined,
						};

						return React.cloneElement(child, { ...props });
					})
					:
					<td></td>
			}
		</>
	);
};

export default TdOptionalWtch;
