import qs from "qs";

// Import Own Components
import { isValidArray } from "Helpers";
import { api }          from ".";

export const genericApi = api.injectEndpoints({
	endpoints : builder => ({
		getData : builder.query({
			query : ({module, params}) =>  `${module}/?${qs.stringify(params)}`,

			providesTags : (result, error, arg) => arg?.tags ? [...arg.tags] : [`${arg.module}`],
		}),
		addAndUpdate : builder.mutation({
			query({module, data, method = "POST"}) {
				const body = buildFormData(data);

				return {
					url    : module,
					method : method,
					body,
				};
			},

			invalidatesTags : (result, error, arg) => arg?.tags ? [...arg.tags] : [`${arg.module}`],

			extraOptions : { maxRetries : 1 },
		}),
		delete : builder.mutation({
			query({ module, data }) {
				const body = buildFormData(data);
				return {
					url    : module,
					method : "DELETE",
					body,
				};
			},

			invalidatesTags : (result, error, arg) => arg?.tags ? [...arg.tags] : [`${arg.module}`],

			extraOptions : { maxRetries : 0 },
		}),
	}),
});

const buildFormData = (data = null) => {
	const body = new FormData();

	if (!data) return body;

	for (const item in data) {

		if (isValidArray(data[item])) {

			data[item].forEach(element => {

				if (typeof element === "object") {
					body.append(`${item}[]`, JSON.stringify(element));

				} else {
					if (data[item] !== undefined || data[item] !== null) {
						body.append(`${item}[]`, element);
					}
				}
			});
			continue;
		}

		if (typeof data[item] === "object") {

			if (data[item] instanceof File) {
				body.append("file", data[item]);
				// body.append(item, data[item]);
				continue;
			}

			if (data[item] instanceof Date ) {
				body.append(item,  data[item]);
				continue;
			}

			body.append(item, JSON.stringify(data[item]));
			continue;
		}

		if (data[item] !== undefined || data[item] !== null) {
			body.append(item, data[item]);
		}
	}
	return body;
};

