import { Card, FormGroup, Label } from "reactstrap";

import { validateCheckBox } from "Helpers";
import { CheckboxInpt }     from "Components/Global";
import TableUbication       from "../TableUbication";
import TableInvesExtInfo    from "../TableInvesExtInfo";
import TableInvesHousing    from "../TableInvesHousing";
import Avergaes             from "Components/Appraisal/BusinessApproach/Avergaes";

const InvesHousing = ({
	delegations : {
		hookForm,
		housingFields,
	},
}) => {
	return (
		<Card className="p-1">
			<div className="row">
				<h5 className="col-md-6 align-self-center subTitle-text mb-1">Investigacin de inmuebles comparables en Renta</h5>
				<FormGroup className="col-md-6">
					<Label>Tipo de Investigacin:</Label>
					<CheckboxInpt
						name="investigation_type"
						inpt_type="check-box"
						label="Condominio Horizontal, Casas Habitacin, Terrenos, otro."
						falseLabel="Condominio Vertical"
						control={hookForm.control}
						rules={{
							validate : validateCheckBox,
							require  : false,
						}}
					/>
				</FormGroup>
			</div>
			{
				housingFields.map(({ id }, index) => (
					<div className="mb-2" key={id}>
						<h2>Comparable {index + 1}:</h2>
						<TableInvesHousing index={index} />
						<TableInvesExtInfo index={index} />
						<TableUbication index={index} />
					</div>
				))
			}
			<h5 className="subtitle">Promedios:</h5>
			<div className="row">
				<Avergaes
					label="Suelo:"
					name="housing_average_floor"
					columnAverage={{
						length   : 6,
						arrayKey : "inves_housing",
						fieldKey : "housing.floor_m2",
						name     : "housing_average_floor",
					}}
				/>
				 <Avergaes
					label="Construccin:"
					name="housing_average_const"
					columnAverage={{
						length   : 6,
						arrayKey : "inves_housing",
						fieldKey : "housing.const_m2",
						name     : "housing_average_const",
					}}
				/>
				<Avergaes
					label="C.U.S.:"
					name="housing_average_cus"
					columnAverage={{
						length   : 6,
						arrayKey : "inves_housing",
						fieldKey : "housing.cus",
						name     : "housing_average_cus",
					}}
				/>
				<Avergaes
					label="Edad:"
					name="housing_average_age"
					columnAverage={{
						length   : 6,
						arrayKey : "inves_housing",
						fieldKey : "housing.age",
						name     : "housing_average_age",
					}}
				/>
				<Avergaes
					label="Precio Renta Mensual:"
					name="housing_average_rent_price"
					columnAverage={{
						length   : 6,
						arrayKey : "inves_housing",
						fieldKey : "housing.rent_price",
						name     : "housing_average_rent_price",
					}}
				/>
				<Avergaes
					label="Precio Unitario:"
					name="housing_average_unit_price"
					columnAverage={{
						length   : 6,
						arrayKey : "inves_housing",
						fieldKey : "housing.unit_price",
						name     : "housing_average_unit_price",
					}}
				/>
			</div>
		</Card>
	);
};

export default InvesHousing;
