import { useFormContext } from "react-hook-form";

import { MapFormInpts } from "Components/Global";
import apprasialOptions from "Resources/data/Appraisal";
import othersOptions    from "Resources/data/othersInfonavit";

const Others = () => {

	const hookForm = useFormContext();

	const { statusOptions } = apprasialOptions;

	const { conservationsState, densityReportOptions } = othersOptions;

	const inpts = [
		{
			text      : "Accesoria:",
			name      : "accesory",
			inpt_type : "check-box-group",
		},
		{
			label   : "Estado de conservacin:",
			name    : "conservation_state",
			options : conservationsState,
			rules   : { required : false },
		},
		{
			label   : "Otro tipo de instalaciones o accesorios especiales en la vivienda:",
			name    : "special_instalations",
			options : { required : false },
		},
		{
			label   : "Otro tipo de instalaciones en el conjunto habitacional:",
			name    : "instalations_housing",
			options : { required : false },
		},
		{
			label   : "Otro tipo de obras complementarias en el conjunto habitacional:",
			name    : "complementary_construction",
			options : { required : false },
		},
		{
			label   : "Otro tipo de obras complementarias en la vivienda:",
			name    : "complementary_construction_housing",
			options : { required : false },
		},
		{
			label   : "Otro tipo de desalojo de aguas:",
			name    : "other_water_discharge",
			options : { required : false },
		},
		{
			label   : "Frecuencia de la Recoleccion municipal de deshechos (en das):",
			name    : "pickup_waste_frequency",
			options : { required : false },
			type    : "number",
		},
		{
			label   : "Reporte de densidad de acuerdo a:",
			name    : "density_report",
			options : densityReportOptions,
			rules   : { required : false },
		},
		{
			label   : "Resultado de Calidad de Proyecto:",
			name    : "quality_project",
			options : { required : false },
		},
		{
			label   : "Calentador de gas de depsito:",
			name    : "gas_boiler",
			options : statusOptions,
			rules   : { required : false },
		},
	];

	return (
		<div className="row">
			<hr />
			<h4>Otros</h4>
			<MapFormInpts className="col-md-6" inpts={inpts} hookForm={hookForm} />
		</div>
	);
};

export default Others;
