import { useFormContext, useWatch } from "react-hook-form";

import CalcByCapt from "./CalcByCapt";

const CalcByCaptContainer = () => {

	const hookForm = useFormContext();

	const grossMensualRent = useWatch({ name : "gross_mensual_rent" });
	const deduction        = useWatch({ name : "physic_rate.deduction" });
	const incomeNetMonthly = useWatch({ name : "physic_rate.income_net_monthly" });
	const referenceAverage = useWatch({ name : "reference_average" });

	return (
		<CalcByCapt
			delegations={{
				hookForm,
				deduction,
				referenceAverage,
				incomeNetMonthly,
				grossMensualRent,
			}}
		/>
	);
};

export default CalcByCaptContainer;
