import { Button } from "reactstrap";

//Our imports
import { FormSelectGroup, InputMaskNumber, RadioButton } from "Components/Global";
import { useWatch }                                      from "react-hook-form";

const statesAppOptions = [
	{
		label : "rea",
		value : 0,
	},
	{
		label : "Banda",
		value : 1,
	},
	{
		label : "Corredor",
		value : 2,
	},
	{
		label : "Enclave",
		value : 3,
	},
];

const SurfcesCalcs = ({
	delegations : {
		setLot,
		hookForm,
	},
}) => {
	const status = useWatch({ name : "surface_f.status"});
	return (
		<div className="my-1">
			<div className="row my-2">
				<p className="mb-1">Para el Factor Superficie:</p>
				<RadioButton
					className="col-md-3 mb-1"
					id={"surface_f_status_0"}
					label="Superficie del conjunto"
					value="0"
					register={hookForm.register}
					name="surface_f.status"
					checked={status === "0"}
					rules={{ required : false }}
				/>
				<RadioButton
					id={"surface_f_status_1 mb-1"}
					className="col-md-3"
					label="Superficie del Sujeto"
					value="1"
					register={hookForm.register}
					name="surface_f.status"
					checked={status === "1"}
					rules={{ required : false }}
				/>
			</div>
			<div className="row my-1">
				<div className="col-md-6 justify-self-end">
					<InputMaskNumber
						disabled
						bsSize="sm"
						icon={<M2Span />}
						name="surface_f.total_land"
						label="Superficie total del terreno (conjunto):"
						hookForm={hookForm}
						rules={{ required : false }}
					/>
					<div className="row justify-content-center mt-1">
						<InputMaskNumber
							bsSize="sm"
							className="col-md-8"
							icon={<M2Span />}
							name="surface_f.type_lot"
							label="Lote Tipo:"
							hookForm={hookForm}
							rules={{ required : false }}
						/>
						<div className="col-md-4 mt-1">
							<Button
								size="sm"
								type="button"
								onClick={setLot}
								color="flat-primary"
							>
								Usar Lote Valuado
							</Button>
						</div>
					</div>
					<FormSelectGroup
						size="sm"
						className="mt-1"
						label="Para los estados que aplican:"
						name="surface_f.states_app"
						options={statesAppOptions}
						hookForm={hookForm}
						menuPortalTarget={document.body}
						rules={{ required : false }}
					/>
				</div>
				<div className="col-md-6">
					<InputMaskNumber
						disabled
						bsSize="sm"
						name="surface_f.lot"
						label="Superficie correspondiente al lote valuado (sujeto):"
						hookForm={hookForm}
						rules={{ required : false }}
					/>
					<InputMaskNumber
						bsSize="sm"
						className="mt-1"
						icon={<span style={{ fontSize : "9px"}}>($/m<sup>2</sup>)</span>}
						name="surface_f.reference_val"
						label="Valor de Referencia:"
						hookForm={hookForm}
						rules={{ required : false }}
					/>
					<InputMaskNumber
						bsSize="sm"
						className="mt-1"
						name="surface_f.val"
						label="Valor:"
						hookForm={hookForm}
						rules={{ required : false }}
					/>
				</div>
			</div>
		</div>
	);
};

const M2Span = () => <span style={{ fontSize : "9px"}}>m<sup>2</sup></span>;

export default SurfcesCalcs;
