import classNames                                       from "classnames";
import { FormGroup, InputGroup, InputGroupText, Label } from "reactstrap";
import { IMaskMixin }                                   from "react-imask";
import { Controller }                                   from "react-hook-form";

import { deepAccessObj } from "Helpers";
import Input             from "../Input";
import "./styles.scss";

const maskedInptsoptions = {
	mask               : Number,
	thousandsSeparator : ",",
	radix              : ".",
	min                : - Number.MAX_SAFE_INTEGER, //To accept negative numbers
	unmask             : true,
};

export const isInvalidNumber = (val) => {
	const isValidNumber = Number.parseInt(val);
	return isNaN(isValidNumber);
};

const InputNumber = ({
	name,
	hookForm,
	rules={ required : true },
	...rest
}) => {
	return (
		<Controller
			name={name}
			rules={rules}
			control={hookForm.control}
			render={({ field : { value, onChange }, formState : { errors : controllerErrors } }) => (
				<MaskedStyledInput
					type="text"
					name={name}
					errors={controllerErrors}
					value={isInvalidNumber(value) ? "" : value?.toString()}
					onAccept={(value) => onChange({ target : { name, value } })}
					{...maskedInptsoptions}
					{...rest}
				/>
			)}
		/>
	);
};

export const MaskedStyledInput = IMaskMixin(({
	name,
	icon,
	label,
	errors,
	inputRef,
	className,
	inputGroupSize,
	labelClassName,
	inputGroupClassName,
	...props
}) => (
	<FormGroup  id="MaskedStyledInput" className={className}>
		{ label && <Label className={labelClassName}>{label}</Label> }
		<>
			<InputGroup
				size={inputGroupSize}
				className={classNames(inputGroupClassName, {
					invalid : !!deepAccessObj(name, errors),
				})}
			>
				{
					<>
						{ icon && (
							<InputGroupText className={ classNames("", { invalid : !!deepAccessObj(name, errors) }) }>
								{ icon }
							</InputGroupText>
						) }
						<Input
							name={name}
							ref={inputRef}
							invalid={!!deepAccessObj(name, errors)}
							{...props}
						/>
					</>
				}
			</InputGroup>
			{ !!deepAccessObj(name, errors) && (
				<span className="text-error mb-1">{ deepAccessObj(name, errors)?.message }</span>
			)}
		</>
	</FormGroup>
));

export default InputNumber;
