import PropTypes           from "prop-types";
import classNames          from "classnames";
import InputPasswordToggle from "@components/input-password-toggle";

import {
	Label,
	FormGroup,
	InputGroup,
	InputGroupText,
} from "reactstrap";

// Import Own Components
import { deepAccessObj } from "Helpers";
import Input             from "Components/Global/Input";
import "./FormInputGroup.scss";

const FormInputGroup = ({
	type,
	icon,
	name,
	label,
	errors,
	register,
	preContent,
	labelClassName,
	inputClassName,
	inputGroupSize,
	className = "form-group-style",
	inputGroupClassName = "mb-1",
	options = {
		required : {
			value   : true,
			message : "Este campo no puede estar vaco.",
		},
	},
	...rest
}) => {

	return (
		<FormGroup  id="FormInputGroup" className={className}>
			{ label && (
				<Label className={labelClassName}>{label}</Label>
			)}
			<>
				<InputGroup
					size={inputGroupSize}
					className={classNames(inputGroupClassName, {
						invalid : !!deepAccessObj(name, errors),
					})}
				>
					{
						type === "password" ?
							(
								<InputPasswordToggle
									htmlFor={name}
									preContent={icon}
									invalid={!!deepAccessObj(name, errors)}
									{...register( name, { ...options})}
									{...rest}
								/>
							) :
							(
								<>
									{ icon && (
										<InputGroupText
											className={ classNames("", {
												invalid : !!deepAccessObj(name, errors),
											})}
										>
											{ icon }
										</InputGroupText>
									) }
									<Input
										type={type}
										name={name}
										autoComplete="off"
										className={inputClassName}
										invalid={!!deepAccessObj(name, errors)}
										{...register(name, { ...options })}
										{...rest}
									/>
								</>
							)
					}
				</InputGroup>
				{ !!deepAccessObj(name, errors) && (
					<span className="text-error mb-1">{ deepAccessObj(name, errors)?.message }</span>
				)}
			</>
		</FormGroup>
	);};

FormInputGroup.propTypes = {
	label               : PropTypes.string,
	type                : PropTypes.string,
	options             : PropTypes.object,
	errors              : PropTypes.object,
	name                : PropTypes.string,
	labelClassName      : PropTypes.string,
	register            : PropTypes.func,
	className           : PropTypes.string,
	inputGroupSize      : PropTypes.string,
	inputClassName      : PropTypes.string,
	inputGroupClassName : PropTypes.string,
	icon                : PropTypes.oneOfType([PropTypes.element, PropTypes.string]),
};

FormInputGroup.defaultProps = {
	label               : "",
	name                : "",
	icon                : "",
	inputClassName      : "",
	labelClassName      : "",
	type                : "tex",
	errors              : {},
	register            : () => {},
	inputGroupSize      : undefined,
	className           : "form-group-style",
	inputGroupClassName : "input-group-merge mb-1",
	options             : {
		required : {
			value   : true,
			message : "Este campo no puede estar vaco.",
		},
	},
};

export default FormInputGroup;
