import { useFormContext } from "react-hook-form";

import { FormSelectGroup } from "Components/Global";
import Form                from "Components/Appraisal/Form";

const hydraulicsOptions = [
	{
		value : 0,
		label : " Cuenta con equipo y accesorios nuevos",
	},
	{
		value : 1,
		label : "Cuenta con equipo y accesorios en condiciones suficientes",
	},
	{
		value : 2,
		label : " Cuenta con equipo parcial y accesorios usados en condiciones insuficientes",
	},
];

const sanitaryOptions = [
	{
		value : 0,
		label : " Cuenta con muebles sanitarios accesorios nuevos",
	},
	{
		value : 1,
		label : " Cuenta con muebles sanitarios accesorios en condiciones suficientes",
	},
	{
		value : 2,
		label : " Cuenta con muebles sanitarios accesorios incompletos en condiciones insuficientes",
	},
];


const electricOptions = [
	{
		value : 0,
		label : " Cuenta con un sistema de dos circuitos y accesorios nuevos",
	},
	{
		value : 1,
		label : " Cuenta con un sistema de un circuito y accesorios en condiciones suficientes",
	},
	{
		value : 2,
		label : " Cuenta con un sistema de un circuito y accesorios en condiciones insuficientes",
	},
];

const gasOptions = [
	{
		value : 0,
		label : " Cuenta con servicio de gas en condiciones ptimas",
	},
	{
		value : 1,
		label : " Cuenta con red de suministro de gas a base de cilindros portatiles en buenas condiciones",
	},
	{
		value : 2,
		label : "  Cuenta con red de suministro bsico usado en condiciones insuficientes",
	},
];

const Instalations = ({ onSubmit, isSubmitingData }) => {

	const hookForm = useFormContext();

	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<FormSelectGroup
				name="hydraulics"
				label="Hidrulica:"
				hookForm={hookForm}
				options={hydraulicsOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="sanitary"
				label="Sanitaria:"
				hookForm={hookForm}
				options={sanitaryOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="electric"
				label="Elctrica:"
				hookForm={hookForm}
				options={electricOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="gas"
				label="Gas:"
				hookForm={hookForm}
				options={gasOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
		</Form>
	);
};

export default Instalations;
