import React                                   from "react";
import { connect }                             from "react-redux";
import { ModalBody, ModalFooter, ModalHeader } from "reactstrap";

import SubmitBtn            from "Components/Global/SubmitBtn";
import FormAsyncSelectGroup from "Components/Global/FormAsyncSelectGroup";
import "./SelectPeritControllerModal.scss";

const SelectPeritControllerModal = ({
	info,
	delegations : {
		hookForm,
		onSubmit,
		isLoading,
		handleCloseModal,
	},
}) => {
	return (
		<div id="SelectPeritControllerModal">
			<ModalHeader
				toggle={handleCloseModal}
			>
				<p>Asignacin Perito/Controlador</p>
			</ModalHeader>
			<form onSubmit={onSubmit}>
				<ModalBody>
					<p>El Perito se asignar al Avalo con clave: <span className="key">{info?.key}</span></p>
					<FormAsyncSelectGroup
						module="users"
						name="id_perito"
						query={{  role : ["perit"], state : [ info?.state ] }}
						placeholder="Selecciona al Perito"
						label="Perito"
						hookForm={hookForm}
					/>
				</ModalBody>
				<ModalFooter>
					<SubmitBtn
						type="submit"
						loading={isLoading}
						label="Asignar"
					/>
				</ModalFooter>
			</form>
		</div>
	);
};
const mapStateToProps = ({ _modalSlice }) => ({
	info : _modalSlice?.props?.selectPeritControllerModal ?? {},
});

export default connect(mapStateToProps)(SelectPeritControllerModal);
