import { useFormContext } from "react-hook-form";

import { getDMSCoordinates, parseDMSHToDecimals } from "Helpers";
import Coordinates                                from "./Coordinates";

const CoordinatesContainer = ({
	line,
	label,
	checked,
	longOrLatOptions,
	center,
	setCenter,
}) => {
	const hookForm = useFormContext();

	const { setValue, getValues } = hookForm;

	const handleParseDecimalsToDMSH = ({ target }) => {
		const { value } = target;
		const coordinates = getDMSCoordinates(value, line);

		handleSetDMSHValues(coordinates);
	};

	const handleSetDMSHValues = (coordinates) => {
		const [ degrees, minutes, seconds, hemisphereIndex ] = coordinates;
		setValue(`${line}_dmsh.degree`, degrees);
		setValue(`${line}_dmsh.minutes`, minutes);
		setValue(`${line}_dmsh.seconds`, seconds);
		setValue(`${line}_dmsh.hemisphere`, longOrLatOptions[hemisphereIndex]);
	};

	const hanldeParseDMSHToDecimals = (key) => ({ target }) => {
		const { value }   = target;

		const dmshValues = {
			degrees    : getValues(`${line}_dmsh.degree`),
			minutes    : getValues(`${line}_dmsh.minutes`),
			seconds    : getValues(`${line}_dmsh.seconds`),
			hemisphere : getValues(`${line}_dmsh.hemisphere`),
		};

		const dmshValuesMostCurrentVals = { ...dmshValues, [key] : value};

		const decimals = parseDMSHToDecimals(dmshValuesMostCurrentVals);

		setValue(`general_information.${line}`, decimals);
	};

	const validateMaxDegreeVal = () => {
		const maxLatDegreeVal  = 90;
		const maxLongDegreeVal = 180;

		const value = line === "lat" ? maxLatDegreeVal : maxLongDegreeVal;
		const message = `Valor invalido. El valor mximo es ${value}`;

		return { value, message };
	};

	const validateMintAndSec = () => {
		return {
			max : {
				value   : 60,
				message : "El valor mximo es 60",
			},
			min : {
				value   : 0,
				message : "El valor mnimo es 0",
			},
		};
	};
	return (
		<Coordinates
			delegations={{
				label,
				line,
				center,
				setCenter,
				checked,
				longOrLatOptions,
				validateMintAndSec,
				validateMaxDegreeVal,
				hanldeParseDMSHToDecimals,
				handleParseDecimalsToDMSH,
			}}
		 />
	);
};

export default CoordinatesContainer;
