import { useEffect, useState } from "react";

//Our Imports
import { genericApi } from "Store/api/genericApi";

const useFetchSelectOptions = (
	url,
	extraQuery = {},
	labelKey = "name",
	valueKey = "_id",
) => {
	const { data, isLoading } = genericApi.useGetDataQuery({ module : url, paramas : { ...extraQuery } });

	const [options, setOptions] = useState([]);

	const parseData = (data) => data.map(item => ({ label : item[labelKey], value : item[valueKey], ...item }));

	const fetchOptions = async () => {
		let parsedData = [];

		if (!data) return;

		if (data?.docs) {
			parsedData = parseData(data.docs);
		} else if (data?.data) {
			parsedData = parseData(data.data);
		}

		setOptions(parsedData);
	};

	useEffect(() => fetchOptions(), [isLoading]);

	return options;
};

export default useFetchSelectOptions;
