import { bindAll, dayjs, isValidArray } from "Helpers";
import { connect }                      from "react-redux";
import { AppraisalSlice, ModalSlice }   from "Store/Slices";
import AppraisalTable                   from "./AppraisalTable";


const AppraisalTableContainer = ({ modalSliceActions, appraisalActions }) => {

	const togglePaginator = () => (val) => appraisalActions.setPageFilter(val);

	const toggleOpenSelectPeritModal = (id, key, state) => (e) => {
		if (key) {
			e.stopPropagation();
			modalSliceActions.openModal({type : "selectPeritControllerModal", props : { id, key, state }});
		}
	};

	const toggleOpenAppraisalMenuModal = (id, key_sofia) => () => modalSliceActions.openModal({type : "apprasialMenuModal", props : { id, key_sofia }});

	const getIDS = (arr) => {
		if (!isValidArray(arr)) {
			return null;
		}
		const ids = arr.map( ({ value }) => value);
		return ids;
	};

	const getDateFormatted = (date) => {
		if (!date) return undefined;
		const backFormattRequired = dayjs(date).format("YYYY-MM-DD");

		return backFormattRequired;
	};

	const getFilters = (filters) =>  {
		const { date, perits, state, ...restOfFilters } = filters;

		const filtersToSend = {
			state  : getIDS(state),
			perits : getIDS(perits),
			date   : getDateFormatted(date),
			...restOfFilters,
		};
		return filtersToSend;
	};

	return (
		<AppraisalTable
			delegations={{
				getFilters,
				togglePaginator,
				toggleOpenSelectPeritModal,
				toggleOpenAppraisalMenuModal,
			}}
		/>
	);
};

const mapDispatchToProps = bindAll({
	ModalSliceActions : ModalSlice.actions,
	AppraisalActions  : AppraisalSlice.actions,
});

export default connect(null, mapDispatchToProps)(AppraisalTableContainer);
