import { useFieldArray, useFormContext } from "react-hook-form";

import { useColumnAverage } from "Helpers/Hooks";
import TableFactors         from "./TableFactors";
import { getFic }           from "Helpers/formulas";

//This value should come from page 1 section "informacin general del inmueble"
const HOUSING_TYPE = 1; // the value 1 is equal to "Casa Habitacin"

const TableFactorsContainer = () => {

	const hookForm = useFormContext();

	const { fields : homFactorsFields } = useFieldArray({ name : "homologation_factors"});

	const calcFic = (values, name) => {
		const result = HOUSING_TYPE === 1 ?  getFic(...values) : 1;

		hookForm.setValue(name, result);
	};

	useColumnAverage({
		length   : 6,
		arrayKey : "homologation_factors",
		fieldKey : "unit_value",
		name     : "average_homologation_factors",
	});

	return (
		<TableFactors
			delegations={{
				calcFic,
				hookForm,
				homFactorsFields,
			}}
		/>
	);
};

export default TableFactorsContainer;
