import dayjs              from "dayjs";
import { useFormContext } from "react-hook-form";

import { FormFlatPickrGroup, FormInputGroup } from "Components/Global";
import { RHFUploadFile, RHFUploadFiles }      from "Components/Global/DropZoneRHF";
import ConfirmFile                            from "./DocumentationFormChild";
import Form                                   from "../Form";

const DAYS_TO_SUBTRACT = 5;

const RHF_RULES = { required : false };

const DocumentationForm = ({ onSubmit, isSubmitingData }) => {

	const hookForm = useFormContext();

	const getMinDate = () => dayjs().subtract(DAYS_TO_SUBTRACT, "day").format("DD/MM/YYYY");

	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<FormFlatPickrGroup
				label= "Fecha de Visita:"
				name= "visit_date"
				control={hookForm.control}
				errors={hookForm.formState.errors}
				rules={RHF_RULES}
				options={{ minDate : getMinDate() }}
			/>
			<div className="mt-1">
				<div className="row">
					<div className="col-md-11">
						<RHFUploadFile
							name="deed.file"
							label="Escritura (Requerido):"
							rules={RHF_RULES}
						/>
					</div>
					<ConfirmFile className="col-md-1" name="deed" fileName="deed.file" />
				</div>
				<FormInputGroup
					name="deed.description"
					type="textarea"
					label="Descripcin"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ required : false }}
				/>
				<RHFUploadFile
					name="plane.file"
					label="Plano"
					rules={RHF_RULES}
				/>
				<FormInputGroup
					name="plane.description"
					type="textarea"
					label="Descripcin"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ required : false }}
				/>
				<div className="row">
					<div className="col-md-11">
						<RHFUploadFile
							name="property_tax_bill.file"
							label="Boleta Predia (Requerido):"
							rules={RHF_RULES}
						/>
					</div>
					<ConfirmFile className="col-md-1" name="property_tax_bill" fileName="property_tax_bill.file" />
				</div>
				<FormInputGroup
					name="property_tax_bill.description"
					type="textarea"
					label="Descripcin"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ required : false }}
				/>
				<div className="row">
					<div className="col-md-11">
						<RHFUploadFile
							name="water_bill.file"
							label="Boleta de Agua (Requerido):"
							rules={RHF_RULES}
						/>
					</div>
					<ConfirmFile className="col-md-1" name="water_bill" fileName="water_bill.file" />
				</div>
				<FormInputGroup
					name="water_bill.description"
					type="textarea"
					label="Descripcin"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ required : false }}
				/>
				<RHFUploadFiles
					name="other_files"
					maxFiles={10}
					label="Otros Archivos (Mximo 10 archivos)"
					rules={RHF_RULES}
				/>
			</div>
		</Form>
	);
};

export default DocumentationForm;
