import { simpleDivison }  from "Helpers/formulas";
import { useFormContext } from "react-hook-form";
import TableInvesHousing  from "./TableInvesHousing";

const TableInvesHousingContainer = ({ index }) => {

	const hookForm     = useFormContext();
	const { setValue } = hookForm;

	const calcUnitPrice = (vals, name) => {
		const [ price, floorSurface, constructionSurface, houssingClass ] = vals;

		const type = houssingClass?.value;
		const result = type === 3 ? simpleDivison(price, floorSurface) : simpleDivison(price, constructionSurface);

		setValue(name, result);
	};

	return (
		<TableInvesHousing
			delegations={{
				index,
				hookForm,
				calcUnitPrice,
			}}
		 />
	);
};

export default TableInvesHousingContainer;
