import PropTypes           from "prop-types";
import { Button, Spinner } from "reactstrap";

const SubmitBtn = ({
	show,
	label,
	loading,
	className,
	handleOnclick,
}) => {
	return (
		<>
			{
				show && (
					loading ?
						(

							<Button.Ripple
								color="primary"
								className={className}
								disabled={loading}
							>
								<Spinner color="white" size="sm" type="grow" />
								<span> Cargando...</span>
							</Button.Ripple>
						)
						:
						(

							<Button.Ripple
								type="submit"
								color="primary"
								disabled={loading}
								className={className}
								onClick={handleOnclick}
							>
								{label}
							</Button.Ripple>
						)
				)
			}
		</>
	);
};

SubmitBtn.propTypes = {
	show          : PropTypes.bool,
	label         : PropTypes.string.isRequired,
	className     : PropTypes.string,
	loading       : PropTypes.bool,
	handleOnclick : PropTypes.func.isRequired,
};

SubmitBtn.defaultProps = {
	show          : true,
	label         : "",
	loading       : false,
	className     : "w-100 mt-1",
	handleOnclick : () => {},
};
export default SubmitBtn;
