import { Button }  from "reactstrap";
import { X }       from "react-feather";
import Flatpickr   from "react-flatpickr";
import { Spanish } from "flatpickr/dist/l10n/es";
import "@styles/react/libs/flatpickr/flatpickr.scss";

import "./styles.scss";

function FlatPickrClenable({
	date,
	handleOnChange,
	...rest
}) {
	return (
		<div className="flatpickr-clear-btn">
			{
				date &&
				<Button
					size="sm"
					color="flat-secondary"
					className="btn-icon rounded-circle btn-icon-flatpickr"
					onClick={() => handleOnChange(undefined)}
				>
					<X size={12} />
				</Button>
			}
			<Flatpickr
				value={date ? new Date(date) : null}
				onChange={([e]) => handleOnChange(e.toString())}
				className="form-control flatpickr-input intp-flatpick"
				options={{ locale : Spanish, dateFormat : "d/m/Y" }}
				{...rest}
			/>
		</div>
	);
}

export default FlatPickrClenable;
