import { Button, Card, Table } from "reactstrap";

import { productSetOfValues, simpleDivisonForFactors } from "Helpers/formulas";
import { FormInputGroup, HelpToolTip }                 from "Components/Global";
import TdInput                                         from "Components/Appraisal/TdInput";
import TdWatcherHOC                                    from "Components/Appraisal/TdWatcherHOC";
import TdCalcAgeHomologation                           from "Components/Appraisal/TdCalcAgeHomologation";
import { TdCalcFR }                                    from "./TableHomologationChild";

const TableHomologation = ({
	delegations : {
		hookForm,
		calcSurface,
		setFactorValues,
		homologationsField,
	},
}) => {
	return (
		<Card className="p-1">
			<div className="row">
				<FormInputGroup
					disabled
					bsSize="sm"
					className="col-md-4"
					label="CUS Inmueble Sujeto:"
					name="cus_subject"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ required : false}}
				/>
				<FormInputGroup
					disabled
					bsSize="sm"
					className="col-md-4"
					label="Superficie de Construccin Sujeto:"
					name="surface_subject"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ required : false}}
				/>
				<FormInputGroup
					disabled
					bsSize="sm"
					className="col-md-4"
					label="Edad del Sujeto:"
					name="age_subject"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ required : false}}
				/>
			</div>
			<div>
				<Button
					outline
					size="sm"
					type="button"
					color="primary"
					onClick={setFactorValues}
				>
					Llenar Factores con 1
				</Button>
			</div>
			<Table className="mt-2" striped bordered responsive>
				<thead>
					<tr>
						<th rowSpan="2" colSpan="1" scope="colgroup" className="text-center align-middle">Nm.</th>
						<th rowSpan="1" colSpan="11"scope="colgroup" className="text-center align-middle">FACTORES</th>
						<th rowSpan="2" colSpan="1" scope="colgroup" className="text-center align-middle">
							<span>F. Resultante </span>
							<HelpToolTip target="help-table-ubication-fr">
								Factor Resultante = (Multiplicatoria de los factores empleados)
							</HelpToolTip>
						</th>
						<th rowSpan="2" colSpan="1" scope="colgroup" className="text-center align-middle">
							<span>Precio Unitario Neto </span>
							<HelpToolTip target="help-table-ubication-pn">
								Precio Unitario Neto = (Valor Unitario[de la investigacin]) x (Factor Resultante)
							</HelpToolTip>
						</th>
					</tr>
					<tr>
						<th rowSpan="1" colSpan="1" scope="colgroup" className="text-center">
							<span>CUS </span>
							<HelpToolTip target="help-table-ubication-cus">
								Factor CUS = (CUS del comparable) / (CUS del sujeto)
							</HelpToolTip>
						</th>
						<th rowSpan="1" colSpan="1" scope="colgroup" className="text-center">
							<span>Superficie </span>
							<HelpToolTip target="help-table-ubication-surface">
								<p>Factor Superficie = raiz 6ta de (Superficie Construccin Comparable / Superficie Construccin Sujeto)</p>
								<p>Nota: Cuando no hay superficie de suelo</p>
							</HelpToolTip>
						</th>
						<th rowSpan="1" colSpan="1" scope="colgroup" className="text-center">Negoc.</th>
						<th rowSpan="1" colSpan="1" scope="colgroup" className="text-center">Zona</th>
						<th rowSpan="1" colSpan="1" scope="colgroup" className="text-center">Ubicacin	</th>
						<th rowSpan="1" colSpan="1" scope="colgroup" className="text-center">Frente</th>
						<th rowSpan="1" colSpan="1" scope="colgroup" className="text-center">Forma</th>
						<th rowSpan="1" colSpan="1" scope="colgroup" className="text-center">Calidad</th>
						<th rowSpan="1" colSpan="1" scope="colgroup" className="text-center">
							<span>Edad </span>
							<HelpToolTip target="help-table-ubication-age">
								<p><b>Si la edad del sujeto es mayor a la del comparable</b></p>
								<p>Factor Edad = 1 - (Edad del sujeto - Edad del comparable) %</p>
								<p className="mt-1"><b>Si la edad del comparable es mayor a la del sujeto</b></p>
								<p>Factor Edad = 1 + (Edad del comparable - Edad del sujeto) %</p>
							</HelpToolTip>
						</th>
						<th rowSpan="1" colSpan="1" scope="colgroup" className="text-center">Topog.</th>
						<th rowSpan="1" colSpan="1" scope="colgroup" className="text-center">
							<p>Otro</p>
							<FormInputGroup
								bsSize="sm"
								name="other_title"
								register={hookForm.register}
								errors={hookForm.formState.errors}
								options={{ required : false}}
							/>
						</th>
					</tr>
				</thead>
				<tbody>
					{
						homologationsField.map(({ id }, index) => (
							<tr key={id}>
								<td align="center">{index + 1}</td>
								<TdWatcherHOC
									name={`homologation.${index}.cus`}
									deps={[
										`inves_housing.${index}.housing.cus`,
										"cus_subject",
									]}
									formula={simpleDivisonForFactors}
								/>
								<TdWatcherHOC
									name={`homologation.${index}.surface`}
									deps={[
										`inves_housing.${index}.housing.const_m2`,
										"surface_subject",
										`inves_housing.${index}.housing.floor_m2`,
									]}
									callback={calcSurface}
								/>
								<TdInput hookForm={hookForm} name={`homologation.${index}.business`} />
								<TdInput hookForm={hookForm} name={`homologation.${index}.zone`} />
								<TdInput hookForm={hookForm} name={`homologation.${index}.ubication`} />
								<TdInput hookForm={hookForm} name={`homologation.${index}.front`} />
								<TdInput hookForm={hookForm} name={`homologation.${index}.shape`} />
								<TdInput hookForm={hookForm} name={`homologation.${index}.quality`} />
								<TdCalcAgeHomologation
									name={`homologation.${index}.age`}
									compAgeKey={`inves_housing.${index}.housing.age`}
									subjAgeKey={"age_subject"}
								/>
								<TdInput hookForm={hookForm} name={`homologation.${index}.topology`} />
								<TdInput hookForm={hookForm} name={`homologation.${index}.other`} />
								<TdCalcFR
									index={index}
									name={`homologation.${index}.fr`}
								/>
								<TdWatcherHOC
									isArrayArgs={true}
									name={`homologation.${index}.unit_price`}
									deps={[
										`inves_housing.${index}.housing.unit_price`,
										`homologation.${index}.fr`,
									]}
									formula={productSetOfValues}
								/>
							</tr>
						))
					}
					<tr>
						<td colSpan="13" align="end">Promedio:</td>
						<TdInput disabled hookForm={hookForm} name="homologation_average" />
					</tr>
				</tbody>
			</Table>
			<FormInputGroup
				type="textarea"
				label="Conclusiones"
				className="mt-2"
				name="homologation_conslution"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				options={{ required : false }}
			/>
		</Card>
	);
};

export default TableHomologation;
