import { Circle, FileText, Monitor, Users } from "react-feather";
import {
	Analytics,
	Rent,
	Chart,
	Gear,
} from "Resources/icons";

const roles = [
	"administrator",
	"consult",
	"guest",
	"perit",
	"administrator_technician",
];


export default [
	{
		id      : "dashboard",
		title   : "Escritorio",
		icon    : <Monitor size={20} />,
		navLink : "/app/dashboard",
		roles   : [...roles],
	},
	{
		id      : "request",
		title   : "Solicitud",
		icon    : <Analytics size={20} />,
		navLink : "/app/request",
		roles   : [...roles],
	},
	{
		id       : "appraise",
		title    : "Avalos",
		icon     : <Rent size={20} />,
		roles    : [...roles],
		children : [
			{
				id      : "appraises",
				title   : "Registros",
				icon    : <Circle />,
				navLink : "/app/appraise/records",
				roles   : [...roles],
			},
		],
	},
	{
		id      : "documnets",
		title   : "Documentos",
		icon    : <FileText size={20} />,
		navLink : "/app/documnets",
		roles   : ["administrator"],
	},
	{
		id      : "reports",
		title   : "Reportes",
		icon    : <Chart size={20} />,
		navLink : "/app/reports",
		roles   : ["administrator"],
	},
	{
		id      : "users",
		title   : "Usuarios",
		icon    : <Users size={20} />,
		navLink : "/app/users",
		roles   : ["administrator"],
	},
	{
		id      : "users",
		title   : "Perfil",
		icon    : <Users size={20} />,
		navLink : "/app/users_details/:id",
		roles   : roles.filter((role) => role !== "administrator"),
	},
	{
		id      : "preferences",
		title   : "Preferencias",
		icon    : <Gear size={20} />,
		navLink : "/app/preferences",
		roles   : ["administrator"],
	},
];
