import { Table } from "reactstrap";

import { FormInputGroup, HelpToolTip, RadioButton } from "Components/Global";
import TdInput                                      from "Components/Appraisal/TdInput";
import TdWatcherHOC                                 from "Components/Appraisal/TdWatcherHOC";
import { useWatch }                                 from "react-hook-form";
import { useEffect }                                from "react";
import { sumSetOfValues }                           from "Helpers/formulas";

const ROWS = {
	age : {
		title : "EDAD (AOS)",
		data  : ["0 - 5", "5 - 20", "20 - 40", "40 - 50", "50 - 60", "MAS DE 60"],
	},
	life_span : {
		title : "VIDA UTIL REMANENTE.",
		data  : ["MAS DE 60", "50 - 60", "40 - 50", "20 - 40", "5 - 20", "TERMINADA"],
	},
	conservation_state : {
		title : "ESTADO DE CONSERVACION",
		data  : ["NUEVA", "MUY BUENA", "BUENO", "REGULAR", "EN PROYECTO", "RUINOSO"],
	},
	project : {
		title : "PROYECTO",
		data  : ["MUY BUENO", "BUENO", "ADECUADO", "REGULAR", "DEFICIENTE", "MALO"],
	},
	rel_sup : {
		title : "REL. SUP. (TERR/CONST)",
		data  : ["Const > Terr (mayor 3-1)", "Const > Terr (hasta 3-1)", "Const > Terr (hasta 2-1)", "Const = Terr", "Terr > Const (hasta 3-1)", "Terr > Const (mayor 3-1)"],
	},
	use : {
		title : "USO DEL INMUEBLE",
		data  : ["CASA UNIF. EN PROYECTO", "EDIF. PROD. HAB-COM", "DEPTO/CASA CONDO", "OFNA/LOCAL CONDOMINIO", "OFNA/LOCAL INDIV.", "BODEGA/INDUSTRIA"],
	},
	zone_clasification : {
		title : "CLASIFICACION. DE LA ZONA",
		data  : ["LUJO", "1er ORDEN", "2do ORDEN", "3er ORDEN", "PROL. SERV/COM", "PROL. SERV/INCOM"],
	},
};

const TableRateCapt = ({
	delegations : {
		hookForm,
		concepts,
		calcPartialRate,
	},
}) => {

	return (
		<div className="my-1">
			<h5 className="text-center">Tasa de Capitalizacin en Base a Puntuacin</h5>
			<Table striped bordered responsive>
				<thead>
					<tr>
						<th rowSpan="1" className="text-center align-middle">Concepto</th>
						<th rowSpan="1" className="text-center align-middle">6.00%</th>
						<th rowSpan="1" className="text-center align-middle">7.00%</th>
						<th rowSpan="1" className="text-center align-middle">8.00%</th>
						<th rowSpan="1" className="text-center align-middle">9.00%</th>
						<th rowSpan="1" className="text-center align-middle">10.00%</th>
						<th rowSpan="1" className="text-center align-middle">11.00%</th>
					</tr>
				</thead>
				<tbody>
					{
						Object.entries(ROWS).map(([key, { title, data }], index) => (
							<tr key={key + index}>
								<td  align="center">{title}</td>
								{
									data.map((label, nestedIndex) => (
										<td key={`table-reate-cap-${nestedIndex}`} align="center">
											<RadioButton
												id={key + nestedIndex}
												label={label}
												value={nestedIndex}
												isTextBreaked={true}
												register={hookForm.register}
												name={`rate_capitalization.concepts.${key}`}
												checked={Number(concepts?.[key]) === Number(nestedIndex)}
											/>
										</td>
									))
								}
							</tr>
						))
					}
					<tr>
						<td>SUMA CALIFICACION</td>
						<TdInput disabled hookForm={hookForm} name="rate_capitalization.rate_sum_0" />
						<TdInput disabled hookForm={hookForm} name="rate_capitalization.rate_sum_1" />
						<TdInput disabled hookForm={hookForm} name="rate_capitalization.rate_sum_2" />
						<TdInput disabled hookForm={hookForm} name="rate_capitalization.rate_sum_3" />
						<TdInput disabled hookForm={hookForm} name="rate_capitalization.rate_sum_4" />
						<TdInput disabled hookForm={hookForm} name="rate_capitalization.rate_sum_5" />
					</tr>
					<tr>
						<td>CAPITALIZACION</td>
						<td align="center">0.8571</td>
						<td align="center">1.0000</td>
						<td align="center">1.1429</td>
						<td align="center">1.2857</td>
						<td align="center">1.4286</td>
						<td align="center">1.5714</td>
					</tr>
					<tr>
						<td>TASAS PARCIALES	</td>
						<TdWatcherHOC
							name="rate_capitalization.rate_partial_0"
							deps={["rate_capitalization.rate_sum_0"]}
							callback={calcPartialRate(0.8571)}
						/>
						<TdWatcherHOC
							name="rate_capitalization.rate_partial_1"
							deps={["rate_capitalization.rate_sum_1"]}
							callback={calcPartialRate(1)}
						/>
						<TdWatcherHOC
							name="rate_capitalization.rate_partial_2"
							deps={["rate_capitalization.rate_sum_2"]}
							callback={calcPartialRate(1.1429)}
						/>
						<TdWatcherHOC
							name="rate_capitalization.rate_partial_3"
							deps={["rate_capitalization.rate_sum_3"]}
							callback={calcPartialRate(1.2857)}
						/>
						<TdWatcherHOC
							name="rate_capitalization.rate_partial_4"
							deps={["rate_capitalization.rate_sum_4"]}
							callback={calcPartialRate(1.4286)}
						/>
						<TdWatcherHOC
							name="rate_capitalization.rate_partial_5"
							deps={["rate_capitalization.rate_sum_5"]}
							callback={calcPartialRate(1.5714)}
						/>
					</tr>
				</tbody>
			</Table>
			<ResultRate hookForm={hookForm} />
		</div>
	);
};

const ResultRate = ({ hookForm }) => {

	const { setValue } = hookForm;

	const partialRates = useWatch({ name : [
		"rate_capitalization.rate_partial_0",
		"rate_capitalization.rate_partial_1",
		"rate_capitalization.rate_partial_2",
		"rate_capitalization.rate_partial_3",
		"rate_capitalization.rate_partial_4",
		"rate_capitalization.rate_partial_5",
	]});

	useEffect(() => {
		const totalSum = sumSetOfValues(partialRates);
		setValue("result_rate", totalSum);
	}, [partialRates]);

	return (
		<div className="d-flex mt-1 ">
			<FormInputGroup
				disabled
				bsSize="sm"
				icon={<span style={{ fontSize : "10px" }}>%</span>}
				className="col-md-6"
				name="result_rate"
				label="Tasa Resultante:"
				register={hookForm.register}
				errors={hookForm.formState.errors}
			/>
			<HelpToolTip target="result_rate-table">
				Tasa Resultante = Sumatoria de las tasas parciales
			</HelpToolTip>
		</div>
	);
};

export default TableRateCapt;
