import { useParams }                              from "react-router-dom";
import { shallowEqual, useDispatch, useSelector } from "react-redux";
//Our Imports
import { FilesSlice }         from "Store/Slices";
import { genericApi }         from "Store/api/genericApi";
import { deepAccessObj }      from "Helpers";
import { useHandlerMutation } from "Helpers/Hooks";
import isValidArray           from "Helpers/isValidArray";
import toastDispacher         from "Helpers/toastDispacher";

const useSendFileWithDesc = ({ dirtyFields, module }) => {

	const { id : appraiseId } = useParams();

	const dispatch = useDispatch();

	const { _filesSlice : {  filesToRemoved } } = useSelector(state => state, shallowEqual);

	const [ deleteItem, { isLoading : isLoadingDelete } ] = genericApi.useDeleteMutation();

	const [ createFiles, { isLoading : isLoadingMutation } ] = useHandlerMutation({});

	const setDescFile = (promises, { description, input_name }) => {
		if (typeof description !== "string") {
			return;
		}

		if (!deepAccessObj(`${input_name}.description`, dirtyFields)) {
			return;
		}

		const dataToSubmit = {
			input_name,
			description,
		};

		const promise = createFiles({
			method : "PUT",
			tags   : ["apprasial_details", "appraisals"],
			module : `appraisals/${module}/${appraiseId}`,
			data   : dataToSubmit,
		}).unwrap();
		promises.push(promise);
	};

	const setFilePromise = (promises, { description, file, input_name }) => {
		if (!(file instanceof File)) {
			setDescFile(promises, { description, input_name });
			return;
		}

		const dataToSubmit = {
			file,
			input_name,
			description,
		};

		const promise = createFiles({
			method : "PUT",
			tags   : ["apprasial_details", "appraisals"],
			module : `appraisals/${module}/${appraiseId}`,
			data   : dataToSubmit,
		}).unwrap();

		promises.push(promise);
	};

	const handleSubmitFiles = async (files = {}) => {
		try {
			const promises = [];

			const entries = Object.entries(files);

			if (!isValidArray(entries)) {
				return;
			}

			const OTHER_FILES = "other_files";

			for (const [key, value] of entries) {
				if (key === OTHER_FILES) {
					if (!isValidArray(value)) continue;

					value.forEach( (file, index) => {
						const max = value.length - 1;
						if (index === max) {
							setFilePromise(promises, { input_name : key,  file });
							return;
						}
						setFilePromise(promises, { input_name : key,  file });
					});
				}

				setFilePromise(promises, { input_name : key, file : value.file, description : value.description });
			}

			const response = await Promise.allSettled(promises);

			const status = response.map( result => result.value.status).filter( status => status !== 200);

			if (status < 1) {
				toastDispacher.filesCRUD.success();
			}

			return status;
		} catch (error) {
			console.error("[Error on handleSubmitFiles]: ", error);
			return error;
		}
	};

	const splitInptName = (inpt_name = "") => {
		const name = inpt_name.split(".")[0];
		return name;
	};

	const handleRemovedFiles = async () => {
		try {
			if (!isValidArray(filesToRemoved)) {
				return;
			}

			const promises = filesToRemoved.map(file => deleteItem({
				tags   : ["apprasial_details", "appraisals"],
				module : `appraisals/${module}/${appraiseId}/${file._id}`,
				data   : { input_name : splitInptName(file?.inpt_name) },
			}).unwrap());


			const response = await Promise.allSettled(promises);

			const status = response.map( result => result.value.status).filter( status => status !== 200);

			dispatch(FilesSlice.actions.filesRemoved());

			if (status > 0) {
				toastDispacher.filesCRUD.error();
			}

			return status;
		} catch (error) {
			console.error("[Error on handleRemovedFiles]: ", error);
			return error;
		}
	};

	return [
		handleSubmitFiles,
		handleRemovedFiles,
		{ isLoading : isLoadingMutation || isLoadingDelete },
	];
};

export default useSendFileWithDesc;
