import {
	ModalBody,
	ModalFooter,
	ModalHeader,
} from "reactstrap";

//Our Imports
import FormInputGroup from "Components/Global/FormInputGroup";
import SubmitBtn      from "Components/Global/SubmitBtn";

const CaptureRequestModal = ({
	delegations : {
		errors,
		register,
		onSubmit,
		handleCloseModal,
	},
}) => {
	return (
		<div id="CaptureRequestModal">
			<ModalHeader
				toggle={handleCloseModal}
			>
				<p className="text-header-modal">Captura de Solicitud</p>
			</ModalHeader>
			<form onSubmit={onSubmit}>
				<ModalBody>
					<p>Antes de seguir con la captura por favor agrega el RUV y CUV.</p>
					<FormInputGroup
						name="ruv"
						label="RUV"
						register={register}
						errors={errors}
					/>
					<FormInputGroup
						name="cuv"
						label="CUV"
						register={register}
						errors={errors}
					/>
				</ModalBody>
				<ModalFooter>
					<SubmitBtn
						type="submit"
						loading={false}
						label="Cambiar Estado"
						onClick={onSubmit}
					/>
				</ModalFooter>
			</form>
		</div>
	);
};

export default CaptureRequestModal;
