import { useFieldArray, useFormContext } from "react-hook-form";

import { surfaceHomologation } from "Helpers/formulas";
import { useColumnAverage }    from "Helpers/Hooks";
import TableHomologation       from "./TableHomologation";

const TableHomologationContainer = () => {

	const hookForm = useFormContext();

	const { control, setValue } = hookForm;

	const { fields : homologationsField } = useFieldArray({
		control,
		name : "homologation",
	});

	const setFactorValues = () => [...Array(6).keys()].forEach((_, index) => {
		setValue(`homologation.${index}.business`, 1);
		setValue(`homologation.${index}.zone`, 1);
		setValue(`homologation.${index}.ubication`, 1);
		setValue(`homologation.${index}.front`, 1);
		setValue(`homologation.${index}.shape`, 1);
		setValue(`homologation.${index}.quality`, 1);
		setValue(`homologation.${index}.topology`, 1);
	});

	const calcSurface = (vals, name) => {
		const [ construction, subject, floor ] = vals;

		const result = (floor === "" || isNaN(floor)) ? surfaceHomologation(construction, subject) : 1;
		setValue(name, result);
	};

	useColumnAverage({
		length   : 6,
		arrayKey : "homologation",
		fieldKey : "unit_price",
		name     : "homologation_average",
	});

	return (
		<TableHomologation
			delegations={{
				hookForm,
				calcSurface,
				setFactorValues,
				homologationsField,
			}}
		/>
	);
};

export default TableHomologationContainer;
