import React       from "react";
import { Button }  from "reactstrap";
import { connect } from "react-redux";
import { Upload }  from "react-feather";

import { bindAll }    from "Helpers";
import { ModalSlice } from "Store/Slices";

const UploadButton = ({
	modalSliceActions,
}) => {
	return (
		<div className="d-flex justify-content-end">
			<Button
				type="button"
				color="primary"
				className="mt-3"
				onClick={() => modalSliceActions.openModal({ type : "uploadFileModal" })}
			>
				<div className="d-flex align-items-center justify-content-evenly">
					<p className="mx-1">Subir PDF</p>
					<Upload size={16} />
				</div>
			</Button>
		</div>
	);
};

const mapDispatchToProps = bindAll({
	ModalSliceActions : ModalSlice.actions,
});

export default connect(null, mapDispatchToProps)(UploadButton);
