import { useFormContext } from "react-hook-form";

import { MapFormInpts } from "Components/Global";

const Surface = () => {

	const hookForm = useFormContext();

	const inpts = [
		{
			label : "Superficie excedente de terreno (superficie de terreno en fracciones):",
			name  : "surface_land_excedent",
			type  : "number",
			rules : { required : false },
		},
	];

	return (
		<div className="row mt-1">
			<hr />
			<h4>Superficie (Nuevos campos 2019)</h4>
			<MapFormInpts className="col-md-6" inpts={inpts} hookForm={hookForm} />
		</div>
	);
};

export default Surface;
