import React             from "react";
import { Button, Table } from "reactstrap";
import { Trash, X }      from "react-feather";

import { FormInputGroup, HelpToolTip } from "Components/Global";
import TdInput                         from "Components/Appraisal/TdInput";
import surfaceHelpImg                  from "Resources/images/FactorSuperfice.png";
import TdWatcherHOC                    from "Components/Appraisal/TdWatcherHOC";
import { getFResult }                  from "Helpers/formulas";


const TableLand = ({
	delegations : {
		hookForm,
		removeRow,
		landFields,
		landFraction,
		appendNewRow,
		setUnitValue,
		handleUndivedLimit,
		calcLandPartialValue,
		handleSetFactorValues,
	},
}) => {
	return (
		<div className="mb-3">
			<h5>Terreno</h5>
			<FormInputGroup
				disabled
				bsSize="sm"
				className="col-md-4"
				icon={<span style={{ fontSize : "9px"}}>m<sup>2</sup></span>}
				name="land_total_surface"
				label="Superficie Total del Conjunto:"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				options={{ required : false }}
			/>
			<Table striped bordered responsive>
				<thead>
					<tr>
						<th rowSpan="2" className="text-center align-middle">Nm.</th>
						<th rowSpan="2" className="text-center align-middle">Fraccin de terreno</th>
						<th rowSpan="2" className="text-center align-middle">
							<span>Valor Unitario Neto </span>
							<HelpToolTip target="help-table-land-unit-val-net">
								<p>Valor Unitario Neto = Valor Unitario Aplicado al Suelo </p>
							</HelpToolTip>
						</th>
						<th rowSpan="2" className="text-center align-middle">rea m<sup>2</sup></th>
						<th rowSpan="1" className="text-center align-middle" colSpan="7" scope="colgroup">
							<span className="mx-1">Factores de eficiencia</span>
							<Button
								outline
								size="sm"
								type="button"
								color="primary"
								onClick={handleSetFactorValues}
							>
								Llenar con 1
							</Button>
						</th>
						<th rowSpan="2" className="text-center align-middle">% Indiviso</th>
						<th rowSpan="2" className="text-center align-middle">
							<span>Valor Parcial del terreno</span>
							<HelpToolTip target="help-table-land-vpt">
								<p>Valor Parcial del Terreno = (Valor Unitario Neto) x (Area) x (Indiviso) x (Factor de eficiencia Resultante)</p>
							</HelpToolTip>
						</th>
						<th rowSpan="2" className="text-center align-middle"><Trash size={14} /></th>
					</tr>
					<tr>
						<th className="text-center" scope="col">Zone</th>
						<th className="text-center" scope="col">Ubic.</th>
						<th className="text-center" scope="col">Frente</th>
						<th className="text-center" scope="col">Forma</th>
						<th className="text-center" scope="col">
							<span>Superficie </span>
							<HelpToolTip
								placement="top"
								target="help-table-land-surface"
								className="custom-style-tooltip-fit-content"
							>
								<img src={surfaceHelpImg} />
							</HelpToolTip>
						</th>
						<th className="text-center" scope="col">Otro</th>
						<th className="text-center" scope="col">
							<span>F Re</span>
							<HelpToolTip target="help-table-land-f-re">
								<p>Factor Resultante = (Multiplicatoria de los factores empleados)</p>
							</HelpToolTip>
						</th>
					</tr>
				</thead>
				<tbody>
					{
						landFields.map( (landField, index) => (
							<tr key={landField.id}>
								<td className="text-center">{index + 1}</td>
								<TdInput hookForm={hookForm} name={`land.${index}.land_fraction`} options={landFraction} />
								<TdWatcherHOC
									name={`land.${index}.net_unit_value`}
									deps={["homologation_unit_val"]}
									callback={setUnitValue}
								/>
								<TdInput hookForm={hookForm} name={`land.${index}.area_m2`} />
								<TdInput hookForm={hookForm} name={`land.${index}.zone`} />
								<TdInput hookForm={hookForm} name={`land.${index}.ubic`} />
								<TdInput hookForm={hookForm} name={`land.${index}.front`} />
								<TdInput hookForm={hookForm} name={`land.${index}.shape`} />
								<TdInput hookForm={hookForm} name={`land.${index}.surface`} disabled />
								<TdInput hookForm={hookForm} name={`land.${index}.other`} />
								<TdWatcherHOC
									isArrayArgs={true}
									name={`land.${index}.f_re`}
									deps={[
										`land.${index}.zone`,
										`land.${index}.ubic`,
										`land.${index}.front`,
										`land.${index}.shape`,
										`land.${index}.surface`,
										`land.${index}.other`,
									]}
									formula={getFResult}
								/>
								<TdWatcherHOC
									disabled={false}
									hookForm={hookForm}
									name={`land.${index}.undivided`}
									callback={handleUndivedLimit}
								/>
								<TdWatcherHOC
									isArrayArgs={true}
									name={`land.${index}.land_partial_value`}
									deps={[
										`land.${index}.net_unit_value`,
										`land.${index}.area_m2`,
										`land.${index}.undivided`,
										`land.${index}.f_re`,
									]}
									callback={calcLandPartialValue}
								/>
								<td className="text-center align-middle">
									<Button.Ripple
										color="flat-danger"
										onClick={removeRow(index)}
										className="btn-icon rounded-circle"
									>
										<X size={14} className="color-danger" />
									</Button.Ripple>
								</td>
							</tr>
						))
					}
					<tr>
						<td colSpan="12" align="end">Valor Total de Terreno:</td>
						<TdInput hookForm={hookForm} name={"land_partial_total_val"} disabled />
						<td colSpan="1"></td>
					</tr>
				</tbody>
			</Table>
			<Button
				size="sm"
				type="button"
				color="primary"
				className="mt-1"
				onClick={appendNewRow}
			>
				Agregar Fraccin
			</Button>
		</div>
	);
};

export default TableLand;
