import { useFormContext } from "react-hook-form";

import { FormSelectGroup } from "Components/Global";
import Form                from "Components/Appraisal/Form";

const waterPotableOptions = [
	{
		value : 0,
		label : " Cuenta con Servicio de Agua Potable y toma domiciliaria en ptimas condiciones",
	},
	{
		value : 1,
		label : "  No cuenta con Servicio de Agua Potable y toma domiciliaria",
	},
];

const drainageOptions = [
	{
		value : 0,
		label : " Cuenta con servicio de drenaje y alcantarillado en ptimas condiciones de funcionamiento",
	},
	{
		value : 1,
		label : " Cuenta con fosa sptica en ptimas condiciones de funcionamiento",
	},
	{
		value : 2,
		label : " No cuenta con servicio de drenaje o fosa sptica",
	},
];

const electricEnergy = [
	{
		value : 0,
		label : "Cuenta con servicio de energa elctrica y acometida en ptimas condiciones",
	},
	{
		value : 1,
		label : " No cuenta con el servicio en su entorno",
	},
];

const lightingPublic = [
	{
		value : 0,
		label : " Cuenta con servicio de alumbrado en la vialidad de acceso en ptimas condiciones",
	},
];

const Infrastructure = ({ onSubmit, isSubmitingData }) => {
	const hookForm = useFormContext();
	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<FormSelectGroup
				name="waster_potable"
				label="Agua Potable:"
				hookForm={hookForm}
				options={waterPotableOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="drainage_road"
				label="Drenaje:"
				hookForm={hookForm}
				options={drainageOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="electric_energy"
				label="Energa Elctrica:"
				hookForm={hookForm}
				options={electricEnergy}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="lighting_public"
				label="Alumbrado Publico:"
				hookForm={hookForm}
				options={lightingPublic}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
		</Form>
	);
};

export default Infrastructure;
