import {
	Divider,
	HeaderPage,
	CalcByCapt,
	InvesHousing,
	RentEstimate,
	StatusApproach,
	AnualDeduction,
	IncomeCapRate,
	ApproachForm,
	TableHomologation,
	ResultApproach,
} from "Components/Appraisal";

import { capitalizationResult } from "Helpers/formulas";

const CapitalizationApproach = ({
	delegations : {
		hookForm,
		onSubmit,
		ResultRef,
		calcCaptRef,
		approachStatus,
		invesHousingRef,
		rentEstimateRef,
		incomeCapRentRef,
		anualDeductionRef,
		tableHomologationRef,
		isLoading,
	},
}) => {
	const { isDirty } = hookForm.formState;

	const sections =[
		{
			ref   : invesHousingRef,
			title : "Investigacin de inmuebles comparables",
		},
		{
			ref   : tableHomologationRef,
			title : "Tabla de Homologacin",
		},
		{
			ref   : rentEstimateRef,
			title : "Estimacin de Renta",
		},
		{
			ref   : anualDeductionRef,
			title : "Deducciones anuales estimadas",
		},
		{
			ref   : incomeCapRentRef,
			title : "Obtencin de la tasa de capitalizacin",
		},
		{
			ref   : calcCaptRef,
			title : "Clculo por capitalizacin de rentas",
		},
		{
			ref   : ResultRef,
			title : "Resultado",
		},
	];

	return (
		<div className="section-table">
			<HeaderPage
				isDirty={isDirty}
				currentIndex={[2, 2]}
				section="body"
				onSubmit={onSubmit}
				isSubmitingData={isLoading}
			/>
			<ApproachForm
				id="approach-capitalization"
				hookForm={hookForm}
				sections={sections}
				onSubmit={onSubmit}
				showIndex={!approachStatus}
				sectionTitle="Enfoque de Capitalizacin de Rentas"
			>
				<StatusApproach approachStatus={approachStatus} />
				{
					!approachStatus &&
					<>
						<Divider innerRef={invesHousingRef} title="Investigacin de inmuebles comparables" />
						<InvesHousing />
						<Divider innerRef={tableHomologationRef} title="Tabla de Homologacin" />
						<TableHomologation />
						<Divider innerRef={rentEstimateRef} title="Estimacin de Renta" />
						<RentEstimate />
						<Divider innerRef={anualDeductionRef} title="Deducciones anuales estimadas" />
						<AnualDeduction />
						<Divider innerRef={incomeCapRentRef} title="Obtencin de la tasa de capitalizacin" />
						<IncomeCapRate />
						<Divider innerRef={calcCaptRef} title="Clculo por capitalizacin de rentas" />
						<CalcByCapt />
						<Divider innerRef={ResultRef} title="Resultado" />
						<ResultApproach
							name="capitalization-result"
							fromula={capitalizationResult}
							title="Resultado del enfoque de capitalizacin:"
							deps={["physic_rate.income_net_monthly", "reference_average"]}
							tooltip="Valor por Capitalizacin = (Renta neta mensual * 12) / (Tasa de Capitalizacin)"
						/>
					</>
				}
			</ApproachForm>
		</div>
	);
};

export default CapitalizationApproach;
