import { useFieldArray, useFormContext } from "react-hook-form";
import FinishesRepetear                  from "./FinishesRepetear";


const ADD_FIELD = {
	name   : "",
	floors : "",
	walls  : "",
	roof   : "",
};

const FinishesRepetearContainer = () => {
	const hookForm = useFormContext();

	const { fields, append, remove } = useFieldArray({
		control : hookForm.control,
		name    : "finish,architectural_finishes",
	});


	const handleAdd = () => append({...ADD_FIELD});

	const removeItem = (index) => () => remove(index);

	return (
		<FinishesRepetear
			delegations={{
				fields,
				hookForm,
				handleAdd,
				removeItem,
			}}
		/>
	);
};

export default FinishesRepetearContainer;
