import { useEffect, useState } from "react";
import { connect }             from "react-redux";
import { useDropzone }         from "react-dropzone";

//Our imports
import { bindAll, isValidArray } from "Helpers";
import { FilesSlice }            from "Store/Slices";
import toaster                   from "Components/Global/Toaster";
import pdfImg                    from "Resources/images/pdf-img.png";
import wordImg                   from "Resources/images/word-img.png";
import iconImg                   from "Resources/images/icon-img.png";
import excelImg                  from "Resources/images/excel-img.png";
import DropeZoneFile             from "./DropeZoneFile";


const DropeZoneFileContainer = ({
	errors,
	fileActions,
	maxFiles = 1,
	label,
	defaultFile = null,
	acceptFiles = { pdf : true },
	setValue = () => {},
	...rest
}) => {


	const [name, setName]   = useState();
	const [file, setFile]   = useState(null);
	const [files, setFiles] = useState([]);

	const image    = acceptFiles?.image && "image/*";
	const pdf      = acceptFiles?.pdf   && "application/pdf";
	const xlsx     = acceptFiles?.xlsx  && "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet";
	const docxType = acceptFiles?.docs  && "application/vnd.openxmlformats-officedocument.wordprocessingml.document";


	const {
		inputRef,
		getRootProps,
		acceptedFiles,
		getInputProps,
		fileRejections,
	} = useDropzone({
		maxFiles,
		accept : [
			pdf,
			xlsx,
			image,
			docxType,
		],
	});

	const handleSetFile = () => {

		if (isValidArray(fileRejections)) {
			toaster.error({
				title : "Ha ocurrido un error.",
				body  : `Ocurri un error al intentar subir los archivos, 
					favor de verificar que la cantidad seleccionada de archivos no supere el limite.`,
			});
		}

		if (!isValidArray(acceptedFiles)) return;

		if (maxFiles > 1) {

			if (files?.length + acceptedFiles.length < maxFiles) {
				const arrayFiles = [...files, ...acceptedFiles];

				setFiles(arrayFiles);
				setValue(arrayFiles);
			} else {
				toaster.error({
					title : "Limite Superado",
					body  : `Superaste el limite permitido para subir archivos, favor de subir un mximo de ${maxFiles} archivos.`,
				});
			}
			return;
		}

		const file = acceptedFiles[0];
		setValue(file);
		setFile(file);
		setName(file.path);
	};


	const handleGetType = (key) => {
		if (typeof(key) !== "string") return;

		if (key?.includes(".docx")) {
			return wordImg;
		} else if (key?.includes(".pdf")) {
			return pdfImg;
		} else if (key?.includes(".xlsx")) {
			return excelImg;
		} else {
			return iconImg;
		}
	};


	const initFile = () => {

		if (isValidArray(defaultFile) && !isValidArray(acceptedFiles)) {
			const arrayFiles  = [];
			defaultFile.forEach(file => {
				if (file && file instanceof File) {
					arrayFiles.push(URL.createObjectURL(file));
				} else if (file) {
					arrayFiles.push(file);
				}
			});
			setFiles(arrayFiles);
			return;
		}

		if (defaultFile && defaultFile instanceof File) {
			// setFile(URL.createObjectURL(defaultFile));
			setFile(defaultFile);
			setName(defaultFile.name);


		} else if (defaultFile) {
			setFile(defaultFile);
			setName(defaultFile?.name ?? "");

		} else  {
			setFile(null);
		}
	};

	const removeAll = () => {

		setName("");
		setValue(null);

		acceptedFiles.length = 0;
		inputRef.current.value = "";
		acceptedFiles.splice(0, acceptedFiles.length);

		if (maxFiles > 1) {
			const filesToRemoved = files.filter(file => !(file instanceof File));

			fileActions.setFilesToRemove(filesToRemoved);
		}
		setFiles([]);
		setFile(null);
	};


	const handleRemoveItem = (index) => {
		const newFiles = [...files];

		const fileRemoved = newFiles.splice(index, 1)?.[0];

		setFiles(newFiles);
		setValue(newFiles);

		if (fileRemoved) return;
		setFileToRemove(fileRemoved);
	};

	const setFileToRemove = (file) => {
		if (!(file instanceof File)) {
			fileActions.setFileToRemove(file);
		}
	};

	useEffect(() => initFile(), [defaultFile]);

	useEffect(() => {
		handleSetFile();

		return () =>{
			URL.revokeObjectURL(file);

			files.forEach(file => {
				if (file && file instanceof File) {
					URL.revokeObjectURL(file);
				}
			});
		};
	}, [acceptedFiles]);

	return (
		<DropeZoneFile
			delegations={{
				file,
				label,
				files,
				name,
				errors,
				maxFiles,
				removeAll,
				acceptFiles,
				getRootProps,
				handleGetType,
				getInputProps,
				setFileToRemove,
				handleRemoveItem,
				...rest,
			}}
		/>
	);
};

const mapDispatchToProps = bindAll({ FileActions : FilesSlice.actions });

export default connect(null, mapDispatchToProps)(DropeZoneFileContainer);
