import classNames         from "classnames";
import { isValidArray }   from "Helpers";
import { UploadCloud, X } from "react-feather";
import { Button }         from "reactstrap";

//OurImports
import "./DropeZoneFile.scss";

const DropeZoneFile = ({
	delegations : {
		file,
		files,
		name,
		label,
		width,
		height,
		errors,
		maxFiles,
		className,
		removeAll,
		getRootProps,
		acceptFiles,
		handleGetType,
		getInputProps,
		setFileToRemove,
		handleRemoveItem,
		customStylesText,
		customStylesLabel,
		customStylesDropZone,
	},
}) => {
	return (
		<div
			id="DropeZoneFile"
			className={className}
		>
			{
				label &&
				<p className={classNames("", {
					label               : !customStylesLabel,
					[customStylesLabel] : customStylesLabel,
				})}>
					{label}
				</p>
			}
			<div
				className={classNames("file-container", {
					"file-uploaded"            : (file || isValidArray(files)) && !customStylesDropZone,
					"content-format-not-file " : (maxFiles > 1 || !file),
					"invalid"                  : errors,
					[customStylesDropZone]     : customStylesDropZone,
				})}
				style={{
					width  : `${width}px`,
					height : `${height}px`,
				}}
				{...getRootProps()}
			>
				<input  {...getInputProps()} />
				<RemoveBtn
					render={!!(file || isValidArray(files))}
					onClick={e => {
						e.stopPropagation();
						removeAll(null);
						setFileToRemove(file);
					}}
				/>
				{
					maxFiles > 1 ?
						(
							isValidArray(files)  ?
								<div className="content-cards">
									{
										files.map((file, index) => (
											<div key={file.path + index} className="files-wrapper">
												<div className="card">
													<div className="card-body">
														<div className="img-text">
															<img className="" src={handleGetType(file.path)} width="23" height="25" />
															<p className="text-name">{file.path}</p>
														</div>
														<RemoveBtn
															render={!!file}
															onClick={e => {
																e.stopPropagation();
																handleRemoveItem(index);
															}}
															className="remove-itme-button"
														/>
													</div>
												</div>
											</div>
										))
									}
								</div>
								:
								<>
									<UploadCloud className={classNames("", { "invalid" : errors })} size={16} />
									<span className={classNames("text customStylesText", {
										[customStylesText] : customStylesText,
									})}>
										Arrastra un mximo de {maxFiles} archivos adicionales.
									</span>
								</>
						)
						:
						(

							file ?
								<div className="card">
									<div className="card-body">
										<img className="img" src={handleGetType(file?.path ? file.path : name)} width="42" height="50" />
										<p className="text-name">{name}</p>
									</div>
								</div>
								:
								<>
									<UploadCloud size={16} />
									<span className={classNames("text customStylesText", {
										[customStylesText] : customStylesText,
										"invalid"          : errors,
									})}>
										Arrastra o da click para agregar un archivo
										{acceptFiles?.["pdf"] ? " (PDF)" : ""}
									</span>
								</>
						)


				}
			</div>
		</div>
	);
};

const RemoveBtn = ({render, className = "removeImage-button", ...rest}) => (
	<Button.Ripple
		type="button"
		color="danger"
		className={classNames(className, {
			show : render,
		})}
		{...rest}
	>
		<X size={12} />
	</Button.Ripple>
);

export default DropeZoneFile;
