import { useDispatch } from "react-redux";

import { FilesSlice } from "Store/Slices";

const useSetURLFileToRemove = () => {
	const dispatch = useDispatch();

	const setURLFileToRemove = (file) => {
		if (!(file instanceof File)) {
			dispatch(FilesSlice.actions.setFileToRemove(file));
		}
	};

	return setURLFileToRemove;
};

export default useSetURLFileToRemove;
