import { Button } from "reactstrap";

import { setOnlyPositiveNumber } from "Helpers";
import { MapFormInpts }          from "Components/Global";

const inpt = [
	{
		label      : "Zona:",
		name       : "efficiency_f.zone",
		type       : "number",
		onKeyPress : setOnlyPositiveNumber,
		options    : { required : false },
	},
	{
		label      : "Ubicacin:",
		name       : "efficiency_f.ubication",
		type       : "number",
		onKeyPress : setOnlyPositiveNumber,
		options    : { required : false },
	},
	{
		label      : "Frente:",
		name       : "efficiency_f.front",
		type       : "number",
		onKeyPress : setOnlyPositiveNumber,
		options    : { required : false },
	},
	{
		label      : "Forma:",
		name       : "efficiency_f.shape",
		type       : "number",
		onKeyPress : setOnlyPositiveNumber,
		options    : { required : false },
	},
	{
		label    : "Superficie:",
		name     : "efficiency_f.surface",
		disabled : true,
		options  : { required : false },
	},
	{
		label      : "Otro:",
		name       : "efficiency_f.other",
		type       : "number",
		onKeyPress : setOnlyPositiveNumber,
		options    : { required : false },
	},
	{
		label    : "Resultante:",
		name     : "efficiency_f.result",
		disabled : true,
		options  : { required : false },
	},
];

const ApprasialEffFac = ({
	delegations : {
		hookForm,
		handleSetFactorValues,
	},
}) => {

	return (
		<>
			<h5 className="mb-1">Factores de Eficiencia en Inmueble Valuado</h5>
			<Button
				outline
				className="mb-1"
				size="sm"
				type="button"
				color="primary"
				onClick={handleSetFactorValues}
			>
				Llenar con 1
			</Button>
			<div className="row">
				<MapFormInpts inpts={inpt} hookForm={hookForm} className="col-md-4" />
			</div>
		</>
	);
};

export default ApprasialEffFac;
