import qs from "qs";

// Import Own Components
import {
	fetchClient,
	createServiceSlice,
	isValidArray,
} from "Helpers";
import * as AllSlices from "Store/Slices";

const GenericServices = createServiceSlice({
	getAll : async ({
		query,
		module,
		callback,
		actionType,
	}, { dispatch, getState }) => {
		try {
			const { _authSlice : { token } } = getState();

			const response = await fetchClient(`/${module}?${qs.stringify(query)}`, { token });

			if (!response.ok) {
				console.error(`[GenericServices.getAll ${module}]`, response.status);
				return { status : response.status, data : {} };
			}

			const data = await response.json();

			handleDispatchAction({actionType, module, dispatch, data});

			if (typeof(callback) === "function") {
				callback({ response, data });
			}

			return { status : response.status, data };

		} catch (err) {
			console.error("[GenericServices.getAll] ", err);
			return 500;
		}
	},
	get : async ({
		query,
		module,
		callback,
		actionType,
	}, { dispatch, getState }) => {
		try {
			const { _authSlice : { token } } = getState();

			const response = await fetchClient(`/${module}?${qs.stringify(query)}`, { token });

			if (!response.ok) {
				console.error(`[GenericServices.getAll ${module}]`, response.status);
				return { status : response.status, data : {} };
			}

			const data = await response.json();

			handleDispatchAction({actionType, module, dispatch, data});

			if (typeof(callback) === "function") {
				callback({ response, data });
			}

			return { status : response.status, data };

		} catch (err) {
			console.error("[GenericServices.get] ", err);
			return 500;
		}
	},
	add : async ({
		module,
		callback,
		actionType,
		dataToSubmit = undefined,
	}, { dispatch, getState }) => {
		try {
			const { _authSlice : { token } } = getState();

			const body = buildFormData(dataToSubmit);

			const response = await fetchClient(`/${module}`, { token, method : "POST", body } );

			const data = await response.json();


			if (!response.ok) {
				console.error(`[GenericServices.add ${module}]`, response.status);

				if (typeof(callback) === "function") {
					callback({ response, data });
				}

				return { status : response.status, data };
			}


			if (typeof(callback) === "function") {
				callback({ response, data });
			}

			handleDispatchAction({actionType, module, dispatch, data});

			return { status : response.status, data };

		} catch (err) {
			if (typeof(callback) === "function") {
				callback({ response : {}, data : {} });
			}
			console.error("[GenericServices.add] ", err);
			return 500;
		}
	},
	update : async ({
		module,
		callback,
		actionType,
		dataToSubmit = undefined,
	}, { dispatch, getState }) => {
		try {
			const { _authSlice : { token } } = getState();

			const body = buildFormData(dataToSubmit);

			const response = await fetchClient(`/${module}`, { token, method : "PUT", body } );

			const data = await response.json();

			if (typeof(callback) === "function") {
				callback({ response, data });
			}

			if (!response.ok) {
				console.error(`[GenericServices.add ${module}]`, response.status);
				return { status : response.status, data };
			}

			handleDispatchAction({actionType, module, dispatch, data});

			return { status : response.status, data };

		} catch (err) {
			console.error("[GenericServices.add] ", err);
			return 500;
		}
	},
	remove : async ({
		module,
		callback,
		actionType,
	}, { dispatch, getState }) => {
		try {
			const { _authSlice : { token } } = getState();

			const response = await fetchClient(`/${module}`, { token, method : "DELETE" } );

			if (!response.ok) {
				console.error(`[GenericServices.remove ${module}]`, response.status);

				if (typeof(callback) === "function") {
					callback({ response, data : {} });
				}

				return { status : response.status, data : {} };
			}

			const data = await response.json();

			handleDispatchAction({actionType, module, dispatch, data});

			if (typeof(callback) === "function") {
				callback({ response, data });
			}

			return { status : response.status, data };

		} catch (err) {
			console.error("[GenericServices.add] ", err);
			return 500;
		}
	},
	patch : async ({
		module,
		callback,
		actionType,
	}, { dispatch, getState }) => {
		try {
			const { _authSlice : { token } } = getState();

			const response = await fetchClient(`/${module}`, { token, method : "PATCH" } );

			if (!response.ok) {
				console.error(`[GenericServices.remove ${module}]`, response.status);
				return { status : response.status, data : {} };
			}

			const data = await response.json();

			handleDispatchAction({actionType, module, dispatch, data});

			if (typeof(callback) === "function") {
				callback({ response, data });
			}

			return { status : response.status, data };

		} catch (err) {
			console.error("[GenericServices.add] ", err);
			return 500;
		}
	},
});

const buildFormData = (data = null) => {
	const body = new FormData();

	if (!data) return body;

	for (const item in data) {

		if (isValidArray(data[item])) {

			data[item].forEach(element => {

				if (typeof element === "object") {
					body.append(`${item}[]`, JSON.stringify(element));

					if (element?.image) body.delete("image");

				} else {
					body.append(`${item}[]`, element);
				}

			});
		} else if (typeof data[item] === "object") {

			body.append(item, JSON.stringify(data[item]));

			if (data[item] instanceof File) body.append("file", data[item]);

			if (data[item] instanceof Date ) {
				body.delete(item);
				body.append(item,  data[item]);
			}
		} else {
			body.append(item, data[item]);
		}
	}

	return body;
};

const handleDispatchAction = ({actionType, module, dispatch, data}) => {
	if (!actionType) return;

	if (typeof(actionType) === "string") {
		const sliceModule = module.split("/")[0];

		let capitalizeSliceModule = sliceModule.charAt(0).toUpperCase() + sliceModule.slice(1);

		if (capitalizeSliceModule.includes("-")) {
			capitalizeSliceModule = capitalizeSliceModule.replace("-", "");
		}

		const sliceType = `${capitalizeSliceModule}Slice`;

		if (AllSlices?.[sliceType]) {
			dispatch(AllSlices[sliceType].actions[actionType](data));
			return;
		}
		throw `[GenericServices/dispatchAction] The ${sliceType} is not defined on "Slices"`;
	}
	throw "[GenericServices/dispatchAction] invalid actionType";
};

export default GenericServices;
