import Form                from "Components/Appraisal/Form";
import { FormSelectGroup } from "Components/Global";
import { useFormContext }  from "react-hook-form";

const pavementsOptions = [
	{
		value : 0,
		label : " Cuenta con pavimentos reglamentarios, nuevos y/o en ptimo estado",
	},
	{
		value : 1,
		label : " Cuenta con pavimentos reglamentarios, en condiciones suficientes",
	},
	{
		value : 2,
		label : "No cuenta con pavimentos o presenta condiciones insuficientes",
	},
];

const sideWalksOptions = [
	{
		value : 0,
		label : " Cuenta con guarniciones, banquetas o andadores nuevos y/o en ptimo estado",
	},
	{
		value : 1,
		label : " Cuenta con guarniciones, banquetas o andadores en condiciones suficientes",
	},
	{
		value : 2,
		label : " No cuenta con guarniciones, banquetas o andadores o presenta condiciones insuficientes",
	},
];

const accessOptions = [
	{
		label : " Los accesos a la colonia son por una vialidad primaria y cumple con las especificaciones",
		value : 0,
	},
	{
		label : " Los accesos a la colonia estan en condiciones suficientes",
		value : 1,
	},
	{
		label : "  Los accesos a la colonia no cumplen con las especificaciones reglamentarias",
		value : 1,
	},
];


const RoadWays = ({ onSubmit, isSubmitingData }) => {

	const hookForm = useFormContext();

	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<FormSelectGroup
				name="pavements"
				label="Pavimentos:"
				hookForm={hookForm}
				options={pavementsOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="sideWalks"
				label="Guarniciones, banquetas, Andadores:"
				hookForm={hookForm}
				options={sideWalksOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="access"
				label="Accesos:"
				hookForm={hookForm}
				options={accessOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
		</Form>
	);
};

export default RoadWays;
