import { Button, Card, Table } from "reactstrap";

import {
	getFResult,
	getUnitValueHomologatin,
	surfaceHomologation,
} from "Helpers/formulas";

import { FormInputGroup, HelpToolTip } from "Components/Global";
import TdInput                         from "Components/Appraisal/TdInput";
import TdWatcherHOC                    from "Components/Appraisal/TdWatcherHOC";

const TableHomologation = ({
	delegations : {
		hookForm,
		homologationFields,
		handleSetFactorValues,
	},
}) => {
	return (
		<Card className="p-1">
			<div>
				<Button
					outline
					size="sm"
					color="primary"
					type="button"
					className="mb-1"
					onClick={handleSetFactorValues}
				>
					Llenar factores con 1
				</Button>
			</div>
			<Table striped bordered responsive>
				<thead>
					<tr>
						<th rowSpan="2" className="text-center align-middle">Nm.</th>
						<th colSpan="8" scope="colgroup" className="text-center">Factores</th>
					</tr>
					<tr>
						<th className="text-center" scope="col">Zona</th>
						<th className="text-center" scope="col">Ubicacin</th>
						<th className="text-center" scope="col">Frente</th>
						<th className="text-center" scope="col">Forma</th>
						<th className="text-center" scope="col">
							<span>Superficie </span>
							<HelpToolTip target="help-table-hom-surface">
								<p>Factor Superficie = raiz 6ta (Superficie Comparable) / (Superfice Sujeto)</p>
								<p>[Aplica solo cuando se conoce el indiviso y la superficie del terreno del conjunto]</p>
								<p>(Ver seccin "Declaraciones y Advertencias" en la Pgina 1)</p>
								<p className="tex-start mt-2">
									La superficie de terreno que se usa para el sujeto, puede ser la de su conjunto,
									o la que le corresponde por su indiviso; esto depende del tipo de investigacin que se este realizando.
								</p>
							</HelpToolTip>
						</th>
						<th className="text-center" scope="col">
							<p>Otro</p>
							<FormInputGroup
								bsSize="sm"
								name="other_title"
								register={hookForm.register}
								options={{ required : false }}
								errors={hookForm.formState.errors}
							/>
						</th>
						<th className="text-center" scope="col">
							<span>F. resultante </span>
							<HelpToolTip target="help-table-hom-f-result">
								<p>Factor Resultante = (Multiplicatoria de los factores empleados)</p>
							</HelpToolTip>
						</th>
						<th className="text-center" scope="col">
							<span>V. Unit ($/m2) </span>
							<HelpToolTip target="help-table-st-unit-val">
								<p>Valor Unitario = (Valor Unitario[del procedimiento residual]) x (Factor Resultante)</p>
							</HelpToolTip>
						</th>
					</tr>
				</thead>
				<tbody>
					{
						homologationFields.map(({ id }, index) => (
							<tr key={id}>
								<td className="text-center">{index + 1}</td>
								<TdInput hookForm={hookForm} name={`homologation.${index}.zone`} />
								<TdInput hookForm={hookForm} name={`homologation.${index}.ubication`} />
								<TdInput hookForm={hookForm} name={`homologation.${index}.front`}  />
								<TdInput hookForm={hookForm} name={`homologation.${index}.shape`} />
								<TdWatcherHOC
									disabled
									name={`homologation.${index}.surface`}
									deps={[
										`unit_value_land.${index}.invest_comp.ground`,
										"superficie_total_land_subject",
									]}
									formula={surfaceHomologation}
								/>
								<TdInput hookForm={hookForm} name={`homologation.${index}.other`} />
								<TdWatcherHOC
									isArrayArgs={true}
									name={`homologation.${index}.f_result`}
									deps={[
										`homologation.${index}.zone`,
										`homologation.${index}.ubication`,
										`homologation.${index}.front`,
										`homologation.${index}.shape`,
										`homologation.${index}.surface`,
										`homologation.${index}.other`,
									]}
									formula={getFResult}
								/>
								<TdWatcherHOC
									name={`homologation.${index}.unit_val_homologation`}
									deps={[
										`statistics_res_process.${index}.unit_value`,
										`homologation.${index}.f_result`,
									]}
									formula={getUnitValueHomologatin}
								/>
							</tr>
						))
					}
					<tr>
						<td colSpan="8" align="right">Promedio:</td>
						<TdInput disabled hookForm={hookForm} name="homologation_average" />
					</tr>
					<tr>
						<td colSpan="8" align="right">Valor Unitario Aplicado al Suelo:</td>
						<TdInput disabled hookForm={hookForm} name="homologation_unit_val" />
					</tr>
				</tbody>
			</Table>
		</Card>
	);
};

export default TableHomologation;
