import { useEffect, useState }        from "react";
import { connect }                    from "react-redux";
import { useHistory }                 from "react-router-dom";
import { UserMinus, UserPlus, UserX } from "react-feather";

//Our imports
import { bindAll }       from "Helpers";
import toaster           from "Components/Global/Toaster";
import { genericApi }    from "Store/api/genericApi";
import ModalSlice        from "Store/Slices/ModalSlice";
import GenericSerivices  from "Store/Services/GenericSerivices";
import ConfirmationModal from "./ConfirmationModal";

const ConfirmationModalContainer = ({
	pageCreditDocs,
	genericSerivices,
	modalSliceActions,
}) => {
	const [loading, setloading] = useState(false);

	const [toasterConent, setToasterContent] = useState({
		titleSuccess : "",
		bodySuccess  : "",
		titleError   : "",
		bodyError    : "",
	});

	const history = useHistory();

	const [
		deleteItem,
		{ isSuccess, isError },
	] = genericApi.useDeleteMutation();


	const handleConfirm = (type, ...rest) => {
		setloading(true);
		actions[type](...rest);
	};

	const actions = {
		//Users
		deleteUser : (_id, identifier, replacePage) => {
			const callback = ({ response }) => {
				setloading(false);

				if (response.ok) {
					modalSliceActions.closeModal("confirmationModal");

					toaster.error({
						icon  : UserX,
						title : "Usuario Eliminado!",
						body  : <p>El usuario <b>{identifier}</b> ha sido eliminado exitosamente!</p>,
					});

					if (replacePage) {
						history.replace("/app/users");
						return;
					}

					genericSerivices.getAll({ module : "users", actionType : "getAllUsers" });
					return;
				}
				toaster.warning({
					title : "Error!",
					body  : "Ocurri un error al tratar de eliminar el usuario. Revise la conexin del su internet.",
				});
			};
			genericSerivices.remove({module : `users/${_id}`, callback });
		},
		disableUser : (_id, identifier) => {
			const callback = ({ response }) => {
				setloading(false);

				if (response.ok) {

					modalSliceActions.closeModal("confirmationModal");
					toaster.warning({
						icon  : UserMinus,
						title : "Usuario Deshabilitado!",
						body  : <p>El usuario <b>{identifier}</b> ha sido actualizado exitosamente!</p>,
					});

					genericSerivices.getAll({ module : "users", actionType : "getAllUsers" });
					return;
				}

				toaster.error({
					title : "Error!",
					body  : "Ocurri un error al tratar de deshabilitar el usuario. Revise la conexin del su internet.",
				});
			};

			genericSerivices.patch({module : `users/${_id}`, callback, actionType : "editStatusUser" });
		},
		enableUser : (_id, identifier) => {
			const callback = ({ response }) => {
				setloading(false);

				if (response.ok) {

					modalSliceActions.closeModal("confirmationModal");
					toaster.info({
						icon  : UserPlus,
						title : "Usuario Habilitado!",
						body  : <p>El usuario <b>{identifier}</b> ha sido actualizado exitosamente!</p>,
					});

					genericSerivices.getAll({ module : "users", actionType : "getAllUsers" });
					return;
				}

				toaster.error({
					title : "Error!",
					body  : "Ocurri un error al tratar de habilitar el usuario. Revise la conexin del su internet.",
				});
			};

			genericSerivices.patch({module : `users/${_id}`, callback, actionType : "editStatusUser" });
		},

		deletCredit : async (_id, identifier) => {
			const callback = ({ response }) => {
				setloading(false);

				if (response.ok) {
					modalSliceActions.closeModal("confirmationModal");

					toaster.error({
						title : "Otorgante de crdito Eliminado!",
						body  : <p>El otorgante de crdito <b>{identifier}</b> ha sido eliminado exitosamente!</p>,
					});

					genericSerivices.getAll({ module : "credit-grantors", actionType : "getAllCredit", query : { page : pageCreditDocs } });
					return;
				}
				toaster.warning({
					title : "Error!",
					body  : "Ocurri un error al tratar de eliminar el usuario. Revise la conexin del su internet.",
				});
			};

			genericSerivices.remove({module : `credit-grantors/${_id}`, callback });
		},

		deleteRequest : (_id, identifier, replacePage) => {
			deleteItem({ module : `petitions/${_id}`, tags : [ "petitions" ] });

			setloading(false);

			modalSliceActions.closeModal("confirmationModal");

			setToasterContent({
				titleSuccess : "Solicitud Eliminda!",
				bodySuccess  : <p>La solicitud <b>{identifier}</b> ha sido eliminada exitosamente!</p>,
				titleError   : "Error!",
				bodyError    : "Ocurri un error al tratar de eliminar la soliciud. Revise la conexin del su internet.",
			});

			if (replacePage) {
				history.replace("/app/request");
				return;
			}

		},
	};

	const handlerToaster = () => {
		if (isSuccess && toasterConent.titleSuccess) {
			toaster.success({
				title : toasterConent.titleSuccess,
				body  : toasterConent.bodySuccess,
			});
		} else if (isError && toasterConent.titleError) {
			toaster.error({
				title : toasterConent.titleError,
				body  : toasterConent.bodyError,
			});
		}
	};

	useEffect(() => handlerToaster(), [isSuccess, isError, toasterConent]);

	return (
		<ConfirmationModal
			delegations={{
				loading,
				handleConfirm,
			}}
		/>
	);
};

const mapDispatchToState = ({ _creditgrantorsSlice  }) => ({
	pageCreditDocs : _creditgrantorsSlice?.page ?? 1,
});

const mapDispatchToProps = bindAll({
	GenericSerivices,
	ModalSliceActions : ModalSlice.actions,
});

export default connect(mapDispatchToState, mapDispatchToProps)(ConfirmationModalContainer);
