import {
	CheckboxInpt,
	FormInputGroup,
	FormSelectGroup,
	FormFlatPickrGroup,
	InputMaskNumber,
} from "Components/Global";
import { isValidArray } from "Helpers";

const TdInput = ({
	options,
	hookForm,
	children,
	inpt_type,
	tdProps = {},
	type="number",
	...rest
}) => {

	return (
		<td {...tdProps}>
			{
				isValidArray(options) ?
					<FormSelectGroup
						{...rest}
						size="sm"
						options={options}
						hookForm={hookForm}
						className="w-100"
						inputGroupClassName="mb-0"
						rules={{ required : false }} //rest.rules ? rest.rules :
						isClearable={rest?.isClearable ? rest.isClearable :  false}
						menuPortalTarget={document.body}
					/>
					:
					inpt_type === "date" ?
						<FormFlatPickrGroup
							{...rest}
							className="mt-0"
							style={{ height : "29.98px" }}
							control={hookForm.control}
							rules={{ required : false }} //options ? options :
							errors={hookForm.formState.errors}
						/>
						:
						inpt_type === "check-box" ?
							<div className="d-flex justify-content-center align-items-center">
								<CheckboxInpt
									{...rest}
									className="m-0"
									customStyles="m-0"
									classNameInpt="p-0"
									rules={{ required : false }}
								/>
							</div>
							:
							type === "number" && inpt_type !== "isNotMasked" ?
								<InputMaskNumber
									{...rest}
									bsSize="sm"
									type="text"
									className="w-100"
									hookForm={hookForm}
									inputGroupClassName="mb-0"
									rules={{ required : false }}
								/>
								:
								<FormInputGroup
									{...rest}
									bsSize="sm"
									type={type}
									className="w-100"
									inputGroupClassName="mb-0"
									register={hookForm.register}
									errors={hookForm.formState.errors}
									options={{ required : false }} //options ? options :
								/>
			}
			{children}
		</td>
	);
};

export default TdInput;
