import { useCallback, useEffect } from "react";
import { Button }                 from "reactstrap";
import { Save }                   from "react-feather";
import { FormProvider }           from "react-hook-form";

import Comments      from "../Comments";
import SectionTitle  from "../SectionTitle";
import ApproachIndex from "../ApproachIndex";
import "./styles.scss";

const ApproachForm = ({
	id,
	children,
	hookForm,
	sections,
	onSubmit,
	sectionTitle,
	showIndex = true,
}) => {
	const { isDirty } = hookForm.formState;

	// handle what happens on key press
	const handleKeyPress = useCallback((event) => {
		if (event.ctrlKey  === true && event.key.toUpperCase() === "S") { //Validate loading fetch
			event.preventDefault();

			if (!isDirty) return;

			onSubmit();
		}
	  }, [isDirty]);

	  useEffect(() => {
		// attach the event listener
		document.addEventListener("keydown", handleKeyPress);

		// remove the event listener
		return () => {
		  document.removeEventListener("keydown", handleKeyPress);
		};
	  }, [handleKeyPress]);

	return (
		<div id="ApproachForm">
			{showIndex && <ApproachIndex id={id} sections={sections} hookForm={hookForm} />}
			<div id={id} className="wrapper">
				<FormProvider {...hookForm}>
					<form onSubmit={onSubmit}>
						<SectionTitle sectionTitle={sectionTitle} />
						<div className="btn-container">
							<Button.Ripple
								type="submit"
								disabled={!isDirty}
								onClick={onSubmit}
								color="primary"
								className="btn-icon"
							>
								<Save size={14} />
							</Button.Ripple>
						</div>
						{children}
					</form>
				</FormProvider>
				<Comments />
			</div>
		</div>
	);
};

export default ApproachForm;
