import { HelpCircle } from "react-feather";

import { UncontrolledTooltip } from "reactstrap";

import { FormInputGroup, MapFormInpts } from "Components/Global";
import Form                             from "Components/Appraisal/Form";

const UrbanEquipment = ({
	delegations : {
		onSubmit,
		hookForm,
		isLoading,
		inptsUrbanEquipmentLevel1,
		inptsUrbanEquipmentLevel2,
		inptsUrbanEquipmentLevel3,
		inptsUrbanEquipmentLevel4,
	},
}) => {

	return (
		<Form onSubmit={onSubmit} isLoading={isLoading}>
			<p className="mb-1"><b>Distancias en metros lineales.</b> En caso de no existir el elemento correspondiente capturar 0.</p>
			<div className="row">
				<h5 className="subTitle-text">Categora nivel 1</h5>
				<MapFormInpts className="col-md-4" inpts={inptsUrbanEquipmentLevel1} hookForm={hookForm} />
			</div>
			<div className="row">
				<h5 className="subTitle-text">Categora nivel 2</h5>
				<MapFormInpts className="col-md-6" inpts={inptsUrbanEquipmentLevel2} hookForm={hookForm} />
			</div>
			<h5 className="subTitle-text">Categora nivel 3</h5>
			<MapFormInpts inpts={inptsUrbanEquipmentLevel3} hookForm={hookForm} />
			<div className="row">
				<h5 className="subTitle-text">Categora nivel 4</h5>
				<MapFormInpts className="col-md-6" inpts={inptsUrbanEquipmentLevel4} hookForm={hookForm} />
			</div>
			<hr />
			<div className="d-flex g-1">
				<FormInputGroup
					disabled
					bsSize="sm"
					className="w-100"
					type="number"
					label="Nivel de Equipamiento Urbano:"
					name="urban_equipment.level_urban_equipment"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ rquired : false }}
				/>
				<HelpCircle id="help-level_urban_equipment" size={14} />
				<UncontrolledTooltip className="custom-style" placement="bottom" target="help-level_urban_equipment">
					<p><b>Niveles de Equipamiento Urbano.</b></p>
					<div className="row text-start">
						<p className="col-2">Nivel 1:</p>
						<p className="col-10">Cuatro o menos elementos de nivel 2.</p>
					</div>
					<div className="row text-start">
						<p className="col-2">Nivel 2:</p>
						<p className="col-10">Iglesias, Mercados o Comercios, Escuelas, Parques o Jardines.</p>
					</div>
					<div className="row text-start">
						<p className="col-2">Nivel 3:</p>
						<p className="col-10">Nivel 2 ms Estacin de Transporte Pblico</p>
					</div>
					<div className="row text-start">
						<p className="col-2">Nivel 4:</p>
						<p className="col-10">Nivel 3 ms Hospitales y Bancos</p>
					</div>
				</UncontrolledTooltip>
			</div>
		</Form>
	);
};

export default UrbanEquipment;
