import { Toaster }                            from "Components/Global";
import { productSetOfValues }                 from "Helpers/formulas";
import { useCalcSurfaceFactor, useColumnSum } from "Helpers/Hooks";
import { useFieldArray, useFormContext }      from "react-hook-form";
import TableLand                              from "./TableLand";

const landFraction = [
	{
		value : 0,
		label : "Privativa",
	},
	{
		value : 1,
		label : "Comn",
	},
];

const TableLandContainer = () => {

	const hookForm = useFormContext();

	const { setValue } = hookForm;


	const { fields : landFields, append, remove } = useFieldArray({
		control : hookForm.control,
		name    : "land",
	});

	const [ surfaceF ] = useCalcSurfaceFactor({ keys : landFields.map((_, index) => `land.${index}.surface`) });

	const fieldLand = {
		land_fraction      : undefined,
		net_unit_value     : "",
		area_m2            : "",
		zone               : "",
		ubic               : "",
		front              : "",
		shape              : "",
		surface            : surfaceF,
		other              : "",
		f_re               : "",
		undivided          : "",
		land_partial_value : "",
	};
	const appendNewRow = () => append({...fieldLand});

	const removeRow = (index) => () => remove(index);

	const handleSetFactorValues = () => landFields.forEach((_, index) => {
		setValue(`land.${index}.zone`,  1);
		setValue(`land.${index}.ubic`,  1);
		setValue(`land.${index}.front`, 1);
		setValue(`land.${index}.shape`, 1);
	});

	const setUnitValue = ([ val ], name) =>{
		if (isNaN(val)) {
			setValue(name, "");
			return;
		}
		setValue(name, val);
	};

	const calcLandPartialValue = (vals, name) => {
		const [ net_unit_value, area_m2, undivided, f_re ] = vals;

		const porcentage = undivided > 0 ? undivided / 100 : 0;

		const result = productSetOfValues([net_unit_value, area_m2, porcentage, f_re]);

		setValue(name, result);
	};

	const handleUndivedLimit = (val, name) => {
		if (val > 100) {
			setValue(name, 100);
			Toaster.warning({
				title : "Advertencia!",
				body  : "El valor del indiviso no puede ser mayor a 100.",
			});
		}

		if (val < 0) {
			setValue(name, 0);
			Toaster.warning({
				title : "Advertencia!",
				body  : "El valor del indiviso no puede ser menor a 0.",
			});
		}
	};

	useColumnSum({
		length   : landFields.length,
		arrayKey : "land",
		fieldKey : "land_partial_value",
		name     : "land_partial_total_val",
	});

	return (
		<TableLand
			delegations={{
				hookForm,
				removeRow,
				landFields,
				landFraction,
				appendNewRow,
				setUnitValue,
				handleUndivedLimit,
				calcLandPartialValue,
				handleSetFactorValues,
			}}
		/>
	);
};

export default TableLandContainer;
