import { connect }   from "react-redux";
import ReactPaginate from "react-paginate";

//Own components
import { bindAll }      from "Helpers";
import GenericSerivices from "Store/Services/GenericSerivices";
import "./Paginate.scss";

const Paginate = ({
	module,
	totalPage,
	options={},
	actionType,
	currentPage = 1,
	handleGetData,
	genericSerivices,
}) => {
	return (
		<ReactPaginate
			pageCount={totalPage}
			forcePage={currentPage - 1}
			breakLabel={"..."}
			pageRangeDisplayed={5}
			marginPagesDisplayed={2}
			breakClassName='page-item'
			activeClassName={"active"}
			pageClassName={"page-item"}
			breakLinkClassName='page-link'
			nextLinkClassName={"page-link"}
			nextClassName={"page-item next"}
			pageLinkClassName={"page-link"}
			previousClassName={"page-item prev"}
			previousLinkClassName={"page-link"}
			containerClassName={"pagination react-paginate d-flex justify-content-end"}
			nextLabel={<span className="align-middle d-none d-md-inline-block">Siguiente</span>}
			previousLabel={<span className='align-middle d-none d-md-inline-block'>Anterior</span>}
			onPageChange={({selected}) => {
				if (typeof handleGetData === "function") {
					handleGetData(selected + 1);
					return;
				}
				genericSerivices.getAll({ module, actionType, query : {page : selected + 1, ...options } });
			}}
		/>
	);
};

const mapDispatchToProps = bindAll({ GenericSerivices });

export default connect(null, mapDispatchToProps)(Paginate);

