import { useEffect, useState }      from "react";
import { useFormContext, useWatch } from "react-hook-form";

import { toastDispacher } from "Helpers";
import zip_codes_mx       from "Resources/data/zip_codes_mx/zip_codes_mx.json";
import TableUbication     from "./TableUbication";

const TableUbicationContainer = ({ index }) => {
	const [settlementOptions, setSettlementOptions] = useState([{ label : "Ingresa un CP", value : "-", isDisabled : true }]);

	const { setValue, clearErrors } = useFormContext();

	const watcherZipCode = useWatch({ name : `inves_housing.${index}.ubication.zipe_code` });

	const setZipCodeValues = () => {
		const { d_ciudad, ...localities } = zip_codes_mx[watcherZipCode];

		delete localities["d_estado"];

		const options = Object.entries(localities).map(([key, values]) => ({  label : key, value : values.id_asenta_cpcons, ...values }) );

		setSettlementOptions(options);

		setValue(`inves_housing.${index}.ubication.delegation`, d_ciudad);
		clearErrors(`inves_housing.${index}.ubication.delegation`);
	};


	useEffect(() => {
		if (watcherZipCode?.length !== 5) return;

		const zipCode = zip_codes_mx[watcherZipCode];

		if (!zipCode) {
			toastDispacher.zipCode.warning(watcherZipCode);

			setSettlementOptions([{ label : "Ingresa un CP", value : "-", isDisabled : true }]);

			setValue(`inves_housing.${index}.ubication.col`, "");
			setValue(`inves_housing.${index}.ubication.delegation`, "");
			return;
		}
		setZipCodeValues();
	}, [watcherZipCode]);

	return (
		<TableUbication
			delegations={{
				index,
				settlementOptions,
			}}
		/>
	);
};

export default TableUbicationContainer;
