import PropTypes  from "prop-types";
import classNames from "classnames";

//Our Imports
import Input          from "Components/Global/Input";
import { Controller } from "react-hook-form";
import { Label }      from "reactstrap";

const CheckboxInpt = ({
	name,
	rules,
	right,
	label,
	control,
	className,
	textStyles,
	checkValue,
	falseLabel,
	classNameInpt,
	customStyles,
	labelRigth,
	handleOnchange,
	...rest
}) => {
	const isHanldeValalid = () => typeof(handleOnchange) === "function";

	const getOnChange = (onChange) => {
		if (isHanldeValalid()) {
			return (e) => {
				handleOnchange(e, onChange);
			};
		}
		return onChange;
	};
	return (
		<div className={className ? className : "mt-1"}>
			{
				control ?
					<Controller
						name={name}
						control={control}
						rules={rules}
						render={({ field: { onChange, value }, formState : { errors } }) => (
							<>
								<div className={classNameInpt ? `form-check form-check-primary ${classNameInpt}` : "form-check form-check-primary" }>
									{
										(label || falseLabel) &&
										<label
											htmlFor={name}
											className={classNames("form-check-label", {
												[textStyles]  : textStyles && !errors?.[name],
												"text-danger" : !!errors?.[name],
											})}
										>
											{
												falseLabel ?
													value ? label : falseLabel
													:
													label
											}
										</label>
									}
									<Input
										{...rest}
										id={name}
										name={name}
										type="checkbox"
										className={classNames("check-inpt", {
											[customStyles] : customStyles,
											"is-invalid"   : !!errors?.[name],
										})}
										value={value}
										checked={value}
										onChange={getOnChange(onChange)}
									/>
								</div>
							</>
						)}
					/>
					:
					<div className={classNameInpt ? `form-check form-check-primary ${classNameInpt}` : "form-check form-check-primary" }>
						<Input
							{...rest}
							id={name}
							name={name}
							type="checkbox"
							className={classNames("check-inpt ", {
								[customStyles] : customStyles,
							})}
							value={checkValue}
							checked={checkValue}
						/>
						{
							label &&
							<Label
								htmlFor={name}
								className={classNames("form-check-label", {
									[textStyles] : textStyles,
								})}
							>
								{label}
							</Label>
						}
					</div>
			}
		</div>
	);
};

CheckboxInpt.propTypes = {
	rules        : PropTypes.object,
	name         : PropTypes.string,
	control      : PropTypes.object,
	customStyles : PropTypes.string,
	textStyles   : PropTypes.string,
	label        : PropTypes.string.isRequired,
};

CheckboxInpt.defaultProps = {
	name         : "",
	label        : "",
	textStyles   : "",
	customStyles : "",
	rules        : {
		required : {
			value   : true,
			message : "Este campo no puede estar vaco.",
		},
	},
};

export default CheckboxInpt;
