import React                                            from "react";
import { Button, Modal, ModalBody, ModalHeader, Table } from "reactstrap";

const ModalEquivalenceTable = ({
	modalContext : {
		data,
		title,
		isOpen,
		callback,
	},
	toggle,
}) => {
	const handleOnclick = (val) => () => {
		callback(val);
		toggle([], "", null)();
	};
	return (
		<Modal
			isOpen={isOpen}
			toggle={toggle([], "", null)}
			className="modal-dialog-centered modal-xl"
		>
			<ModalHeader toggle={toggle([], "", null)}>
				{title}
			</ModalHeader>
			<ModalBody>
				<Table responsive bordered striped>
					<thead>
						<tr align="center">
							<th scope="col">PERIODO DE OCUPACION (AOS)</th>
							<th scope="col">1 MES</th>
							<th scope="col">2 MESES</th>
							<th scope="col">3 MESES</th>
							<th scope="col">4 MESES</th>
							<th scope="col">5 MESES</th>
							<th scope="col">6 MESES</th>
							<th scope="col">7 MESES</th>
							<th scope="col">8 MESES</th>
							<th scope="col">9 MESES</th>
							<th scope="col">10 MESES</th>
							<th scope="col">11 MESES</th>
						</tr>
					</thead>
					<tbody>
						{
							data.map( (nestedVals, index) => (
								<tr align="center" key={index}>
									<th scope="row">{index + 1}</th>
									{
										nestedVals.map((val, nestedIndex) => (
											<td
												key={`row-${nestedIndex}`}
											>
												 <Button.Ripple
													size="sm"
													type="button"
													color="flat-primary"
													onClick={handleOnclick(val)}
												>
													{val}%
												</Button.Ripple>
											</td>
										))
									}
								</tr>
							))
						}
					</tbody>
				</Table>
			</ModalBody>
		</Modal>
	);
};

export default ModalEquivalenceTable;
