
//Our Imports
import { useEffect }                from "react";
import { useFormContext, useWatch } from "react-hook-form";
import UrbanEquipment               from "./UrbanEquipment";

const UrbanEquipmentContainer = ({ onSubmit, isLoading }) => {

	const hookForm = useFormContext();

	//The order is important, becaus we need to watch the values and we are going to access with the index
	//So if you need to change the oreder, you need to change the index in each validation
	const inptsUrbanEquipmentLevel1 = [
		{
			label   : "Distancia Canchas Deportivas:",
			name    : "urban_equipment.distance_sports_courts",
			type    : "number",
			options : { rquired : false },
		},
		{
			label   : "Distancia Centro Comunitario:",
			name    : "urban_equipment.distance_community_center",
			type    : "number",
			options : { rquired : false },
		},
		{
			label   : "Distancia Centro Deportivo:",
			name    : "urban_equipment.distance_sports_center",
			type    : "number",
			options : { rquired : false },
		},
		{
			label   : "Distancia Plazas Pblicas:",
			name    : "urban_equipment.distance_public_plazas",
			type    : "number",
			options : { rquired : false },
		},
		{
			label   : "Distancia Supermercados:",
			name    : "urban_equipment.distance_supermarkets",
			type    : "number",
			options : { rquired : false },
		},
		{
			label   : "Nmero de Locales Comerciales:",
			name    : "urban_equipment.number_commercial_locals",
			type    : "number",
			options : { rquired : false },
		},
	];

	const inptsUrbanEquipmentLevel2 = [
		{
			label   : "Distancia Iglesia:",
			name    : "urban_equipment.distance_church",
			type    : "number",
			options : { rquired : false },
		},
		{
			label   : "Distancia Jardines:",
			name    : "urban_equipment.distance_gardens",
			type    : "number",
			options : { rquired : false },
		},
		{
			label   : "Distancia Parques:",
			name    : "urban_equipment.distance_parks",
			type    : "number",
			options : { rquired : false },
		},
		{
			label   : "Distancia Locales Comerciales:",
			name    : "urban_equipment.distance_commercial_locals",
			type    : "number",
			options : { rquired : false },
		},
		{
			label   : "Distancia Mercados:",
			name    : "urban_equipment.distance_markets",
			type    : "number",
			options : { rquired : false },
		},
		{
			label   : "Distancia Primarias:",
			name    : "urban_equipment.distance_primary",
			type    : "number",
			options : { rquired : false },
		},
		{
			label   : "Distancia Secundarias:",
			name    : "urban_equipment.distance_secondary",
			type    : "number",
			options : { rquired : false },
		},
		{
			label   : "Distancia Preparatorias:",
			name    : "urban_equipment.distance_high_school",
			type    : "number",
			options : { rquired : false },
		},
		{
			label   : "Distancia Universidad:",
			name    : "urban_equipment.distance_university",
			type    : "number",
			options : { rquired : false },
		},
		{
			label   : "Distancia Otras Escuelas:",
			name    : "urban_equipment.distance_other_schools",
			type    : "number",
			options : { rquired : false },
		},
	];

	const inptsUrbanEquipmentLevel3 = [
		{
			label   : "Acceso o estacin de Transporte Publico:",
			name    : "urban_equipment.access_public_transport",
			type    : "number",
			options : { rquired : false },
		},
	];

	const inptsUrbanEquipmentLevel4 = [
		{
			label   : "Servicios de Salud Primer Nivel (Consultorios y Clnicas):",
			name    : "urban_equipment.services_health_first_level",
			type    : "number",
			options : { rquired : false },
		},
		{
			label   : "Servicios de Salud Segundo Nivel (Hospitales Regionales y Generales):",
			name    : "urban_equipment.services_health_second_level",
			type    : "number",
			options : { rquired : false },
		},
		{
			label   : "Servicios de Salud Tercer Nivel (Hospitales de Especialidades):",
			name    : "urban_equipment.services_health_third_level",
			type    : "number",
			options : { rquired : false },
		},
		{
			label   : "Distancia Bancos:",
			name    : "urban_equipment.distance_banks",
			type    : "number",
			options : { rquired : false },
		},
	];

	const inptsLevel2 = useWatch({ name : inptsUrbanEquipmentLevel2.map(inpt => inpt.name) });

	const inptsLevel3 = useWatch({ name : inptsUrbanEquipmentLevel3.map(inpt => inpt.name) });

	const inptsLevel4 = useWatch({ name : inptsUrbanEquipmentLevel4.map(inpt => inpt.name) });


	const isLevel2 = () => {
		//Rules to  be level 2: the value of these inputs is greater than 0
		//distance_church, distance_commercial_locals or distance_markets, *schools(1 at least), distance_parks or distance_gardens.

		const inptsToNumber = inptsLevel2.map(inpt => parseInt(inpt));

		//validate the distance_church field, this field is required to be level 2
		if (!inptsToNumber[0] > 0) {
			return false;
		}
		//validate distance_gardens or distance_parks fields, these fields are required to be level 2
		if (!inptsToNumber[1] > 0 && !inptsToNumber[2]) {
			return false;
		}
		//validate distance_commercial_locals or istance_markets, these fields are required to be level 2
		if (!inptsToNumber[3] > 0 && !inptsToNumber[4]) {
			return false;
		}
		//validate distance_primary, distance_secondary, distance_high_school, distance_university, distance_other_schools, you need to have at least one school to be level 2
		if (!inptsToNumber[5] > 0 && !inptsToNumber[6] > 0 && !inptsToNumber[7] > 0 && !inptsToNumber[8] > 0 && !inptsToNumber[9] > 0) {
			return false;
		}

		return true;
	};

	const isLevel3 = () => {
		//Rules to  be level 3: the value of these inputs is greater than 0 and level2 = true
		//access_public_transport
		const inptsToNumber = inptsLevel3.map(inpt => parseInt(inpt));

		if (!inptsToNumber[0] > 0) {
			return false;
		}
		return true;
	};

	const isLevel4 = () => {
		//Rules to  be level 4: the value of these inputs is greater than 0 and level3 = true
		//services_health_first_level, services_health_second_level, services_health_third_level, distance_banks
	    const inptsToNumber = inptsLevel4.map(inpt => parseInt(inpt));

	    if (!inptsToNumber[0] > 0 && !inptsToNumber[1] > 0 && !inptsToNumber[2] > 0) {
	        return false;
	    }

		if ( !inptsToNumber[3] > 0) {
			return false;
		}
	    return true;
	};


	useEffect(() => {
		if (!isLevel2()) {
		    hookForm.setValue("urban_equipment.level_urban_equipment", 1);
			return;
		}

		if (!isLevel3()) {
			hookForm.setValue("urban_equipment.level_urban_equipment", 2);
			return;
		}

		if (!isLevel4()) {
			hookForm.setValue("urban_equipment.level_urban_equipment", 3);
			return;
		}

		hookForm.setValue("urban_equipment.level_urban_equipment", 4);

	}, [inptsLevel2, inptsLevel3, inptsLevel4]);


	return (
		<UrbanEquipment
			delegations={{
				onSubmit,
				hookForm,
				isLoading,
				inptsUrbanEquipmentLevel1,
				inptsUrbanEquipmentLevel2,
				inptsUrbanEquipmentLevel3,
				inptsUrbanEquipmentLevel4,
			}}
		/>
	);
};

export default UrbanEquipmentContainer;
