import { useFormContext } from "react-hook-form";

import { FormSelectGroup } from "Components/Global";
import Form                from "Components/Appraisal/Form";


const groundingOptions = [
	{
		value : 0,
		label : " No presenta asentamientos irregulares, humedades o salitre",
	},
	{
		value : 1,
		label : " Presenta humedades o salitre que requiere mantenimiento",
	},
	{
		value : 2,
		label : " Presenta asentamientos, humedades o salitre, queno garantizan la seguridad estructural",
	},
];

const structureOptions = [
	{
		value : 0,
		label : " No presenta grietas ni zonas afectadas por humedades o salitre",
	},
	{
		value : 1,
		label : " Presentan fisuras o zonas afectadas por humedades o salitre que requieren reparacin",
	},
	{
		value : 2,
		label : " Presentan grietas que no garantizan la seguridad estructural",
	},
];

const slabsOptions = [
	{
		value : 0,
		label : " No presenta flechas, grietas, o zonas afectadas por humedades o salitre",
	},
	{
		value : 1,
		label : " Presenta zonas afectadas por humedades o salitre que requieren mantenimiento",
	},
	{
		value : 2,
		label : "  Presenta zonas afectadas por humedades que no garantizan la seguridad estructural",
	},
];

const fencesOptions = [
	{
		value : 0,
		label : " Las bardas cumplen con las especificaciones y se encuentran en ptimo estado",
	},
	{
		value : 1,
		label : " La vivienda no tiene bardas, y el reglamento estatal y/o local no las exige o presentan fisuras",
	},
	{
		value : 2,
		label : " Se requieren bardas y no las tiene o estan en malas condiciones",
	},
];

const waterproofingOptions = [
	{
		value : 0,
		label : " Presenta impermeabilizacin nueva y/o condiciones ptimas",
	},
	{
		value : 1,
		label : " Presenta impermeabilizacin en condiciones suficientes",
	},
	{
		value : 2,
		label : " No considera impermeabilizacin, o la existente se encuentra en condiciones insuficientes",
	},
];

const PlaceUnderConstruction = ({ onSubmit, isSubmitingData }) => {

	const hookForm = useFormContext();

	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<FormSelectGroup
				name="grounding"
				label="Cimentacin:"
				hookForm={hookForm}
				options={groundingOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="structure"
				label="Estructura:"
				hookForm={hookForm}
				options={structureOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="walls"
				label="Muros:"
				hookForm={hookForm}
				options={groundingOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="slabs"
				label="Losas:"
				hookForm={hookForm}
				options={slabsOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="fences"
				label="Bardas:"
				hookForm={hookForm}
				options={fencesOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="waterproofing"
				label="Impermeabilizacin:"
				hookForm={hookForm}
				options={waterproofingOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
		</Form>
	);
};

export default PlaceUnderConstruction;
