import { bindAll } from "Helpers";
import { connect } from "react-redux";

import { ModalSlice } from "Store/Slices";
import DescGeneral    from "./DescGeneral";

const DescGeneralContainer = ({ modalSliceActions, onSubmit, isSubmitingData }) => {

	const handleOpenModal = () => modalSliceActions.openModal({ type : "equivalenceTable" });

	return (
		<DescGeneral
			delegations={{
				onSubmit,
				isSubmitingData,
				handleOpenModal,
			}}
		/>
	);
};

const mapDispatchToProps = bindAll({
	ModalSliceActions : ModalSlice.actions,
});

export default connect(null, mapDispatchToProps)(DescGeneralContainer);
