// Import Own Components
import defaultImg from "Resources/images/defaultImg.jpg";

const HandleImage = ({
	image,
	delegations : {
		erroredImage,
		setErroredImage,
	},
	...props
}) => erroredImage
	? <img {...props} src={defaultImg} />
	: (
		<img
			{...props}
			src={image || ""}
			onError={() => setErroredImage(true)}
		/>
	);

export default HandleImage;
