import React                   from "react";
import { HelpCircle }          from "react-feather";
import { UncontrolledTooltip } from "reactstrap";

const HelpToolTip = ({
	target,
	children,
	placement="bottom",
	className = "custom-style",
}) => {
	return (
		<>
			<HelpCircle id={target} size={14} />
			<UncontrolledTooltip className={className} placement={placement} target={target}>
				{children}
			</UncontrolledTooltip>
		</>
	);
};

export default HelpToolTip;
