
import { useEffect, useState }     from "react";
import { useForm, useFormContext } from "react-hook-form";

import ConstructionMatrix from "./ConstructionMatrix";

const SCORE = {
	surface             : [ 1, 1, 1, 2, 3, 3, 4],
	type                : [ 7, 8, 8, 8, 9, 9, 11],
	structure_walls     : [ 8, 11, 18, 19, 20, 21, 22 ],
	mezzanine           : [ 1, 4, 5, 7, 8, 9, 10],
	covers              : [ 4, 5, 6, 9, 13, 14, 16],
	finishes_walls      : [0, 4, 5, 6, 8, 9, 12 ],
	floors              : [0, 2, 4, 5, 6, 7, 10 ],
	facade              : [0, 3, 4, 5, 7, 13, 23 ],
	windows             : [1, 2, 4, 5, 10, 13, 16 ],
	bath_kitchen_covers : [0, 2, 3, 9, 12, 16, 20 ],
	bath_furniture      : [5, 8, 12, 25, 34, 46, 56 ],
};

const ConstructionMatrixContainer = ({ tableIndex, toggle, info }) => {

	const { setValue : setData }  = useFormContext();

	const defaultValues = {
		surface             : info?.surface ?? 0,
		type                : info?.type ?? 0,
		structure_walls     : info?.structure_walls ?? 0,
		mezzanine           : info?.mezzanine ?? 0,
		covers              : info?.covers ?? 0,
		finishes_walls      : info?.finishes_walls ?? 0,
		floors              : info?.floors ?? 0,
		facade              : info?.facade ?? 0,
		windows             : info?.windows ?? 0,
		bath_kitchen_covers : info?.bath_kitchen_covers ?? 0,
		bath_furniture      : info?.bath_furniture ?? 0,
	};

	const { reset, setValue } = useForm({ defaultValues });

	const [totalScore, setTotalScore]       = useState(0);
	const [checkedMatrix, setCheckedMatrix] = useState({...defaultValues});
	const [classType, setClassType]         = useState({
		val   : "",
		label : "",
	});

	const handleOnchange = (key, val) => () => {
		setCheckedMatrix({ ...checkedMatrix, [key] : val });
		setValue(key, val);
	};

	const getTotalScore = () => {
		const entries = Object.entries(checkedMatrix);

		const result = entries.reduce( (prev, current) => {
			const [key, val] = current;

			const score =  SCORE[key][val];

			return prev + score;
		}, 0);
		setTotalScore(result);
		return result;
	};

	const getScore = (key) => {
		const vals  = SCORE[key];
		const index = checkedMatrix[key];

		return vals[index];
	};

	const getClassNum = () => {
		switch (true) {
			case totalScore < 39:
				setClassType({ val : 1, label : "Precaria"});
				break;
			case totalScore < 61:
				setClassType({ val : 2, label : "Ecnomica"});
				break;
			case totalScore < 86:
				setClassType({ val : 3, label : "Mediana"});
				break;
			case totalScore < 116:
				setClassType({ val : 4, label : "Buena"});
				break;
			case totalScore < 146:
				setClassType({ val : 5, label : "Muy Buena"});
				break;
			case totalScore < 181:
				setClassType({ val : 6, label : "Especial"});
				break;
			default:
				setClassType({ val : 7, label : "Lujo"});
				break;
		}
	};

	const onSubmit  = () => {
		setData(`construction.${tableIndex}.class`, checkedMatrix);
		setData(`construction.${tableIndex}.class.class_type`, classType?.val);
		toggle();
	};

	useEffect(() => getClassNum(), [totalScore]);

	useEffect(() => getTotalScore(), [checkedMatrix]);

	useEffect(() => {
		reset({...defaultValues});
		setCheckedMatrix({...defaultValues});
	}, [tableIndex]);


	return (
		<ConstructionMatrix
			delegations={{
				onSubmit,
				getScore,
				classType,
				totalScore,
				tableIndex,
				checkedMatrix,
				handleOnchange,
			}}
		/>
	);
};

export default ConstructionMatrixContainer;
