import { numberFormat, numToLetters } from "Helpers";
import { useFormContext }             from "react-hook-form";

const CommercialValue = () => {

	const { getValues } = useFormContext();

	const getComercialVal = () => {
		const comercial_value = getValues("comercial_value");

		if (comercial_value < 10) {
			const roundedNumber = Math.round(comercial_value);
			return roundedNumber;
		}

		if (comercial_value < 100) {
			const roundedNumber = Math.round(comercial_value / 10) * 10;
			return roundedNumber;
		}

		const roundedNumber = Math.round(comercial_value / 100) * 100;
		return roundedNumber;
	};

	const comercial_val = getComercialVal();

	return (
		<div className="row">
			<div className="col-md-6 row ">
				<p className="fw-bolder col-md-6 text-md-end text-center">Valor Comercial:</p>
				<p className="col-md-6 text-md-start text-center">{numberFormat(comercial_val)}</p>
			</div>
			<div className="col-md-6 row ">
				<p className="fw-bolder col-md-3 text-md-end text-center">Con letra:</p>
				<p className="col-md-9 text-md-start text-center">{numToLetters(comercial_val)}</p>
			</div>
		</div>
	);
};

export default CommercialValue;
