import { numberFormat }   from "Helpers";
import { RadioButton }    from "Components/Global";
import Form               from "Components/Appraisal/Form";
import Divider            from "Components/Appraisal/Divider";
import { useFormContext } from "react-hook-form";

const LivingTypeResults = ({
	delegations : {
		onSubmit,
		livingType,
		isSubmitingData,
	},
}) => {

	const hookForm = useFormContext();
	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<div className="row">
				<div className="col-md-6">
					<Divider title="Tipo de Vivienda" />
					<div className="row g-1">
						<RadioButton
							className="my-1 col-md-6"
							id={"housing_type_0"}
							label="Unifamiliar"
							value="0"
							register={hookForm.register}
							name="housing_type"
							checked={livingType === "0"}
							rules={{ required : false }}
						/>
						<RadioButton
							className="col-md-6"
							id={"housing_type_1"}
							label="Triplex"
							value="1"
							register={hookForm.register}
							name="housing_type"
							checked={livingType === "1"}
							rules={{ required : false }}
						/>
						<RadioButton
							className="col-md-6"
							id={"housing_type_2"}
							label="Duplex"
							value="2"
							register={hookForm.register}
							name="housing_type"
							checked={livingType === "2"}
							rules={{ required : false }}
						/>
						<RadioButton
							className="col-md-6"
							id={"housing_type_3"}
							label="Condominio"
							value="3"
							register={hookForm.register}
							name="housing_type"
							checked={livingType === "3"}
							rules={{ required : false }}
						/>
					</div>
				</div>
				<div className="col-md-6">
					<Divider title="Resultado del Dictamen" />
					{/* TODO: These fields are filled when the appraisal is closed and they entered manually */}
					<p className="my-1 text-center">El resultado se captura antes de imprimir el avalo.</p>
					<div className="row">
						<p className="col-md-6 text-center text-md-end">Calificacin:</p>
						<p className="col-md-6 text-center text-md-start fw-bolder">{numberFormat(0)}</p>
						<p className="col-md-6 text-center text-md-end">Resultado:</p>
						<p className="col-md-6 text-center text-md-start fw-bolder">{numberFormat(0)}</p>
					</div>
				</div>
			</div>
		</Form>
	);
};

export default LivingTypeResults;
