import { Fragment } from "react";
import { Card }     from "reactstrap";

import { isValidArray, numberFormat } from "Helpers";
import AppraisalHOCPageFetcher        from "Components/Appraisal/AppraisalHOCPageFetcher";
import { PARAMS, DEFAULT_FETCH_DATA } from "Pages/app/Appraisal/CapitalizationApproach";
import TableRentEstimate              from "../TableRentEstimate";

const RentEstimate = ({  appraise = DEFAULT_FETCH_DATA }) => {

	const { construction, construction_partial_total_val } = appraise.physical_approach;

	return (
		<Card className="p-1">
			<TableRentEstimate />
			<p className="mt-1">El factor de insidencia resulta de la relacin entre los valores de reposicion nuevo, estimados en el enfoque de costos.</p>
			<h5 className="subTitle-text mt-2 text-center">Tipos de Construccin (Enfoque fsico)</h5>
			<div className="row text-center gy-1">
				<p className="fw-bolder col-6">Tipo de Construccin</p>
				<p className="fw-bolder col-6">Superficie m<sup>2</sup></p>
				{
					isValidArray(construction) &&
					construction.map(({ area_m2 }, index) => (
						<Fragment key={`RentEstimate-${index}`}>
							<p className="col-6">Habitacional</p>
							<p className="col-6">{numberFormat(area_m2)}</p>
						</Fragment>
					))
				}
				<hr />
				<p className="col-6">Superficie Total:</p>
				<p className="col-6">{numberFormat(construction_partial_total_val)}</p>
			</div>
		</Card>
	);
};
export default AppraisalHOCPageFetcher(RentEstimate, PARAMS);
