import { useFormContext } from "react-hook-form";

import finishOptions from "Resources/data/Finishes";

import { MapFormInpts } from "Components/Global";
import Form             from "Components/Appraisal/Form";
import CreableMsg       from "Components/Appraisal/CreableMsg";
import FinishesRepetear from "../FinishesRepetear";
import Parking          from "../Parking";

const NEW_OPTION_ID = 1;

const Finishes = ({ onSubmit, isSubmitingData }) => {

	const hookForm = useFormContext();

	const {
		//general
		roofOptions,
		wallOptions,
		floorsOptions,
		//kitchen
		floorKitchenOptions,
		wallKitchenOptions,
		//bath
		floorBathOptions,
		wallBathOptions,
		//yarn
		floorYarnOptions,
		roofYarnOptions,
		//facade
		wallFacadeOptions,
		//bathServiceRooms
		floorBathServiceOptions,
		wallBathServiceOptions,
	} = finishOptions;

	const inptsReceiver = [
		{
			label       : "Pisos:",
			name        : "finish.receiver_floors",
			inpt_type   : "creatable",
			options     : floorsOptions,
			rules       : { required : false },
			newOptionId : floorsOptions.length + NEW_OPTION_ID,
		},
		{
			label       : "Muros:",
			name        : "finish.receiver_walls",
			inpt_type   : "creatable",
			options     : wallOptions,
			rules       : { required : false },
			newOptionId : wallOptions.length + NEW_OPTION_ID,
		},
		{
			label       : "Plafn:",
			name        : "finish.receiver_roof",
			inpt_type   : "creatable",
			options     : roofOptions,
			rules       : { required : false },
			newOptionId : roofOptions.length + NEW_OPTION_ID,
		},
	];

	const inptDiningLivingRoom = [
		{
			label       : "Pisos:",
			name        : "finish.dng_lvng_floors",
			inpt_type   : "creatable",
			options     : floorsOptions,
			rules       : { required : false },
			newOptionId : floorsOptions.length + NEW_OPTION_ID,
		},
		{
			label       : "Muros:",
			name        : "finish.dng_lvng_walls",
			inpt_type   : "creatable",
			options     : wallOptions,
			rules       : { required : false },
			newOptionId : wallOptions.length + NEW_OPTION_ID,
		},
		{
			label       : "Plafn:",
			name        : "finish.dng_lvng_roof",
			inpt_type   : "creatable",
			options     : roofOptions,
			rules       : { required : false },
			newOptionId : roofOptions.length + NEW_OPTION_ID,
		},
	];

	const inptskitchen = [
		{
			label       : "Pisos:",
			name        : "finish.kitchen_floors",
			inpt_type   : "creatable",
			options     : floorKitchenOptions,
			rules       : { required : false },
			newOptionId : floorKitchenOptions.length + NEW_OPTION_ID,

		},
		{
			label       : "Muros:",
			name        : "finish.kitchen_walls",
			inpt_type   : "creatable",
			options     : wallKitchenOptions,
			rules       : { required : false },
			newOptionId : wallKitchenOptions.length + NEW_OPTION_ID,

		},
		{
			label       : "Plafn:",
			name        : "finish.kitchen_roof",
			inpt_type   : "creatable",
			options     : roofOptions,
			rules       : { required : false },
			newOptionId : roofOptions.length + NEW_OPTION_ID,

		},
	];

	const bedRooms = [
		{
			label       : "Pisos:",
			name        : "finish.bedrooms_floors",
			inpt_type   : "creatable",
			options     : floorsOptions,
			rules       : { required : false },
			newOptionId : floorsOptions.length + NEW_OPTION_ID,
		},
		{
			label       : "Muros:",
			name        : "finish.bedrooms_walls",
			inpt_type   : "creatable",
			options     : wallOptions,
			rules       : { required : false },
			newOptionId : wallOptions.length + NEW_OPTION_ID,
		},
		{
			label       : "Plafn:",
			name        : "finish.bedrooms_roof",
			inpt_type   : "creatable",
			options     : roofOptions,
			rules       : { required : false },
			newOptionId : roofOptions.length + NEW_OPTION_ID,
		},
	];

	const bathRooms = [
		{
			label       : "Pisos:",
			name        : "finish.bathrooms_floors",
			inpt_type   : "creatable",
			options     : floorBathOptions,
			rules       : { required : false },
			newOptionId : floorBathOptions.length + NEW_OPTION_ID,
		},
		{
			label       : "Muros:",
			name        : "finish.bathrooms_walls",
			inpt_type   : "creatable",
			options     : wallBathOptions,
			rules       : { required : false },
			newOptionId : wallBathOptions.length + NEW_OPTION_ID,

		},
		{
			label       : "Plafn:",
			name        : "finish.bathrooms_roof",
			inpt_type   : "creatable",
			options     : roofOptions,
			rules       : { required : false },
			newOptionId : roofOptions.length + NEW_OPTION_ID,

		},
	];

	const yard = [
		{
			label       : "Pisos:",
			name        : "finish.yard_floors",
			inpt_type   : "creatable",
			options     : floorYarnOptions,
			rules       : { required : false },
			newOptionId : floorYarnOptions.length + NEW_OPTION_ID,
		},
		{
			label       : "Muros:",
			name        : "finish.yard_walls",
			inpt_type   : "creatable",
			options     : wallOptions,
			rules       : { required : false },
			newOptionId : wallOptions.length + NEW_OPTION_ID,

		},
		{
			label       : "Plafn:",
			name        : "finish.yard_roof",
			inpt_type   : "creatable",
			options     : roofYarnOptions,
			rules       : { required : false },
			newOptionId : roofYarnOptions.length + NEW_OPTION_ID,

		},
	];

	const facade = [
		{
			label       : "Muros:",
			name        : "finish.facade_walls",
			inpt_type   : "creatable",
			options     : wallFacadeOptions,
			rules       : { required : false },
			newOptionId : wallFacadeOptions.length + NEW_OPTION_ID,
		},
	];

	const playRoom = [
		{
			label       : "Pisos:",
			name        : "finish.play_room_floors",
			inpt_type   : "creatable",
			options     : floorsOptions,
			rules       : { required : false },
			newOptionId : floorsOptions.length + NEW_OPTION_ID,
		},
		{
			label       : "Muros:",
			name        : "finish.play_room_walls",
			inpt_type   : "creatable",
			options     : wallOptions,
			rules       : { required : false },
			newOptionId : wallOptions.length + NEW_OPTION_ID,
		},
		{
			label       : "Plafn:",
			name        : "finish.play_room_roof",
			inpt_type   : "creatable",
			options     : roofOptions,
			rules       : { required : false },
			newOptionId : roofOptions.length + NEW_OPTION_ID,
		},
	];

	const serviceRooms = [
		{
			label       : "Pisos:",
			name        : "finish.service_rooms_floors",
			inpt_type   : "creatable",
			options     : floorsOptions,
			rules       : { required : false },
			newOptionId : floorsOptions.length + NEW_OPTION_ID,

		},
		{
			label       : "Muros:",
			name        : "finish.service_rooms_walls",
			inpt_type   : "creatable",
			options     : wallOptions,
			rules       : { required : false },
			newOptionId : wallOptions.length + NEW_OPTION_ID,
		},
		{
			label       : "Plafn:",
			name        : "finish.service_rooms_roof",
			inpt_type   : "creatable",
			options     : roofOptions,
			rules       : { required : false },
			newOptionId : roofOptions.length + NEW_OPTION_ID,
		},
	];

	const bathServiceRooms = [
		{
			label       : "Pisos:",
			name        : "finish.bath_service_rooms_floors",
			inpt_type   : "creatable",
			options     : floorBathServiceOptions,
			rules       : { required : false },
			newOptionId : floorBathServiceOptions.length + NEW_OPTION_ID,
		},
		{
			label       : "Muros:",
			name        : "finish.bath_service_rooms_walls",
			inpt_type   : "creatable",
			options     : wallBathServiceOptions,
			rules       : { required : false },
			newOptionId : wallBathServiceOptions.length + NEW_OPTION_ID,
		},
		{
			label       : "Plafn:",
			name        : "finish.bath_service_rooms_roof",
			inpt_type   : "creatable",
			options     : roofOptions,
			rules       : { required : false },
			newOptionId : roofOptions.length + NEW_OPTION_ID,
		},
	];

	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<CreableMsg />
			<div className="row mt-1">
				<MapFormInpts className="col-md-4" inpts={inptsReceiver} hookForm={hookForm}  title="Recibidor" />
			</div>
			<div className="row">
				<MapFormInpts className="col-md-4" inpts={inptDiningLivingRoom} hookForm={hookForm}  title="Estancia - Comedor" />
			</div>
			<div className="row">
				<MapFormInpts className="col-md-4" inpts={inptskitchen} hookForm={hookForm}  title="Cocina" />
			</div>
			<div className="row">
				<MapFormInpts className="col-md-4" inpts={bedRooms} hookForm={hookForm}  title="Recmaras" />
			</div>
			<div className="row">
				<MapFormInpts className="col-md-4" inpts={bathRooms} hookForm={hookForm}  title="Baos" />
			</div>
			<div className="row">
				<MapFormInpts className="col-md-4" inpts={yard} hookForm={hookForm}  title="Patio de Servicio" />
			</div>

			<Parking />

			<div className="row">
				<MapFormInpts className="col-md-4" inpts={facade} hookForm={hookForm}  title="Fachada" />
			</div>
			<div className="row">
				<MapFormInpts className="col-md-4" inpts={playRoom} hookForm={hookForm}  title="Sala de Juegos" />
			</div>
			<div className="row">
				<MapFormInpts className="col-md-4" inpts={serviceRooms} hookForm={hookForm}  title="Cuarto(s) de Servicio" />
			</div>
			<div className="row">
				<MapFormInpts className="col-md-4" inpts={bathServiceRooms} hookForm={hookForm}  title="Bao(s) de Servicio" />
			</div>
			<FinishesRepetear />
		</Form>
	);
};

export default Finishes;
