
import Form                      from "../Form";
import EcoTech                   from "./EcoTech";
import EnergySaver               from "./EnergySaver";
import HabitationalSet           from "./HabitationalSet";
import CirculationsAreas         from "./CirculationsAreas";
import InfonavitNewFields        from "./InfonavitNewFields";
import ComplementaryConstruction from "./ComplementaryConstruction";
import GeneralDescription        from "./GeneralDescription";
import Floor                     from "./Floor";
import Surface                   from "./Surface";
import Others                    from "./Others";

const Infonavit = ({ onSubmit, isSubmitingData }) => {
	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<CirculationsAreas />
			<HabitationalSet />
			<InfonavitNewFields />
			<EnergySaver />
			<EcoTech />
			<ComplementaryConstruction />
			<Others />
			<GeneralDescription />
			<Floor />
			<Surface />
		</Form>
	);
};

export default Infonavit;
