import { connect }                   from "react-redux";
import { Button, Collapse, Spinner } from "reactstrap";
import InfiniteScroll                from "react-infinite-scroll-component";

//Our Importations
import { isValidArray } from "Helpers";
import { UserImage }    from "Components/Global";
import Input            from "Components/Global/Input";
import "./Comments.scss";


const comments = [
	{
		"_id"     : "61eef7b5f397453b006561d8",
		"type"    : "user",
		"comment" : "Comentario",
		"user"    : {
			"_id"   : "6137bb708f17b6315124ce38",
			"name"  : "River",
			"image" : "http://placeimg.com/500/500",
		},
		"createdAt" : "2022-01-24 13:02:13",
	},
];

const Comments = ({
	user,
	delegations : {
		page,
		errors,
		getTime,
		loading,
		register,
		totalDocs,
		totalPages,
		fetchMoreData,
		isCollapseOpen,
		handleSubmitForm,
		setTsCollapseOpen,
		handleCancelComment,
	},
}) => {


	return (
		<div id="Comments">
			<h4 className="mb-1">{totalDocs} {totalDocs !== 1 ? "Comentarios": "Comentario"}</h4>
			<div className="d-flex">
				<UserImage
					name={user?.name ?? ""}
					image={user?.image ?? ""}
				/>
				<Input
					type="textarea"
					id="comment"
					name="comment"
					className="mx-1"
					placeholder="Agregar Comentario"
					invalid={!!errors?.comment?.type}
					onFocus={ () => setTsCollapseOpen(true)}
					{...register("comment", { required : true })}
				/>
			</div>

			<Collapse
				isOpen={isCollapseOpen}
			    >
				<div className="comments-btns-wrapper mt-1">
					<Button
						outline
						color="danger"
						disabled={loading}
						className="primary-btn"
						onClick={handleCancelComment}
					>
						Cancelar
					</Button>
					<Button
						color="primary"
						disabled={loading}
						className="primary-btn"
						onClick={handleSubmitForm}
					>
						{
							loading ?
								<>

									<Spinner color='white' size='sm' type='grow' />
									<span className='ml-50'>Cargando...</span>
								</>
								:
								<>
									Comentar
								</>
						}
					</Button>
				</div>
			</Collapse>

			<InfiniteScroll
				dataLength={comments.length ?? 0}
				next={fetchMoreData}
				hasMore={page < totalPages}
				loader={<h4>Cargando...</h4>}
			>
				<div className="carsContent">
					{
						isValidArray(comments) && comments.map((info) =>(
							<div
								key={info?._id}
								className="massage-content mt-2"
							>
								<UserImage
									imgHeight={40}
									imgWidth={40}
									name={info?.user?.name ?? ""}
									image={info?.user?.image ?? ""}
								/>
								<div>
									<div>
										<span className="user-name">{info?.user?.name}</span>
										<span className="date">{getTime(info?.createdAt) ?? ""}</span>
									</div>
									<p>{info?.comment}</p>
								</div>
							</div>
						))
					}
				</div>
			</InfiniteScroll>
		</div>
	);
};
const mapStateToProps = ({ _authSlice }) => ({
	user : _authSlice?.user ?? {},
});

export default connect(mapStateToProps)(Comments);
