import React                      from "react";
import { ModalBody, ModalHeader } from "reactstrap";

import tableImg from "Resources/images/TablaEquivalencia.png";
const EquivalenceTable = ({
	delegations : {
		handleCloseModal,
	},
}) => {
	return (
		<>
			<ModalHeader
				toggle={handleCloseModal}
			>
			</ModalHeader>
			<ModalBody>
				<img
					width="100%"
					height="100%"
					src={tableImg}
				/>
			</ModalBody>
		</>
	);
};

export default EquivalenceTable;
