import { Button } from "reactstrap";
import { Save }   from "react-feather";

import "./Form.scss";
import { useFormContext } from "react-hook-form";

const Form = ({ children, onSubmit, isSubmitingData }) => {

	const { formState : { isDirty } } = useFormContext();

	return (
		<form id="Form" onSubmit={onSubmit}>
			<div className="btn-container">
				<Button.Ripple
					type="submit"
					onClick={onSubmit}
					color="primary"
					className="btn-icon"
					disabled={!isDirty || isSubmitingData}
				>
					<Save size={14} />
				</Button.Ripple>
			</div>
			<div className="inpts-wrapper">
				{children}
			</div>
		</form>

	);
};

export default Form;
