import { useFormContext } from "react-hook-form";
import TableInvesCalcs    from "./TableInvesCalcs";

const TableInvesCalcsContainer = ({ index }) => {

	const hookForm = useFormContext();

	return (
		<TableInvesCalcs
			delegations={{
				index,
				hookForm,
			}}
		/>
	);
};

export default TableInvesCalcsContainer;
