import { useEffect }                from "react";
import { useFormContext, useWatch } from "react-hook-form";

import { sumSetOfValues } from "Helpers/formulas";

const useColumnSum = ({ arrayKey, fieldKey, length = 0, name  }) => {

	const { setValue } = useFormContext();

	const vals = useWatch({ name : [...Array(length).keys()].map((index) => `${arrayKey}.${index}.${fieldKey}`) });

	useEffect(() => {
		const totalSum = sumSetOfValues(vals);

		setValue(name, totalSum);
	}, [vals]);

	return vals;

};

export default useColumnSum;
