import PropTypes            from "prop-types";
import { FormGroup, Label } from "reactstrap";
import classNames           from "classnames";
import Flatpickr            from "react-flatpickr";
import { Spanish }          from "flatpickr/dist/l10n/es";
import "@styles/react/libs/flatpickr/flatpickr.scss";

//Our imports
import "./FormFlatPickrGroup.scss";
import { Controller } from "react-hook-form";

const FormFlatPickrGroup = ({
	icon,
	name,
	label,
	errors,
	rules,
	control,
	required,
	disabled,
	options,
	className="mt-1",
	...rest
}) => {
	const splitName = name.split(".");

	const getErrors = (controllerErrors) => {

		if (splitName.length > 1) {
			return controllerErrors?.[splitName[0]]?.[splitName[1]] ?? "";
		}
		return controllerErrors?.[name] ?? "";
	};

	return (
		<FormGroup id="FormFlatPickrGroup" className={className}>
			{ label && (
				<Label>{label}</Label>
			)}
			{
				control ?
					<Controller
						name={name}
						control={control}
						rules={rules}
						render={({ field: { onChange, value }, formState : { errors : controllerErrors} }) => (
							<Flatpickr
								className={classNames("form-control flatpickr-input", {
									invalid         : (getErrors(controllerErrors)?.type),
									"disabled-inpt" : disabled,
								})}
								name={name}
								value={value}
								onChange={onChange}
								disabled={disabled}
								options={{
									locale     : Spanish,
									dateFormat : "d/m/Y",
									...options,
								}}
								{...rest}
							/>
						)}
					/>
					:
					<Flatpickr
						name={name}
						className={classNames("form-control flatpickr-input", {
							invalid         : (getErrors(errors)?.type),
							"disabled-inpt" : disabled,
						})}
						disabled={disabled}
						{...rest}
					/>
			}
			{!!getErrors() && (
				<span className="text-error mb-1">{ getErrors()?.message }</span>
			)}
		</FormGroup>
	);
};

FormFlatPickrGroup.propTypes = {
	rules    : PropTypes.object,
	disabled : PropTypes.bool,
	label    : PropTypes.string,
	options  : PropTypes.object,
	errors   : PropTypes.object,
	name     : PropTypes.string,
	icon     : PropTypes.oneOfType([PropTypes.element, PropTypes.string]),
};

FormFlatPickrGroup.defaultProps = {
	label    : "",
	errors   : {},
	name     : "",
	disabled : false,
	options  : { locale : Spanish, dateFormat : "d/m/Y" },
	icon     : "",
	rules    : {
		required : {
			value   : true,
			message : "Este campo no puede estar vaco.",
		},
	},
};

export default FormFlatPickrGroup;
