import React, { useState } from "react";

import { useFormContext } from "react-hook-form";

//Our Imports
import { CheckboxInpt, FormInputGroup } from "Components/Global";
import { geoOptions }                   from "Resources/data/GeneralInformationForm";
import Coordinates                      from "../Coordinates";
import CoordinatesNumber                from "../CoordinatesNumber";
import { Mapa }                         from "Helpers";

const { latDirectionOptions, longDirectionOptions } = geoOptions;

const Georeference = ({ mapCenter }) => {

	const hookForm = useFormContext();

	const [checked, setChecked] = useState({ "decimal" : true });
	const defaultLat = parseFloat(mapCenter.lat) || 40.7128;
	const defaultLng = parseFloat(mapCenter.lng) || -74.0060;

	const [center, setCenter] = useState({
		lat : defaultLat,
		lng : defaultLng,
	});

	const changePoints = (newcenter) => {
		const newpoints = {
			lat : isNaN(newcenter.lat) ? defaultLat : newcenter.lat,
			lng : isNaN(newcenter.lng) ? defaultLng : newcenter.lng,
		};

		setCenter(newpoints);
	};
	return (
		<>
			<h4 className="mb-1">Informacin de Georreferencia</h4>
			<p>
				{/* Nota: La georreferencias deben tener <b>7 decimales</b>. Los formatos automticos de sofa usarn siempre 7 decimales. En caso de captura manual,
				deben completarse los decimales, siendo el ltimo digito alguno diferente de cero. En caso que el septimo decimal sea 0 se recomienda cambiar por 1. */}
				Nota: Las georreferencias deben tener <b>7 decimales</b>.<br />
				En capturas manuales, asegrese de completar todos los decimales y cambie el sptimo a 1 en caso de ser 0.
			</p>
			<div className="group mb-1">
				<CheckboxInpt
					label="Decimales"
					value={checked.decimal}
					checkValue={checked.decimal}
					onChange={()=> setChecked({ "decimal" : true,  "degrees" : false })}
				/>
				<CheckboxInpt
					label="Grados"
					value={checked.degrees}
					checkValue={checked.degrees}
					onChange={()=> setChecked({ "degrees" : true, "decimal" : false })}
				/>
			</div>
			<div className="row p-1">
				<div className="col-md-6">
					{
						checked.degrees ? (
							<>
								<Coordinates label="Latitud" line="lat" center={center} setCenter={setCenter} checked={checked} longOrLatOptions={latDirectionOptions} />
								<Coordinates label="Longitud" line="long" center={center} setCenter={setCenter} checked={checked} longOrLatOptions={longDirectionOptions} />
							</>
						) : (
							<>
								<CoordinatesNumber label="Latitud" line="lat" center={center} setCenter={setCenter} checked={checked} />
								<CoordinatesNumber label="Longitud" line="long" center={center} setCenter={setCenter} checked={checked} />
							</>
						)
					}
					<FormInputGroup
						bsSize="sm"
						className="col-md-12"
						type="number"
						label="Altitud:"
						name="general_information.alt"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						options={{ required : false }}
					/>
				</div>
				<div className="col-md-6">
					<Mapa center={center} onMapClick={changePoints} />
				</div>
			</div>
		</>
	);
};


export default Georeference;
