import { Button, UncontrolledTooltip } from "reactstrap";
import { HelpCircle, X }               from "react-feather";

import { FormInputGroup } from "Components/Global";

const RepetearForm = ({
	delegations : {
		fields,
		hookForm,
		handleAdd,
		removeItem,
		collectionName,
		handleAddTitle,
	},
}) => {
	return (
		<div id="RepetearForm">
			<p className="mb-2">Puedes subir un archivo(PDF) en lugar de llenar las <b>Colindancias</b></p>
			{
				fields.map((field, index) => (
					<div key={field.id } className="row">
						<p className="col-md-1"><b>{index + 1}</b></p>
						{
							field?.title !== undefined ?
								<FormInputGroup
									bsSize="sm"
									className="col-md-10"
									label="Ttulo"
									name={`${collectionName}.${index}.title`}
									register={hookForm.register}
									errors={hookForm.formState.errors}
									options={{ required : false }}
								/>
								:
								<div className="col-md-10">
									<div className="row w-100">
										<div className="col-md-4">
											<div className="d-flex">
												<FormInputGroup
													bsSize="sm"
													className="w-100"
													label="Direccin"
													name={`${collectionName}.${index}.address`}
													register={hookForm.register}
													errors={hookForm.formState.errors}
													options={{ required : false }}
												/>
												<HelpCircle id={`help-address-${index}`} size={14} />
												<UncontrolledTooltip className="text-end" placement="bottom" target={`help-address-${index}`}>
													<p className="text-start">Al Norte:</p>
													<p className="text-start">Al Sur:</p>
													<p className="text-start">Al Este:</p>
													<p className="text-start">Al Oeste:</p>
												</UncontrolledTooltip>
											</div>
										</div>

										<FormInputGroup
											bsSize="sm"
											type="number"
											className="col-md-4"
											placeholder="En metros"
											label="Distancia"
											name={`${collectionName}.${index}.distance`}
											register={hookForm.register}
											errors={hookForm.formState.errors}
											options={{ required : false }}
										/>
										<FormInputGroup
											bsSize="sm"
											type="textarea"
											label="Descripcin"
											className="col-md-4"
											name={`${collectionName}.${index}.description`}
											register={hookForm.register}
											errors={hookForm.formState.errors}
											options={{ required : false }}
										/>
									</div>
								</div>
						}
						<Button.Ripple
							color="flat-danger"
							className="btn-icon col-md-1 delete-btn-repetear"
							onClick={removeItem(index)}
						>
							<X size={18} />
						</Button.Ripple>
					</div>
				))
			}
			<Button.Ripple
				type="button"
				onClick={handleAddTitle}
				color="flat-primary"
			>
				Agregar Ttulo
			</Button.Ripple>
			<Button.Ripple
				type="button"
				onClick={handleAdd}
				color="flat-primary"
			>
				Agregar Colindancia
			</Button.Ripple>
		</div>
	);
};

export default RepetearForm;
