import { useColumnSum }                  from "Helpers/Hooks";
import { useFieldArray, useFormContext } from "react-hook-form";
import TableRentEstimate                 from "./TableRentEstimate";

const TableRentEstimateContainer = () => {

	const fieldRentEstimate = {
		profitable_unit : undefined,
		surface         : "",
		unit_rent_m2    : "",
		incidence_val   : "",
		mensual_rent    : "",
	};

	const hookForm     = useFormContext();
	const { setValue } = hookForm;

	const { fields : rentEstimateFields, append, remove } = useFieldArray({
		control : hookForm.control,
		name    : "rent_estimate",
	});

	const appendNewRow = () => append(fieldRentEstimate);

	const removeRow = (index) => () => remove(index);

	const handleSetValue = ([ val ], name) => setValue(name, val);

	useColumnSum({
		length   : 6,
		arrayKey : "rent_estimate",
		fieldKey : "mensual_rent",
		name     : "gross_mensual_rent",
	});

	return (
		<TableRentEstimate
			delegations={{
				hookForm,
				removeRow,
				appendNewRow,
				handleSetValue,
				rentEstimateFields,
			}}
		/>
	);
};

export default TableRentEstimateContainer;
