import React              from "react";
import { Button }         from "reactstrap";
import { useFormContext } from "react-hook-form";

import { MapFormInpts } from "Components/Global";
import finishOptions    from "Resources/data/Finishes";

const NEW_OPTION_ID = 1;

const Parking = ({
	delegations : {
		toggle,
		covered,
	},
}) => {
	const hookForm = useFormContext();

	const {
		floorParkingOptions,
		wallParkingOptions,
		roofParkingOptions,
	} = finishOptions;

	const floor = {
		label       : "Pisos:",
		name        : "finish.parking_floors",
		inpt_type   : "creatable",
		options     : floorParkingOptions,
		rules       : { required : false },
		newOptionId : floorParkingOptions.length + NEW_OPTION_ID,
	};

	const rest = [
		{
			label       : "Muros:",
			name        : "finish.parking__walls",
			inpt_type   : "creatable",
			options     : wallParkingOptions,
			rules       : { required : false },
			newOptionId : wallParkingOptions.length + NEW_OPTION_ID,

		},
		{
			label       : "Plafn:",
			name        : "finish.parking__roof",
			inpt_type   : "creatable",
			options     : roofParkingOptions,
			rules       : { required : false },
			newOptionId : roofParkingOptions.length + NEW_OPTION_ID,

		},
	];

	return (
		<div className="mb-1">
			<div className="row">
				<MapFormInpts className="col-md-4" inpts={ covered ? [floor] : [floor, ...rest]} hookForm={hookForm}  title="Estacionamiento" />
			</div>
			<Button.Ripple
				type="button"
				color='primary'
				className="btn-sm "
				onClick={toggle}
			>
				{covered ? "Cubierto" : "Descubierto"}
			</Button.Ripple>
		</div>
	);
};

export default Parking;
