import { useState }  from "react";
import { useWatch }  from "react-hook-form";
import { useParams } from "react-router-dom";

import { CheckboxInpt }               from "Components/Global";
import { genericApi }                 from "Store/api/genericApi";
import { DEFAULT_FETCH_DATA, PARAMS } from "Pages/app/Appraisal/AppraisalDoc/AppraisalDocContants";
import AppraisalHOCPageFetcher        from "../AppraisalHOCPageFetcher";


const ConfirmFile = ({ name, fileName, className, appraise = DEFAULT_FETCH_DATA }) => {
	const { id : appraiseId } = useParams();

	const fileWatcher = useWatch({ name : fileName });

	const { documentation } = appraise;
	const fileInfo = documentation?.[name];

	const [checked, setChecked] = useState(fileInfo?.status);

	const [ handlerMutation, { isLoading } ] = genericApi.useAddAndUpdateMutation();

	const handleChange = (e) => {
		const { target : { checked } } = e;
		setChecked(checked);
		handlerMutation({
			module : `/appraisals/atachment-file/status/${appraiseId}`,
			method : "PATCH",
			data   : { input_name : name },
		});
	};

	return (
		<div className={className}>
			<CheckboxInpt
				name={name}
				type="checkbox"
				className="mt-1"
				checkValue={checked}
				disabled={ isLoading || !fileWatcher }
				onChange={handleChange}
			/>
		</div>
	);
};

export default AppraisalHOCPageFetcher(ConfirmFile, PARAMS);
