import { useFormContext } from "react-hook-form";

import apprasialOptions from "Resources/data/Appraisal";
import { MapFormInpts } from "Components/Global";

const EnergySaver = () => {

	const { statusOptions } = apprasialOptions;

	const hookForm = useFormContext();

	const inpts = [
		{
			text      : "Calentador de paso de gas:",
			name      : "gas_heater",
			inpt_type : "check-box-group",
		},
		{
			text      : "Calentador solar de agua:",
			name      : "solar_water_heater",
			inpt_type : "check-box-group",
		},
		{
			text      : "Captacin de agua pluvial:",
			name      : "aqueduct_water_capture",
			inpt_type : "check-box-group",
		},
		{
			text      : "Captacin de energa elctrica:",
			name      : "electric_capture",
			inpt_type : "check-box-group",
		},
		{
			text      : "Dos circuitos de electricidad:",
			name      : "two_electric_circuits",
			inpt_type : "check-box-group",
		},
		{
			text      : "rea de contenedor para elaboracin de composta:",
			name      : "area_container",
			inpt_type : "check-box-group",
		},
		{
			text      : "Aislamiento trmico en muro (con sello FIDE):",
			name      : "termic_isolation_fide",
			inpt_type : "check-box-group",
		},
		{
			text      : "Aislamiento trmico en puertas y ventanas:",
			name      : "termic_isolation_doors_windows",
			inpt_type : "check-box-group",
		},
		{
			text      : "Aislamiento trmico en el techo (con NOM MX 460):",
			name      : "termic_isolation_ceiling",
			inpt_type : "check-box-group",
		},
		{
			text      : "Economizador de agua doble botn (Sistema dual para wc):",
			name      : "dual_flush_toilet",
			inpt_type : "check-box-group",
		},
		{
			text      : "Fotoceldas para alumbrado pblico:",
			name      : "fotocells_lighting",
			inpt_type : "check-box-group",
		},
		{
			text      : "Proteccin solar en las ventanas:",
			name      : "window_solar_protection",
			inpt_type : "check-box-group",
		},
		{
			text      : "Regadera ahorradora de agua (Compensadora de flujo):",
			name      : "shower_saving_water",
			inpt_type : "check-box-group",
		},
		{
			text      : "Sistema de reuso de aguas jabonosas:",
			name      : "reuse_water_system",
			inpt_type : "check-box-group",
		},
		{
			text      : "Sistema de reuso del agua residual tratada:",
			name      : "reuse_water_system_residual_treated",
			inpt_type : "check-box-group",
		},
		{
			text      : "Llaves ahorradoras de agua (vlvulas para uso domstico) bao:",
			name      : "faucet_saving_water_bath",
			options   : statusOptions,
			rules     : { required : false },
			inpt_type : "row-select",
		},
		{
			text      : "Llaves ahorradoras de agua (vlvulas para uso domstico) cocina:",
			name      : "faucet_saving_water_kitchen",
			options   : statusOptions,
			rules     : { required : false },
			inpt_type : "row-select",
		},
		{
			text      : "WC economizador de agua (5 litros):",
			name      : "wc_economizer_water_5lt",
			options   : statusOptions,
			rules     : { required : false },
			inpt_type : "row-select",
		},
		{
			text      : "WC economizador de agua (4.8 litros):",
			name      : "wc_economizer_water_4lt",
			options   : statusOptions,
			rules     : { required : false },
			inpt_type : "row-select",
		},
		{
			text      : "WC economizador de agua (6 litros):",
			name      : "wc_economizer_water_6lt",
			options   : statusOptions,
			rules     : { required : false },
			inpt_type : "row-select",
		},
	];

	return (
		<div className="row">
			<hr />
			<h4>Ahorro de Energa</h4>
			<p>Sistemas o elementos ahorradores de energa en la vivienda</p>
			<MapFormInpts className="col-md-6" inpts={inpts} hookForm={hookForm} />
		</div>
	);
};

export default EnergySaver;
