import { useFormContext } from "react-hook-form";

import { FormInputGroup }                from "Components/Global";
import { RHFUploadFile, RHFUploadFiles } from "Components/Global/DropZoneRHF";
import Form                              from "../Form";

const RHF_RULES = { required : false };

const UploadPicturesForm = ({ onSubmit, isSubmitingData }) => {

	const hookForm = useFormContext();

	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<RHFUploadFile
				label="Fachada"
				name="facade.file"
				rules={RHF_RULES}
			/>
			<FormInputGroup
				name="facade.description"
				type="textarea"
				label="Descripcin"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				options={{ required : false }}
			/>
			<RHFUploadFile
				name="localization_map.file"
				label="Croquis de Localizacin:"
				rules={RHF_RULES}
			/>
			<FormInputGroup
				name="localization_map.description"
				type="textarea"
				label="Descripcin"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				options={{ required : false }}
			/>
			<RHFUploadFile
				name="rear_facade.file"
				label="Fachada Posterior:"
				rules={RHF_RULES}
			/>
			<FormInputGroup
				name="rear_facade.description"
				type="textarea"
				label="Descripcin"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				options={{ required : false }}
			/>
			<RHFUploadFile
				name="dinning_room.file"
				label="Estancia / Comedor:"
				rules={RHF_RULES}
			/>
			<FormInputGroup
				name="dinning_room.description"
				type="textarea"
				label="Descripcin"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				options={{ required : false }}
			/>
			<RHFUploadFile
				name="kitchen.file"
				label="Cocina:"
				rules={RHF_RULES}
			/>
			<FormInputGroup
				name="kitchen.description"
				type="textarea"
				label="Descripcin"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				options={{ required : false }}
			/>
			<RHFUploadFile
				name="bathroom.file"
				label="Bao:"
				rules={RHF_RULES}
			/>
			<FormInputGroup
				name="bathroom.description"
				type="textarea"
				label="Descripcin"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				options={{ required : false }}
			/>
			<RHFUploadFile
				name="bedroom.file"
				label="Recmara:"
				rules={RHF_RULES}
			/>
			<FormInputGroup
				name="bedroom.description"
				type="textarea"
				label="Descripcin"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				options={{ required : false }}
			/>
			<RHFUploadFile
				name="environment.file"
				label="Entorno:"
				rules={RHF_RULES}
			/>
			<FormInputGroup
				name="environment.description"
				type="textarea"
				label="Descripcin"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				options={{ required : false }}
			/>
			<RHFUploadFile
				name="sketch_property.file"
				label="Croquis del inmueble:"
				rules={RHF_RULES}
			/>
			<FormInputGroup
				name="sketch_property.description"
				type="textarea"
				label="Descripcin"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				options={{ required : false }}
			/>
			<RHFUploadFile
				name="bedroom_two.file"
				label="Recamara dos:"
				rules={RHF_RULES}
			/>
			<FormInputGroup
				name="bedroom_two.description"
				type="textarea"
				label="Descripcin"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				options={{ required : false }}
			/>
			<RHFUploadFile
				name="microlocation.file"
				label="Microlocalizacion (opcional):"
				rules={RHF_RULES}
			/>
			<FormInputGroup
				name="microlocation.description"
				type="textarea"
				label="Descripcin"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				options={{ required : false }}
			/>
			<RHFUploadFiles
				name="other_files"
				maxFiles={10}
				label="Otros Archivos (Mximo 10 archivos. Opcional)"
				rules={RHF_RULES}
			/>
		</Form>
	);
};

export default UploadPicturesForm;
