import Form         from "Components/Appraisal/Form";
import RepetearForm from "../RepetearForm";
import UploadButton from "../UploadButton";

const PrivateBoundaries = ({
	...rest
}) => {
	return (
		<Form {...rest}>
			<RepetearForm collectionName="private_boundaries" />
			<UploadButton />
		</Form>
	);
};

export default PrivateBoundaries;
