import { bindAll }        from "Helpers";
import { connect }        from "react-redux";
import { ModalSlice }     from "Store/Slices";
import ApprasialMenuModal from "./ApprasialMenuModal";

const ApprasialMenuModalContainer = ({ id, modalSliceActions }) => {

	const handleCloseModal = () => modalSliceActions.closeModal("apprasialMenuModal");

	return (
		<ApprasialMenuModal
			delegations={{
				handleCloseModal,
			}}
		/>
	);
};
const mapStateToProps = ({ _modalSlice }) => ({
	id : _modalSlice?.props?.apprasialMenuModal?.id ?? "",
});

const mapDispatchToProps = bindAll({
	ModalSliceActions : ModalSlice.actions,
});

export default connect(mapStateToProps, mapDispatchToProps)(ApprasialMenuModalContainer);
