import { connect } from "react-redux";
import { Button}   from "reactstrap";
import { File }    from "react-feather";

import {AvatarText, Paginate, Table} from "Components/Global";
import { genericApi }                from "Store/api/genericApi";
import "./AppraisalTable.scss";
import { getTime }                   from "Helpers";

const DEFAULT_APPRASIAL_DATA = { docs : [], totalPages : 1 };

const DATE_FORMAT = { day : "2-digit",  month : "2-digit",  year : "numeric"  };

const AppraisalTable = ({
	filters,
	delegations : {
		getFilters,
		togglePaginator,
		toggleOpenSelectPeritModal,
		toggleOpenAppraisalMenuModal,
	},
}) => {

	const { data = {}, isFetching } = genericApi.useGetDataQuery({
		module : "appraisals",
		params : getFilters(filters),
	});

	const { appraisals = DEFAULT_APPRASIAL_DATA } = data;

	return (
		<div id="AppraisalTable">
			<Table loading={isFetching} hover responsive>
				<thead>
					<tr>
						<th  className="text-center">
							Clave Sofia
						</th>
						<th  className="text-center">
							Fecha
						</th>
						<th  className="text-center">
							Solicitante
						</th>
						<th  className="text-center">
							Direccin
						</th>
						<th  className="text-center">
							Perito
						</th>
						<th  className="text-center">
							Controlador
						</th>
						<th className="text-end">
							Acciones
						</th>
					</tr>
				</thead>
				<tbody>
					{
						appraisals.docs.map(({_id, id_state, ...apprasial}) => (
							<tr key={_id} onClick={toggleOpenAppraisalMenuModal(_id, apprasial?.key_sofia)}>
								<td className="text-center">
									{ apprasial?.key_sofia ? apprasial?.key_sofia : _id }
								</td>
								<td className="text-center">
									{getTime(apprasial?.createdAt, DATE_FORMAT)}
								</td>
								<td className="text-center">{apprasial?.applicant}</td>
								<td className="text-center">{apprasial?.address}.</td>
								<td className="pending" onClick={toggleOpenSelectPeritModal(_id, "CLAJUYSR", id_state)}>
									{
										apprasial?.perit?.name ?
											<div className="text">
												<AvatarText
													size={21}
													image={apprasial?.perit?.url}
													name={apprasial?.perit?.name}
												/>
											</div>
											:
											<p className="text">(Pendiente)</p>
									}
								</td>
								<td className="pending" onClick={toggleOpenSelectPeritModal(_id, "", id_state)}>
									<AvatarText
										size={21}
										image={apprasial?.controller?.url}
										name={apprasial?.controller?.name}
									/>
								</td>
								<td align="center">
									<Button.Ripple
										color="flat-info"
										className="btn-icon rounded-circle"
										onClick={() => {}}
									>
										<File size={16} />
									</Button.Ripple>
								</td>
							</tr>
						))
					}
				</tbody>
			</Table>
			<Paginate
				currentPage={filters.page}
				totalPage={appraisals.totalPages}
				handleGetData={togglePaginator()}
			/>
		</div>
	);
};

const mapDispatchToState = ({ _authSlice, _appraisalSlice }) => ({
	type_shf : _authSlice?.user?.type_shf ?? "",
	filters  : _appraisalSlice.filters,
});

export default connect(mapDispatchToState)(AppraisalTable);
