import { Controller } from "react-hook-form";
import Select         from "react-select";
import {
	Label,
} from "reactstrap";

import { deepAccessObj, reactSelectStyles } from "Helpers";
import "./FormSelectGroup.scss";

const FormSelectGroup = ({
	name,
	size,
	value,
	label,
	errors,
	hookForm,
	className = "mb-1",
	rules={
		required : {
			value   : true,
			message : "Este campo no puede estar vaco.",
		},
	},
	isClearable = true,
	...rest
}) => {
	return (
		<div id="FormSelectGroup" className={className}>
			{ label && (
				<Label>{label}</Label>
			)}
			{
				hookForm ?
					<Controller
						name={name}
						control={hookForm.control}
						rules={rules}
						render={({ field: { onChange, value }, formState : { errors : controllerErrors } },) => (
							<>
								<Select
									{...rest}
									name={name}
									defaultOptions
									value={value}
									onChange={onChange}
									className={"react-select"}
									classNamePrefix="select"
									isClearable={isClearable}
									styles={reactSelectStyles(deepAccessObj(name, controllerErrors), size)}
								/>
								{ !!deepAccessObj(name, controllerErrors) && (
									<span className="text-error mb-1">{ deepAccessObj(name, controllerErrors)?.message }</span>
								)}
							</>
						)}
					/>
					:
					<>
						<Select
							{...rest}
							defaultOptions
							value={value}
							className={"react-select"}
							isClearable={isClearable}
							classNamePrefix="select"
							styles={reactSelectStyles(deepAccessObj(name, errors), size) }
						/>
						{ !!deepAccessObj(name, errors) && (
							<span className="text-error mb-1">{ deepAccessObj(name, errors)?.message }</span>
						)}
					</>
			}
		</div>
	);
};

export default FormSelectGroup;
