import {
	useState,
	useEffect,
} from "react";
import { connect } from "react-redux";
import { useForm } from "react-hook-form";


//Our Importations
import Comments from "./Comments";
import dayjs    from "dayjs";

const CommentsContainer = ({
	id,
	pages,
	totalDocs,
	curret_page,
}) => {

	const {
		register,
		setValue,
		clearErrors,
		handleSubmit,
		formState : { errors },
	} = useForm();

	const [loading]                   = useState(false);
	const [totalPages, setTotalPages] = useState(1);
	const [page, setPage]             = useState(1);
	const [isCollapseOpen, setTsCollapseOpen] = useState(false);

	const fetchMoreData = () => {
		if (page < totalPages) {
			setPage(prev =>  prev + 1);
			// genericService.get(id, "comments", () => {}, { page : page + 1});
		}
	};


	const handleCancelComment = () => {
		clearErrors();
		setValue("comment", "");
		setTsCollapseOpen(false);
	};

	const handleSubmitForm = (...args) => {

		handleSubmit(async (data) => {
			console.log(data);

		})(args);
	};

	const getTime = (createdAt) => dayjs(createdAt).format("DD/MM/YYYY HH:mm");

	useEffect(() => {
		if (pages && curret_page) {
			setPage(curret_page);
			setTotalPages(pages);
		}
	}, [curret_page, pages]);

	return (
		<Comments
			delegations={{
				page,
				errors,
				getTime,
				loading,
				register,
				totalDocs,
				totalPages,
				fetchMoreData,
				isCollapseOpen,
				handleSubmitForm,
				setTsCollapseOpen,
				handleCancelComment,
			}}
		/>
	);
};

const mapStateToProps = ({ _commentsReducer }) => ({
	curret_page : _commentsReducer?.page ?? 1,
	totalDocs   : _commentsReducer?.totalDocs ?? 1,
	pages       : _commentsReducer?.totalPages ?? 1,

});

export default connect(mapStateToProps)(CommentsContainer);
