import React                                   from "react";
import { ModalBody, ModalFooter, ModalHeader } from "reactstrap";

//Our Imports
import SubmitBtn      from "Components/Global/SubmitBtn";
import FormInputGroup from "Components/Global/FormInputGroup";
import "./ReturnStatusModal.scss";

const ReturnStatusModal = ({
	delegations : {
		hookForm,
		onSubmit,
		isLoading,
		handleCloseModal,
	},
}) => {
	return (
		<div id="ReturnStatusModal">
			<ModalHeader
				toggle={handleCloseModal}
			>
				<p className="text-header-modal">Regresar Estatus</p>
			</ModalHeader>
			<form onSubmit={onSubmit}>
				<ModalBody>
					<p>Antes de continuar, porfavor explica cual es la razn para regresar de estado.</p>
					<FormInputGroup
						name="subject"
						label="Motivo"
						type="textarea"
						labelClassName="text-checkbox-modal mt-1"
						placeholder="Escribe el motivo"
						register={hookForm.register}
						errors={hookForm.formState.errors}
					/>
				</ModalBody>
				<ModalFooter>
					<SubmitBtn
						type="submit"
						loading={isLoading}
						label="Cambiar Estado"
						onClick={onSubmit}
					/>
				</ModalFooter>
			</form>
		</div>
	);
};

export default ReturnStatusModal;
