
import { useEffect, useState }      from "react";
import { useFormContext, useWatch } from "react-hook-form";

import { removeDiacriticalMark } from "Helpers";
import { genericApi }            from "Store/api/genericApi";
import federalEntities           from "Resources/data/EstadosMunicipiosMx";
import GeneraPropertylInfo       from "./GeneraPropertylInfo";

const GeneraPropertylInfoContainer = () => {

	const [states, setStates]                 = useState([]);
	const [municipalities, setMunicipalities] = useState([]);

	const { data } = genericApi.useGetDataQuery({ module : "places" });

	const { setValue } = useFormContext();

	const stateWatcher        = useWatch({ name : "general_information.state" });
	const municipalityWatcher = useWatch({ name : "general_information.municipality"});

	const handleSetStates = () => {
		if (!data?.data) return;

		const states = data.data.map(state =>  ({ label : state.name, value : state._id }) );

		setStates(states);
	};

	const handleSetMunicipalities = () => {
		cleanMunicipalityFields();

		if (!stateWatcher?.value) {
			setMunicipalities([]);
			return;
		}

		const state = removeDiacriticalMark(stateWatcher?.label);

		console.log(state, federalEntities);

		setMunicipalities(federalEntities[state]);
	};

	const cleanMunicipalityFields = () => setValue("general_information.municipality", null);

	const cleanExpertForm = () => {
		setValue("controller", null);
		setValue("perit", null);
	};

	useEffect(() => handleSetStates(), [data]);

	useEffect(() => {
		handleSetMunicipalities();
		cleanExpertForm();
	}, [stateWatcher]);

	return (
		<GeneraPropertylInfo
			delegations={{
				states,
				municipalities,
				municipalityWatcher,
			}}
		/>
	);
};

export default GeneraPropertylInfoContainer;
