import {
	numToLetters,
} from "Helpers";

import Form    from "Components/Appraisal/Form";
import Divider from "Components/Appraisal/Divider";

import {
	CheckboxInpt,
	FormInputGroup,
	FormFlatPickrGroup,
	FormAsyncSelectGroup,
} from "Components/Global";
import CommercialValue from "../CommercialValue";

const ConclusionSection = ({
	delegations : {
		onSubmit,
		hookForm,
		referredValue,
		isSubmitingData,
		referredValueCheck,
	},
}) => {
	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<CommercialValue />
			<CheckboxInpt
				name="referred_value_check"
				label="Usar Valor Referido:"
				control={hookForm.control}
				rules={{
					required : false,
				}}
			/>
			{
				referredValueCheck &&
				<div className="my-1">
					<p className="mb-1">
						Seleccione la Fecha del Valor Referido o capturela en el formato AAAA-MM-DD, (el da es opcional) y obtendrn automticamente los ndices (en base a el ndice Nacional de Precios al Consumidor), as como el clculo de el valor referido.
						En caso de ser necesario, capture directamente los valores de los ndices y/o el valor referido.
					</p>
					<div className="row">
						<FormFlatPickrGroup
							className="col-md-6"
							label="Fecha del Valor Referido:"
							name="referred_val_date"
							control={hookForm.control}
							errors={hookForm.formState.errors}
							rules={{ required : false  }}
						/>
						<FormInputGroup
							className="col-md-6"
							name="prev_index"
							label="Indice Anterior:"
							register={hookForm.register}
							errors={hookForm.formState.errors}
							options={{ required : false }}
						/>
						<FormInputGroup
							className="col-md-6"
							name="current_index"
							label="Indice Actual:"
							register={hookForm.register}
							errors={hookForm.formState.errors}
							options={{ required : false }}
						/>
						<FormInputGroup
							className="col-md-6"
							name="refered_val_factor"
							label="Factor para el Clculo del Valor Referido:"
							register={hookForm.register}
							errors={hookForm.formState.errors}
							options={{ required : false }}
						/>
						<FormInputGroup
							type="number"
							name="refered_val"
							className="col-md-6"
							label="Valor Referido:"
							register={hookForm.register}
							errors={hookForm.formState.errors}
							options={{ required : false }}
						/>
						<div className="col-md-6">
							<p>Valor Referido con Letra:</p>
							<p>{ referredValue ? numToLetters(referredValue)  : "-" }</p>
						</div>
					</div>
				</div>
			}
			<Divider title="Seleccione al Valuador y Controlador que aparecern en cada uno de los formatos de la impresin del avalo" />
			<h5 className="fw-bolder">Formato SHF:</h5>
			<div className="row ">
				<div className="col-md-6">
					<p className="mb-1 text-center">Valuador</p>
					<FormAsyncSelectGroup
						size="sm"
						module="users"
						name="id_valuer"
						placeholder="Valuador"
						query={{  typeShf : "valuer" }}
						hookForm={hookForm}
						menuPortalTarget={document.body}
						rules={{ required : false}}
					/>
					<p>
						Estn disponibles para esta seleccin los usuarios con tipo de Registro SHF como Valuador y/o Controlador.
					</p>
				</div>
				<div className="col-md-6">
					<p className="mb-1 text-center">Controlador</p>
					<FormAsyncSelectGroup
						size="sm"
						module="users"
						name="id_controller"
						query={{  typeShf : "controller" }}
						placeholder="Controlador"
						hookForm={hookForm}
						menuPortalTarget={document.body}
						rules={{ required : false }}
					/>
					<p>
						Estn disponibles para esta seleccin los usuarios con tipo de Registro SHF como Controlador.
					</p>
				</div>
			</div>
		</Form>
	);
};

export default ConclusionSection;
