import { bindAll }      from "Helpers";
import { connect }      from "react-redux";
import { ModalSlice }   from "Store/Slices";
import EquivalenceTable from "./EquivalenceTable";

const EquivalenceTableContainer = ({ modalSliceActions }) => {

	const handleCloseModal = () => modalSliceActions.closeModal("equivalenceTable");

	return (
		<EquivalenceTable
			delegations={{
				handleCloseModal,
			}}
		/>
	);
};

const mapDispatchToProps = bindAll({
	ModalSliceActions : ModalSlice.actions,
});

export default connect(null, mapDispatchToProps)(EquivalenceTableContainer);
