import { useFormContext } from "react-hook-form";

import { FormSelectGroup } from "Components/Global";
import Form                from "Components/Appraisal/Form";

const distancesOptions = [
	{
		value : 0,
		label : " Cuenta con equipamiento a una distancia menor de 3 km",
	},
	{
		value : 1,
		label : " Cuenta con equipamiento a una distancia entre 3 y 5 km",
	},
	{
		value : 2,
		label : " Cuenta con equipamiento a una distancia mayor de 5 km",
	},
];

const vigilanceOptions = [
	{
		value : 0,
		label : "Cuenta con el servicio",
	},
	{
		value : 1,
		label : "No cuenta con el servicio",
	},
];

const garbageCollector = [
	{
		value : 0,
		label : " Cuenta con el servicio a menos de 100 m",
	},
	{
		value : 1,
		label : " No cuenta con el servicio",
	},
];


const Equipment = ({ onSubmit, isSubmitingData }) => {

	const hookForm = useFormContext();

	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<FormSelectGroup
				name="education"
				label="Educacin:"
				hookForm={hookForm}
				options={distancesOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="health"
				label="Salud:"
				hookForm={hookForm}
				options={distancesOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="supply"
				label="Abasto:"
				hookForm={hookForm}
				options={distancesOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="recreation"
				label="Recreacin:"
				hookForm={hookForm}
				options={distancesOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="transport"
				label="Transporte:"
				hookForm={hookForm}
				options={distancesOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="vigilance"
				label="Vigilancia:"
				hookForm={hookForm}
				options={vigilanceOptions}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				name="garbage_collector"
				label="Recoleccin de Basura:"
				hookForm={hookForm}
				options={garbageCollector}
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
		</Form>
	);
};

export default Equipment;
