import { Percent }        from "react-feather";
import { useFormContext } from "react-hook-form";

import { MapFormInpts }    from "Components/Global";
import Form                from "Components/Appraisal/Form";
import descGeneralSection4 from "Resources/data/DescGeneralSection4";
import LifeSpan            from "../LifeSpan";
import { Button }          from "reactstrap";
import { useIsCDMX }       from "Helpers/Hooks";

const { shfOptions, tdfOptions, projectQualityOptions, elevatorOptions } = descGeneralSection4;

const DescGeneral = ({
	delegations : {
		onSubmit,
		isSubmitingData,
		handleOpenModal,
	},
}) => {
	const hookForm = useFormContext();

	const isCDMX = useIsCDMX();

	const inptDescription = [
		{
			type    : "textarea",
			label   : "Uso Actual y Distribucin del Inmueble	",
			name    : "description_general.housing_description",
			options : { required : false },
		},
		{
			type    : "textarea",
			label   : "Comentarios Relevantes al Estado de Conservacin:",
			name    : "description_general.conservations_state",
			options : { required : false },
		},
	];

	const inptsPropertyGeneralClass = [
		{
			label   : "Segn SHF:",
			name    : "description_general.shf",
			options : shfOptions,
			rules   : { required : false },
		},
		{
			label       : "Segn TDF:",
			name        : "description_general.tdf",
			isDisabled  : !isCDMX(),
			placeholder : "Solo aplica para avalos de CDMX",
			options     : tdfOptions,
			rules       : { required : false },
		},
	];

	const inpts = [
		{
			type    : "number",
			name    : "description_general.number_rooms",
			label   : "Nmero de Recmaras:",
			options : { required : false },
		},
		{
			label   : "Calidad del Proyecto:",
			name    : "description_general.project_quality",
			options : projectQualityOptions,
			rules   : { required : false },
		},
		{
			type    : "number",
			label   : "Nmero de Baos:",
			name    : "description_general.number_bathrooms",
			options : { required : false },
		},
		{
			type    : "number",
			min     : 0,
			max     : 100,
			icon    : <Percent size={12} />,
			label   : "Porcentaje de Obra:",
			name    : "description_general.percentage_construction",
			options : { required : false },
		},
		{
			type    : "number",
			label   : "Medios Baos:",
			name    : "description_general.number_bathrooms_media",
			options : { required : false },
		},
		{
			type    : "number",
			min     : 0,
			max     : 100,
			icon    : <Percent size={12} />,
			label   : "Porcentaje de Avance de Obra en Areas Comunitarias:",
			name    : "description_general.percentage_construction_community",
			options : { required : false },
		},
		{
			type    : "number",
			label   : "Niveles:",
			name    : "description_general.levels",
			options : { required : false },
		},
		{
			type    : "number",
			label   : "Unidades Rentables:",
			name    : "description_general.units_rentable",
			options : { required : false },
		},
		{
			type    : "number",
			label   : "Espacios de Estacionamiento:",
			name    : "description_general.parking_spaces",
			options : { required : false },
		},
		{
			type    : "number",
			label   : "Unidades Rentables Generales:",
			name    : "description_general.units_rentable_general",
			options : { required : false },
		},
		{
			type    : "number",
			label   : "Elevador:",
			name    : "description_general.elevator",
			options : elevatorOptions,
			rules   : { required : false },
		},
	];

	return (
		<div id="DescGeneral">
			<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
				<MapFormInpts inpts={inptDescription} hookForm={hookForm}  />
				<div className="row">
					<MapFormInpts className="col-md-6" inpts={inptsPropertyGeneralClass} hookForm={hookForm}  title="Clase general del Inmueble" />
					<div className="d-flex justify-content-end">
						<Button
							size="sm"
							type="button"
							color="flat-primary"
							onClick={handleOpenModal}
						>
							Ver tabla de equivalencias...
						</Button>
					</div>
				</div>
				<hr />
				<div className="row mt-2">
					<MapFormInpts className="col-md-6" inpts={inpts} hookForm={hookForm} />
				</div>
				<LifeSpan />
			</Form>
		</div>
	);
};

export default DescGeneral;
