import { lazy } from "react";

// Import Own Components
import { PrivateRoute, SessionFreeRoute } from "Helpers";

const TemplateTitle = "%s - Inprodi Webb App";

const DefaultRoute = "/auth/login";

// Auth
const LoginPage           = lazy(() => import("Pages/auth/LoginPage"));
const EmailSendPage       = lazy(() => import("Pages/auth/EmailSendPage"));
const RestorePassword     = lazy(() => import("Pages/auth/RestorePassword"));
const SuccessRestore      = lazy(() => import("Pages/auth/SuccessRestore"));
const RecoverPasswordPage = lazy(() => import("Pages/auth/RecoverPasswordPage"));

// App
const Dashboard           = lazy(() => import("Pages/app/DashBoardPage"));
const Users               = lazy(() => import("Pages/app/UsersPage"));
const UsersDetails        = lazy(() => import("Pages/app/UsersDetailsPage"));

const PreferencesPage     = lazy(() => import("Pages/app/PreferencesPage"));

const RequestPage         = lazy(() => import("Pages/app/RequestPage"));
const RequestCreatePage   = lazy(() => import("Pages/app/RequestCreatePage"));
const RequestDeteailsPage = lazy(() => import("Pages/app/RequestDeteailsPage"));

//Appraisal
const Appraisals          = lazy(() => import("Pages/app/Appraisal/Appraisals"));
const AddAppraisal        = lazy(() => import("Pages/app/Appraisal/AddAppraisal"));
const AppraisalDoc        = lazy(() => import("Pages/app/Appraisal/AppraisalDoc"));
const AppraisalRequest    = lazy(() => import("Pages/app/Appraisal/AppraisalRequest"));
const AppraisalGeo        = lazy(() => import("Pages/app/Appraisal/AppraisalGeo"));
const AppraisalSection1   = lazy(() => import("Pages/app/Appraisal/AppraisalSection1"));
const AppraisalSection2   = lazy(() => import("Pages/app/Appraisal/AppraisalSection2"));
const AppraisalSection3   = lazy(() => import("Pages/app/Appraisal/AppraisalSection3"));
const AppraisalSection4   = lazy(() => import("Pages/app/Appraisal/AppraisalSection4"));
const AppraisalSection5   = lazy(() => import("Pages/app/Appraisal/AppraisalSection5"));
const InfonavitePage      = lazy(() => import("Pages/app/Appraisal/InfonavitePage"));
//Tables Approach-Appraisal
const BusinessApproach       = lazy(() => import("Pages/app/Appraisal/BusinessApproach"));
const PhysicalApproach       = lazy(() => import("Pages/app/Appraisal/PhysicalApproach"));
const CapitalizationApproach = lazy(() => import("Pages/app/Appraisal/CapitalizationApproach"));

//Final section Appraisal
const Conclusion          = lazy(() => import("Pages/app/Appraisal/Conclusion"));
const Costs               = lazy(() => import("Pages/app/Appraisal/Costs"));
const TechnicalOpinion    = lazy(() => import("Pages/app/Appraisal/TechnicalOpinion"));

//UploadPictures
const UploadPictures  = lazy(() => import("Pages/app/Appraisal/UploadPictures"));

// Misc
const ErrorPage     = lazy(() => import("Pages/misc/ErrorPage"));
const ForbiddenPage = lazy(() => import("Pages/views/NotAuthorized"));

const Routes = [
	{
		path      : "/auth/login",
		exact     : "true",
		layout    : "BlankLayout",
		component : props => <SessionFreeRoute {...props} component={LoginPage}  />,
	},
	{
		path      : "/auth/recover_password",
		exact     : "true",
		layout    : "BlankLayout",
		component : props => <SessionFreeRoute {...props} component={RecoverPasswordPage} />,
	},
	{
		path      : "/auth/email_send",
		exact     : "true",
		layout    : "BlankLayout",
		component : props => <SessionFreeRoute {...props} component={EmailSendPage} />,
	},
	{
		path      : "/auth/restore_password",
		exact     : "true",
		layout    : "BlankLayout",
		component : props => <SessionFreeRoute {...props} component={RestorePassword} />,
	},
	{
		path      : "/auth/restore_completed",
		exact     : "true",
		layout    : "BlankLayout",
		component : props => <SessionFreeRoute {...props} component={SuccessRestore} />,
	},
	{
		path      : "/app/dashboard",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={Dashboard} roles={["administrator", "consult", "guest", "perit", "administrator_technician"]} />,
	},
	{
		path      : "/app/users",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={Users}  roles={["administrator"]} />,
	},
	{
		path      : "/app/users_details/:id",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={UsersDetails}  roles={["administrator", "consult", "guest", "perit", "administrator_technician"]} />,
	},
	{
		path      : "/app/preferences",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={PreferencesPage} roles={["administrator"]} />,
	},
	{
		path      : "/app/request",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={RequestPage} roles={["administrator", "consult", "guest", "perit", "administrator_technician" ]} />,
	},
	{
		path      : "/app/create_request/:id",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={RequestCreatePage} roles={["administrator", "consult", "guest", "perit", "administrator_technician" ]} />,
	},
	{
		path      : "/app/details_request/:id",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={RequestDeteailsPage} roles={["administrator", "consult", "guest", "perit", "administrator_technician" ]} />,
	},
	{
		path      : "/app/appraise/records",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={Appraisals} roles={["administrator", "consult", "guest", "perit", "administrator_technician" ]} />,
	},
	{
		path      : "/app/appraise/add_appraisal",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={AddAppraisal} roles={["administrator"]} />,
	},
	{
		path      : "/app/appraise/documentation/:id",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={AppraisalDoc} roles={["administrator", "consult", "guest", "perit", "administrator_technician" ]} />,
	},
	{
		path      : "/app/appraise/request/:id",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={AppraisalRequest} roles={["administrator", "consult", "guest", "perit", "administrator_technician" ]} />,
	},
	{
		path      : "/app/appraise/geolocation/:id",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={AppraisalGeo} roles={["administrator", "consult", "guest", "perit", "administrator_technician" ]} />,
	},
	{
		path      : "/app/appraise/section-1/:id",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={AppraisalSection1} roles={["administrator", "consult", "guest", "perit", "administrator_technician" ]} />,
	},
	{
		path      : "/app/appraise/section-2/:id",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={AppraisalSection2} roles={["administrator", "consult", "guest", "perit", "administrator_technician" ]} />,
	},
	{
		path      : "/app/appraise/section-3/:id",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={AppraisalSection3} roles={["administrator", "consult", "guest", "perit", "administrator_technician" ]} />,
	},
	{
		path      : "/app/appraise/section-4/:id",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={AppraisalSection4} roles={["administrator", "consult", "guest", "perit", "administrator_technician" ]} />,
	},
	{
		path      : "/app/appraise/business-approach/:id",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={BusinessApproach} roles={["administrator", "consult", "guest", "perit", "administrator_technician" ]} />,
	},
	{
		path      : "/app/appraise/physical-approach/:id",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={PhysicalApproach} roles={["administrator", "consult", "guest", "perit", "administrator_technician" ]} />,
	},
	{
		path      : "/app/appraise/income-capitalization-approach/:id",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={CapitalizationApproach} roles={["administrator", "consult", "guest", "perit", "administrator_technician" ]} />,
	},
	{
		path      : "/app/appraise/section-5/:id",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={AppraisalSection5} roles={["administrator", "consult", "guest", "perit", "administrator_technician" ]} />,
	},
	{
		path      : "/app/appraise/section-infonavit/:id",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={InfonavitePage} roles={["administrator", "consult", "guest", "perit", "administrator_technician" ]} />,
	},
	{
		path      : "/app/appraise/conclusion/:id",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={Conclusion} roles={["administrator", "consult", "guest", "perit", "administrator_technician" ]} />,
	},
	{
		path      : "/app/appraise/appraisal-cost/:id",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={Costs} roles={["administrator", "consult", "guest", "perit", "administrator_technician" ]} />,
	},
	{
		path      : "/app/appraise/dtc/:id",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={TechnicalOpinion} roles={["administrator", "consult", "guest", "perit", "administrator_technician" ]} />,
	},
	{
		path      : "/app/appraise/pictures/:id",
		exact     : "true",
		layout    : "VerticalLayout",
		component : props => <PrivateRoute {...props} component={UploadPictures} roles={["administrator", "consult", "guest", "perit", "administrator_technician" ]} />,
	},
	{
		path      : "/app/error/403",
		component : props => <PrivateRoute {...props} component={ForbiddenPage}  roles={["administrator", "consult", "guest", "perit", "administrator_technician" ]} />,
		layout    : "VerticalLayout",
		exact     : "true",
	},
	{
		path      : "/error",
		exact     : "true",
		layout    : "BlankLayout",
		component : ErrorPage,
	},
];

export { DefaultRoute, TemplateTitle, Routes };
