import { Button } from "reactstrap";
import { X }      from "react-feather";

import { FormInputGroup } from "Components/Global";

const FinishesRepetear = ({
	delegations : {
		fields,
		hookForm,
		handleAdd,
		removeItem,
	},
}) => {
	return (
		<div className="mt-1">
			<hr />
			<div className="row">
				<h5 className="col-md-5 subTitle-text">Ms Espacios Arquitectnicos</h5>
				<h5 className="col-md-7 subTitle-text">Acabados</h5>
			</div>
			{
				fields.map((field, index) => (
					<div key={field.id } className="row">
						<div className="col-md-5">
							<FormInputGroup
								bsSize="sm"
								label="Nombre:"
								name={`finish.architectural_finishes.${index}.name`}
								register={hookForm.register}
								errors={hookForm.formState.errors}
								options={{ required : false  }}
							/>
						</div>
						<div className="col-md-6">
							<FormInputGroup
								bsSize="sm"
								label="Pisos:"
								name={`finish.architectural_finishes.${index}.floors`}
								register={hookForm.register}
								errors={hookForm.formState.errors}
								options={{ required : false  }}
							/>
							<FormInputGroup
								bsSize="sm"
								label="Muros:"
								name={`finish.architectural_finishes.${index}.walls`}
								register={hookForm.register}
								errors={hookForm.formState.errors}
								options={{ required : false  }}
							/>
							<FormInputGroup
								bsSize="sm"
								label="Plafn:"
								name={`finish.architectural_finishes.${index}.roof`}
								register={hookForm.register}
								errors={hookForm.formState.errors}
								options={{ required : false  }}
							/>
						</div>

						<Button.Ripple
							color="flat-danger"
							className="btn-icon col-md-1 delete-btn-repetear"
							onClick={removeItem(index)}
						>
							<X size={18} />
						</Button.Ripple>
					</div>
				))
			}
			<Button.Ripple
				type="button"
				onClick={handleAdd}
				color="flat-primary"
			>
				Agregar otro espacio arquitectnico
			</Button.Ripple>
		</div>
	);
};

export default FinishesRepetear;
