import { UncontrolledTooltip } from "reactstrap";
import { HelpCircle }          from "react-feather";
import { useFormContext }      from "react-hook-form";

import { FormInputGroup, MapFormInpts } from "Components/Global";
import Form                             from "Components/Appraisal/Form";

import apprasialOptions           from "Resources/data/Appraisal";
import urbanEquipmentOptions      from "Resources/data/UrbanEquipment";
import urbanInfrastructureOptions from "Resources/data/UrbanInfrastructure";
import CreableMsg                 from "Components/Appraisal/CreableMsg";

const {  exsitenceOptions, exsitenceBooleanOptions } = apprasialOptions;

const {
	septicTank,
	waterDistributionOptions,
	supplyTypesOptions,
	publicLightingOptions,
	residualWaterCollectionOptions,
} = urbanInfrastructureOptions;

const {
	gasOptions,
	levelEconomic,
	materialOptions,
	vigilanceOptions,
	densityHousingOptions,
	materialSidewalkOptions,
	zoneClasificationOptions,
	materialGuarnitionsOptions,
} = urbanEquipmentOptions;

const UrbanInfrastructure = ({
	delegations : {
		onSubmit,
		isLoading,
	},
}) => {

	const hookForm = useFormContext();

	const inptsWater = [
		{
			label   : "Distribucin de Agua Potable:",
			name    : "urban_infrastructure.water_distribution",
			options : waterDistributionOptions,
			rules   : { required : false },
		},
		{
			label   : "Recoleccin de Aguas Residuales:",
			name    : "urban_infrastructure.residual_water_collection",
			options : residualWaterCollectionOptions,
			rules   : { required : false },
		},
		{
			label   : "Recoleccin de Desechos Slidos:",
			name    : "urban_infrastructure.solid_waste_collection",
			options : exsitenceBooleanOptions,
			rules   : { required : false },
		},
	];

	const inptsElectricity = [
		{
			label   : "Suministro de Electrificacin:",
			name    : "urban_infrastructure.electricity_supply",
			options : supplyTypesOptions,
			rules   : { required : false },
		},
		{
			label   : "Acometida al Inmueble (Electrificacin):",
			name    : "urban_infrastructure.electricity_connection",
			options : exsitenceBooleanOptions,
			rules   : { required : false },
		},
		{
			label   : "Alumbrado Pblico:",
			name    : "urban_infrastructure.public_lighting",
			options : publicLightingOptions,
			rules   : { required : false },
		},
	];

	const inptsWaterDrain = [
		{
			label   : "Drenaje Pluvial en la Calle:",
			name    : "urban_infrastructure.street_water_drain",
			options : exsitenceBooleanOptions,
			rules   : { required : false },
		},
		{
			label   : "Drenaje Pluvial en la Zona:",
			name    : "urban_infrastructure.zone_water_drain",
			options : exsitenceBooleanOptions,
			rules   : { required : false },
		},
		{
			label   : "Sistema Mixto:",
			name    : "urban_infrastructure.mixed_water_drain",
			options : exsitenceBooleanOptions,
			rules   : { required : false },
		},
		{
			label   : "Fosa Sptica:",
			name    : "urban_infrastructure.septic_tank",
			options : septicTank,
			rules   : { required : false },
		},
	];

	const inptsTelephony = [
		{
			label   : "Suministro Telefnico:",
			name    : "urban_infrastructure.telephone_supply",
			options : supplyTypesOptions,
			rules   : { required : false },
		},
		{
			label   : "Acometida al Inmueble (Telefnico):",
			name    : "urban_infrastructure.telephone_connection",
			options : exsitenceBooleanOptions,
			rules   : { required : false },
		},
		{
			label   : "Sealizacin de Vias:",
			name    : "urban_infrastructure.traffic_signs",
			options : exsitenceOptions,
			rules   : { required : false },
		},
		{
			label   : "Nomenclatura de Calles Circundantes:",
			name    : "urban_infrastructure.street_nomenclature",
			options : exsitenceOptions,
			rules   : { required : false },
		},

	];

	const inptsMaterilas = [
		{
			label       : "Material de las Vialidades:",
			name        : "urban_infrastructure.material_of_roads",
			inpt_type   : "creatable",
			options     : materialOptions,
			rules       : { required : false },
			newOptionId : 6,
		},
		{
			label       : "Material de Banquetas:",
			name        : "urban_infrastructure.material_of_sidewalks",
			inpt_type   : "creatable",
			options     : materialSidewalkOptions,
			rules       : { required : false },
			newOptionId : 4,
		},
		{
			label       : "Material de Guarniciones:",
			name        : "urban_infrastructure.material_of_guarnitions",
			inpt_type   : "creatable",
			options     : materialGuarnitionsOptions,
			rules       : { required : false },
			newOptionId : 2,
		},
	];

	const inptsUrbanTransport = [
		{
			name    : "urban_infrastructure.urban_distance",
			label   : "Distancia (metros):",
			type    : "number",
			options : { required : false },
		},
		{
			name    : "urban_infrastructure.urban_frequency",
			label   : "Frecuencia (minutos):",
			type    : "number",
			options : { required : false },
		},
	];

	const inptsSubUrbanTransport = [
		{
			name    : "urban_infrastructure.suburban_number",
			type    : "number",
			label   : "Distancia (metros):",
			options : { required : false },
		},
		{
			label   : "Frecuencia (minutos):",
			name    : "urban_infrastructure.suburban_frequency",
			type    : "number",
			options : { required : false },
		},
	];

	const inpts = [
		{
			label   : "Vigilancia:",
			name    : "urban_infrastructure.vigilance",
			options : vigilanceOptions,
			rules   : { required : false },
		},
		{
			label   : "Gas Natural:",
			name    : "urban_infrastructure.natural_gas",
			options : gasOptions,
			rules   : { required : false },
		},
	];

	const inptsInfrestructures = [
		{
			label   : "Frecuencia Recoleccin Desechos (Municipal):",
			name    : "urban_infrastructure.frequency_recolection_municipal",
			type    : "number",
			options : { required : false },
		},
		{
			label   : "Ancho Banquetas:",
			name    : "urban_infrastructure.width_sidewalks",
			type    : "number",
			options : { required : false },
		},
		{
			label   : "Ancho Guarniciones:",
			name    : "urban_infrastructure.width_guarnitions",
			type    : "number",
			options : { required : false },
		},
		{
			label   : "Ancho Vialidades:",
			name    : "urban_infrastructure.width_roads",
			type    : "number",
			options : { required : false },
		},
		{
			label   : "Nivel Socio-Econmico Zona:",
			name    : "urban_infrastructure.level_economic_zone",
			options : levelEconomic,
			rules   : { required : false },
		},
		{
			label   : "Clasificacin Zona:",
			name    : "urban_infrastructure.zone_classification",
			options : zoneClasificationOptions,
			rules   : { required : false },
		},
		{
			label   : "Densidad Habitacional:",
			name    : "urban_infrastructure.density_housing",
			options : densityHousingOptions,
			rules   : { required : false },
		},

	];

	return (
		<Form onSubmit={onSubmit}  isSubmitingData={isLoading}>
			<CreableMsg className="mb-1" />
			<div className="row">
				<div className="col-md-6">
					<MapFormInpts inpts={inptsWater} hookForm={hookForm} title="Agua" />
				</div>
				<div className="col-md-6">
					<MapFormInpts inpts={inptsElectricity} hookForm={hookForm} title="Electrificacin" />
				</div>
				<div className="col-md-6">
					<MapFormInpts inpts={inptsWaterDrain} hookForm={hookForm} title="Drenaje"  />
				</div>
				<div className="col-md-6">
					<MapFormInpts inpts={inptsTelephony} hookForm={hookForm} title="Telefona" />
				</div>
			</div>
			<div className="row">
				<MapFormInpts className="col-md-4" inpts={inptsMaterilas} hookForm={hookForm} title="Materiales"  />
			</div>

			<div className="row">
				<div className="col-md-6">
					<div className="row">
						<MapFormInpts className="col-md-6" inpts={inptsUrbanTransport} hookForm={hookForm} title="Transporte Urbano" />
					</div>
				</div>
				<div className="col-md-6">
					<div className="row">
						<MapFormInpts className="col-md-6" inpts={inptsSubUrbanTransport} hookForm={hookForm} title="Transporte Suburbano" />
					</div>
				</div>
			</div>
			<div className="row">
				<MapFormInpts className="col-md-6"  inpts={inpts} hookForm={hookForm} />
			</div>
			<div className=" d-flex g-1">
				<FormInputGroup
					bsSize="sm"
					disabled
					className="w-100"
					type="number"
					label="Nivel de Infraestructura:"
					name="urban_infrastructure.infrastructure_level"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ required : false }}
				/>

				<HelpCircle id="help-infrastructure_level" size={14} />
				<UncontrolledTooltip className="custom-style" placement="bottom" target="help-infrastructure_level">
					<p><b>Niveles de Infraestructura Urbana.</b></p>
					<div className="row text-start">
						<p className="col-2">Nivel 1:</p>
						<p className="col-10">No tiene alguno de los tres servicios bsicos del nivel 2.</p>
					</div>
					<div className="row text-start">
						<p className="col-2">Nivel 2:</p>
						<p className="col-10">Cuenta con agua potable, drenaje y luz en la zona.</p>
					</div>
					<div className="row text-start">
						<p className="col-2">Nivel 3:</p>
						<p className="col-10">Nivel 2 ms alumbrado pblico y vialidades terminadas (con banquetas).</p>
					</div>
					<div className="row text-start">
						<p className="col-2">Nivel 4:</p>
						<p className="col-10">Nivel 3 ms gas natural y vigilancia privada.</p>
					</div>
				</UncontrolledTooltip>
			</div>
			<MapFormInpts inpts={inptsInfrestructures} hookForm={hookForm} title="Infaestructura" />
		</Form>
	);
};

export default UrbanInfrastructure;
