import { useWatch }               from "react-hook-form";
import { Card, FormGroup, Label } from "reactstrap";

import TableSources    from "../TableSources";
import TableUbication  from "../TableUbication";
import TableInvestComp from "../TableInvestComp";

import { HelpToolTip, InputMaskNumber, RadioButton } from "Components/Global";

const UnitValueLand = ({
	delegations : {
		hookForm,
		unitValueLandFields,
	},
}) => {

	const approachStatus = useWatch({ name : "approach_status"});

	return (
		<Card className="p-1">
			<FormGroup>
				<div>
					<Label style={{ marginRight : "3px"}}>Tipo de Investigacin:</Label>
					<HelpToolTip target="help-type-investigations-unit-value">
						<p className="text-start mb-1">
							<b>El tipo de investigacin determina como aplica el factor de superficie en la Homologacin.</b>
						</p>
						<ul  className="text-start">
							<li>
								Para investigacin de condominios Horizontales y/o casas habitacin:
								<ul>
									<li>
										Aplicara usando la superficie del terreno correspondiente al sujeto.
									</li>
								</ul>
							</li>
							<li className="mt-1">
								Para investigacin de condominios verticales:
								<ul>
									<li>
										Aplicara usando la superfice del conjunto del sujeto.
									</li>
								</ul>
							</li>
						</ul>
					</HelpToolTip>
				</div>
				<RadioButton
					className="my-1"
					id={"approach_status_0"}
					label="Condominio Horizontal, Casas Habitacin, Terrenos, otro."
					value="0"
					register={hookForm.register}
					name="approach_status"
					checked={approachStatus === "0"}
					rules={{ required : false }}
				/>
				<RadioButton
					id={"approach_status_1"}
					label="Condominio Vertical"
					value="1"
					register={hookForm.register}
					name="approach_status"
					checked={approachStatus === "1"}
					rules={{ required : false }}
				/>
			</FormGroup>
			{
				unitValueLandFields.map(({ id }, index ) => (
					<div className="mb-2" key={id}>
						<h2>Comparable {index + 1}:</h2>
						<TableInvestComp index={index} />
						<div className="my-1">
							<TableSources index={index} />
						</div>
						<TableUbication index={index} />
					</div>
				))
			}
			<div className="row">
				<h3 className="col-md-3">Promedio del precio unitario ($/M2):</h3>
				<InputMaskNumber
					disabled
					type="text"
					bsSize="sm"
					hookForm={hookForm}
					className="col-md-5"
					name="invest_comp_average"
					inputGroupClassName="mb-0"
				/>
			</div>
		</Card>
	);
};

export default UnitValueLand;
