import { Table } from "reactstrap";

import { TdInput }              from "Components/Appraisal";
import { HelpToolTip }          from "Components/Global";
import TdWatcherHOC             from "Components/Appraisal/TdWatcherHOC";
import { getCUS, getUnitPrice } from "Helpers/formulas";

const TableInvesCalcs = ({
	delegations : {
		index,
		hookForm,
	},
}) => {
	return (
		<Table striped bordered responsive>
			<thead>
				<tr>
					<th colSpan="2" scope="colgroup" className="text-center"></th>
					<th colSpan="2" scope="colgroup" className="text-center">Superficies m<sup>2</sup></th>
					<th colSpan="1" scope="colgroup" className="text-center"></th>
					<th colSpan="1" scope="colgroup" className="text-center"></th>
					<th colSpan="3" scope="colgroup" className="text-center"></th>
					<th colSpan="2" scope="colgroup" className="text-center">Ubicacin</th>
				</tr>
				<tr>
					<th className="text-center" scope="col">Edad</th>
					<th className="text-center" scope="col">Terreno</th>
					<th className="text-center" scope="col">Construccin</th>
					<th className="text-center" scope="col">Vida til Terreno</th>
					<th className="text-center" scope="col">Vida til Construccin</th>
					<th className="text-center" scope="col">
						<span>C.U.S. </span>
						<HelpToolTip target={`help-cus-table-${index}`}>
							<p>CUS = (Superficie Construida) / (Superficie de Terreno)</p>
						</HelpToolTip>
					</th>
					<th className="text-center w-offer-price" scope="col">Precio - Oferta $</th>
					<th className="text-center w-unit-price" scope="col">
						<span>Precio Unitario ($ /m<sup>2</sup>) </span>
						<HelpToolTip target={`unit-price-table-${index}`}>
							<p>Precio Unitario = (Precio Oferta) / (Superficie Construida)</p>
						</HelpToolTip>
					</th>
					<th className="text-center w-200" scope="col">Calle</th>
					<th className="text-center w-200" scope="col">Colonia</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<TdInput hookForm={hookForm} name={`comparable_property.${index}.inv_calcs.age`} />
					<TdInput hookForm={hookForm} name={`comparable_property.${index}.inv_calcs.land`} />
					<TdInput hookForm={hookForm} name={`comparable_property.${index}.inv_calcs.building`} />
					<TdInput hookForm={hookForm} name={`comparable_property.${index}.inv_calcs.land_lifespan`} />
					<TdInput hookForm={hookForm} name={`comparable_property.${index}.inv_calcs.building_lifespan`} />
					<TdWatcherHOC
						name={`comparable_property.${index}.inv_calcs.cus`}
						deps={[
							`comparable_property.${index}.inv_calcs.building`,
							`comparable_property.${index}.inv_calcs.land`,
						]}
						formula={getCUS}
					/>
					<TdInput hookForm={hookForm} name={`comparable_property.${index}.inv_calcs.offer_price`} />
					<TdWatcherHOC
						name={`comparable_property.${index}.inv_calcs.unit_price`}
						deps={[
							`comparable_property.${index}.inv_calcs.offer_price`,
							`comparable_property.${index}.inv_calcs.building`,
						]}
						formula={getUnitPrice}
					/>
					<TdInput hookForm={hookForm} name={`comparable_property.${index}.inv_calcs.street`} type="text" />
					<TdInput hookForm={hookForm} name={`comparable_property.${index}.inv_calcs.colony`} type="text" />
				</tr>
			</tbody>
		</Table>
	);
};

export default TableInvesCalcs;
