import { useFormContext } from "react-hook-form";

import StructureOptions from "Resources/data/Strucutre";
import { MapFormInpts } from "Components/Global";
import Form             from "Components/Appraisal/Form";
import CreableMsg       from "Components/Appraisal/CreableMsg";

const NEW_OPTION_ID = 1;

const Structure = ({ onSubmit, isSubmitingData }) => {

	const hookForm = useFormContext();

	const {
		lathsEntrepisos,
		cementatinOptions,
		stairsFloorOptions,
		wallasColumOptions,
	} = StructureOptions;

	const inpts = [
		{
			label       : "Cimentacin (probable):",
			name        : "structure.cementation",
			inpt_type   : "creatable",
			options     : cementatinOptions,
			rules       : { rquired : false },
			newOptionId : cementatinOptions.length + NEW_OPTION_ID,
		},
		{
			label       : "Muros Trabes Y Columnas:",
			name        : "structure.walls_columns",
			inpt_type   : "creatable",
			options     : wallasColumOptions,
			rules       : { rquired : false },
			newOptionId : wallasColumOptions.length + NEW_OPTION_ID,

		},
		{
			label       : "Losas y Entrepisos:",
			name        : "structure.laths_entrepisos",
			inpt_type   : "creatable",
			options     : lathsEntrepisos,
			rules       : { rquired : false },
			newOptionId : lathsEntrepisos.length + NEW_OPTION_ID,
		},
		{
			label       : "Escaleras (Piso):",
			name        : "structure.stairs_floor",
			inpt_type   : "creatable",
			options     : stairsFloorOptions,
			rules       : { rquired : false },
			newOptionId : stairsFloorOptions.length + NEW_OPTION_ID,

		},
	];

	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<CreableMsg />
			<div className="row mt-1">
				<MapFormInpts className="col-md-6" inpts={inpts} hookForm={hookForm}  />
			</div>
		</Form>
	);
};

export default Structure;
