import { useEffect, useState } from "react";

import { Card, CardBody } from "reactstrap";
import { Info }           from "react-feather";
import { useFormContext } from "react-hook-form";

import { useFetchSelectOptions } from "Helpers/Hooks";
import toastDispacher            from "Helpers/toastDispacher";
import { setOnlyPositiveNumber } from "Helpers";
import zip_codes_mx              from "Resources/data/zip_codes_mx/zip_codes_mx.json";

import { FormInputGroup, FormSelectGroup } from "Components/Global";

const ApplicantForm = () => {
	const municipalityKey = "applicant.municipality";
	const zipCodeKey = "applicant.zip_code";
	const hookForm = useFormContext();
	const placesOptions = useFetchSelectOptions("places");
	const [colonyOptions, setColonyOptions] = useState([]);

	const { register, formState } = hookForm;

	const watcherZipCode = hookForm.watch(zipCodeKey);

	const setZipCodeValues = () => {
		const { d_estado, d_ciudad, ...localities } = zip_codes_mx[watcherZipCode];

		delete localities["d_estado"];
		delete localities["c_estado"];
		delete localities["c_mnpio"];

		const state = placesOptions.find(place => place.label === d_estado);

		if (Object.keys(localities).length>0) {
			const colonys = Object.entries(localities).map(([key, values]) => ({ label : key, value : values.d_asenta }));
			setColonyOptions(colonys);
		}

		hookForm.setValue("applicant.state", state);
		hookForm.setValue(municipalityKey, d_ciudad);
		hookForm.clearErrors(municipalityKey);
	};


	useEffect(() => {
		if (watcherZipCode?.length !== 5) return;

		const zipCode = zip_codes_mx[watcherZipCode];

		if (!zipCode) {
			toastDispacher.zipCode.warning(watcherZipCode);

			hookForm.setValue(municipalityKey, "");
			return;
		}
		setZipCodeValues();
	}, [watcherZipCode]);

	const person_type = [
		{
			label	: "Fisica",
			value	: 0,
		},
		{
			label	: "Moral",
			value	: 1,
		},
	];

	return (
		<Card className="mt-1">
			<div className="d-flex align-items-center px-1 pt-1">
				<Info size={16} />
				<p className="mx-1">DATOS DEL SOLICITANTE</p>
			</div>
			<hr />
			<CardBody>
				<div className="row mt-2">
					<FormInputGroup
						bsSize="sm"
						name="applicant.paternal_last_name"
						className="col-md-4"
						label="Apellido Paterno"
						register={register}
						options={{
							required : true,
						}}
						errors={formState.errors}
					/>
					<FormInputGroup
						bsSize="sm"
						name="applicant.maternal_last_name"
						className="col-md-4"
						label="Apellido Materno"
						register={register}
						options={{
							required : true,
						}}
						errors={formState.errors}
					/>
					<FormInputGroup
						bsSize="sm"
						name="applicant.name"
						className="col-md-4"
						label="Nombre(s)"
						register={register}
						options={{
							required : true,
						}}
						errors={formState.errors}
					/>
					<FormSelectGroup
						size="sm"
						label="Tipo de Persona"
						name="applicant.person_type"
						className="col-md-4 mb-1"
						options={person_type}
						rules={
							{
								required : false,
							}
						}
						hookForm={hookForm}
						menuPortalTarget={document.body}
						errors={formState.errors}
					/>
					<FormInputGroup
						bsSize="sm"
						name="applicant.mancomunated"
						className="col-md-4"
						label="Mancomunado"
						register={register}
						options={{
							required : false,
						}}
					/>
					<FormInputGroup
						bsSize="sm"
						name="applicant.rfc"
						className="col-md-4"
						placeholder="GOMC940510123"
						label="RFC"
						register={register}
						options={{
							required : false,
							pattern  : {
								value   : /^[A-Z]{4}[0-9]{6}[A-Z0-9]{3}$/i,
								message : "RFC invlido",
							},
						}}
						errors={formState.errors}
					/>
					<FormInputGroup
						bsSize="sm"
						name="applicant.curp"
						className="col-md-4"
						placeholder="GOMC940510HDFRRN08"
						label="CURP"
						register={register}
						options={{
							required : false,
							pattern  : {
								value   : /^[A-Z]{4}[0-9]{6}[H|M]{1}[A-Z]{5}[0-9]{2}$/i,
								message : "CURP invlido",
							},
						}}
						errors={formState.errors}
					/>
					<FormInputGroup
						bgSize="sm"
						size="sm"
						name="applicant.tel"
						className="col-md-4"
						label="Telfono"
						register={register}
						options={{
							required : false,
						}}
					/>
					<FormInputGroup
						bsSize="sm"
						name="applicant.phone"
						className="col-md-4"
						label="Celular"
						register={register}
						options={{
							required : false,
						}}
					/>
					<FormInputGroup
						name="applicant.zip_code"
						label="Cdigo Postal"
						bsSize="sm"
						options={{
							required : {
								value : false,
							},
							minLength : {
								value   : 5,
								message : "El cdigo postal debe de contener 5 dgitos",
							},
						}}
						register={register}
						className="col-md-4"
						maxLength={5}
						onKeyPress={setOnlyPositiveNumber}
						errors={formState.errors}
					/>
					<FormInputGroup
						bsSize="sm"
						name="applicant.municipality"
						className="col-md-4"
						label="Municipalidad"
						register={register}
						options={{
							required : false,
						}}
					/>
					<FormSelectGroup
						size="sm"
						label="Colonia"
						placeholder="Selecciona la colonia"
						name="applicant.colony"
						className="col-md-4 mb-1"
						options={colonyOptions ? colonyOptions : []}
						rules={
							{
								required : false,
							}
						}
						hookForm={hookForm}
						errors={formState.errors}
						menuPortalTarget={document.body}
					/>
					<FormSelectGroup
						size="sm"
						name="applicant.state"
						placeholder="Selecciona el estado"
						className="col-md-4"
						label="Estado"
						options={placesOptions ? placesOptions : []}
						rules={
							{
								required : false,
							}
						}
						hookForm={hookForm}
						errors={formState.errors}
						menuPortalTarget={document.body}
					/>
					<FormInputGroup
						bsSize="sm"
						name="applicant.street"
						className="col-md-4"
						label="Calle"
						register={register}
						options={{
							required : false,
						}}
					/>
					<FormInputGroup
						bsSize="sm"
						name="applicant.num_ext"
						className="col-md-4"
						label="Nmero Exterior"
						register={register}
						options={{
							required : false,
						}}
					/>
					<FormInputGroup
						bsSize="sm"
						name="applicant.num_int"
						className="col-md-4"
						label="Nmero Interior"
						register={register}
						options={{
							required : false,
						}}
					/>
					<FormInputGroup
						bsSize="sm"
						name="applicant.block"
						className="col-md-4"
						label="Manzana"
						register={register}
						options={{
							required : false,
						}}
					/>
					<FormInputGroup
						bsSize="sm"
						name="applicant.lot"
						className="col-md-4"
						label="Lote"
						register={register}
						options={{
							required : false,
						}}
					/>
					<FormInputGroup
						bsSize="sm"
						name="applicant.security_code"
						className="col-md-4"
						label="Nmero de Seguridad Social"
						register={register}
						options={{
							required : false,
						}}
					/>
				</div>
			</CardBody>
		</Card>
	);
};

export default ApplicantForm;
