import TdInput                from "Components/Appraisal/TdInput";
import TdWatcherHOC           from "Components/Appraisal/TdWatcherHOC";
import { HelpToolTip }        from "Components/Global";
import { productSetOfValues } from "Helpers/formulas";
import { Trash, X }           from "react-feather";
import { Button, Table }      from "reactstrap";


const profitableUnitOptions = [
	{
		value : 0,
		label : "Habitacional",
	},
	{
		value : 1,
		label : "Comercio",
	},
	{
		value : 2,
		label : "Oficinas",
	},
	{
		value : 3,
		label : "Industria",
	},
	{
		value : 4,
		label : "Comunicaciones",
	},
];

const TableRentEstimate = ({
	delegations : {
		hookForm,
		removeRow,
		appendNewRow,
		handleSetValue,
		rentEstimateFields,
	},
}) => {
	return (
		<div>
			<Table striped bordered responsive>
				<thead>
					<tr>
						<th className="text-center align-middle">Nm.</th>
						<th className="text-center align-middle">Unidad rentable</th>
						<th className="text-center align-middle">Superficie</th>
						<th className="text-center align-middle">
							<span>renta unit/ m2</span>
							<HelpToolTip target="help-table-rent-unit-rent">
								Renta Unitaria = (Promedio Homologacin)
							</HelpToolTip>
						</th>
						<th className="text-center align-middle">Incidencia de Valor</th>
						<th className="text-center align-middle">Renta Mensual
							<span>Renta Mensual</span>
							<HelpToolTip target="help-table-rent-mensual-rent">
								Renta Mensual = (Renta Unitaria) x (Superficie de Construccin) x (Factor Insidencia)
							</HelpToolTip>
						</th>
						<th className="text-center align-middle"><Trash size={14} /></th>
					</tr>
				</thead>
				<tbody>
					{
						rentEstimateFields.map( (landField, index) => (
							<tr key={landField.id}>
								<td className="text-center">{index + 1}</td>
								<TdInput hookForm={hookForm} name={`rent_estimate.${index}.profitable_unit`} options={profitableUnitOptions} />
								<TdInput hookForm={hookForm} name={`rent_estimate.${index}.surface`} />
								<TdWatcherHOC
									name={`rent_estimate.${index}.unit_rent_m2`}
									deps={["homologation_average"]}
									callback={handleSetValue}
								/>
								<TdInput hookForm={hookForm} name={`rent_estimate.${index}.incidence_val`} />
								<TdWatcherHOC
									isArrayArgs={true}
									name={`rent_estimate.${index}.mensual_rent`}
									deps={[
										`rent_estimate.${index}.surface`,
										`rent_estimate.${index}.unit_rent_m2`,
										`rent_estimate.${index}.incidence_val`,
									]}
									formula={productSetOfValues}
								/>
								<td className="text-center align-middle">
									<Button.Ripple
										color="flat-danger"
										onClick={removeRow(index)}
										className="btn-icon rounded-circle"
									>
										<X size={14} className="color-danger" />
									</Button.Ripple>
								</td>
							</tr>
						))
					}
					<tr>
						<td colSpan="5" align="end">Renta Mensual Bruta:</td>
						<TdInput hookForm={hookForm} name="gross_mensual_rent" disabled />
						<td colSpan="1" align="end"></td>
					</tr>
				</tbody>
			</Table>
			<Button
				size="sm"
				type="button"
				color="primary"
				className="mt-1"
				onClick={appendNewRow}
			>
				Agregar Unidad Rentable
			</Button>
		</div>
	);
};

export default TableRentEstimate;
