import { Card, FormGroup } from "reactstrap";

import {
	FormInputGroup,
	RadioButton,
} from "Components/Global";

import TableInvesCalcs   from "../TableInvesCalcs";
import TableInvesSelects from "../TableInvesSelects";
import DescMarketInfo    from "../DescMarketInfo";
import Avergaes          from "../Avergaes";

const ComparableProperty = ({
	delegations : {
		hookForm,
		approachStatus,
		investigationType,
		compPropertyFields,
	},
}) => {
	return (
		<Card className="p-1">
			<div id="ComparableProperty">
				<div className="row">
					<div className="col-md-6">
						<FormGroup>
							<p className="text-center">Enfoque: </p>
							<div className="row justify-content-around">
								<RadioButton
									className="my-1 col-md-6"
									id={"approach_status_0"}
									label="Usar Enfoque"
									value="0"
									classNameLabelGruop="d-flex align-items-center"
									register={hookForm.register}
									name="approach_status"
									checked={approachStatus === "0"}
									rules={{ required : false }}
								/>
								<RadioButton
									className="my-1 col-md-6"
									id={"approach_status_1"}
									label="Cancelar Enfoque (Inmuebles Atpicos)"
									value="1"
									register={hookForm.register}
									classNameLabelGruop="d-flex align-items-center"
									name="approach_status"
									checked={approachStatus === "1"}
									rules={{ required : false }}
								/>
							</div>
						</FormGroup>
						{
							approachStatus === "1" &&
							<FormInputGroup
								bsSize="sm"
								type="textarea"
								label="Comentarios correspondientes al Inmueble Atpico y/o Cancelacin del Enfoque de Mercado:"
								name="approach_comment_cancel"
								register={hookForm.register}
								errors={hookForm.formState.errors}
								options={{ required : false }}
							/>
						}
					</div>
					<FormGroup className="col-md-6">
						<p className="text-center">Tipo de Investigacin:</p>
						<div className="row">
							<RadioButton
								className="my-1 col-md-6"
								id={"investigation_type_0"}
								label="Condominio Horizontal, Casas Habitacin, Terrenos, otro."
								value="0"
								classNameLabelGruop="d-flex align-items-center"
								register={hookForm.register}
								name="investigation_type"
								checked={investigationType === "0"}
								rules={{ required : false }}
							/>
							<RadioButton
								className="my-1 col-md-6"
								id={"investigation_type_1"}
								label="Condominio Vertical"
								value="1"
								register={hookForm.register}
								classNameLabelGruop="d-flex align-items-center"
								name="investigation_type"
								checked={investigationType === "1"}
								rules={{ required : false }}
							/>
						</div>
					</FormGroup>
				</div>
				{
					compPropertyFields.map(({ id }, index) => (
						<div className="mb-2" key={id}>
							<h2>
								Comparable {index + 1}:
							</h2>

							<TableInvesCalcs index={index} />
							<TableInvesSelects index={index} />
							<DescMarketInfo index={index} />
						</div>
					))
				}
				<h5 className="subtitle">Promedios:</h5>
				<div className="row">
					<Avergaes
						label="Edad:"
						name="average_age"
						columnAverage={{
							length   : 6,
							arrayKey : "comparable_property",
							fieldKey : "inv_calcs.age",
							name     : "average_age",
						}}
					/>
					<Avergaes
						label="Terreno:"
						name="average_land"
						columnAverage={{
							length   : 6,
							arrayKey : "comparable_property",
							fieldKey : "inv_calcs.land",
							name     : "average_land",
						}}
					/>
					<Avergaes
						label="Construccin:"
						name="average_building"
						columnAverage={{
							length   : 6,
							arrayKey : "comparable_property",
							fieldKey : "inv_calcs.building",
							name     : "average_building",
						}}
					/>
					<Avergaes
						label="Precio Unitario ($ /m2):"
						name="average_unit_price"
						columnAverage={{
							length   : 6,
							arrayKey : "comparable_property",
							fieldKey : "inv_calcs.unit_price",
							name     : "average_unit_price",
						}}
					/>
				</div>
			</div>
		</Card>
	);
};

export default ComparableProperty;
