import isValidArray                 from "Helpers/isValidArray";
import { useEffect, useState }      from "react";
import { useFormContext, useWatch } from "react-hook-form";


const useCalcSurfaceFactor = ({ keys }) => {
	const { setValue } = useFormContext();

	const [stateSurface, setStateSurface] = useState(0);

	const surfaceValuedLot = useWatch({ name : "surface_f.lot"});
	const surfaceTypeLot   = useWatch({ name : "surface_f.type_lot"});

	const calcSurface = () => {
		if (surfaceTypeLot < 1) {
			handleSetValues(0);
			return;
		}

		const result = surfaceValuedLot / surfaceTypeLot;
		const surface = getSurfaceF(result);

		handleSetValues(surface);
	};

	const handleSetValues = (surface) => {
		setStateSurface(surface);

		if (isValidArray(keys)) {
			keys.forEach(key => setValue(key, surface));
			return;
		}
		setValue(keys, surface);
	};

	useEffect(() => {
		calcSurface();
	}, [surfaceValuedLot, surfaceTypeLot]);

	return [stateSurface, calcSurface, surfaceValuedLot, surfaceTypeLot];
};

const getSurfaceF = (val) => {
	switch (true) {
		case val < 2.1:
			return 1.00;

		case val < 3.1:
			return  0.98;

		case val < 4.1:
			return 0.96;

		case val < 5.1:
			return 0.94;

		case val < 6.1:
			return 0.92;

		case val < 7.1:
			return 0.90;

		case val < 8.1:
			return 0.88;

		case val < 9.1:
			return 0.86;

		case val < 10.1:
			return 0.84;

		case val < 11.1:
			return 0.82;

		case val < 12.1:
			return 0.80;

		case val < 13.1:
			return 0.78;

		case val < 14.1:
			return 0.76;

		case val < 15.1:
			return 0.74;

		case val < 16.1:
			return 0.72;

		case val < 17.1:
			return 0.70;

		case val < 18.1:
			return 0.68;

		case val < 19.1:
			return 0.66;

		case val < 20.1:
			return 0.64;

		default:
			return 0.62;
	}
};

export default useCalcSurfaceFactor;
