
//Our Imports
import "./ListOfFiles.scss";

const ListOfFiles = ({
	delegations : {
		files,
		handleGetType,
		handleTransleteFileName,
	},
}) => {

	return (
		<div id="ListOfFiles">
			<div className="grid-wrapper">
				{
					files.map(file => (
						<a
							download
							key={file?._id}
							href={file?.location}
						>
							<h5 className="file-title">{handleTransleteFileName(file?.description)}</h5>
							<div className="card">
								<div className="card-body">
									<img className="img" src={handleGetType(file?.name)}  width="40" height="50" />
									<p className="text-name">{file?.name}</p>
								</div>
							</div>
						</a>
					))
				}
			</div>
		</div>
	);
};

export default ListOfFiles;
