import { Table } from "reactstrap";

import { getAdjustedPrice } from "Helpers/formulas";
import { HelpToolTip }      from "Components/Global";
import TdInput              from "Components/Appraisal/TdInput";
import TdWatcherHOC         from "Components/Appraisal/TdWatcherHOC";
import TdOptionalWtch       from "Components/Appraisal/TdOptionalWtch";

//If you want, you could move it to resorce dir
const featuresOptions = [
	{
		value : 0,
		label : "Casa Habitacin",
	},
	{
		value : 1,
		label : "Terreno c/Construccin",
	},
	{
		value : 2,
		label : "Terreno intermedio",
	},
	{
		value : 3,
		label : "Terreno en esquina",
	},
];

const TableInvestComp = ({
	delegations : {
		index,
		hookForm,
		calcUnitPrice,
		handleShowTDFields,
	},
}) => {
	return (
		<Table striped bordered responsive>
			<thead>
				<tr>
					<th colSpan="1" scope="colgroup" className="text-center"></th>
					<th colSpan="2" scope="colgroup" className="text-center">Superficies</th>
					<th colSpan="6" scope="colgroup" className="text-center"></th>
				</tr>
				<tr>
					<th className="text-center" scope="col">Caractersticas</th>
					<th className="text-center" scope="col">Suelo m<sup>2</sup></th>
					<th className="text-center" scope="col">Construccin m<sup>2</sup></th>
					<th className="text-center" scope="col">Precio Oferta</th>
					<th className="text-center" scope="col">Negociacin</th>
					<th className="text-center" scope="col">
						<span>Precio Ajustado </span>
						<HelpToolTip target={"help-table-inves-comp-price"+ index}>
							Precio Ajustado = (Oferta) x (Factor Negociacin)
						</HelpToolTip>
					</th>
					<th className="text-center" scope="col">
						<span>Precio Unitario ($/m<sup>2</sup>)</span>
						<HelpToolTip target={"help-table-inves-comp-unit-price"+ index}>
							<ul className="text-start">
								<li>
									<b>Para casas Habitacin y Terrenos con construccin:</b>
									<ul>
										<li>Precio Unitario = (Precio Ajustado) / (Superficie de Construccin)</li>
									</ul>
								</li>
								<li className="mt-1">
									<b>Para Terrenos:</b>
									<ul>
										<li>Precio Unitario = (Precio Ajustado) / (Superficie de Suelo)</li>
									</ul>
								</li>
							</ul>
						</HelpToolTip>
					</th>
					<th className="text-center" scope="col">Clasificacin</th>
					<th className="text-center" scope="col">rea de Valor</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<TdInput hookForm={hookForm} name={`unit_value_land.${index}.invest_comp.features`} options={featuresOptions} />
					<TdInput hookForm={hookForm} name={`unit_value_land.${index}.invest_comp.ground`} />
					<TdOptionalWtch handleShowField={handleShowTDFields} name={`unit_value_land.${index}.invest_comp.features`}>
						<TdInput hookForm={hookForm} name={`unit_value_land.${index}.invest_comp.construction`} />
					</TdOptionalWtch>
					<TdInput hookForm={hookForm} name={`unit_value_land.${index}.invest_comp.offer_price`} />
					<TdInput hookForm={hookForm} name={`unit_value_land.${index}.invest_comp.negotation`} />
					<TdWatcherHOC
						name={`unit_value_land.${index}.invest_comp.adjusted_price`}
						deps={[
							`unit_value_land.${index}.invest_comp.offer_price`,
							`unit_value_land.${index}.invest_comp.negotation`,
						]}
						formula={getAdjustedPrice}
					/>
					<TdWatcherHOC
						name={`unit_value_land.${index}.invest_comp.unit_price`}
						deps={[
							`unit_value_land.${index}.invest_comp.adjusted_price`,
							`unit_value_land.${index}.invest_comp.features`,
							`unit_value_land.${index}.invest_comp.construction`,
							`unit_value_land.${index}.invest_comp.ground`,
						]}
						callback={calcUnitPrice}
					/>
					<TdOptionalWtch handleShowField={handleShowTDFields} name={`unit_value_land.${index}.invest_comp.features`}>
						<TdInput hookForm={hookForm} name={`unit_value_land.${index}.invest_comp.clasification`} />
					</TdOptionalWtch>
					<TdInput hookForm={hookForm} name={`unit_value_land.${index}.invest_comp.value_area`} />
				</tr>
			</tbody>
		</Table>
	);
};

export default TableInvestComp;
