import { useState }       from "react";
import { Card }           from "reactstrap";
import { useFormContext } from "react-hook-form";

import { HelpToolTip } from "Components/Global";
import InptWatcherHOC  from "Components/Appraisal/InptWatcherHOC";

import {
	roundFixed,
	sumSetOfValues,
	getFiscalDepreciation,
} from "Helpers/formulas";

import { PARAMS, DEFAULT_FETCH_DATA }               from "Pages/app/Appraisal/CapitalizationApproach";
import AppraisalHOCPageFetcher                      from "Components/Appraisal/AppraisalHOCPageFetcher";
import ModalEquivalenceTable                        from "../ModalEquivalenceTable";
import { InptsRow }                                 from "./AnualDeductionChild";
import { MATRIX_CONSERVATION, MATRIX_UNEMPLOYMENT } from "./AnualDeduction.constants";


const AnualDeduction = ({ appraise = DEFAULT_FETCH_DATA }) => {

	const {
		construction_total_val,
		total_val_commun_area,
		total_val_element,
	} = appraise.physical_approach;

	const hookForm = useFormContext();

	const { setValue } = hookForm;

	const [modal, setModal] = useState({ isOpen : false, data : [], title : "" });

	const toggle = (data, title, callback) => ()=> setModal(prev => ({ isOpen : !prev.isOpen, data : data, title, callback }));

	const handleSeteValue = (name) => (val) => setValue(name, val);

	const getConstructionVal = () => Number(construction_total_val) + Number(total_val_commun_area) + Number(total_val_element);

	const calcIPorcentage = (gross_mensual_rent, key) => {
		const constructionVal = getConstructionVal();
		const result = getFiscalDepreciation(constructionVal, gross_mensual_rent);
		setValue(key, result);
	};

	return (
		<Card className="p-1">
			<ModalEquivalenceTable modalContext={modal} toggle={toggle} />
			<div className="row">
				<h4 className="col-md-6 subTitle-text mb-1">Deducciones mensuales estimadas</h4>
				<div className="col-md-6">
					<p>Valor de las Construcciones(enfoque fsico):</p>
					<h4 className="w-bolder mb-1">{getConstructionVal()}</h4>
				</div>
			</div>
			<InptsRow
				prefixName="a"
				hookForm={hookForm}
				onClick={toggle(MATRIX_UNEMPLOYMENT, "VACIOS", handleSeteValue("a_porcentage"))}
				label="a) Porcentajes de desocupacin (vacos)"
			/>
			<InptsRow
				prefixName="b"
				hookForm={hookForm}
				label="b) Impuesto Predial"
			/>
			<InptsRow
				prefixName="c"
				hookForm={hookForm}
				label="c) Servicio de Agua (en su caso)"
			/>
			<InptsRow
				prefixName="d"
				hookForm={hookForm}
				onClick={toggle(MATRIX_CONSERVATION, "CONSERVACION Y MANTENIMIENTO", handleSeteValue("d_porcentage"))}
				label="d) Conservacin y mantenimiento"
			/>
			<InptsRow
				prefixName="e"
				hookForm={hookForm}
				label="e) Administracin"
			/>
			<InptsRow
				prefixName="f"
				hookForm={hookForm}
				label="f) Energa Elctrica (en su caso)"
			/>
			<InptsRow
				prefixName="g"
				hookForm={hookForm}
				label="g) Seguros"
			/>
			<InptsRow
				prefixName="h"
				hookForm={hookForm}
				label="h) Otros (indicar)"
			/>
			<InptsRow
				prefixName="i"
				hookForm={hookForm}
				label="i) Depreciacin Fiscal"
				InptPorcentage={() => (
					<div className="d-flex col-md-3">
						<InptWatcherHOC
							className="w-100"
							label="Porcentaje%"
							name="i_porcentage"
							callback={calcIPorcentage}
							deps={["gross_mensual_rent"]}
						/>
						<HelpToolTip target={"help-anual-deduction-i-porcentage"}>
							Depreciacin Fiscal = ( (Valor de las construcciones * 0.05) / 12 ) / Renta mensual bruta
						</HelpToolTip>
					</div>
				)}
			/>
			<InptsRow
				prefixName="j"
				hookForm={hookForm}
				label="j) Deducciones fiscales ( b + c + d + e + f + g)"
				InptPorcentage={() => (
					<InptWatcherHOC
						isArrayArgs={true}
						className="col-md-3"
						label="Porcentaje%"
						name="j_porcentage"
						formula={sumSetOfValues}
						deps={[
							"b_porcentage",
							"c_porcentage",
							"d_porcentage",
							"e_porcentage",
							"f_porcentage",
							"g_porcentage",
						]}
					/>
				)}
			/>
			<InptsRow
				prefixName="k"
				hookForm={hookForm}
				label="k) Impuesto sobre la renta"
			/>
			<InptsRow
				prefixName="sum"
				hookForm={hookForm}
				label="Suma ( a + h + j + k )"
				InptPorcentage={() => (
					<InptWatcherHOC
						isArrayArgs={true}
						className="col-md-3"
						label="Porcentaje%"
						name="sum_porcentage"
						formula={sumSetOfValues}
						deps={[
							"a_porcentage",
							"h_porcentage",
							"j_porcentage",
							"k_porcentage",
						]}
					/>
				)}
			/>
			<div className="row align-items-center gy-1">
				<p className="fw-bolder col-md-3">En nmero redondos</p>
				<div className="col-md-3" />

				<InptWatcherHOC
					className="col-md-3"
					label="Porcentaje%"
					name={"rounded_porcentage"}
					deps={["sum_porcentage" ]}
					formula={roundFixed}
				/>
			</div>
		</Card>
	);
};


export default AppraisalHOCPageFetcher(AnualDeduction, PARAMS);
