import { useEffect }                from "react";
import { useFormContext, useWatch } from "react-hook-form";

import { isValidArray } from "Helpers";
import TdInput          from "../TdInput";

const TdWatcherHOC = ({
	name,
	deps,
	formula,
	callback,
	isArrayArgs,
	...rest
}) => {
	const hookForm = useFormContext();

	//The dep's order is important
	const watcher = useWatch({ name : isValidArray(deps) ? [...deps] : name });

	const handleSeteValue = () => {
		const result = isArrayArgs ? formula([...watcher]) : formula(...watcher);

		hookForm.setValue(name, result);
	};

	useEffect(() => {
		typeof(callback) !== "function" ?  handleSeteValue() : callback(watcher, name);
	}, [watcher]);

	return (
		<TdInput disabled hookForm={hookForm} name={name} {...rest} />
	);
};

export default TdWatcherHOC;
