//the reason of this componet is because there is a bug that araise when we use an array at name deps, that causes an infinity loop.

import { useEffect }                from "react";
import { useFormContext, useWatch } from "react-hook-form";

import { productSetOfValues } from "Helpers/formulas";
import TdInput                from "Components/Appraisal/TdInput";

//So, the solution of this is that we need to state independent watchers.
export const TdCalcFR = ({ index, name }) => {
	const hookForm     = useFormContext();
	const { setValue } = hookForm;

	const cusWatcher       = useWatch({ name : `homologation.${index}.cus` });
	const surfaceWatcher   = useWatch({ name : `homologation.${index}.surface` });
	const businessWatcher  = useWatch({ name : `homologation.${index}.business` });
	const zoneWatcher      = useWatch({ name : `homologation.${index}.zone` });
	const ubicationWatcher = useWatch({ name : `homologation.${index}.ubication` });
	const frontWatcher     = useWatch({ name : `homologation.${index}.front` });
	const shapeWatcher     = useWatch({ name : `homologation.${index}.shape` });
	const qualityWatcher   = useWatch({ name : `homologation.${index}.quality` });
	const ageWatcher       = useWatch({ name : `homologation.${index}.age` });
	const topologyWatcher  = useWatch({ name : `homologation.${index}.topology` });
	const otherWatcher     = useWatch({ name : `homologation.${index}.other` });

	useEffect(() => {
		const result = productSetOfValues([
			cusWatcher,
			surfaceWatcher,
			businessWatcher,
			zoneWatcher,
			ubicationWatcher,
			frontWatcher,
			shapeWatcher,
			qualityWatcher,
			ageWatcher,
			topologyWatcher,
			otherWatcher,
		]);
		setValue(name, result);
	}, [
		cusWatcher,
		surfaceWatcher,
		businessWatcher,
		zoneWatcher,
		ubicationWatcher,
		frontWatcher,
		shapeWatcher,
		qualityWatcher,
		ageWatcher,
		topologyWatcher,
		otherWatcher,
	]);

	return (
		<TdInput disabled hookForm={hookForm} name={name} />
	);

};
