import { Table } from "reactstrap";

import TdInput from "Components/Appraisal/TdInput";

const housingClassOptions = [
	{
		label : "Mnima",
		value : 1,
	},
	{
		label : "Econmica",
		value : 2,
	},
	{
		label : "Interes Social",
		value : 3,
	},
	{
		label : "Medio",
		value : 4,
	},
	{
		label : "Semilujo",
		value : 5,
	},
	{
		label : "Residencial",
		value : 6,
	},
	{
		label : "Residencial Plus",
		value : 7,
	},
	{
		label : "nica",
		value : 8,
	},
];

const conservationState = [
	{
		label : "Ruinoso",
		value : 1,
	},
	{
		label : "Malo",
		value : 2,
	},
	{
		label : "Regular",
		value : 3,
	},
	{
		label : "Bueno",
		value : 4,
	},
	{
		label : "Muy Bueno",
		value : 5,
	},
	{
		label : "Nuevo",
		value : 6,
	},
	{
		label : "Recientemente Remodelado",
		value : 7,
	},
];

const otherConsiderationsOptions = [
	{
		label : "Superior",
		value : 0,
	},
	{
		label : "Similar",
		value : 1,
	},
	{
		label : "Inferior",
		value : 2,
	},
];

const referenceOptions = [
	{
		label : "Cntrica",
		value : 1,
	},
	{
		label : "Intermedia",
		value : 2,
	},
	{
		label : "Periferica",
		value : 3,
	},
	{
		label : "De Expansin",
		value : 4,
	},
	{
		label : "Rural",
		value : 5,
	},
];

const TableInvesSelects = ({
	delegations : {
		index,
		hookForm,
	},
}) => {


	return (
		<Table  striped bordered responsive>
			<thead>
				<tr>
					<th colSpan="1"></th>
					<th colSpan="1"></th>
					<th colSpan="1"></th>
					<th colSpan="1"></th>
					<th colSpan="1"></th>
					<th colSpan="1"></th>
					<th colSpan="1"></th>
					<th colSpan="3" scope="colgroup" className="text-center">Promotor</th>
				</tr>
				<tr>
					{/* <th className="text-center" scope="col">Nm.</th> */}
					<th className="text-center w-200" scope="col">Clase de Inmueble</th>
					<th className="text-center" scope="col">Estado de Conservacin</th>
					<th className="text-center" scope="col">Otras Consideraciones</th>
					<th className="text-center" scope="col">Referencia de Proximidad</th>
					<th className="text-center" scope="col">Clasificacin</th>
					<th className="text-center" scope="col">Nombre</th>
					<th className="text-center" scope="col">Lada</th>
					<th className="text-center" scope="col">Telfono</th>
					<th className="text-center" scope="col">URL</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<TdInput hookForm={hookForm} name={`comparable_property.${index}.inv_selects.housing_class`} options={housingClassOptions} />
					<TdInput hookForm={hookForm} name={`comparable_property.${index}.inv_selects.conservation_state`} options={conservationState} />
					<TdInput hookForm={hookForm} name={`comparable_property.${index}.inv_selects.other_considerations`}  options={otherConsiderationsOptions} />
					<TdInput hookForm={hookForm} name={`comparable_property.${index}.inv_selects.reference`} options={referenceOptions} />
					<TdInput hookForm={hookForm} name={`comparable_property.${index}.inv_selects.clasification`} type="text" />
					<TdInput hookForm={hookForm} name={`comparable_property.${index}.inv_selects.name`} type="text" />
					<TdInput hookForm={hookForm} name={`comparable_property.${index}.inv_selects.lada`} type="text" />
					<TdInput hookForm={hookForm} name={`comparable_property.${index}.inv_selects.phone`} inpt_type="isNotMasked" />
					<TdInput hookForm={hookForm} name={`comparable_property.${index}.inv_selects.url`} type="text" />
				</tr>
			</tbody>
		</Table>
	);
};

export default TableInvesSelects;
