import { FormGroup, Label } from "reactstrap";

import { validateCheckBox } from "Helpers";
import { CheckboxInpt }     from "Components/Global";

const StatusApproach = ({
	delegations  : {
		hookForm,
		approachStatus,
	},
}) => {

	return (
		<>
			<FormGroup>
				<Label>Enfoque: </Label>
				<CheckboxInpt
					name="approach_status"
					inpt_type="check-box"
					label="Cancelar Enfoque (Inmuebles Atpicos)"
					falseLabel="Usar Enfoque"
					control={hookForm.control}
					rules={{
						validate : validateCheckBox,
					}}
				/>
			</FormGroup>
			{
				approachStatus &&
					<p>
						De acuerdo a las reglas de carcter general que establecen la metodologa para la valuacin de inmuebles objeto de crditos garantizados a la
						vivienda, emitidas por Sociedad Hipotecaria Federal, S. N. C., en su Capitulo V, regla Vigesimosegunda,
						no es necesaria la aplicacin de este enfoque.
					</p>
			}
		</>
	);
};

export default StatusApproach;
