import { useFieldArray, useFormContext } from "react-hook-form";

import { useColumnAverage }                                from "Helpers/Hooks";
import { getNetPayments, getUnitValueConstruction, getVi } from "Helpers/formulas";
import TableStResPrc                                       from "./TableStResPrc";

const TableStResPrcContainer = () => {

	const hookForm = useFormContext();

	const { control, setValue } = hookForm;

	const { fields : statisticsResProcessFields } = useFieldArray({
		control,
		name : "statistics_res_process",
	});


	const calcUnitValue = (vals, name) => {
		const [ f, features, ground, unit_price ] = vals;

		const result = features?.value >= 2 ? unit_price : getUnitValueConstruction(f, ground);

		setValue(name, isNaN(result) ? 0 : result);
	};

	const handleShowTDFields = (fieldValues) =>{
		const isHousingOrConstruction = fieldValues?.value;
		return isHousingOrConstruction < 2;
	};

	const calcVi = (vals, name) => {
		const [ adjusted_price, bObject ] = vals;

		const b = Number(bObject?.label) ?? 0;

		const result = getVi(adjusted_price, b);

		setValue(name, result);
	};

	const calcPn = (vals, name) => {
		const [payments = 0, ageDemerit = 0, conservationObj = 0] = vals;

		const conservLabel = conservationObj?.label ?? "";
		const conservLabelSplited = conservLabel.split("/"); //label = "Tag/Number"
		const conservation = Number(conservLabelSplited?.[1]) ?? 0;

		const result = getNetPayments(payments, ageDemerit, conservation);

		setValue(name, result);
	};

	useColumnAverage({
		length   : 6,
		arrayKey : "statistics_res_process",
		fieldKey : "unit_value",
		name     : "statistics_res_process_average",
	});

	return (
		<TableStResPrc
			delegations={{
				calcPn,
				calcVi,
				hookForm,
				calcUnitValue,
				handleShowTDFields,
				statisticsResProcessFields,
			}}
		/>
	);
};

export default TableStResPrcContainer;
