import React from "react";

const RadioButton = ({
	id,
	name,
	label,
	register,
	className,
	isTextBreaked,
	handleOnchage = () => {},
	classNameLabelGruop,
	rules = { required : true },
	...rest
}) => {
	return (
		<div className={className}>
			<label  htmlFor={id} style={{ fontSize : "12px", cursor : "pointer" }}>
				{
					isTextBreaked  &&
					<>
						{label}
						<br />
					</>
				}
				<div className={classNameLabelGruop}>
					<input
						id={id}
						type="radio"
						className="form-check-input cursor-pointer mx-1"
						name={name}
						{...register(name, {
							...rules,
							onChange : handleOnchage,
						})}
						{...rest}
					/>
					{ !isTextBreaked  && <> {label}</>}
				</div>

			</label>
		</div>
	);
};

export default RadioButton;
