import { useFormContext, useWatch } from "react-hook-form";

import ComparationAnalysis from "./ComparationAnalysis";

const ComparationAnalysisContainer = () => {

	const hookForm     = useFormContext();
	const { setValue } = hookForm;

	const approachStatusFactor = useWatch({ name : "approach_status_factor"});

	const handleSetValue = ([ value ], name) => setValue(name, value);

	const setMaxValue = (values, name) => {
		const max = Math.max(...values);
		setValue(name, max);
	};

	const setMinValue = (values, name) => {
		const min = Math.min(...values);
		setValue(name, min);
	};

	const setCompAverage = (values, name) => {
		const [ cus, factor, status ] = values;
		const average =  status.toString() === "0" ? cus : factor;

		setValue(name, average);
	};

	const calcComp = (type) => (values, name) => {
		const copy = [...values];
		type === "min" ? setMinValue(copy, name) : setMaxValue(copy, name) ;
	};

	return (
		<ComparationAnalysis
			delegations={{
				hookForm,
				calcComp,
				setMaxValue,
				setMinValue,
				handleSetValue,
				setCompAverage,
				approachStatusFactor,
			}}
		/>
	);
};

export default ComparationAnalysisContainer;
