import { Percent }        from "react-feather";
import { useFormContext } from "react-hook-form";

//Our Imports
import { FormTreePicker, MapFormInpts } from "Components/Global";
import Form                             from "Components/Appraisal/Form";
import CreableMsg                       from "Components/Appraisal/CreableMsg";
import envOptions                       from "Resources/data/Enviroment";
import "./Enviroment.scss";

const {
	populationDensity,
	typeConstructionOptions,
	zoneClasificationOptions,
	enviromentContaminationOptions,
	proximityUrbanReferenceOptions,
} = envOptions;

const Environment = ({ onSubmit, isLoading }) => {

	const hookForm = useFormContext();

	const inpts = [
		{
			type        : "textarea",
			label       : "Tipo de construccin Predominante:",
			name        : "environment.type_construction",
			inpt_type   : "creatable",
			options     : typeConstructionOptions,
			rules       : { required : false },
			newOptionId : 4,
		},
		{
			type    : "textarea",
			label   : "Vias de Acceso:",
			name    : "environment.access_ways",
			options : { required : false },
		},
		{
			label   : "Referencia de Proximidad Urbana:",
			name    : "environment.proximity_urban_reference",
			options : proximityUrbanReferenceOptions,
			rules   : { required : false },
		},
		{
			label   : "Densidad de Poblacin:",
			name    : "environment.population_density",
			options : populationDensity,
			rules   : { required : false },
		},
		{
			label   : "ndice de Saturacin:",
			name    : "environment.saturation_index",
			type    : "number",
			min     : 0,
			max     : 100,
			icon    : <Percent size={14} />,
			options : { required : false },
		},
		{
			label   : "Contaminacin Ambiental:",
			name    : "environment.environmental_contamination",
			options : enviromentContaminationOptions,
			rules   : { required : false },
		},
	];

	return (
		<div id="Enviroment">
			<Form onSubmit={onSubmit} isLoading={isLoading}>
				<CreableMsg className="mb-1" />
				<FormTreePicker
					size="sm"
					hookForm={hookForm}
					treeData={zoneClasificationOptions}
					name="environment.zone_classification"
					rules={{ required : false }}
				/>
				<MapFormInpts inpts={inpts} hookForm={hookForm}  />
			</Form>
		</div>
	);
};

export default Environment;

