import { FormInputGroup }      from "Components/Global";
import { useState, useEffect } from "react";
import { useFormContext }      from "react-hook-form";

const Coordinates = ({
	delegations: {
		line,
		label,
		center,
		setCenter,
		checked,
	},
}) => {

	const hookForm = useFormContext();

	const { register, formState, setValue } = hookForm;
	const defaultLat = 40.7128;
	const defaultLng = -74.0060;

	const [lat, setLat] = useState(center.lat !== null ? center.lat : defaultLat);
	const [lng, setLng] = useState(center.lng !== null ? center.lng : defaultLng);

	const handleLatChange = (newlat) => {
		const val = parseFloat(newlat.target.value);
		const newpoints = {
			lat : isNaN(val) ? center.lat : val,
			lng : isNaN(lng) ? center.lng : lng,
		};

		setLat(val);
		setCenter(newpoints);
	};

	const handleLngChange = (newlng) => {
		const val = parseFloat(newlng.target.value);
		const newpoints = {
			lat : isNaN(lat) ? center.lat : lat,
			lng : isNaN(val) ? center.lng : val,
		};

		setLng(val);
		setCenter(newpoints);
	};

	useEffect(() => {
		const lat = center.lat;
		const lng = center.lng;
		setLat(lat);
		setLng(lng);

		setValue(`general_information.${line}`, line === "lat" ? lat : lng);
	}, [center]);

	return (
		<div className="row">
			{
				lat && lng && center && (
					<FormInputGroup
						key={`general_information.${line}`}
						className="col-md-12"
						bsSize="sm"
						type="number"
						label={`${label} :`}
						value={ line === "lat" ? ( lat ? lat : "" ) : ( lng ? lng : "" ) }
						name={`general_information.${line}`}
						register={register}
						errors={formState.errors}
						options={{ required : false, onChange : ( line === "lat" ? handleLatChange : handleLngChange ) }}
					/>
				)
			}
		</div>
	);
};

export default Coordinates;
