import { useFormContext } from "react-hook-form";

import { MapFormInpts }                 from "Components/Global";
import ComplementaryConstructionOptions from "Resources/data/ComplementaryConstruction";
import apprasialOptions                 from "Resources/data/Appraisal";

const ComplementaryConstruction = () => {

	const hookForm = useFormContext();

	const { statusOptions } = apprasialOptions;

	const { internetOptions, airconditioningUbicationsOptions } = ComplementaryConstructionOptions;

	const inpts = [
		{
			text      : "Barda perimetral en el conjunto:",
			name      : "perimeter_wall",
			inpt_type : "check-box-group",
		},
		{
			text      : "Canchas Deportivas:",
			name      : "sports_fields",
			inpt_type : "check-box-group",
		},
		{
			text      : "Caseta de vigilancia:",
			name      : "guardhouse",
			inpt_type : "check-box-group",
		},
		{
			text      : "Voz y datos (Internet):",
			name      : "internet",
			inpt_type : "row-select",
			options   : internetOptions,
			rules     : { required : false },
		},
		{
			text      : "Aire acondicionado:",
			name      : "air_conditioning",
			inpt_type : "check-box-group",
		},
		{
			text      : "Cisterna:",
			name      : "tank",
			inpt_type : "check-box-group",
		},
		{
			text      : "Ducto para clima:",
			name      : "duct",
			inpt_type : "check-box-group",
		},
		{
			text      : "Estacionamiento Techado en el Conjunto:",
			name      : "roofing_parking",
			inpt_type : "check-box-group",
		},
		{
			text      : "Estacionamiento Techado en la Vivienda:",
			name      : "roofing_parking_housing",
			inpt_type : "check-box-group",
		},
		{
			text      : "Cajones de estacionamiento adicionales para visitas:",
			name      : "additional_parking",
			inpt_type : "check-box-group",
		},
		{
			text      : "Interfon:",
			name      : "interphone",
			inpt_type : "check-box-group",
		},
		{
			text      : "Jaula de tendido:",
			name      : "laundry_cage",
			inpt_type : "check-box-group",
		},
		{
			text      : "Juegos Infantiles:",
			name      : "child_games",
			inpt_type : "check-box-group",
		},
		{
			text      : "Red de agua purificada:",
			name      : "purified_water_system",
			inpt_type : "check-box-group",
		},
		{
			text      : "Rejas de proteccin en ventanas:",
			name      : "window_protection_bars",
			inpt_type : "check-box-group",
		},
		{
			text      : "Separacin de basura contenedores:",
			name      : "rubbish_bins",
			inpt_type : "row-select",
			options   : statusOptions,
			rules     : { required : false },
		},
		{
			text      : "Sistema de descarga de aguas residuales:",
			name      : "residual_water_system",
			inpt_type : "check-box-group",
		},
		{
			text      : "Sistemas de deteccin de movimiento para encendido de luces:",
			name      : "motion_sensors_light",
			inpt_type : "check-box-group",
		},
		{
			text      : "Tratamiento de agua:",
			name      : "water_treatment",
			inpt_type : "check-box-group",
		},
		{
			text      : "Tratamiento de y reutilizacin de aguas jabonosas:",
			name      : "water_treatment_soapy_waters",
			inpt_type : "check-box-group",
		},
		{
			text      : "Ubicacin aire acondicionado:",
			name      : "air_conditioning_ubication",
			inpt_type : "row-select",
			options   : airconditioningUbicationsOptions,
			rules     : { required : false },
		},
		{
			text      : "Suministro de agua purificada en la vivienda:",
			name      : "purified_water_supply",
			inpt_type : "check-box-group",
		},
	];

	return (
		<div className="row mb-1">
			<hr />
			<h4>Obras Complementarias Vivienda</h4>
			<MapFormInpts className="col-md-6" inpts={inpts} hookForm={hookForm} />
		</div>
	);
};

export default ComplementaryConstruction;
