
import { Label } from "reactstrap";

import { InputMaskNumber } from "Components/Global";
import InptWatcherHOC      from "Components/Appraisal/InptWatcherHOC";
import { roundFixed }      from "Helpers/formulas";

const ConclusionRateCapt = ({
	delegations : {
		hookForm,
		rateMethod,
		physicMethod,
		bussinesMethod,
		referenceAverage,
		calcReferenceAverage,
	},
}) => {

	return (
		<div className="mt-1">
			<h5>Conclusin para la obtencin de la tasa de capitalizacin.</h5>
			<div className="my-1">
				<Label style={{ fontSize : "14px"}}>A) Mtodo Fsico:</Label>
				<span> {physicMethod}%</span>
			</div>
			<div className="my-1">
				<Label style={{ fontSize : "14px"}}>B) Mtodo de Mercado:</Label>
				<span> {bussinesMethod}%</span>
			</div>
			<div className="my-1">
				<Label style={{ fontSize : "14px"}}>C) Mtodo por Puntos:</Label>
				<span> {rateMethod}%</span>
			</div>
			<InputMaskNumber
				bsSize="sm"
				icon={<span style={{ fontSize : "10px" }}>%</span>}
				className="mt-1 col-md-6"
				name="other_method"
				label="D) Otro"
				hookForm={hookForm}
				rules={{ required : false }}
			/>
			<InptWatcherHOC
				bsSize="sm"
				icon={<span style={{ fontSize : "10px" }}>%</span>}
				className="mt-1 col-md-6"
				name="reference_average"
				label="PROMEDIO DE REFERENCIA"
				callback={calcReferenceAverage}
				deps={[
					"physic_rate.net_rate",
					"business_rate.net_rate",
					"result_rate",
					"other_method",
				]}
				rules={{ required : false }}
			/>
			<div className="my-1">
				<Label style={{ fontSize : "14px"}}>A utilizar, en nmeros redondos</Label>
				<span> {roundFixed(referenceAverage)}%</span>
			</div>
		</div>
	);
};

export default ConclusionRateCapt;
