import { Eye, X }                     from "react-feather";
import { Button }                     from "reactstrap";
import { Controller, useFormContext } from "react-hook-form";
import classNames                     from "classnames";

import { isValidArray } from "Helpers";
import toaster          from "Components/Global/Toaster";
import UploadSingleFile from "../UploadSingleFile";
import UploadMultiFiles from "../UploadMultiFiles";

import pdfImg   from "Resources/images/pdf-img.png";
import wordImg  from "Resources/images/word-img.png";
import iconImg  from "Resources/images/icon-img.png";
import excelImg from "Resources/images/excel-img.png";


export const handleGetName = (file) => {
	if (file?.path) {
		return file.path;
	}

	if (file?.name) {
		return file.name;
	}
	return "";
};

export const handleGetType = (key) => {
	if (typeof(key) !== "string") return;

	if (key?.includes(".docx")) {
		return wordImg;
	} else if (key?.includes(".pdf")) {
		return pdfImg;
	} else if (key?.includes(".xlsx")) {
		return excelImg;
	} else {
		return iconImg;
	}
};


export function RHFUploadFile({
	name,
	rules = { required : true },
	...other
}) {
	const { control } = useFormContext();
	return (
		<Controller
			name={name}
			control={control}
			rules={rules}
			render={({ field, fieldState: { error } }) => {
				const checkError = !!error && !field.value;

				const handleDrop = acceptedFiles => {
					const file = acceptedFiles[0];

					if (file) {
						const createObjectURL = Object.assign(file, { preview : URL.createObjectURL(file) });
						field.onChange(createObjectURL);
					}

					return null;
				};

				return (
					<UploadSingleFile
						name={name}
						file={field.value}
						error={checkError}
						onDrop={handleDrop}
						onChange={field.onChange}
						accept="application/pdf"
						{...other}
					/>
				);
			}}
		/>
	);
}

export function RHFUploadFiles({
	name,
	maxFiles = 5,
	rules = { required : false },
	...other
}) {
	const { control } = useFormContext();

	const image    = "image/*";
	const pdf      = "application/pdf";
	const xlsx     = "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet";
	const docxType = "application/vnd.openxmlformats-officedocument.wordprocessingml.document";

	const accept = [image, pdf, xlsx, docxType];

	return (
		<Controller
			name={name}
			control={control}
			rules={{ required : false }}
			render={({ field, fieldState: { error } }) => {

				const checkError = !!error && !field.value;

				const handleOnchage = (files) => {
					if (!isValidArray(files)) {
						return;
					}
					const newFiles = files.map(file => {
						if (file instanceof File) {
							const createObjectURL = Object.assign(file, { preview : URL.createObjectURL(file) });

							return createObjectURL;
						}
						return file;

					});

					field.onChange(newFiles);
				};

				const handleSetFiles = (files) => {
					if (files?.length < maxFiles) {
						handleOnchage(files);
						return;
					}
					toaster.error({
						title : "Limite Superado",
						body  : `Superaste el limite permitido para subir archivos, favor de subir un mximo de ${maxFiles} archivos.`,
					});
				};

				const handleDrop = (acceptedFiles) => {
					const prevFiles = isValidArray(field.value) ? [ ...field.value ] : [];

					const files =[ ...prevFiles, ...acceptedFiles ];

					handleSetFiles(files);
					return null;
				};

				return (
					<UploadMultiFiles
						name={name}
						accept={accept}
						error={checkError}
						files={field.value}
						onDrop={handleDrop}
						onChange={field.onChange}
						{...other}
					/>
				);
			}}
		/>
	);
}

export const RemoveBtn = ({render, onClick, className = "removeImage-button" }) => (
	<Button.Ripple
		type="button"
		color="danger"
		className={classNames(className, { show : render })}
		onClick={onClick}
	>
		<X size={12} />
	</Button.Ripple>
);

export const EyeBtn = ({render, onClick, className = "" }) => (
	<Button.Ripple
		type="button"
		color="primary"
		className={classNames(`${className}`, { show : render })}
		onClick={onClick}
	>
		<Eye size={12} />
	</Button.Ripple>
);

