import { NavLink } from "react-router-dom";

//Our Imports
import { apprasialNavigation } from "Helpers";
import MenuSection             from "../MenuSection";
import "./Menu.scss";

const Menu = ({ id }) => {
	const { navigationHeader, navigationBody, navigationFooter } = apprasialNavigation;
	return (
		<div id="Menu">
			<div className="row  g-2">
				<h1 className="col-md-3">SOFIA</h1>
				{
					navigationHeader.map(({label, to}) => (
						<NavLink
							key={to}
							to={`${to}/${id}`}
							className="link col-md-3"
						>
							{label}
						</NavLink>
					))
				}
			</div>
			<hr />
			<div className="row  g-2">
				{
					navigationBody.map(({title, options}) => (
						<MenuSection id={id} key={title} title={title} options={options} />
					))
				}
			</div>
			<hr />
			<div className="row  g-2">
				{
					navigationFooter.map(({title, options}) => (
						<MenuSection id={id} key={title} title={title} options={options} />
					))
				}
			</div>
		</div>
	);
};

export default Menu;
