import { Button, Table } from "reactstrap";
import { Trash, X }      from "react-feather";

import { FormInputGroup, HelpToolTip, InputMaskNumber } from "Components/Global";
import TdInput                                          from "Components/Appraisal/TdInput";
import TdWatcherHOC                                     from "Components/Appraisal/TdWatcherHOC";

import { getAgeF, getFResultTwoFactors, getVrNet } from "Helpers/formulas";
import apprasialOptions                            from "Resources/data/Appraisal";

const { conservationOptions }  = apprasialOptions;

const TableCommonAreas = ({
	delgations : {
		hookForm,
		removeRow,
		comunAreas,
		descOptions,
		appendNewRow,
		handleDescChange,
		mountOtherFields,
		handleUndivedLimit,
		calcPartialValCommon,
	},
}) => {

	return (
		<div className="mb-3">
			<div className="row align-items-end mb-1">
				<h5 className="col-md-8">Areas e instalaciones comunes (solo en condominios)</h5>
				<InputMaskNumber
					bsSize="sm"
					className="col-md-4"
					name="general_undivided"
					label="Indiviso general de Areas Comunes:"
					hookForm={hookForm}
					rules={{ required : false }}
				/>
			</div>
			<Table striped bordered responsive>
				<thead>
					<tr>
						<th colSpan="1" rowSpan="3" className="text-center align-middle">Clave</th>
						<th colSpan="1" rowSpan="3" className="text-center align-middle  w-200">Descripcin</th>
						<th colSpan="1" rowSpan="3" className="text-center align-middle">Unidad</th>
						<th colSpan="1" rowSpan="3" className="text-center align-middle">Cant.</th>
						<th colSpan="1" rowSpan="3" className="text-center align-middle">VR nuevo</th>
						<th colSpan="6" rowSpan="1" className="text-center align-middle">Factores</th>
						<th colSpan="1" rowSpan="3" className="text-center align-middle">
							<span>F. Re. </span>
							<HelpToolTip target="help-common-area-age-f-re">
								<p>Factor Resultante = (Factor Edad) x (Factor Conservacion) </p>
							</HelpToolTip>
						</th>
						<th colSpan="1" rowSpan="3" className="text-center align-middle">
							<span>V.R.Neto </span>
							<HelpToolTip target="help-common-area-age-vr-net">
								<p>V.R. Neto = (V.R. Nuevo) x (Factor Resultante)</p>
							</HelpToolTip>
						</th>
						<th colSpan="1" rowSpan="3" className="text-center align-middle">% Indiviso</th>
						<th colSpan="1" rowSpan="3" className="text-center align-middle">
							<span>Valor Parcial Areas Comunes </span>
							<HelpToolTip target="help-common-area-age-vr-partial">
								<p>V.R. Parcial = (Cantidad) x (V.R. Neto) x (Indiviso)</p>
							</HelpToolTip>
						</th>
						<th colSpan="1" rowSpan="3" className="text-center align-middle"><Trash size={14} /></th>
					</tr>
					<tr>
						<th colSpan="3" rowSpan="1" scope="col" className="text-center">Demrito por Edad</th>
						<th colSpan="3" rowSpan="2" scope="col" className="text-center align-middle  w-200">Conservacin</th>
					</tr>
					<tr>
						<th colSpan="1" rowSpan="1" className="text-center" scope="col">Edad</th>
						<th colSpan="1" rowSpan="1" className="text-center" scope="col">VUT</th>
						<th colSpan="1" rowSpan="1" className="text-center" scope="col">
							<span>Factor Edad</span>
							<HelpToolTip target="help-common-area-age-factor">
								<p>Demerito = (0.1) VUT + (0.9) (VUT - Edad)</p>
								<p>No puede ser menor de .60</p>
							</HelpToolTip>
						</th>
					</tr>
				</thead>
				<tbody>
					{
						comunAreas.map( (comunArea, index) => (
							<tr key={comunArea.id}>
								<TdInput hookForm={hookForm} name={`comun_areas.${index}.key`}  type="text" />
								<TdWatcherHOC
									hookForm={hookForm}
									options={descOptions}
									name={`comun_areas.${index}.desc`}
									callback={handleDescChange(index)}
								>
									{
										mountOtherFields?.[index] &&
										<FormInputGroup
											bsSize="sm"
											className="w-100 mt-1"
											name={`comun_areas.${index}.desc_other`}
											inputGroupClassName="mb-0"
											register={hookForm.register}
											errors={hookForm.formState.errors}
											options={{
												required         : true,
												shouldUnregister : true,
											}}
										/>
									}
								</TdWatcherHOC>
								<TdInput hookForm={hookForm} name={`comun_areas.${index}.unit`} />
								<TdInput hookForm={hookForm} name={`comun_areas.${index}.amount`} />
								<TdInput hookForm={hookForm} name={`comun_areas.${index}.vr`} />
								<TdInput hookForm={hookForm} name={`comun_areas.${index}.age_demerit`} />
								<TdInput hookForm={hookForm} name={`comun_areas.${index}.vut`} />
								<TdWatcherHOC
									name={`comun_areas.${index}.age_factor`}
									deps={[
										`comun_areas.${index}.vut`,
										`comun_areas.${index}.age_demerit`,
									]}
									formula={getAgeF}
								/>
								<TdInput hookForm={hookForm} name={`comun_areas.${index}.conservation`}  tdProps={{ colSpan : "3" }} options={conservationOptions} />
								<TdWatcherHOC
									name={`comun_areas.${index}.f_re`}
									deps={[
										`comun_areas.${index}.age_factor`,
										`comun_areas.${index}.conservation`,
									]}
									formula={getFResultTwoFactors}
								/>
								<TdWatcherHOC
									name={`comun_areas.${index}.net_ver`}
									deps={[
										`comun_areas.${index}.vr`,
										`comun_areas.${index}.f_re`,
									]}
									formula={getVrNet}
								/>
								<TdWatcherHOC
									disabled={false}
									name={`comun_areas.${index}.undivided`}
									callback={handleUndivedLimit}
								/>
								<TdWatcherHOC
									isArrayArgs={true}
									name={`comun_areas.${index}.partial_value`}
									deps={[
										`comun_areas.${index}.amount`,
										`comun_areas.${index}.net_ver`,
										`comun_areas.${index}.undivided`,
									]}
									callback={calcPartialValCommon}
								/>
								<td className="text-center align-middle">
									<Button.Ripple
										color="flat-danger"
										onClick={removeRow(index)}
										className="btn-icon rounded-circle"
									>
										<X size={14} className="color-danger" />
									</Button.Ripple>
								</td>
							</tr>
						))
					}
					<tr>
						<td colSpan="14" align="end">Valor Total de Areas e Instalaciones Comunes:</td>
						<TdInput hookForm={hookForm} name="total_val_commun_area" disabled />
						<td colSpan="1" align="end"></td>
					</tr>
				</tbody>
			</Table>
			<Button
				size="sm"
				type="button"
				color="primary"
				className="mt-1"
				onClick={appendNewRow}
			>
				Agregar Area
			</Button>
		</div>
	);
};

export default TableCommonAreas;
