import ReturnStatus               from "../ReturnStatusModal";
import ConfirmationModal          from "../ConfirmationModal";
import ApprasialMenuModal         from "../ApprasialMenuModal";
import CaptureRequestModal        from "../CaptureRequestModal";
import AddPreferencesModal        from "../AddPreferencesModal";
import SelectPeritControllerModal from "../SelectPeritControllerModal";
import EquivalenceTable           from "../EquivalenceTable";

const modalList = {
	confirmationModal : {
		Component : ConfirmationModal,
	},
	addPreferencesModal : {
		Component : AddPreferencesModal,
	},
	returnStatus : {
		Component : ReturnStatus,
	},
	captureRequestModal : {
		Component : CaptureRequestModal,
	},
	selectPeritControllerModal : {
		Component : SelectPeritControllerModal,
	},
	apprasialMenuModal : {
		size      : "lg",
		Component : ApprasialMenuModal,
	},
	equivalenceTable : {
		size      : "xl",
		Component : EquivalenceTable,
	},
};

export default modalList;
