import { useState }     from "react";
import { FormProvider } from "react-hook-form";
import PerfectScrollbar from "react-perfect-scrollbar";
import {
	Nav,
	Card,
	NavItem,
	TabPane,
	NavLink,
	CardBody,
	TabContent,
} from "reactstrap";
import Comments     from "../Comments";
import SectionTitle from "../SectionTitle";

//Our Import
import "./Tabs.scss";

const Tabs = ({
	tabs,
	section,
	onSubmit,
	hookForm,
	isSubmitingData,
	data,
	mapCenter,
}) => {

	const [active, setActive] = useState(0);

	const toggle = (tab) => () => {
		// hookForm.trigger();
	  if (active !== tab) {
			setActive(tab);
	  }
	};

	return (
		<>
			<Card id="Tabs" className="card-contet">
				<Nav className="nav-styles" tabs fill>
					{
						tabs.map(({title}, index) => (
							<NavItem key={index}>
								<NavLink
									active={active === index}
									onClick={toggle(index)}
								>
									{title}
								</NavLink>
							</NavItem>
						))
					}
				</Nav>
				<PerfectScrollbar>
					<CardBody className="card-body-style">
						{ section && <SectionTitle sectionTitle={section} /> }
						<FormProvider {...hookForm}>
							<TabContent activeTab={active}>
								{
									tabs.map(({ Tab, sectionTitle }, index) => (
										<TabPane tabId={index} key={index}>
											{ sectionTitle && <SectionTitle sectionTitle={sectionTitle} /> }
											<Tab onSubmit={onSubmit} isSubmitingData={isSubmitingData} data={data} mapCenter={mapCenter} />
										</TabPane>
									))
								}
							</TabContent>
						</FormProvider>
					</CardBody>
				</PerfectScrollbar>
			</Card>
			<Comments />
		</>
	);
};

export default Tabs;
