import { useEffect } from "react";

//Our Imports
import toastDispacher from "Helpers/toastDispacher";
import { genericApi } from "Store/api/genericApi";

const useHandlerMutation = ({
	config = {},
	toastName = "",
	errorCallback = () => {},
	successCallback = () => {},
	options = {
		succesParam : "",
		errorParam  : "",
	},
}) => {
	const [
		handlerMutation,
		{ isLoading, isSuccess, isError, error, ...rest },
	] = genericApi.useAddAndUpdateMutation(config);

	const handlerToaster = () => {
		if (isSuccess) {
			if (toastDispacher?.[toastName]?.success) {
				toastDispacher[toastName].success(options.succesParam);
			}
			successCallback();
		} else if (isError) {
			errorCallback(error);

			if (error.status === 409) {
				toastDispacher?.[toastName]?.error409(options.errorParam);
				return;
			}

			toastDispacher?.[toastName]?.error(options.errorParam);
		}
	};

	useEffect(() => handlerToaster(), [isSuccess, isError]);

	return [ handlerMutation, { isLoading, isSuccess, isError, error, ...rest } ];
};

export default useHandlerMutation;
