import { useParams } from "react-router-dom";

import { genericApi } from "Store/api/genericApi";

const  AppraisalHOCPageFetcher = (Wrapped, params) => (props) => {
	const { id : appraiseId } = useParams();

	const { data, isFetching } = genericApi.useGetDataQuery({
		params,
		module : `appraisals/${appraiseId}`,
		tags   : ["apprasial_details"],
	});

	if (isFetching) {
		return  <>Cargando Formulario...</>;
	}

	const { appraise } = data;

	return <Wrapped {...props} appraise={appraise} />;
};

export default AppraisalHOCPageFetcher;
