import { useDispatch } from "react-redux";
import { useHistory }  from "react-router-dom";

//Our imports
import toastDispacher               from "Helpers/toastDispacher";
import { ModalSlice, RequestSlice } from "Store/Slices";
import { useHandlerMutation }       from "Helpers/Hooks";

const useChangeRequestStatus = ({
	request,
}) => {

	const history  = useHistory();
	const dispatch = useDispatch();

	const errorCallback = (error) => {
		if (error.status !== 409) return;

		const { data  } = error;

		dispatch(RequestSlice.actions.setFormErrors(data.data.errors));

		history.push(`/app/create_request/${request._id}?type=${request.petition_type}`);
	};

	const [ patchRequest, { isLoading  } ] = useHandlerMutation({
		errorCallback,
		toastName : "statusPetition",
		tags      : ["history"],
	});

	const handleChangeStatus = (request, newStatus) => () => {
		const { status, cuv, ruv, _id } = request;

		// if (status > 1) return;

		if (status > newStatus) {
			dispatch(ModalSlice.actions.openModal({ type : "returnStatus", props : { newStatus, id : _id } }));
			return;
		}

		if (status === 0 && newStatus !== 1 && !cuv && !ruv) {
			toastDispacher.statusPetition.warning();
			return;
		}

		if (status !== 0 && newStatus === 2) {
			if (!cuv && !ruv) {
				dispatch(ModalSlice.actions.openModal({ type : "captureRequestModal", props : { newStatus, id : _id} }));
				return;
			}
		}

		const method       = "PATCH";
		const dataToSubmit = { status : newStatus };

		patchRequest({ module : `petitions/${_id}/status`,  data : dataToSubmit, method, tags : ["petitions", "petition_details"] });
	};

	return [ handleChangeStatus, { isLoading } ];
};

export default useChangeRequestStatus;
