import TdInput   from "Components/Appraisal/TdInput";
import { Table } from "reactstrap";

const zoneOptions = [
	{
		value : 0,
		label : "Superior",
	},
	{
		value : 1,
		label : "Similar",
	},
	{
		value : 2,
		label : "Inferior",
	},
];

const TableInvesExtInfo = ({
	delegations : {
		index,
		hookForm,
	},
}) => {

	return (
		<div className="mt-1">
			<Table striped bordered responsive>
				<thead>
					<tr>
						<th rowSpan="2" colSpan="1" scope="colgroup" className="text-center">Clasificacin</th>
						<th rowSpan="2" colSpan="1" scope="colgroup" className="text-center">Zona</th>
						<th rowSpan="1" colSpan="2" scope="colgroup" className="text-center">Promotor</th>
					</tr>
					<tr>
						<th className="text-center" scope="col">Nombre</th>
						<th className="text-center" scope="col">Telefono</th>
					</tr>
				</thead>
				<tbody>
					<tr>
						<TdInput hookForm={hookForm} name={`inves_housing.${index}.extra_data.clasification`} type="text" />
						<TdInput hookForm={hookForm} name={`inves_housing.${index}.extra_data.zone`} options={zoneOptions} />
						<TdInput hookForm={hookForm} name={`inves_housing.${index}.extra_data.name`} type="text" />
						<TdInput hookForm={hookForm} name={`inves_housing.${index}.extra_data.phone`} inpt_type="isNotMasked" />
					</tr>
				</tbody>
			</Table>
		</div>
	);
};

export default TableInvesExtInfo;
