import { NavLink } from "react-router-dom";
import "./MenuSection.scss";

const MenuSection = ({ id, title, options }) => {
	return (
		<div id="MenuSection" className="col-md-4">
			<h5 className="mb-2">{title}</h5>
			{
				options.map(({to, label}) => (
					<div key={to}>
						<NavLink className="link" to={`${to}/${id}`}>{label}</NavLink>
					</div>
				))
			}
		</div>
	);
};

export default MenuSection;
