import { connect } from "react-redux";
import { useForm } from "react-hook-form";

//Our Imports
import { bindAll }            from "Helpers";
import { ModalSlice }         from "Store/Slices";
import CaptureRequestModal    from "./CaptureRequestModal";
import { useHandlerMutation } from "Helpers/Hooks";

const CaptureRequestModalContainer = ({
	id,
	newStatus,
	modalSliceActions,
}) => {
	const { register, handleSubmit, formState : { errors } } = useForm();

	const handleCloseModal = () => modalSliceActions.closeModal("captureRequestModal");

	const [ patchRequest, { isLoading } ] = useHandlerMutation({
		successCallback : handleCloseModal,
		toastName       : "statusPetition",
	});

	const onSubmit = (event, ...args) => {
		event.preventDefault();

		handleSubmit(data => {
			const method       = "PATCH";
			const dataToSubmit = {
				status : newStatus,
				...data,
			};

			patchRequest({
				method,
				data   : dataToSubmit,
				module : `petitions/${id}/status`,
				tags   : ["petitions", "petition_details"],
			});
		})(...args);
	};

	return (
		<CaptureRequestModal
			delegations={{
				errors,
				onSubmit,
				register,
				isLoading,
				handleCloseModal,
			}}
		/>
	);
};

const mapStateToProps = ({ _modalSlice }) => ({
	id        : _modalSlice?.props?.captureRequestModal?.id        ?? "",
	newStatus : _modalSlice?.props?.captureRequestModal?.newStatus ?? "",
});

const mapDispatchToProps = bindAll({
	ModalSliceActions : ModalSlice.actions,
});

export default connect(mapStateToProps, mapDispatchToProps)(CaptureRequestModalContainer);
