
import { useEffect }                from "react";
import { useFormContext, useWatch } from "react-hook-form";

//Our imports
import Surfaces from "./Surfaces";

const SurfacesContainer = ({ onSubmit, isSubmitingData }) => {
	const hookForm = useFormContext();

	const { setValue } = hookForm;

	const undivided          = useWatch({ name : "surface.undivided" });
	const superficieTotaLand = useWatch({ name : "surface.superficie_total_land" });

	const superficieTotalLandSubject    = useWatch({ name : "surface.superficie_total_land_subject" });
	const superficieTotalConstruction   = useWatch({ name : "surface.superficie_total_construction" });


	const calSuperficieTotalLandSubject = () => {
		const numUndivided          = Number(undivided) ?? 0;
		const numSuperficieTotaLand = Number(superficieTotaLand) ?? 0;

		const result = (numUndivided / 100) * numSuperficieTotaLand;

		setValue("surface.superficie_total_land_subject", result.toFixed(3));
	};

	const calCUS = () => {
		const numSuperficieTotalLandSubject  = Number(superficieTotalLandSubject) ?? 0;
		const numSuperficieTotalConstruction = Number(superficieTotalConstruction) ?? 0;

		if (numSuperficieTotalLandSubject > 0) {
			const result = numSuperficieTotalConstruction / numSuperficieTotalLandSubject;

			setValue("surface.cus", result.toFixed(3));
			return;
		}
		setValue("surface.cus", 0);
	};

	const copyLandSubjectValue = () => {
		const numSuperficieTotalLandSubject  = Number(superficieTotalLandSubject) ? Number(superficieTotalLandSubject) : 0;


		setValue("surface.superficie_asentada_record", numSuperficieTotalLandSubject, { shouldDirty : true });
	};

	const copyConstructionValue = () => {
		const numsuperficieAccesory          = Number(hookForm.getValues("surface.superficie_accesory")) ?  Number(hookForm.getValues("surface.superficie_accesory")) : 0;
		const numSuperficieTotalConstruction = Number(superficieTotalConstruction) ? Number(superficieTotalConstruction) : 0;

		const result = numsuperficieAccesory + numSuperficieTotalConstruction;

		setValue("surface.superficie_asentada_record", result, { shouldDirty : true });
	};

	useEffect(() => {
		if (undivided > 100 || undivided < 0) {
			setValue("surface.undivided", 0);
		}

		calSuperficieTotalLandSubject();
	}, [superficieTotaLand, undivided]);

	useEffect(() => {
		calCUS();
	}, [superficieTotalLandSubject, superficieTotalConstruction]);

	useEffect(() => {
		setValue("surface.superficie_salable", superficieTotalConstruction);
	}, [superficieTotalConstruction]);


	return (
		<Surfaces
			delegations={{
				hookForm,
				onSubmit,
				isSubmitingData,
				copyLandSubjectValue,
				copyConstructionValue,
			}}
		/>
	);
};

export default SurfacesContainer;
