// ** React Imports
import { Fragment, useState } from "react";
import { useHistory }         from "react-router-dom";

// ** Dropdowns Imports
import UserDropdown from "./UserDropdown";

import "./style.scss";

// ** Third Party Components
// import { Sun, Moon } from "react-feather";

// ** Reactstrap Imports
import {
	NavItem,
	NavLink,
	DropdownItem,
	DropdownMenu,
	Dropdown,
	DropdownToggle,
	Collapse,
	Badge,
} from "reactstrap";

//Own components

const NavbarUser = ({modalSlice, isOpenFilters, ...props}) => {
	const history = useHistory();

	const [dropdownOpen, setDropdownOpen] = useState(false);
	const [collapse, setCollapse] = useState(false);
	const toggle = (e) => {
		e.preventDefault();

		setCollapse(!collapse);
	};

	const toggleDropdown = () => {
		setDropdownOpen(!dropdownOpen);
	};

	const btnOptions = [
		{
			label    : "Nueva Solicitud",
			value    : "",
			children : [
				{
					label : "Lnea 2",
					value : "/app/create_request/id?type=0",
				},
				{
					label : "Lnea 3",
					value : "/app/create_request/id?type=1",
				},
			],
		},
		{
			label : "Nuevo Avalo",
			value : "/app/appraise/add_appraisal",
		},
	];

	const handleOnClickFastAction = (route) => {
		if (!route) return;

		history.push(route);
	};

	return (
		<Fragment>
			<div className='bookmark-wrapper d-flex align-items-center'>
				<NavItem className='d-none d-lg-block'>
					<NavLink className='nav-link-style'>
						{/* <ThemeToggler /> */}
						<Dropdown isOpen={dropdownOpen} toggle={toggleDropdown}>
							<DropdownToggle caret color="primary">
								<span>Creacin Rpida</span>
							</DropdownToggle>
							<DropdownMenu className="custom__dropdown">
								{btnOptions.map((item) => (
									<Fragment key={item.label}>
										{item.children ? (
											<>
												<DropdownItem header>
													<div className="d-flex items-center align-center align-items-center gap-1" onClick={toggle}>
														{item.label}
														<Badge size="sm" color="primary">
															{collapse ? "-" : "+"}
														</Badge>
													</div>
												</DropdownItem>
												<Collapse isOpen={collapse}>
													{item.children.map((child) => (
														<DropdownItem
															key={child.label}
															onClick={() => handleOnClickFastAction(child.value)}
															style={{paddingLeft : "30px", width : "100%"}}
														>
															{child.label}
														</DropdownItem>
													))}
												</Collapse>
											</>
										) : (
											<DropdownItem
												tag="a"
												onClick={() => handleOnClickFastAction(item.value)}
											>
												{item.label}
											</DropdownItem>
										)}
									</Fragment>
								))}
							</DropdownMenu>
						</Dropdown>
					</NavLink>
				</NavItem>
			</div>
			<ul className='nav navbar-nav align-items-center ms-auto'>
				<UserDropdown />
			</ul>
		</Fragment>
	);
};

export default NavbarUser;
