import { useFormContext, useWatch } from "react-hook-form";

// import { useEffect } from "react";

import { Form }         from "Components/Appraisal";
import { MapFormInpts } from "Components/Global";

import {
	landTypeOptions,
	personTypeOptions,
	livingTypeOptions,
	propertyTypeOptions,
	sourceRequestedOptions,
	propertyRegimeOptions,
} from "Resources/data/GeneralInformationForm";

import { setOnlyPositiveNumber } from "Helpers";
import { useIsCDMX }             from "Helpers/Hooks";
import Georeference              from "../Georeference";

import { AddUser } from "Resources/icons";
import "./style.css";


const GeneralInformationForm = ({ onSubmit, isSubmitingData, mapCenter }) => {

	const hookForm = useFormContext();

	const handleValidateCDMX = useIsCDMX();

	const { setValue } = hookForm;

	const property_typeWatcher = useWatch({ name : "general_information.property_type"});

	const handleRenderInptsHeader = () => {
		if (property_typeWatcher?.value === "0" || property_typeWatcher?.value === 0) {
			return inptsHeader;
		}
		const inptsHeaderWithoutLandType = inptsHeader.filter(inpt => inpt.name !== "general_information.land_type");

		setValue("general_information.land_type", null);

		return inptsHeaderWithoutLandType;
	};

	const handleAddOwner = () => {
		hookForm.fieldArrayNames.append({
			general_information : {
				person_type        : null,
				paternal_last_name : null,
				maternal_last_name : null,
				name               : null,
				rfc              	 : null,
				curp             	 : null,
			},
			address : {
				street       : null,
				outer_number : null,
				inner_number : null,
				colony       : null,
				municipality : null,
				state      	 : null,
				zip_code   	 : null,
			},
		});
	};

	const inptsHeader = [
		{
			name    : "general_information.property_type",
			label   : "Tipo de Inmueble a Valuar:",
			options : propertyTypeOptions,
			rules   : { required : false },
		},
		{
			name    : "general_information.land_type",
			label   : "Tipo de Terreno:",
			options : landTypeOptions,
			rules   : { required : true },
		},
		{
			name    : "general_information.housing_type",
			label   : "Tipo de vivienda",
			options : livingTypeOptions,
			rules   : { required : false },
		},
		{
			disabled    : !handleValidateCDMX(),
			name        : "general_information.cadastral",
			label       : "Cuenta Catastral",
			placeholder : "Solo aplica para avalos de CDMX",
			options     : { required : false },
		},
	];

	const inptsHeader2 = [
		{
			name    : "general_information.number_property_tax",
			label   : "Cuenta Predial:",
			type    : "text",
			options : { required : false },
		},
		{
			name    : "general_information.number_water_tax",
			label   : "Cuenta Agua:",
			type    : "text",
			options : { required : false },
		},
	];

	const inptsOwner = [
		{
			name    : "general_information.person_type",
			label   : "Tipo de Persona:",
			options : personTypeOptions,
			rules   : { required : false },
		},
		{
			name    : "general_information.paternal_last_name",
			label   : "Apellido Paterno:",
			options : { required : false },
		},
		{
			name    : "general_information.maternal_last_name",
			label   : "Apellido Materno:",
			options : { required : false },
		},
		{
			name    : "general_information.name",
			label   : "Nombre:",
			options : { required : false },
		},
		{
			name    : "general_information.rfc",
			label   : "RFC",
			options : {
				required : false,
				pattern  : {
					value   : /^([A-Z\x26]{3,4}([0-9]{2})(0[1-9]|1[0-2])(0[1-9]|1[0-9]|2[0-9]|3[0-1]))([A-Z\d]{3})?$/i,
					message : "RFC invlido",
				},
			},
		},
		{
			name    : "general_information.curp",
			label   : "CURP",
			options : {
				required : false,
				min      : 5,
				pattern  : {
					value   : /^[A-Z]{4}[0-9]{6}[H|M]{1}[A-Z]{5}[0-9]{2}$/i,
					message : "CURP invlido",
				},
			},
		},
	];

	const inptsAddress = [
		{
			name    : "general_information.street",
			label   : "Calle:",
			options : { required : false },
		},
		{
			name    : "general_information.outer_number.alphanumeric",
			label   : "Nmero exterior:",
			options : { required : false },
		},
		{
			name    : "general_information.inner_number.alphanumeric",
			label   : "Nmero interior:",
			options : { required : false },
		},
		{
			name    : "general_information.colony",
			label   : "Colonia:",
			options : { required : false },
		},
		{
			name    : "general_information.municipality",
			label   : "Delegacin/Municipio:",
			options : { required : false },
		},
		{
			name    : "general_information.state",
			label   : "Entidad Federativa:",
			options : { required : false },
		},
		{
			name       : "general_information.zip_code",
			label      : "Cdigo Postal:",
			maxLength  : 5,
			onKeyPress : setOnlyPositiveNumber,
			options    : {
				required  : false,
				minLength : {
					value   : 5,
					message : "El cdigo postal debe de tener 5 carcteres",
				},
			 },
		},
	];

	const inptsDeed = [
		{
			label   : "Nmero de la Escritura:",
			options : { required : false },
			name    : "general_information.deed_number",
		},
		{
			label     : "Fecha de la Escritura:",
			name      : "general_information.deed_date",
			inpt_type : "date",
			rules     : { required : false },
		},
		{
			label   : "Firmada ante Notario:",
			name    : "general_information.notary_name",
			options : { required : false },
		},
		{
			label   : "Fuente Consultada para la Obtencin del Indiviso:",
			name    : "general_information.source_requested",
			options : sourceRequestedOptions,
			rules   : { required : false },
		},
		{
			label   : "Regimen de propiedad:",
			name    : "general_information.property_regime",
			options : propertyRegimeOptions,
			rules   : { required : false },
		},
	];

	const inptsDeedExtra = [
		{
			label   : "Distrito Judicial:",
			name    : "general_information.judicial_district",
			options : { required : false },
		},
		{
			label   : "Nmero Notara:",
			name    : "general_information.notary_number",
			type    : "number",
			options : { required : false },
		},
		{
			label   : "Nmero Volumen:",
			name    : "general_information.volume_number",
			type    : "number",
			options : { required : false },
		},
	];

	const inptsContract = [
		{
			label   : "Adquiriente (Nombre):",
			name    : "general_information.acquirer_name",
			options : { required : false },
		},
		{
			label   : "Enajenante (Nombre):",
			name    : "general_information.transferor_name",
			options : { required : false },
		},
		{
			label   : "Adquiriente (Materno):",
			name    : "general_information.acquirer_mother_last_name",
			options : { required : false },
		},
		{
			label   : "Enajenante (Materno):",
			name    : "general_information.transferor_mother_last_name",
			options : { required : false },
		},
		{
			label   : "Adquiriente (Paterno):",
			name    : "general_information.acquirer_last_name",
			options : { required : false },
		},
		{
			label   : "Enajenante (Paterno):",
			name    : "general_information.transferor_last_name",
			options : { required : false },
		},
		{
			label     : "Fecha Contrato:",
			name      : "general_information.contract_date",
			inpt_type : "date",
			rules     : { required : false },
		},
	];

	const inptsAlignment = [
		{
			label     : "Fecha Alineamiento:",
			inpt_type : "date",
			name      : "general_information.alignment_date",
			rules     : { required : false },
		},
		{
			label   : "Nmero Folio Alineamiento:",
			name    : "general_information.number_alignment_folio",
			options : { required : false },
		},
	];

	const inptsJudment = [
		{
			inpt_type : "date",
			name      : "general_information.date_judgment",
			label     : "Fecha Sentencia:",
			rules     : { required : false },
		},
		{
			name    : "general_information.judgment_court",
			label   : "Juzgado Sentencia:",
			options : { required : false },
		},
		{
			name    : "general_information.number_judgment_expedient",
			label   : "Nmero Expediente Sentencia:",
			options : { required : false },
		},
	];

	const inptsOther = [
		{
			inpt_type : "date",
			name      : "general_information.date_other_source",
			label     : "Fecha Otra Fuente:",
			rules     : { required : false },
		},
		{
			name    : "general_information.number_other_source_folio",
			label   : "Nmero de Folio Otra Fuente:",
			options : { required : false },
		},
	];

	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<MapFormInpts inpts={handleRenderInptsHeader()} hookForm={hookForm} />
			<div className="row">
				<MapFormInpts inpts={inptsHeader2} hookForm={hookForm} className="col-6" />
			</div>
			<hr />
			<h5>Propietario</h5>
			<div className="position-relative owner__container">
				<abbr title="Agregar otro propietario" onClick={handleAddOwner}><i className="owner__icon"><AddUser /></i></abbr>
				<MapFormInpts inpts={inptsOwner} hookForm={hookForm} />
				<h5>Direccin del Propietario</h5>
				<div className="row p-1">
					<MapFormInpts className="col-md-6" inpts={inptsAddress} hookForm={hookForm} />
				</div>
			</div>
			<MapFormInpts inpts={inptsDeed} hookForm={hookForm} title="Escrituras" />
			<hr />
			<Georeference mapCenter={mapCenter} hookForm={hookForm} />
			<MapFormInpts inpts={inptsDeedExtra} hookForm={hookForm} title="Escrituras (Informacin Adicional)" />
			<div className="row">
				<MapFormInpts inpts={inptsContract} hookForm={hookForm} title="Contrato Privado" className="col-6" />
			</div>
			<MapFormInpts inpts={inptsAlignment} hookForm={hookForm} title="Alineamiento" />
			<MapFormInpts inpts={inptsJudment} hookForm={hookForm} title="Sentencia" />
			<MapFormInpts inpts={inptsOther} hookForm={hookForm} title="Otra Fuente" />
		</Form>
	);
};

export default GeneralInformationForm;
