import useDebounce from "./useDebounce";

const useSearcher = (
	refetch,
	moduleName,
	extraQuery = {},
	labelKey = "name",
	valueKey = "_id",
) => {
	const handleInputChange = useDebounce(async (inputValue, callback) => {
		const result = await refetch({
			moduleName,
			search : inputValue,
			extraQuery,
		}).unwrap();

		const parsedData = result?.docs?.map?.(item => ({
			label : item[labelKey],
			value : item[valueKey],
		}));

		return callback(parsedData || []);
	}, 700);

	return handleInputChange;
};

export default useSearcher;
