import { getAverage }               from "Helpers/formulas";
import { useFormContext, useWatch } from "react-hook-form";

import ConclusionRateCapt from "./ConclusionRateCapt";

const ConclusionRateCaptContainer = () => {

	const hookForm     = useFormContext();
	const { setValue } = hookForm;

	const physicMethod     = useWatch({ name : "physic_rate.net_rate" });
	const bussinesMethod   = useWatch({ name : "business_rate.net_rate" });
	const rateMethod       = useWatch({ name : "result_rate" });
	const referenceAverage = useWatch({ name : "reference_average" });

	const calcReferenceAverage = (vals, name) => {
		const average = getAverage(vals);
		setValue(name, average);
	};

	return (
		<ConclusionRateCapt
			delegations={{
				hookForm,
				rateMethod,
				physicMethod,
				bussinesMethod,
				referenceAverage,
				calcReferenceAverage,
			}}
		/>
	);
};

export default ConclusionRateCaptContainer;
