import { useFormContext } from "react-hook-form";

import { MapFormInpts }          from "Components/Global";
import Form                      from "Components/Appraisal/Form";
import apprasialOptions          from "Resources/data/Appraisal";
import { setOnlyPositiveNumber } from "Helpers";

const ApplicantInfo = ({ onSubmit, isSubmitingData}) => {

	const hookForm = useFormContext();

	const { personTypeOptions } = apprasialOptions;

	const inpts = [
		{
			label   : "Tipo de Persona:",
			name    : "applicant.person_type",
			options : personTypeOptions,
			rules   : { required : false },
		},
		{
			label   : "Apellido Paterno:",
			name    : "applicant.paternal_last_name",
			options : { required : false },
		},
		{
			label   : "Apellido Materno:",
			name    : "applicant.maternal_last_name",
			options : { required : false },
		},
		{
			label   : "Nombre(s):",
			name    : "applicant.name",
			options : { required : false },
		},
		{
			label   : "RFC:",
			name    : "applicant.rfc",
			options : {
				required : false,
				pattern  : {
					value   : /^([A-Z\x26]{3,4}([0-9]{2})(0[1-9]|1[0-2])(0[1-9]|1[0-9]|2[0-9]|3[0-1]))([A-Z\d]{3})?$/i,
					message : "RFC invlido",
				},
			},
		},
		{
			label   : "CURP:",
			name    : "applicant.curp",
			options : {
				required : false,
				min      : 5,
				pattern  : {
					value   : /^[A-Z]{4}[0-9]{6}[H|M]{1}[A-Z]{5}[0-9]{2}$/i,
					message : "CURP invlido",
				},
			},
		},
		{
			label   : "Calle:",
			name    : "applicant.street",
			options : { required : false },
		},
		{
			label   : "Nmero Exterior:",
			name    : "applicant.num_ext",
			options : { required : false },
		},
		{
			label   : "Nmero Interior:",
			name    : "applicant.num_int",
			options : { required : false },
		},
		{
			label   : "Colonia:",
			name    : "applicant.colony",
			options : { required : false },
		},
		{
			label      : "Cdigo Postal:",
			name       : "applicant.zip_code",
			maxLength  : 5,
			onKeyPress : setOnlyPositiveNumber,
			options    : {
				required  : false,
				minLength : {
					value   : 5,
					message : "El cdigo postal debe de tener 5 carcteres",
				},
			 },
		},
		{
			label   : "Delegacin o Municipio:",
			name    : "applicant.municipality",
			options : { required : false },
		},
		{
			label   : "Entidad:",
			name    : "applicant.state",
			options : { required : false },
		},
	];

	return (
		<Form  onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<div className="row">
				<MapFormInpts className="col-md-6" inpts={inpts} hookForm={hookForm}  />
			</div>
		</Form>
	);
};

export default ApplicantInfo;
