import React     from "react";
import { Table } from "reactstrap";

import TdInput from "Components/Appraisal/TdInput";

const TableConstructionPrice = ({
	delegations : {
		hookForm,
		construictionPricesFields,
	},
}) => {
	return (
		<Table striped bordered responsive>
			<thead>
				<tr>
					<th className="text-center">Fuente</th>
					<th className="text-center">P. unit. ($/m<sup>2</sup>)	</th>
					<th className="text-center">Descripcin</th>
				</tr>
			</thead>
			<tbody>
				{
					construictionPricesFields.map(({ id }, index) => (
						<tr key={id}>
							<TdInput hookForm={hookForm} type="text" name={`construction_price.${index}.source`} />
							<TdInput hookForm={hookForm} name={`construction_price.${index}.unit_price`} />
							<TdInput hookForm={hookForm} name={`construction_price.${index}.desc`} type="textarea" />
						</tr>
					))
				}
				<tr>
					<td align="right">
						Promedio:
					</td>
					<TdInput disabled hookForm={hookForm} name={"average_construction_price"} />
					<td></td>
				</tr>
			</tbody>
		</Table>
	);
};

export default TableConstructionPrice;
