import { numberFormat }               from "Helpers";
import { DEFAULT_FETCH_DATA, PARAMS } from "Pages/app/Appraisal/CapitalizationApproach";
import AppraisalHOCPageFetcher        from "Components/Appraisal/AppraisalHOCPageFetcher";

const PhysicAndBusinessApproachResult = ({ appraise = DEFAULT_FETCH_DATA }) => {

	const { physic_result } = appraise.physical_approach;

	const { unit_amount_value } = appraise.business_approach;

	return (
		<>
			<div className="col-md-6 text-center">
				<h4>Resultado del Enfoque Fsico</h4>
				<h4 className="w-bolder ">{numberFormat(physic_result)}</h4>
			</div>
			<div className="col-md-6 text-center">
				<h4>Monto Unitario Aplicable (Enfoque de Mercado)</h4>
				<h4 className="w-bolder">{numberFormat(unit_amount_value)}</h4>
			</div>
		</>
	);
};
export default AppraisalHOCPageFetcher(PhysicAndBusinessApproachResult, PARAMS);
