
//Our Components
import { FormInputGroup } from "Components/Global";
import { Button }         from "reactstrap";

const LifeSpan = ({
	delegations : {
		hookForm,
		optionalYear,
		handleChange,
		handleSeteOptionalYear,
	},
}) => {
	return (
		<div className="row p-3 pt-1">
			<div className="col-md-6">
				<h5 className="text-center">Vida til Total</h5>
				<div className="row">
					<FormInputGroup
						bsSize="sm"
						type="number"
						label="En Aos:"
						name="description_general.lifespan_years"
						className="col-md-6"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						options={{ required : false }}
					/>
					<FormInputGroup
						bsSize="sm"
						type="number"
						label="En Meses:"
						disabled
						name="description_general.lifespan_months"
						className="col-md-6"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						options={{ required : false }}
					/>
				</div>
			</div>
			<div className="col-md-6">
				<h5 className="text-center">Edad</h5>
				<div className="row">
					<FormInputGroup
						bsSize="sm"
						type="number"
						label="En Aos:"
						name="description_general.age_years"
						className="col-md-6"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						options={{ required : false, onChange : handleChange }}
					/>
					<FormInputGroup
						bsSize="sm"
						type="number"
						label="En Meses:"
						disabled
						name="description_general.age_months"
						className="col-md-6"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						options={{ required : false }}
					/>
				</div>
			</div>
			<div className="mt-1 col-md-6">
				<h5 className="text-center">Vida til Remanente</h5>
				<div className="row">
					<FormInputGroup
						disabled
						type="number"
						bsSize="sm"
						label="En Aos:"
						name="description_general.remainder_lifespan_years"
						className="col-md-6"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						options={{ required : false }}
					/>
					<FormInputGroup
						disabled
						type="number"
						bsSize="sm"
						label="En Meses:"
						name="description_general.remainder_lifespan_months"
						className="col-md-6"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						options={{ required : false }}
					/>
				</div>
			</div>
			<div className="mt-1 col-md-6">
				<h5 className="text-center">Ao de terminacin o remodelacin de la obra:</h5>
				<FormInputGroup
					disabled
					type="number"
					bsSize="sm"
					label="En Aos:"
					name="description_general.finish_year"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ required : false }}
				/>
				{
					optionalYear &&
					<Button
						type="button"
						color="flat-primary"
						onClick={handleSeteOptionalYear}
					>
						Usar ao {optionalYear}
					</Button>
				}
			</div>
		</div>
	);
};

export default LifeSpan;
