import { useEffect, useState }      from "react";
import { useFormContext, useWatch } from "react-hook-form";

import { Toaster } from "Components/Global";
import Land        from "./Land";

const LandContainer = ({
	onSubmit,
	isSubmitingData,
}) => {
	const hookForm = useFormContext();

	const [percentage, setPercentage] = useState(0);

	const freeArea      = useWatch({ name : "free_area" });
	const allowedLevels = useWatch({ name : "num_levels_allowed" });

	const calPercentage = () => {
		const numFreeArea = Number(freeArea);

		if (numFreeArea > 100) {
			Toaster.warning({
				title : "Atencin",
				body  : "El porcentaje debe ser menor o igual a 100.",
			});
			return;
		}

		if (numFreeArea < 0) {
			Toaster.warning({
				title : "Atencin",
				body  : "El porcentaje debe ser menor o igual a 0.",
			});
			return;
		}

		const totalArea = 100 - numFreeArea;

		setPercentage(totalArea);
	};

	const calIntensityContruction = () => {
		const numPercentage = Number(percentage);

		const numAllowedLevels = Number(allowedLevels);

		if (!numPercentage && numPercentage !== 0) {
			return;
		}
		if (!numAllowedLevels && numAllowedLevels !== 0) {
			return;
		}

		const result = (numAllowedLevels * numPercentage) / 100;

		hookForm.setValue("construction_intensity", {label : result, value : result});
	};

	useEffect(() => {
		calPercentage();
	}, [freeArea]);

	useEffect(() => {
		calIntensityContruction();
	}, [percentage, allowedLevels]);

	return (
		<Land
			delegations={{
				hookForm,
				onSubmit,
				isSubmitingData,
			}}
		/>
	);
};

export default LandContainer;
