
import { Button, UncontrolledTooltip } from "reactstrap";
import { HelpCircle, Percent }         from "react-feather";

import { FormInputGroup } from "Components/Global";
import Form               from "Components/Appraisal/Form";

const Surfaces = ({
	delegations : {
		hookForm,
		onSubmit,
		isSubmitingData,
		copyLandSubjectValue,
		copyConstructionValue,
	},
}) => {
	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<div className="row">
				<FormInputGroup
					type="number"
					label="Superficie total del Terreno del Conjunto:"
					name="surface.superficie_total_land"
					icon={<p>m<sup>2</sup></p>}
					className="col-md-6"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ required : false }}
				/>
				<FormInputGroup
					label="Segn:"
					name="surface.superficie_total_land_endorsement"
					className="col-md-6"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ required : false }}
				/>
				<FormInputGroup
					type="number"
					label="Indiviso:"
					name="surface.undivided"
					min="0"
					icon={<Percent size={14} />}
					className="col-md-6"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ required : false }}
				/>
			</div>
			<div className="row">
				<FormInputGroup
					disabled
					type="number"
					label="Superficie total del Terreno del Sujeto:"
					name="surface.superficie_total_land_subject"
					className="col-md-6"
					icon={<p>m<sup>2</sup></p>}
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ required : false }}
				/>
			</div>
			<div className="row">
				<FormInputGroup
					type="number"
					label="Superficie total de la Construccin:"
					icon={<p>m<sup>2</sup></p>}
					name="surface.superficie_total_construction"
					className="col-md-6"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ required : false }}
				/>
				<FormInputGroup
					label="Segn:"
					name="surface.superficie_total_construction_endorsement"
					className="col-md-6"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ required : false }}
				/>
			</div>
			<div className="row">
				<div className="d-flex col-md-6">
					<FormInputGroup
						disabled
						bsSize="sm"
						type="number"
						label="CUS:"
						className="w-100"
						name="surface.cus"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						options={{ required : false }}
					/>
					<HelpCircle id="help-cus" size={14} />
					<UncontrolledTooltip className="custom-style" placement="bottom" target="help-cus">
						<p>CUS = (Superficie Construida) / (Superficie de Terreno)</p>
					</UncontrolledTooltip>
				</div>
			</div>
			<div className="row">
				<FormInputGroup
					label="Superficie Accesoria (volados, marquesinas, etc):"
					icon={<p>m<sup>2</sup></p>}
					name="surface.superficie_accesory"
					className="col-md-6"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ required : false }}
				/>
			</div>
			<div className="row">
				<FormInputGroup
					label="Superficie Vendible:"
					icon={<p>m<sup>2</sup></p>}
					name="surface.superficie_salable"
					className="col-md-6"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					options={{ required : false }}
				/>
			</div>
			<div className="row">
				<div className="col-md-6">
					<FormInputGroup
						type="number"
						label="Superficie Asentada en Escritura:"
						name="surface.superficie_asentada_record"
						icon={<p>m<sup>2</sup></p>}
						register={hookForm.register}
						errors={hookForm.formState.errors}
						options={{ required : false }}
					/>
					<Button
						type="button"
						color="flat-primary"
						onClick={copyLandSubjectValue}
					>
						Usar las medidas del terreno
					</Button>
					<Button
						type="button"
						color="flat-primary"
						onClick={copyConstructionValue}
					>
						Usar las medidas de la construccin
					</Button>
				</div>
			</div>
		</Form>
	);
};

export default Surfaces;
