import { useFormContext } from "react-hook-form";

import { MapFormInpts } from "Components/Global";

const HabitationalSet = () => {
	const hookForm = useFormContext();

	const inpts = [
		{
			text      : "Botones de elevador con sealizacin en braile:",
			name      : "brille_elevator",
			inpt_type : "check-box-group",
		},
		{
			text      : "Cajones de estacionamiento adicionales para personas con discapacidad:",
			name      : "parking_disabled",
			inpt_type : "check-box-group",
		},
		{
			text      : "No. de departamentos de inicio a trmino, en braile, que se ubican por cada piso de un edificio:",
			name      : "number_departments_braille",
			inpt_type : "check-box-group",
		},
		{
			text      : "No. De piso en braile en el inicio y fin del barandal:",
			name      : "railing_braille",
			inpt_type : "check-box-group",
		},
		{
			text      : "Sealizacin en braile de calles interiores:",
			name      : "braille_signaling_streets",
			inpt_type : "check-box-group",
		},
		{
			text      : "Sealizacin en braile de: No. o nombre de edificio:",
			name      : "braille_signaling_building",
			inpt_type : "check-box-group",
		},
	];

	return (
		<div className="row mt-1">
			<hr />
			<h4>Elementos Especiales del Conjunto Habitacional</h4>
			<MapFormInpts className="col-md-6" inpts={inpts} hookForm={hookForm} />
		</div>
	);
};

export default HabitationalSet;
