
//Our Imports
import pdfImg      from "Resources/images/pdf-img.png";
import wordImg     from "Resources/images/word-img.png";
import iconImg     from "Resources/images/icon-img.png";
import excelImg    from "Resources/images/excel-img.png";
import ListOfFiles from "./ListOfFiles.jsx";

const ListOfFilesContainer = ({ files, names }) => {

	const handleTransleteFileName = (fileName) => names?.[fileName] ?? fileName;

	const handleGetType = (key) => {
		if (typeof(key) !== "string") return;

		if (key?.includes(".docx")) {
			return wordImg;
		} else if (key?.includes(".pdf")) {
			return pdfImg;
		} else if (key?.includes(".xlsx")) {
			return excelImg;
		} else {
			return iconImg;
		}
	};

	return (
		<ListOfFiles
			delegations={{
				files,
				handleGetType,
				handleTransleteFileName,
			}}
		/>
	);
};

export default ListOfFilesContainer;
