const COMMON_AREA_FIELD = {
	key           : "",
	desc          : "",
	unit          : "",
	amount        : "",
	vr            : "",
	age           : "",
	vut           : "",
	age_factor    : "",
	conservation  : undefined,
	f_re          : "",
	net_ver       : "",
	undivided     : "",
	partial_value : "",
};

const TableCommonAreasContainer = () => {

	const hookForm     = useFormContext();
	const { setValue } = hookForm;

	const { fields : comunAreas, append, remove } = useFieldArray({
		control : hookForm.control,
		name    : "comun_areas",
	});

	const [mountOtherFields, setMountOtherFields] = useState({});


	const appendNewRow = () => append(COMMON_AREA_FIELD);

	const removeRow = (index) => () => remove(index);

	const calcPartialValCommon = (vals, name) => {
		const [ amount, net_vr, undivided ] = vals;

		const porcentage = undivided > 0 ? undivided / 100 : 0;

		const result = productSetOfValues([amount, net_vr, porcentage]);
		setValue(name, result);
	};

	const handleDescChange = (indexRow) => (val) => {
		if (typeof val?.label !== "string" && typeof val?.value !== "string") {
			setValue(`comun_areas.${indexRow}.key`, "");
			setMountOtherFields(prev => ({ ...prev,  [indexRow] : false }));
			return;
		}

		const [ indexOptions, nestedIndex ] = val.value.split("-") ?? [0, 0];

		const length = descOptions[indexOptions].options.length - 1;

		const status = length === Number(nestedIndex) ? true : false;

		setMountOtherFields(prev => ({ ...prev,  [indexRow] : status }));

		const key = val.label.split("-")?.[0] ?? "";

		setValue(`comun_areas.${indexRow}.key`, key);
	};

	const handleUndivedLimit = (val, name) => {
		if (val > 100) {
			setValue(name, 100);
			Toaster.warning({
				title : "Advertencia!",
				body  : "El valor del indiviso no puede ser mayor a 100.",
			});
		}

		if (val < 0) {
			setValue(name, 0);
			Toaster.warning({
				title : "Advertencia!",
				body  : "El valor del indiviso no puede ser menor a 0.",
			});
		}
	};

	useColumnSum({
		length   : comunAreas.length,
		arrayKey : "comun_areas",
		fieldKey : "partial_value",
		name     : "total_val_commun_area",
	});

	return (
		<TableCommonAreas
			delgations={{
				hookForm,
				removeRow,
				comunAreas,
				descOptions,
				appendNewRow,
				handleDescChange,
				mountOtherFields,
				handleUndivedLimit,
				calcPartialValCommon,
			}}
		/>
	);
};

export default TableCommonAreasContainer;
