import React, { useEffect, useState } from "react";
import { useFormContext, useWatch }   from "react-hook-form";

import DescMarketInfo from "./DescMarketInfo";

const deps = [
	"type_housing",
	"bedrooms",
	"bathrooms",
	"dining_rooms",
	"kitchens",
	"garden",
	"service_rooms",
	"service_bathrooms",
	"covered_parking",
	"parking",
	"common_areas",
];

const DescMarketInfoContainer = ({ index }) => {

	const hookForm = useFormContext();

	const watcher = useWatch({ control : hookForm.control, name : deps.map(dep =>  `comparable_property.${index}.desc_market.${dep}`) });

	const [isOpen, setIsOpen] = useState({});

	const toggleOpen = (id) => () => setIsOpen(prev => ({ ...prev, [id] : !prev[id] }));

	const getSingularOrPluralVal = (index, amount) => {
		switch (index) {
			case 1:
				return amount !== "1" ? " recamaras": " recamara";
			case 2:
				return amount !== "1" ? " baos": " bao";
			case 3:
				return amount !== "1" ? " estancias": " estancia";
			case 4:
				return amount !== "1" ? " comedores": " comedore";
			case 5:
				return amount !== "1" ? " jardines": " jardin";
			case 6:
				return amount !== "1" ? " cuartos de servicio": " cuarto de servicio";
			case 7:
				return amount !== "1" ?  " baos de servicio": " bao de servicio";
			case 8:
				return amount !== "1" ?  " estacionamientos cubiertos": " estacionamiento cubierto";
			case 9:
				return amount !== "1" ?  " estacionamientos descubiertos": " estacionamiento descubierto";
		}
	};


	const handlePutText = (current, validValues, isFirstLabel) => {
		if ( !current ) {
			return "";
		}

		let text = current;

		if ( validValues.length === 2 ) {
			text += " y ";

		} else if (validValues.length > 2) {
			text += isFirstLabel ? " con " : ", ";

		} else {
			text += ".";
		}
		validValues.pop();

		return text;
	};

	const buildDescInpt = () => {
		const [
			typeHousing,
			bedrooms,
			bathrooms,
			diningRooms,
			kitchens,
			garden,
			serviceRooms,
			serviceBathrooms,
			coveredParking,
			parking,
			commonAreas,
		] = watcher;

		const validValues = watcher.filter(value => (value !== "" && value !== null && value !== undefined));

		if (commonAreas?.value === 1) {
			validValues.pop();
		}

		const value = `${handlePutText(typeHousing && typeHousing.label, validValues, true)}`

	    + `${handlePutText(bedrooms && bedrooms + getSingularOrPluralVal(1, bedrooms), validValues)}`

		+ `${handlePutText(bathrooms && bathrooms + getSingularOrPluralVal(2, bathrooms), validValues)}`

		+ `${handlePutText(diningRooms && diningRooms + getSingularOrPluralVal(3, diningRooms), validValues)}`

		+ `${handlePutText(kitchens && kitchens + getSingularOrPluralVal(4, kitchens), validValues)}`

		+ `${handlePutText(garden && garden + getSingularOrPluralVal(5, garden), validValues)}`

		+ `${handlePutText(serviceRooms && serviceRooms + getSingularOrPluralVal(6, serviceRooms), validValues)}`

		+ `${handlePutText(serviceBathrooms && serviceBathrooms + getSingularOrPluralVal(7, serviceBathrooms), validValues)}`

		+ `${handlePutText(coveredParking && coveredParking + getSingularOrPluralVal(8, coveredParking), validValues)}`

		+ `${handlePutText(parking && parking + getSingularOrPluralVal(9, parking), validValues, true)}`

		+ `${handlePutText(commonAreas?.value === 0 && "reas comunes", validValues)}`;

		hookForm.setValue(`comparable_property.${index}.desc_market.desc`, value);
	};

	useEffect(() => buildDescInpt(), [watcher]);

	return (
		<DescMarketInfo
			delegations={{
				index,
				isOpen,
				hookForm,
				toggleOpen,
			}}
		/>
	);
};

export default DescMarketInfoContainer;
