import React       from "react";
import { connect } from "react-redux";
import {
	Modal,
	Button,
	ModalBody,
	ModalHeader,
	ModalFooter,
} from "reactstrap";

import { RHFUploadFile } from "Components/Global/DropZoneRHF";
import { FormProvider }  from "react-hook-form";

const RHF_RULES = {  required : false };

const UploadFileModal = ({
	isOpen,
	delegations : {
		hookForm,
		onSubmit,
		isLoading,
		toggleModal,
	},
}) => {
	return (
		<Modal
			isOpen={isOpen}
			toggle={toggleModal}
			className="modal-dialog-centered"
		>
			<FormProvider {...hookForm}>
				<form onSubmit={onSubmit}>
					<ModalHeader toggle={toggleModal}></ModalHeader>
					<ModalBody>
						<RHFUploadFile
							name="land_boundarie"
							label="Escritura"
							rules={RHF_RULES}
						/>
						<RHFUploadFile
							name="private_boundarie"
							label="Plano"
							rules={RHF_RULES}
						/>
					</ModalBody>
					<ModalFooter>
						<Button type="submit" color='primary' outline disabled={isLoading}>
							Guardar
						</Button>
					</ModalFooter>
				</form>
			</FormProvider>
		</Modal>
	);
};
const mapStateToProps = ({ _modalSlice }) => ({ isOpen : _modalSlice?.uploadFileModal ?? false });

export default connect(mapStateToProps)(UploadFileModal);
