import { useEffect, useState }      from "react";
import { useFormContext, useWatch } from "react-hook-form";

import { toastDispacher } from "Helpers";
import zip_codes_mx       from "Resources/data/zip_codes_mx/zip_codes_mx.json";
import TableUbication     from "./TableUbication";

const TableUbicationContainer = ({ index }) => {

	const [settlementOptions, setSettlementOptions] = useState([{ label : "No hay colonias disponibles", isDisabled : true  }]);

	const { setValue, clearErrors, getValues } = useFormContext();

	const watcherZipCode = useWatch({ name : `unit_value_land.${index}.ubication.zipe_code` });
	const watcherColony = useWatch({ name : `unit_value_land.${index}.ubication.colony` });

	const setZipCodeValues = () => {
		const { c_mnpio, d_ciudad, ...localities} = zip_codes_mx[watcherZipCode];

		delete localities["d_estado"];
		delete localities["c_mnpio"];
		delete localities["c_estado"];
		const options = Object.entries(localities).map(([key, values]) => ({  label : key, value : values.id_asenta_cpcons, ...values }) );

		setSettlementOptions(options);

		setValue(`unit_value_land.${index}.ubication.delegation_key`, c_mnpio);
		setValue(`unit_value_land.${index}.ubication.delegation`, d_ciudad);
		clearErrors(`unit_value_land.${index}.ubication.delegation`);
	};

	const setColonyKey = () => {
		setValue(`unit_value_land.${index}.ubication.colony_ke`, watcherColony.id_asenta_cpcons);
	};

	useEffect(() => {
		if (watcherZipCode?.length !== 5) return;

		const zipCode = zip_codes_mx[watcherZipCode];

		if (!zipCode) {
			toastDispacher.zipCode.warning(watcherZipCode);

			setSettlementOptions([]);

			setValue(`unit_value_land.${index}.ubication.colony_ke`, "");
			setValue(`unit_value_land.${index}.ubication.colony`, "");
			setValue(`unit_value_land.${index}.ubication.delegation`, "");
			setValue(`unit_value_land.${index}.ubication.delegation_key`, "");
			return;
		}
		setZipCodeValues();
	}, [watcherZipCode]);

	useEffect(() => {
		if (!watcherColony) return;

		const currentValue = getValues(`unit_value_land.${index}.ubication.colony_ke`);

		if (watcherColony.id_asenta_cpcons !== currentValue) {
			setColonyKey();
		}
	}, [watcherColony]);

	return (
		<TableUbication
			delegations={{
				index,
				settlementOptions,
			}}
		/>
	);
};

export default TableUbicationContainer;
