import React            from "react";
import { FormProvider } from "react-hook-form";

const FormRHF = ({ hookFormMethods, onSubmit = () => {}, children }) => {
	return (
		<FormProvider { ...hookFormMethods }>
			<form onSubmit={onSubmit}>
				{children}
			</form>
		</FormProvider>
	);
};

export default FormRHF;
