import { Fragment } from "react";
import { Alert }    from "reactstrap";
import { useWatch } from "react-hook-form";

const FactorQualityWarning = () => {

	const qualityFactors = useWatch({ name : [...Array(6).keys()].map((index) => `homologation_factors.${index}.quality` ) });

	return (
		<>
			{
				qualityFactors.map( (val, index) => {
					return (
						<Fragment key={"alert-quality-homologation" + index}>
							{
								(val < .8 || val > 1.2) &&
								<Alert  className="mt-1" color='primary'>
									<div className='alert-body'>
										<span className='fw-bold'>El inmueble {index + 1} no es homologable. </span>
										<span>El Factor calidad debe estar en el rango 0.8 - 1.2</span>
									</div>
								</Alert>
							}
						</Fragment>
					);
				})
			}
		</>
	);
};

export default FactorQualityWarning;
