import { connect } from "react-redux";
import { useForm } from "react-hook-form";

//Our Imports
import { bindAll }                from "Helpers";
import { ModalSlice }             from "Store/Slices";
import { useHandlerMutation }     from "Helpers/Hooks";
import SelectPeritControllerModal from "./SelectPeritControllerModal";

const SelectPeritControllerModalContainer = ({
	id,
	modalSliceActions,
}) => {
	const { handleSubmit, ...hookForm } = useForm();

	const [ editPerit, { isLoading } ] = useHandlerMutation({
		toastName : "peritEdited",
	});

	const handleCloseModal = () => modalSliceActions.closeModal("selectPeritControllerModal");

	const onSubmit = handleSubmit(async (data) => {
		await editPerit({
			module : `appraisals/perit/${id}`,
			method : "PUT",
			data   : { id_perit : data.id_perito.value },
			tags   : ["appraisals"],
		}).unwrap();

		handleCloseModal();
	});

	return (
		<SelectPeritControllerModal
			delegations={{
				hookForm,
				onSubmit,
				isLoading,
				handleCloseModal,
			}}
		/>
	);
};
const mapStateToProps = ({ _modalSlice }) => ({
	id : _modalSlice?.props?.selectPeritControllerModal?.id ?? "",
});

const mapDispatchToProps = bindAll({
	ModalSliceActions : ModalSlice.actions,
});

export default connect(mapStateToProps, mapDispatchToProps)(SelectPeritControllerModalContainer);
