
//Our Componets
import useSearcher          from "Helpers/Hooks/useSearcher";
import { searcherApi }      from "Store/api/searcher";
import FormAsyncSelectGroup from "./FormAsyncSelectGroup.jsx";

const FormAsyncSelectGroupContainer = ({
	module,
	query,
	 ...rest
}) => {

	const [searchFn] = searcherApi.useLazySearchQuery();

	const handleSearch = useSearcher(searchFn, module, query);

	return (
		<FormAsyncSelectGroup
			{...rest}
			handleSearch={handleSearch}
		 />
	);
};

export default FormAsyncSelectGroupContainer;
