import { Button } from "reactstrap";
import classnames from "classnames";
import { X }      from "react-feather";

//Oir Imporations
import "./DropZoneImg.scss";

const DropZoneImg = ({
	delegations : {
		imageUrl,
		setImage,
		callback,
		defaultImage,
		defaultImageW,
		defaultImageH,
		getRootProps,
		customErrors,
		getInputProps,
		backgroundSize,
		classNameContent,
		classNameDropZone,
	},
}) => {
	return (
		<div id="DropZoneImg">
			<div
				className={classnames(classNameDropZone, {
					invalid : customErrors?.image,
				})}
				style={
					{
						backgroundSize,
						backgroundPosition : "center",
						backgroundRepeat   : "no-repeat",
						backgroundImage    : `url(${imageUrl})`,
					}
				}
				{...getRootProps()}
			>
				<input {...getInputProps()} />
				<Button.Ripple
					color="danger"
					type="button"
					className={classnames("removeImage-button", {
						show : imageUrl,
					})}
					onClick={e => {
						e.stopPropagation();
						setImage(null);
						callback();
					}}
				>
					<X size={14} />
				</Button.Ripple>
				{
					 imageUrl ?
						(
							<img src={imageUrl} hidden />
						)
						:
						(
							<div
								className={classNameContent}
								style={{
									width              : defaultImageW,
									height             : defaultImageH,
									backgroundPosition : "center",
									backgroundSize     : "contain",
									backgroundRepeat   : "no-repeat",
									backgroundImage    : `url(${defaultImage})`,
								}}
							>
								{
									!defaultImage ?
										(
											<>
												Arrastra la imagen<br />
												<span className="files">(.png)</span>
											</>
										) :
										(
											<div>&nbsp;</div>
										)
								}
							</div>
						)
				}
			</div>
		</div>
	);
};
export default DropZoneImg;
