
import { useEffect }                from "react";
import { useFormContext, useWatch } from "react-hook-form";
import apprasialOptions             from "Resources/data/Appraisal";
import urbanEquipmentOptions        from "Resources/data/UrbanEquipment";
import urbanInfrastructureOptions   from "Resources/data/UrbanInfrastructure";
import UrbanInfrastructure          from "./UrbanInfrastructure";

const { materialOptions, vigilanceOptions, gasOptions, materialSidewalkOptions } = urbanEquipmentOptions;
const { waterDistributionOptions, publicLightingOptions } = urbanInfrastructureOptions;

const { exsitenceBooleanOptions } = apprasialOptions;

const UrbanInfrastructureContainer = ({ onSubmit, isLoading }) => {

	const hookForm = useFormContext();

	//The order is important, becaus we need to watch the values and we are going to access with the index
	const inptsLevel2 = useWatch({
		name : [
			"urban_infrastructure.water_distribution",
			"urban_infrastructure.electricity_connection",
			"urban_infrastructure.zone_water_drain",
			"urban_infrastructure.street_water_drain",
		],
	});

	const inptsLevel3 = useWatch({ name : [
		"urban_infrastructure.material_of_roads",
		"urban_infrastructure.material_of_sidewalks",
		"urban_infrastructure.public_lighting",
	]});

	const inptsLevel4 = useWatch({ name : ["urban_infrastructure.vigilance", "urban_infrastructure.natural_gas"]});

	const isLevel2 = () => {
		//Rules to  be level 2: the value of these inputs must be ("Existe")
		const [water_distribution, electricity_connection, zone_water_drain, street_water_drain] = inptsLevel2;

		const { 0 : WITH_WATER_SUPPLY } = waterDistributionOptions;
		const { 0 : EXIST }             = exsitenceBooleanOptions;

		if (Number(water_distribution?.value) !== WITH_WATER_SUPPLY.value) {
			return false;
		}
		if (Number(electricity_connection?.value) !== EXIST.value) {
			return false;
		}
		if (Number(zone_water_drain?.value) !== EXIST.value && Number(street_water_drain?.value) !== EXIST.value) {
			return false;
		}

		return true;
	};


	const isLevel3 = () => {
		const [ material_of_roads, material_of_sidewalks, public_lighting ] = inptsLevel3;

		const { 0 : DIRT_ROADS, 5 : NOT_EXIST } = materialOptions;
		const { 3 : NOT_EXIST_SIDEWALK }        = materialSidewalkOptions;
		const { 0 : WITHOUT_LIGTHS }            = publicLightingOptions;

		if (Number(material_of_roads?.value) === DIRT_ROADS.value || Number(material_of_roads?.value) === NOT_EXIST.value || !Number(material_of_roads?.value)) {
			return false;
		}
		if (Number(material_of_sidewalks?.value) === NOT_EXIST_SIDEWALK.value  || !Number(material_of_sidewalks?.value)) {
			return false;
		}
		if (Number(public_lighting?.value) === WITHOUT_LIGTHS.value || !Number(public_lighting?.value)) {
			return false;
		}
		return true;
	};

	const isLevel4 = () => {
		const [ vigilance, natural_gas ]  = inptsLevel4;
		const { 2 : VIGILANCE_NOT_EXIST } = vigilanceOptions;
		const { 2 : GAS_NOT_EXIST }       = gasOptions;

		if (Number(vigilance?.value) === VIGILANCE_NOT_EXIST.value || vigilance?.value === undefined || vigilance?.value === null) {
			return false;
		}

		if (Number(natural_gas?.value) === GAS_NOT_EXIST.value || natural_gas?.value === undefined || natural_gas?.value === null) {
			return false;
		}
		return true;
	};

	useEffect(() => {
		if (!isLevel2()) {
		    hookForm.setValue("urban_infrastructure.infrastructure_level", 1);
			return;
		}
		if (!isLevel3()) {
			hookForm.setValue("urban_infrastructure.infrastructure_level", 2);
			return;
		}

		if (!isLevel4()) {
			hookForm.setValue("urban_infrastructure.infrastructure_level", 3);
			return;
		}

		hookForm.setValue("urban_infrastructure.infrastructure_level", 4);
	}, [inptsLevel2, inptsLevel3, inptsLevel4]);

	return (
		<UrbanInfrastructure
			delegations={{
				onSubmit,
				isLoading,
			}}
		/>
	);
};

export default UrbanInfrastructureContainer;
