import { Table } from "reactstrap";

import { HelpToolTip } from "Components/Global";
import TdInput         from "Components/Appraisal/TdInput";
import TdWatcherHOC    from "Components/Appraisal/TdWatcherHOC";
import { getCUS }      from "Helpers/formulas";


const housingClassOptions = [
	{
		label : "Depto. en Cond.",
		value : 0,
	},
	{
		label : " Casa Habitacin ",
		value : 1,
	},
	{
		label : " Casa Hab. en Cond. ",
		value : 2,
	},
	{
		label : " Cajn de Estac. ",
		value : 3,
	},
	{
		label : " Terreno ",
		value : 4,
	},
];

const TableInvesHousing = ({
	delegations : {
		index,
		hookForm,
		calcUnitPrice,
	},
}) => {

	return (
		<Table striped bordered responsive>
			<thead>
				<tr>
					<th rowSpan="2" colSpan="1" scope="colgroup" className="text-center">Clase de Inmueble</th>
					<th rowSpan="1" colSpan="2" scope="colgroup" className="text-center">Superficies</th>
					<th rowSpan="2" colSpan="1" scope="colgroup" className="text-center">
						<span>C.U.S.</span>
						<HelpToolTip target={"help-table-inves-housing-cus" + index}>
							CUS = (Superficie de Construccin) / (Superficie de Suelo)
						</HelpToolTip>
					</th>
					<th rowSpan="2" colSpan="1" scope="colgroup" className="text-center">Edad</th>
					<th rowSpan="2" colSpan="1" scope="colgroup" className="text-center">Precio Renta Mensual</th>
					<th rowSpan="2" colSpan="1" scope="colgroup" className="text-center">
						<span>Precio Unitario</span>
						<HelpToolTip target={"help-table-inves-housing-unit-price" + index }>
							<p>Precio Unitario = Precio / Superficie de Construccin</p>
							<p className="my-1">O en caso de cajones de estacionamiento:</p>
							<p>Precio Unitario = Precio / Superficie de Suelo</p>
						</HelpToolTip>
					</th>
				</tr>
				<tr>
					<th className="text-center" scope="col">Suelo m<sup>2</sup></th>
					<th className="text-center" scope="col">Const m<sup>2</sup></th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<TdInput hookForm={hookForm} name={`inves_housing.${index}.housing.housing_class`} options={housingClassOptions} />
					<TdInput hookForm={hookForm} name={`inves_housing.${index}.housing.floor_m2`} />
					<TdInput hookForm={hookForm} name={`inves_housing.${index}.housing.const_m2`} />
					<TdWatcherHOC
						name={`inves_housing.${index}.housing.cus`}
						deps={[
							`inves_housing.${index}.housing.const_m2`,
							`inves_housing.${index}.housing.floor_m2`,
						]}
						formula={getCUS}
					/>
					<TdInput hookForm={hookForm} name={`inves_housing.${index}.housing.age`} />
					<TdInput hookForm={hookForm} name={`inves_housing.${index}.housing.rent_price`} />
					<TdWatcherHOC
						name={`inves_housing.${index}.housing.unit_price`}
						deps={[
							`inves_housing.${index}.housing.rent_price`,
							`inves_housing.${index}.housing.floor_m2`,
							`inves_housing.${index}.housing.const_m2`,
							`inves_housing.${index}.housing.housing_class`,
						]}
						callback={calcUnitPrice}
					/>
				</tr>
			</tbody>
		</Table>
	);
};

export default TableInvesHousing;
