import { useState }                      from "react";
import { useFieldArray, useFormContext } from "react-hook-form";

import { useColumnSum }       from "Helpers/Hooks";
import { productSetOfValues } from "Helpers/formulas";
import TableConstruction      from "./TableConstruction";

const CONSTRUCTION_FIELD = {
	type_construction : undefined,
	level_num         : "",
	class             : {
		surface             : "0",
		type                : "0",
		structure_walls     : "0",
		mezzanine           : "0",
		covers              : "0",
		finishes_walls      : "0",
		floors              : "0",
		facade              : "0",
		windows             : "0",
		bath_kitchen_covers : "0",
		bath_furniture      : "0",
		class_type          : "1",
	},
	area_m2         : "",
	new_vr          : "",
	age             : "",
	vut             : "",
	age_factor      : "",
	conservation    : undefined,
	other_av        : "",
	f_re            : "",
	net_vr          : "",
	partial_val     : "",
	additional_info : {
		type       : undefined,
		undivided  : "",
		level_rank : undefined,
		use        : undefined,
		class      : undefined,
	},
};

const TableConstructionContainer = () => {

	const hookForm = useFormContext();


	const { setValue } = hookForm;

	const [isOpen, setIsOpen] = useState({
		matrix     : null,
		modalIndex : null,
	});

	const { fields : constructionFields, append, remove } = useFieldArray({
		control : hookForm.control,
		name    : "construction",
	});

	const removeRow = (index) => () => remove(index);

	const appendNewRow = () => append({...CONSTRUCTION_FIELD});

	const handleOpen = (key, val) => () => setIsOpen(prev =>  prev[key] === val ? { ...prev, [key] : null } : { ...prev, [key] : val });

	const calcFRe = (vals, name) => {
		const [ age_factor, conservation, other_av ] = vals;

		const labelSplited = conservation?.label ? conservation.label.split("-") : [];

		const conservationVal = labelSplited?.[0] ?? 0;

		const result = productSetOfValues([age_factor, conservationVal, other_av]);

		setValue(name, result);
	};

	useColumnSum({
		length   : constructionFields.length,
		arrayKey : "construction",
		fieldKey : "partial_val",
		name     : "construction_total_val",
	});


	return (
		<TableConstruction
			delegations={{
				isOpen,
				calcFRe,
				hookForm,
				removeRow,
				handleOpen,
				appendNewRow,
				constructionFields,
			}}
		/>
	);
};

export default TableConstructionContainer;
