import React                from "react";
import { Button, Collapse } from "reactstrap";

import {
	FormInputGroup,
	FormSelectGroup,
} from "Components/Global";

import setOnlyPositiveNunmber from "Helpers/setOnlyPositiveNumber";


const commonAreas = [
	{
		value : 0,
		label : "S ",
	},
	{
		value : 1,
		label : "No",
	},
];

const descHousingType = [
	{
		label : "Departamento en condominio",
		value : 0,
	},
	{
		label : "Casa en condominio",
		value : 1,
	},
	{
		label : "Casa habitacin unifamiliar",
		value : 2,
	},
	{
		label : "Casa habitacin plurifamiliar",
		value : 3,
	},
	{
		label : "Inmueble de Productos",
		value : 4,
	},
];
const DescMarketInfo = ({
	delegations : {
		index,
		isOpen,
		hookForm,
		toggleOpen,
	},
}) => {

	return (
		<div className="mt-3">
			<div className="d-flex">
				<div className="w-fit-content">
					<p className="mb-1">Descripcin:</p>
					<Button
						outline
						type="button"
						color="primary"
						className="btn-sm"
						onClick={toggleOpen(index)}
					>
						{isOpen?.[index] ? "Cerrar Men" : "Abrir Men"}
					</Button>
				</div>
				<div className="w-75 mx-1">
					<FormInputGroup
						type="textarea"
						name={`comparable_property.${index}.desc_market.desc`}
						register={hookForm.register}
						errors={hookForm.formState.errors}
						options={{ required : false }}
					/>

					<Collapse isOpen={isOpen?.[index]}>
						<div className="row gy-1">
							<h5 className="col-md-3">Descripcin del inmueble:</h5>
							<FormSelectGroup
								size="sm"
								name={`comparable_property.${index}.desc_market.type_housing`}
								className="col-md-3"
								label="Tipo de Inmueble"
								hookForm={hookForm}
								errors={hookForm.formState.errors}
								options={descHousingType}
								rules={{ required : false }}
							/>

							<FormInputGroup
								min="1"
								bsSize="sm"
								type="number"
								onKeyPress={setOnlyPositiveNunmber}
								className="col-md-3"
								label="Recamaras"
								name={`comparable_property.${index}.desc_market.bedrooms`}
								register={hookForm.register}
								errors={hookForm.formState.errors}
								options={{ required : false }}
							/>
							<FormInputGroup
								min="1"
								bsSize="sm"
								type="number"
								onKeyPress={setOnlyPositiveNunmber}
								className="col-md-3"
								label="Baos"
								name={`comparable_property.${index}.desc_market.bathrooms`}
								register={hookForm.register}
								errors={hookForm.formState.errors}
								options={{ required : false }}
							/>
							<FormInputGroup
								min="1"
								bsSize="sm"
								type="number"
								onKeyPress={setOnlyPositiveNunmber}
								className="col-md-3"
								label="Estancia(s)"
								name={`comparable_property.${index}.desc_market.dining_rooms`}
								register={hookForm.register}
								errors={hookForm.formState.errors}
								options={{ required : false }}
							/>
							<FormInputGroup
								min="1"
								bsSize="sm"
								type="number"
								onKeyPress={setOnlyPositiveNunmber}
								className="col-md-3"
								label="Comedor(es)"
								name={`comparable_property.${index}.desc_market.kitchens`}
								register={hookForm.register}
								errors={hookForm.formState.errors}
								options={{ required : false }}
							/>
							<FormInputGroup
								min="1"
								bsSize="sm"
								type="number"
								onKeyPress={setOnlyPositiveNunmber}
								className="col-md-3"
								label="Jardn"
								name={`comparable_property.${index}.desc_market.garden`}
								register={hookForm.register}
								errors={hookForm.formState.errors}
								options={{ required : false }}
							/>
							<FormInputGroup
								min="1"
								bsSize="sm"
								type="number"
								onKeyPress={setOnlyPositiveNunmber}
								className="col-md-3"
								label="Cuartos de Servicio"
								name={`comparable_property.${index}.desc_market.service_rooms`}
								register={hookForm.register}
								errors={hookForm.formState.errors}
								options={{ required : false }}
							/>
							<FormInputGroup
								min="1"
								bsSize="sm"
								type="number"
								onKeyPress={setOnlyPositiveNunmber}
								className="col-md-3"
								label="Baos de Servicio"
								name={`comparable_property.${index}.desc_market.service_bathrooms`}
								register={hookForm.register}
								errors={hookForm.formState.errors}
								options={{ required : false }}
							/>
							<FormInputGroup
								min="1"
								bsSize="sm"
								type="number"
								onKeyPress={setOnlyPositiveNunmber}
								className="col-md-3"
								label="Estacionamiento(s) cubierto"
								name={`comparable_property.${index}.desc_market.covered_parking`}
								register={hookForm.register}
								errors={hookForm.formState.errors}
								options={{ required : false }}
							/>
							<FormInputGroup
								min="1"
								bsSize="sm"
								type="number"
								onKeyPress={setOnlyPositiveNunmber}
								className="col-md-3"
								label="Estacionamiento(s) descubiertos"
								name={`comparable_property.${index}.desc_market.parking`}
								register={hookForm.register}
								errors={hookForm.formState.errors}
								options={{ required : false }}
							/>
							<FormSelectGroup
								size="sm"
								name={`comparable_property.${index}.desc_market.common_areas`}
								className="col-md-3"
								label="Areas Comunes"
								hookForm={hookForm}
								errors={hookForm.formState.errors}
								options={commonAreas}
								rules={{ required : false }}
							/>
						</div>
					</Collapse>
				</div>
			</div>
		</div>
	);
};

export default DescMarketInfo;
