import { useFormContext } from "react-hook-form";
import TableInvesExtInfo  from "./TableInvesExtInfo";

const TableInvesExtInfoContainer = ({ index }) => {

	const hookForm = useFormContext();

	return (
		<TableInvesExtInfo
			delegations={{
				index,
				hookForm,
			}}
		/>
	);
};

export default TableInvesExtInfoContainer;
