import { Card, CardBody } from "reactstrap";
import { Info }           from "react-feather";
import { useFormContext } from "react-hook-form";

import { setOnlyPositiveNumber }           from "Helpers";
import { FormInputGroup, FormSelectGroup } from "Components/Global";
import { levelOptions }                    from "Resources/data/RequestForm";

const GeneraPropertylInfo = ({
	delegations : {
		states,
		municipalities,
		municipalityWatcher,
	},
}) => {
	const hookForm = useFormContext();

	const { register, formState } = hookForm;

	return (
		<Card>
			<div className="d-flex align-items-center px-1 pt-1">
				<Info size={16} />
				<p className="mx-1">INFORMACION GENERAL DEL INMUEBLE</p>
			</div>
			<hr />
			<CardBody>
				<div className="col-md-6">
					<p className="fw-bolder mb-1">Nuevo Avalo de Mercado Abierto Individual</p>
					<p>
						Avalo de inmueble para obtencin de crdito ante GE Money, FOVISSSTE, etc.
						Llene cuidadosamente todos los datos de esta pgina para dar de alta el avalo.
					</p>
				</div>
				<div className="row mt-2">
					<FormSelectGroup
						size="sm"
						label="Estado"
						name="general_information.state"
						className="col-md-6 mb-1"
						options={states}
						hookForm={hookForm}
						menuPortalTarget={document.body}
					/>
					<FormSelectGroup
						size="sm"
						label="Delegacin o Municipio"
						name="general_information.municipality"
						className="col-md-6 mb-1"
						options={municipalities}
						hookForm={hookForm}
						menuPortalTarget={document.body}
					/>
					{
						municipalityWatcher?.value !== undefined &&
						<>
							<FormInputGroup
								bsSize="sm"
								name="general_information.folio"
								className="col-md-6"
								label="Si el avaluo es para cualquier institucin, favor de capturar el Folio otorgado por el banco: (En caso de lo contrario, dejar el campo vacio)."
								options={{ required : false }}
								register={register}
								errors={formState.errors}
							/>
							<FormInputGroup
								bsSize="sm"
								name="general_information.colony"
								className="col-md-6"
								label="Colonia"
								register={register}
								errors={formState.errors}
								options={{ required : true }}
							/>
							<FormInputGroup
								bsSize="sm"
								name="general_information.street"
								className="col-md-6"
								label="Calle"
								register={register}
								errors={formState.errors}
							/>
							<FormInputGroup
								bsSize="sm"
								type="text"
								name="general_information.num_ext"
								className="col-md-3"
								label="Nmero Exterior"
								register={register}
								errors={formState.errors}
							/>
							<FormInputGroup
								bsSize="sm"
								type="text"
								name="general_information.num_int"
								className="col-md-3"
								label="Nmero Interior"
								register={register}
								errors={formState.errors}
								options={{ required : false }}
							/>
							<FormInputGroup
								bsSize="sm"
								maxLength={5}
								className="col-md-2"
								name="general_information.zip_code"
								label="Cdigo Postal"
								onKeyPress={setOnlyPositiveNumber}
								register={register}
								errors={formState.errors}
								options={{
									required : {
										value   : true,
										message : "Este campo es requerido",
									},
									minLength : {
										value   : 5,
										message : "El cdigo postal debe de contener 5 dgitos",
									},
								}}
							/>
							<FormInputGroup
								bsSize="sm"
								className="col-md-5"
								name="general_information.section"
								label="Supermanzana"
								register={register}
								errors={formState.errors}
								options={{ required : false }}
							/>
							<FormInputGroup
								bsSize="sm"
								className="col-md-5"
								name="general_information.sub_section"
								label="Manzana"
								register={register}
								errors={formState.errors}
								options={{ required : false }}
							/>
							<FormInputGroup
								bsSize="sm"
								className="col-md-2"
								type="number"
								name="general_information.lot"
								label="Lote"
								register={register}
								errors={formState.errors}
								options={{ required : false }}
							/>
							<FormInputGroup
								bsSize="sm"
								className="col-md-10"
								name="general_information.condominium"
								label="Condominio"
								register={register}
								errors={formState.errors}
								options={{ required : false }}
							/>
							<FormInputGroup
								bsSize="sm"
								className="col-md-10"
								name="general_information.building"
								label="Edificio"
								register={register}
								errors={formState.errors}
								options={{ required : false }}
							/>
							<FormSelectGroup
								size="sm"
								label="Nivel"
								className="col-md-2"
								name="general_information.level"
								hookForm={hookForm}
								options={levelOptions}
								rules={{ required : false }}
							/>
							<FormInputGroup
								bsSize="sm"
								className="col-md-6"
								name="general_information.streets.0"
								label="Entre Calle(1)"
								register={register}
								errors={formState.errors}
							/>
							<FormInputGroup
								bsSize="sm"
								className="col-md-6"
								name="general_information.streets.1"
								label="Entre Calle(2)"
								register={register}
								errors={formState.errors}
							/>
						</>
					}
				</div>
			</CardBody>
		</Card>
	);
};

export default GeneraPropertylInfo;
