import { useEffect }               from "react";
import { connect }                 from "react-redux";
import { Modal as ModalReacstrap } from "reactstrap";

// Import Own Components
import { bindAll }    from "Helpers";
import { ModalSlice } from "Store/Slices";
import { useHistory } from "react-router-dom";
import modalList      from "./ModalList";

const Modal = ({ modalSliceActions,  modalSlice }) => {

	const { props, ...allModals } = modalSlice;

	const history = useHistory();

	useEffect(() => {
		const callToUnlisten = history.listen(() => {
			modalSliceActions.closeAll();
		});

		return () => callToUnlisten();
	}, [history]);

	return (
		<>
			{ Object.entries(allModals).map(([ key, value ]) => {

				if (!modalList[key]) {
					return ;
				}

				const {
					Component,
					className = "modal-dialog-centered",
				} = modalList[key];

				return (
					<ModalReacstrap
						key={key}
						size={modalList[key]?.size ? modalList[key].size : "md"}
						isOpen={value}
						unmountOnClose={true}
						className={className}
						toggle={() => modalSliceActions.closeModal(key)}
					>
						<Component {...props} />
					</ModalReacstrap>
				);

			})}
		</>
	);
};

const mapStateToProps = ({ _modalSlice }) => ({ modalSlice : _modalSlice ?? {} });

const mapDispatchToProps = bindAll({ ModalSliceActions : ModalSlice.actions });

export default connect(mapStateToProps, mapDispatchToProps)(Modal);
