import { useFieldArray, useFormContext } from "react-hook-form";
import HomogolationAnalysis              from "./HomogolationAnalysis";

const HomogolationAnalysisContainer = () => {

	const hookForm = useFormContext();

	const { setValue } = hookForm;

	const { fields : justifications } = useFieldArray({ name : "justifications" });

	const setFactorValues = () => [...Array(6).keys()].forEach((_, index) => {
		setValue(`homologation_factors.${index}.surface`, 1);
		setValue(`homologation_factors.${index}.negotation`, 1);
		setValue(`homologation_factors.${index}.zone`, 1);
		setValue(`homologation_factors.${index}.ubication`, 1);
		setValue(`homologation_factors.${index}.front`, 1);
		setValue(`homologation_factors.${index}.shape`, 1);
		setValue(`homologation_factors.${index}.quality`, 1);
		setValue(`homologation_factors.${index}.topography`, 1);
	});

	return (
		<HomogolationAnalysis
			delegations={{
				hookForm,
				justifications,
				setFactorValues,
			}}
		/>
	);
};

export default HomogolationAnalysisContainer;
