import { Card, CardBody }           from "reactstrap";
import { useFormContext, useWatch } from "react-hook-form";

import { FormAsyncSelectGroup } from "Components/Global";

const ExpertForm = () => {

	const hookForm = useFormContext();

	const stateWatcher = useWatch({ name : "general_information.state" });

	return (
		<Card className="mt-1">
			<CardBody>
				<div className="row">
					<FormAsyncSelectGroup
						key="controlles-async"
						size="sm"
						module="users"
						name="controller"
						className="col-md-6"
						hookForm={hookForm}
						label="Seleccione al Controlador que revisar este avalo:"
						query={{  typeShf : "controller", state : [ stateWatcher?.value ] }}
						menuPortalTarget={document.body}
					/>
					<FormAsyncSelectGroup
						key="perit-async"
						size="sm"
						module="users"
						name="perit"
						className="col-md-6"
						hookForm={hookForm}
						label="Seleccione al Perito que realizar este avalo:"
						query={{  role : "perit",  state : [ stateWatcher?.value ] }}
						menuPortalTarget={document.body}
					/>
				</div>
			</CardBody>
		</Card>
	);
};

export default ExpertForm;
