import { Card, Table } from "reactstrap";

import {
	getDemerit,
	getFLandValue,
	getTotalPayments,
} from "Helpers/formulas";

import { HelpToolTip } from "Components/Global";
import TdInput         from "Components/Appraisal/TdInput";
import TdWatcherHOC    from "Components/Appraisal/TdWatcherHOC";
import TdOptionalWtch  from "Components/Appraisal/TdOptionalWtch";

const bOptions = [
	{
		value : 0,
		label : "0.00",
	},
	{
		value : 1,
		label : "0.05",
	},
	{
		value : 2,
		label : "0.10",
	},
	{
		value : 3,
		label : "0.15",
	},
];

const conservOptions = [
	{
		value : 0,
		label : "Ruinoso/0.0",
	},
	{
		value : 1,
		label : "Malo/0.8",
	},
	{
		value : 2,
		label : "Bueno/1.0",
	},
	{
		value : 3,
		label : "Muy Bueno/1.1",
	},
];

const TableStResPrc = ({
	delegations : {
		calcPn,
		calcVi,
		hookForm,
		calcUnitValue,
		handleShowTDFields,
		statisticsResProcessFields,
	},
}) => {
	return (
		<Card className="p-1">
			<Table striped bordered responsive>
				<thead>
					<tr>
						<th rowSpan="2" className="text-center align-middle">Nm.</th>
						<th colSpan="3" scope="colgroup" className="text-center"></th>
						<th colSpan="3" scope="colgroup" className="text-center">Demrito por Edad</th>
						<th colSpan="5" scope="colgroup" className="text-center"></th>
					</tr>
					<tr>
						<th className="text-center" scope="col">
							<p>Beneficio neto del promotor</p>
							<p className="text-lowercase">b</p>
						</th>
						<th className="text-center" scope="col">
							<span>(VI (1- <span className="text-lowercase">b</span>))</span>
							<HelpToolTip target="help-table-st-res-perc-vi">
								<p>VI (1 - b) = (Precio Ajustado) x ( 1 - b )</p>
							</HelpToolTip>
						</th>
						<th className="text-center" scope="col">pagos ($/m<sup>2</sup>)</th>
						<th className="text-center" scope="col">Edad</th>
						<th className="text-center" scope="col">VUT</th>
						<th className="text-center" scope="col">
							<span>Demrito</span>
							<HelpToolTip target="help-table-st-res-demerit">
								<p>Demrito = ( (0.1)VUT + (0.9)(VUT - Edad) ) / VUT</p>
							</HelpToolTip>
						</th>
						<th className="text-center" scope="col">Conserv</th>
						<th className="text-center" scope="col">
							<span>Pagos Netos($/m<sup>2</sup>) </span>
							<HelpToolTip target="help-table-st-res-pn">
								<p>Pagos Netos = (Pagos) x (Demerito por edad) x (Conservacion)</p>
							</HelpToolTip>
						</th>
						<th className="text-center" scope="col">
							<span>Pagos Totales </span>
							<HelpToolTip target="help-table-st-res-pt">
								<p>Pagos Totales = (Superficie de Construccin) x (Pagos Netos) </p>
							</HelpToolTip>
						</th>
						<th className="text-center" scope="col">
							<span>F </span>
							<HelpToolTip target="help-table-st-res-f">
								<p>F : Valor del Terreno =  VI (1 - b)  -  (Pagos Totales)</p>
								<p><b>F=[VI (1-b)]-Pt</b></p>
							</HelpToolTip>
						</th>
						<th className="text-center" scope="col">
							<span>Valor Unitario($/m<sup>2</sup>)</span>
							<HelpToolTip target="help-table-st-res-unit-val">
								<ul className="text-start">
									<li>
										<b>Para casas Habitacin y Terrenos con construccin:</b>
										<ul>
											<li>Valor Unitario = (F) / (Superficie de Suelo)</li>
										</ul>
									</li>
									<li className="mt-1">
										<b>Para Terrenos:</b>
										<ul>
											<li>Precio Unitario = Precio Unitario [de la investigacin]</li>
										</ul>
									</li>
								</ul>
							</HelpToolTip>
						</th>
					</tr>
				</thead>
				<tbody>
					{
						statisticsResProcessFields.map(({ id }, index) => (
							<tr key={id}>
								<td className="text-center">{index + 1}</td>
								<TdOptionalWtch handleShowField={handleShowTDFields} name={`unit_value_land.${index}.invest_comp.features`}>
									<TdInput
										options={bOptions}
										hookForm={hookForm}
										name={`statistics_res_process.${index}.b`}
									/>
								</TdOptionalWtch>
								<TdOptionalWtch handleShowField={handleShowTDFields} name={`unit_value_land.${index}.invest_comp.features`}>
									<TdWatcherHOC
										name={`statistics_res_process.${index}.vi`}
										deps={[
											`unit_value_land.${index}.invest_comp.adjusted_price`,
											`statistics_res_process.${index}.b`,
										]}
										callback={calcVi}
									/>
								</TdOptionalWtch>
								<TdOptionalWtch handleShowField={handleShowTDFields} name={`unit_value_land.${index}.invest_comp.features`}>
									<TdInput
										hookForm={hookForm}
										name={`statistics_res_process.${index}.pays_m`}
									/>
								</TdOptionalWtch>
								<TdOptionalWtch
									handleShowField={handleShowTDFields}
									name={`unit_value_land.${index}.invest_comp.features`}
								>
									<TdInput hookForm={hookForm} name={`statistics_res_process.${index}.age`} />
								</TdOptionalWtch>
								<TdOptionalWtch handleShowField={handleShowTDFields} name={`unit_value_land.${index}.invest_comp.features`}>
									<TdInput
										hookForm={hookForm}
										name={`statistics_res_process.${index}.vut`}
									 />
								</TdOptionalWtch>
								<TdOptionalWtch handleShowField={handleShowTDFields} name={`unit_value_land.${index}.invest_comp.features`}>
									<TdWatcherHOC
										name={`statistics_res_process.${index}.demerit`}
										deps={[
											`statistics_res_process.${index}.vut`,
											`statistics_res_process.${index}.age`,
										]}
										formula={getDemerit}
									/>
								</TdOptionalWtch>
								<TdOptionalWtch
									handleShowField={handleShowTDFields}
									name={`unit_value_land.${index}.invest_comp.features`}
								>
									<TdInput
										options={conservOptions}
										hookForm={hookForm}
										name={`statistics_res_process.${index}.conserv`}
									/>
								</TdOptionalWtch>
								<TdOptionalWtch handleShowField={handleShowTDFields} name={`unit_value_land.${index}.invest_comp.features`}>
									<TdWatcherHOC
										name={`statistics_res_process.${index}.pn`}
										deps={[
											`statistics_res_process.${index}.pays_m`,
											`statistics_res_process.${index}.demerit`,
											`statistics_res_process.${index}.conserv`,
										]}
										callback={calcPn}
									/>
								</TdOptionalWtch>
								<TdOptionalWtch handleShowField={handleShowTDFields} name={`unit_value_land.${index}.invest_comp.features`}>
									<TdWatcherHOC
										name={`statistics_res_process.${index}.pt`}
										deps={[
											`unit_value_land.${index}.invest_comp.construction`,
											`statistics_res_process.${index}.pn`,
										]}
										formula={getTotalPayments}
									/>
								</TdOptionalWtch>
								<TdOptionalWtch handleShowField={handleShowTDFields} name={`unit_value_land.${index}.invest_comp.features`}>
									<TdWatcherHOC
										name={`statistics_res_process.${index}.f`}
										deps={[
											`statistics_res_process.${index}.vi`,
											`statistics_res_process.${index}.pt`,
										]}
										formula={getFLandValue}
									/>
								</TdOptionalWtch>
								<TdWatcherHOC
									name={`statistics_res_process.${index}.unit_value`}
									deps={[
										`statistics_res_process.${index}.f`,
										`unit_value_land.${index}.invest_comp.features`,
										`unit_value_land.${index}.invest_comp.ground`,
										`unit_value_land.${index}.invest_comp.unit_price`,
									]}
									callback={calcUnitValue}
								/>
							</tr>
						))
					}
					<tr>
						<td colSpan="11" align="right">Promedio</td>
						<TdInput disabled hookForm={hookForm} name="statistics_res_process_average" />
					</tr>
				</tbody>
			</Table>
		</Card>
	);
};

export default TableStResPrc;
