import classNames      from "classnames";
import { useDropzone } from "react-dropzone";
import { UploadCloud } from "react-feather";

import { isValidArray }                            from "Helpers";
import { useSetURLFileToRemove }                   from "Helpers/Hooks";
import { handleGetName, handleGetType, RemoveBtn } from "../DropZoneRHF";
import "./styles.scss";
import { EyeBtn }                                  from "../DropZoneRHF/DropZoneRHF";


const UploadMultiFiles = ({
	name,
	files,
	label,
	accept,
	onDrop,
	onChange,
	className,
	maxFiles = 5,
	errors = false,
}) => {

	const setURLFileToRemove = useSetURLFileToRemove();

	const { getRootProps, getInputProps } = useDropzone({
		accept,
		onDrop,
		maxFiles,
	});


	const handleRemoveFile = (index) => (e) => {
		e.stopPropagation();
		const splicedFiles = files.map( file => {
			if ((file instanceof File)) {
				return file;
			}

			return { ...file, inpt_name : name };
		});

		const [ fileRemoved ] = splicedFiles.splice(index, 1);

		onChange(splicedFiles);

		// we need to store all the files that were uploaded on the server, because later we're going to use them to delete from the server on the custom hook "useSendFiles/handleRemoveFiles"
		if (fileRemoved?.location) {
			setURLFileToRemove(fileRemoved);
		}
	};

	const handleRemoveFiles = (e) => {
		e.stopPropagation();
		onChange(null);
		files.forEach(file => {
			if (file?.location) {
				setURLFileToRemove({ ...file, inpt_name : name });
			}
		});
	};

	const buildUniqueKey = (file, index) => {
		if (files?.path) {
			return `${file.path}-${index}`;
		}
		if (files?.description) {
			return `${file.description}-${index}`;
		}
		return new Date().toString() + index;
	};

	const handleOpen = (file) => (e) => {
		e.stopPropagation();
		if (file?.preview) {
			window.open(file.preview);
			return;
		}
		window.open(file.location);
	};

	return (
		<div
			id="UploadMultiFile"
			className={className}
		>
			{ label && <p className="label">{label}</p>}
			<div
				className={classNames("file-container", {
					"file-uploaded"           : isValidArray(files),
					"content-format-not-file" : !isValidArray(files),
					"invalid"                 : errors,
				})}
				{...getRootProps()}
			>
				<input  {...getInputProps()} />
				<RemoveBtn
					render={isValidArray(files)}
					onClick={handleRemoveFiles}
				/>
				{

					isValidArray(files)  ?
						<div className="content-cards">
							{
								files.map((file, index) => (
									<div key={buildUniqueKey(file, index)} className="files-wrapper p-1">
										<div className="card">
											<RemoveBtn
												render={!!file}
												onClick={handleRemoveFile(index)}
											/>
											<EyeBtn
												render={!!file}
												className="show-file"
												onClick={handleOpen(file)}
											/>
											<div className="card-body">
												<div className="img-text">
													<img src={ handleGetType( handleGetName(file) ) } width="23" height="25" />
													<p className="text-name">{ handleGetName(file) }</p>
												</div>
											</div>
										</div>
									</div>
								))
							}
						</div>
						:
						<>
							<UploadCloud className={classNames("", { "invalid" : errors })} size={16} />
							<span className="text customStylesText">
								Arrastra un mximo de {maxFiles} archivos adicionales.
							</span>
						</>
				}
			</div>
		</div>
	);
};

export default UploadMultiFiles;
