import { getFResult }               from "Helpers/formulas";
import { useCalcSurfaceFactor }     from "Helpers/Hooks";
import { useEffect }                from "react";
import { useFormContext, useWatch } from "react-hook-form";
import ApprasialEffFac              from "./ApprasialEffFac";

const ApprasialEffFacContainer = () => {

	const hookForm = useFormContext();

	const { setValue } = hookForm;

	const vals = useWatch({ name : [
		"efficiency_f.zone",
		"efficiency_f.ubication",
		"efficiency_f.front",
		"efficiency_f.shape",
		"efficiency_f.surface",
		"efficiency_f.other",
	]});

	const handleSetFactorValues = () => {
		const efficiency_f = {
			zone      : 1,
			ubication : 1,
			front     : 1,
			shape     : 1,
			other     : 1,
		};

		setValue("efficiency_f", efficiency_f);
	};
	const handleSetResult = () => {
		const result = getFResult(vals);

		setValue("efficiency_f.result", result);
	};

	useCalcSurfaceFactor({ keys : "efficiency_f.surface" });

	useEffect(() => handleSetResult(), [vals]);

	return (
		<ApprasialEffFac
			delegations={{
				hookForm,
				handleSetFactorValues,
			}}
		/>
	);
};

export default ApprasialEffFacContainer;
