import { useEffect, useState } from "react";

// Import Own Components
import HandleImage from "./HandleImage.jsx";

const HandleImageContainer = props => {
	const [erroredImage, setErroredImage] = useState(false);

	useEffect(() => {
		if (props?.image) {
			setErroredImage(false);
		}
	}, [props?.image]);

	return (
		<HandleImage
			{...props}
			delegations={{
				erroredImage,
				setErroredImage,
			}}
		/>
	);
};

export default HandleImageContainer;
