import React     from "react";
import { Table } from "reactstrap";

import {
	productSetOfValues,
	surfaceHomologation,
	simpleDivisonForFactors,
} from "Helpers/formulas";

import { FormInputGroup, HelpToolTip } from "Components/Global";
import TdInput                         from "Components/Appraisal/TdInput";
import TdWatcherHOC                    from "Components/Appraisal/TdWatcherHOC";
import TdCalcAgeHomologation           from "Components/Appraisal/TdCalcAgeHomologation/TdCalcAgeHomologation";

import ficImg               from "Resources/images/FIC.jpg";
import FactorQualityWarning from "../FactorQualityWarning/FactorQualityWarning";


const TableFactors = ({
	delegations : {
		calcFic,
		hookForm,
		homFactorsFields,
	},
}) => {
	return (
		<>
			<Table striped bordered responsive>
				<thead>
					<tr>
						<th colSpan="1" scope="colgroup" className="text-center"></th>
						<th colSpan="12" scope="colgroup" className="text-center">Factores</th>
						<th colSpan="2" scope="colgroup" className="text-center"></th>
					</tr>
					<tr>
						<th className="text-center" scope="col">Nm.</th>
						<th className="text-center" scope="col">
							<span>Fic </span>
							<HelpToolTip className="custom-style-tooltip-fit-content " target={"help-fic"}>
								<img src={ficImg}  />
							</HelpToolTip>
						</th>
						<th className="text-center" scope="col">
							<span>C.U.S. </span>
							<HelpToolTip target={"table-factors-cus"}>
								<p>Factor CUS = (Valor Unitario [Homologacin por CUS]) / (Valor Unitario [Investigacin])</p>
							</HelpToolTip>
						</th>
						<th className="text-center" scope="col">
							<span>Superficie </span>
							<HelpToolTip target={"table-factors-surface"}>
								<p>Factor Superficie = raiz 6ta de (Superficie Construccin Comparable) / (Superficie Construccin Sujeto)</p>
							</HelpToolTip>
						</th>
						<th className="text-center" scope="col">Negociacin</th>
						<th className="text-center" scope="col">Zona</th>
						<th className="text-center" scope="col">Ubicacin</th>
						<th className="text-center" scope="col">Frente</th>
						<th className="text-center" scope="col">Forma</th>
						<th className="text-center" scope="col">Calidad</th>
						<th className="text-center" scope="col">
							<span>Edad </span>
							<HelpToolTip target={"table-factors-age"}>
								<ul>
									<p>Si la edad del <b>sujeto</b> es mayor a la <b>comparable</b>:</p>
									<li>
										<p>Factor Edad: 1 - (Edad del Sujeto - Edad Comparable) %</p>
									</li>
									<p>Si la edad del <b>comparable</b> es mayor a la del <b>sujeto</b>:</p>
									<li>
										<p>Factor Edad: 1 + (Edad Comparable - Edad del Sujeto) %</p>
									</li>
								</ul>
							</HelpToolTip>
						</th>
						<th className="text-center" scope="col">Topografa</th>
						<th className="text-center" scope="col">
							<p>Otro</p>
							<FormInputGroup
								bsSize="sm"
								name="homologation_factors_other_title"
								register={hookForm.register}
								errors={hookForm.formState.errors}
								options={{ required : false }}
							/>
						</th>
						<th className="text-center" scope="col">
							<span>Factor Resultante	</span>
							<HelpToolTip target={"table-factors-resulted"}>
								<p>Factor Resultante = Multiplicatoria de los factores empleados</p>
							</HelpToolTip>
						</th>
						<th className="text-center" scope="col">
							<span>Valor Unitario($ /m<sup>2</sup>)	</span>
							<HelpToolTip target={"table-factors-unit-value"}>
								<p>Valor Unitario = (Precio Unitario) x (Factor Resultante)</p>
							</HelpToolTip>
						</th>
					</tr>
				</thead>
				<tbody>
					{
						homFactorsFields.map(({ id }, index) => (
							<tr key={id}>
								<td className="text-center">{index + 1}</td>
								<TdWatcherHOC
									name={`homologation_factors.${index}.fic`}
									deps={[
									`comparable_property.${index}.inv_calcs.offer_price`,
									`comparable_property.${index}.inv_calcs.land`,
									`comparable_property.${index}.inv_calcs.building`,
									"land_surface_subj",
									"building_surface_subj",
									"unit_value_applied_land",
									]}
									callback={calcFic}
								/>
								<TdWatcherHOC
									name={`homologation_factors.${index}.cus`}
									deps={[
									`cus.${index}.unit_val`,
									`comparable_property.${index}.inv_calcs.unit_price`,
									]}
									formula={simpleDivisonForFactors}
								/>
								<TdWatcherHOC
									name={`homologation_factors.${index}.surface`}
									deps={[
									`comparable_property.${index}.inv_calcs.building`,
									"building_surface_subj",
									]}
									formula={surfaceHomologation}
								/>
								<TdInput hookForm={hookForm} name={`homologation_factors.${index}.negotation`} />
								<TdInput hookForm={hookForm} name={`homologation_factors.${index}.zone`} />
								<TdInput hookForm={hookForm} name={`homologation_factors.${index}.ubication`} />
								<TdInput hookForm={hookForm} name={`homologation_factors.${index}.front`} />
								<TdInput hookForm={hookForm} name={`homologation_factors.${index}.shape`} />
								<TdInput hookForm={hookForm} name={`homologation_factors.${index}.quality`} />
								<TdCalcAgeHomologation
									name={`homologation_factors.${index}.age`}
									compAgeKey={`comparable_property.${index}.inv_calcs.age`}
									subjAgeKey={"homologation_factors_age_subj"}
								/>
								<TdInput hookForm={hookForm} name={`homologation_factors.${index}.topography`} />
								<TdInput hookForm={hookForm} name={`homologation_factors.${index}.other`} />
								<TdWatcherHOC
									isArrayArgs={true}
									name={`homologation_factors.${index}.result_factor`}
									deps={[
									`homologation_factors.${index}.fic`,
									`homologation_factors.${index}.cus`,
									`homologation_factors.${index}.surface`,
									`homologation_factors.${index}.negotation`,
									`homologation_factors.${index}.zone`,
									`homologation_factors.${index}.ubication`,
									`homologation_factors.${index}.front`,
									`homologation_factors.${index}.shape`,
									`homologation_factors.${index}.quality`,
									`homologation_factors.${index}.age`,
									`homologation_factors.${index}.topography`,
									`homologation_factors.${index}.other`,
									]}
									formula={productSetOfValues}
								/>
								<TdWatcherHOC
									isArrayArgs={true}
									name={`homologation_factors.${index}.unit_value`}
									deps={[
									`comparable_property.${index}.inv_calcs.unit_price`,
									`homologation_factors.${index}.result_factor`,
									]}
									formula={productSetOfValues}
								/>
							</tr>
						))
					}
					<tr>
						<td colSpan="14" align="right">
							Promedio:
						</td>
						<TdInput disabled hookForm={hookForm} name="average_homologation_factors" />
					</tr>
				</tbody>
			</Table>
			<FactorQualityWarning />
		</>
	);

};

export default TableFactors;
