import { useEffect }                from "react";
import { useFormContext, useWatch } from "react-hook-form";
import { Card }                     from "reactstrap";

import {
	HelpToolTip,
	FormInputGroup,
	InputMaskNumber,
	RadioButton,
} from "Components/Global";

import { productSetOfValues } from "Helpers/formulas";
import InptWatcherHOC         from "../../InptWatcherHOC";

const UNIT_PRICESES       = [...Array(6).keys()].map((index) => `comparable_property.${index}.inv_calcs.unit_price`);
const UNIT_VALUES_CUS     = [...Array(6).keys()].map((index) => `cus.${index}.unit_val`);
const UNIT_VALUES_FACTORS = [...Array(6).keys()].map((index) => `homologation_factors.${index}.unit_value`);

const ComparationAnalysis = ({
	delegations : {
		hookForm,
		calcComp,
		setMaxValue,
		setMinValue,
		handleSetValue,
		setCompAverage,
		approachStatusFactor,
	},
}) => {

	return (
		<Card className="p-1">
			<div className="row">
				<div className="col-md-6">
					<h5 className="subTitle-text">Resultado natural de la investigacin de inmuebles similares</h5>
					<InptWatcherHOC
						label="Mximo:"
						name="max_investigation"
						callback={setMaxValue}
						deps={UNIT_PRICESES}
						rules={{ required : false }}
					/>
					<InptWatcherHOC
						label="Mnimo:"
						name="min_investigation"
						callback={setMinValue}
						deps={UNIT_PRICESES}
						rules={{ required : false }}
					/>
					<InptWatcherHOC
						label="Promedio:"
						name="average_investigation"
						callback={handleSetValue}
						deps={["average_unit_price"]}
						rules={{ required : false }}
					/>
				</div>
				<div className="col-md-6">
					<h5 className="subTitle-text">Resultado de los anlisis de comparacin de inmuebles similares</h5>
					<div className="row justify-content-around">
						<RadioButton
							className="my-1 col-md-6"
							id={"approach_status_factor_0"}
							label="Usar Homologacin por CUS."
							value="0"
							classNameLabelGruop="d-flex align-items-center"
							register={hookForm.register}
							name="approach_status_factor"
							checked={approachStatusFactor === "0"}
							rules={{ required : false }}
						/>
						<RadioButton
							className="my-1 col-md-6"
							id={"approach_status_factor_1"}
							label="Usar Homologacin de Factores."
							value="1"
							register={hookForm.register}
							classNameLabelGruop="d-flex align-items-center"
							name="approach_status_factor"
							checked={approachStatusFactor === "1"}
							rules={{ required : false }}
						/>
					</div>
					<InptCalcHosingComp
						className="mt-1"
						label="Mximo:"
						name="max_comparation"
						formula={calcComp("max")}
						rules={{ required : false }}
					/>
					<InptCalcHosingComp
						className="mt-1"
						label="Mnimo:"
						name="min_comparation"
						formula={calcComp("min")}
						rules={{ required : false }}
					/>
					<InptWatcherHOC
						label="Promedio:"
						name="average_comparation"
						callback={setCompAverage}
						deps={["cus_average", "average_homologation_factors", "approach_status_factor"]}
						rules={{ required : false }}
					/>
				</div>
			</div>
			<div className="mt-1">
				<h5 className="subTitle-text">Factor Comercial</h5>
				<div className="row">
					<FormInputGroup
						bsSize="sm"
						className="col-md-6"
						label="Titulo:"
						name="commercial_factor_title"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						options={{ required : false }}
					/>
					<InputMaskNumber
						bsSize="sm"
						className="w-100"
						label="Valor (Usar 1 en caso de que no aplique):"
						name="commercial_factor_value"
						hookForm={hookForm}
						rules={{ required : false }}
					/>
				</div>
			</div>
			<div className="mt-1">
				<h5 className="subTitle-text">Monto Unitario Aplicable:</h5>
				<div className="row">
					<FormInputGroup
						bsSize="sm"
						className="col-md-6"
						label="Titulo:"
						name="unit_amount_title"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						options={{ required : false }}
					/>
					<div className="col-md-6 d-flex">
						<InptWatcherHOC
							isArrayArgs={true}
							label="Valor:"
							className="w-100"
							name="unit_amount_value"
							formula={productSetOfValues}
							deps={["average_comparation", "commercial_factor_value"]}
							rules={{ required : false }}
						/>

						<HelpToolTip target={"help-unit_amount_value"}>
							<p>Monto unitario Aplicable = Promedio del Analisis Utilizado x Factor Comercial</p>
						</HelpToolTip>
					</div>
				</div>
			</div>
			<div className="mt-1">
				<h5 className="subTitle-text">Superficie Vendible:</h5>
				<div className="row">
					<FormInputGroup
						bsSize="sm"
						className="col-md-6"
						label="Titulo:"
						name="salable_title"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						options={{ required : false }}
					/>
					<div className="d-flex col-md-6">
						<InputMaskNumber
							disabled
							bsSize="sm"
							className="w-100"
							label="Valor:"
							name="salable_value"
							hookForm={hookForm}
							rules={{ required : false }}
						/>
						<HelpToolTip target={"help-salable_value"}>
							<p>Superficie Vendible (Seccin 2.3)</p>
						</HelpToolTip>
					</div>
				</div>
			</div>
			<hr />
		</Card>
	);
};

const InptCalcHosingComp = ({
	name,
	formula,
	...rest
}) => {
	const hookForm = useFormContext();

	//The dep's order is important
	const unitValsCus    = useWatch({ name : UNIT_VALUES_CUS });
	const unitValsFactor = useWatch({ name : UNIT_VALUES_FACTORS });
	const approachStatus = useWatch({ name : "approach_status_factor" });


	useEffect(() => {
		approachStatus.toString() === "0" ? formula(unitValsCus, name) : formula(unitValsFactor, name);
	}, [unitValsFactor, unitValsCus, approachStatus]);

	return (
		<InputMaskNumber
			{...rest}
			disabled
			bsSize="sm"
			name={name}
			hookForm={hookForm}
		/>
	);
};

export default ComparationAnalysis;
