import { useFormContext } from "react-hook-form";

import { getUnitPrice } from "Helpers/formulas";
import TableInvestComp  from "./TableInvestComp";

const TableInvestCompContainer = ({ index }) => {

	const hookForm = useFormContext();

	const { setValue } = hookForm;

	const calcUnitPrice = (vals, name) => {
		const [ adjustedPrice, features, construction, ground ] = vals;

		const surface = features?.value >= 2 ? ground : construction;

		const result = getUnitPrice(adjustedPrice, surface);

		setValue(name, result);
	};

	const handleShowTDFields = (fieldValues) =>{
		const isHousingOrConstruction = fieldValues?.value;
		return isHousingOrConstruction < 2;
	};

	return (
		<TableInvestComp
			delegations={{
				index,
				hookForm,
				calcUnitPrice,
				handleShowTDFields,
			}}
		/>
	);
};

export default TableInvestCompContainer;
