import { setOnlyPositiveNumber } from "Helpers";
import { MapFormInpts }          from "Components/Global";
import Form                      from "Components/Appraisal/Form";
import generallInfoOptions       from "Resources/data/Generalnfo";

const GeneralInfo = ({
	delegations : {
		onSubmit,
		hookForm,
		isSubmitingData,
		getMunicipalities,
	},
}) => {

	const { levelOptions, destinatonsAppraisalOptions } = generallInfoOptions;

	const inpts = [
		{
			label   : "Destino del Avalo:",
			name    : "general_information.appraisal_destination",
			options : destinatonsAppraisalOptions,
			rules   : { required : false },
		},
		{
			label    : "Estado:",
			name     : "general_information.state",
			disabled : true,
			rules    : {
				required : {
					value   : true,
					message : "Este campo es requerido",
				},
			},
		},
		{
			label      : "Delegacin o Municipio:",
			name       : "general_information.municipality",
			isDisabled : true,
			options    : getMunicipalities(),
			rules      : {
				required : {
					value   : true,
					message : "Este campo es requerido",
				},
			},
		},
		{
			label   : "Calle:",
			name    : "general_information.street",
			options : {
				required : {
					value   : true,
					message : "Este campo es requerido",
				},
			},
		},
		{
			label   : "Nmero Exterior:",
			type    : "string",
			name    : "general_information.num_ext",
			options : {
				required : {
					value   : true,
					message : "Este campo es requerido",
				},
			},
		},
		{
			label   : "Nmero Interior:",
			type    : "string",
			name    : "general_information.num_int",
			options : { required : false },
		},
		{
			label   : "Colonia:",
			name    : "general_information.colony",
			options : {
				 required : {
					value   : true,
					message : "Este campo es requerido",
				},
			},
		},
		{
			label   : "Clave de Colonia:",
			name    : "general_information.colony_key",
			options : { required : false },
		},
		{
			label      : "Cdigo Postal:",
			maxLength  : 5,
			onKeyPress : setOnlyPositiveNumber,
			name       : "general_information.zip_code",
			options    : {
				required : {
					value   : true,
					message : "Este campo es requerido",
				},
				minLength : {
					value   : 5,
					message : "El cdigo postal debe de tener 5 carcteres",
				},
			 },
		},
		{
			label   : "Supermanzana:",
			name    : "general_information.section",
			options : { required : false },
		},
		{
			label   : "Manzana:",
			name    : "general_information.sub_section",
			options : { required : false },
		},
		{
			label   : "Lote:",
			type    : "number",
			name    : "general_information.lot",
			options : { required : false },
		},
		{
			label   : "Condominio:",
			name    : "general_information.condominium",
			options : { required : false },
		},
		{
			label   : "Edificio:",
			name    : "general_information.building",
			options : { required : false },
		},
		{
			label   : "Nivel:",
			name    : "general_information.level",
			options : levelOptions,
			rules   : { required : false },
		},
	];

	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<div className="row">
				<MapFormInpts className="col-md-6" inpts={inpts} hookForm={hookForm}  />
			</div>
		</Form>
	);
};

export default GeneralInfo;
