import { useFormContext } from "react-hook-form";

import Form                from "Components/Appraisal/Form";
import { FormSelectGroup } from "Components/Global";

const restrictionsOptions = [
	{
		value : 0,
		label : "No colinda con las zonas de riesgo",
	},
	{
		value : 1,
		label : " Colinda con las zonas de riesgo con proteccin",
	},
	{
		value : 2,
		label : " Colinda con las zonas de riesgo sin proteccin",
	},
];

const riskZoneOptions = [
	{
		value : 0,
		label : " No colinda con las zonas de riesgo",
	},
	{
		value : 1,
		label : " Colinda con las zonas de riesgo mencionadas con obras de proteccin",
	},
	{
		value : 2,
		label : " Colinda con las zonas de riesgo mencionadas sin obras de proteccin",
	},
];

const RiskZones = ({ onSubmit, isSubmitingData }) => {

	const hookForm = useFormContext();

	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<FormSelectGroup
				options={restrictionsOptions}
				hookForm={hookForm}
				name="restrictions"
				label="Restricciones:"
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				options={riskZoneOptions}
				hookForm={hookForm}
				name="affectation_zone"
				label="Zonas de Afectacin:"
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
			<FormSelectGroup
				options={riskZoneOptions}
				hookForm={hookForm}
				name="hosuing_areas"
				label="Areas Inundables:"
				menuPortalTarget={document.body}
				rules={{ required : false }}
			/>
		</Form>
	);
};

export default RiskZones;
