import { useState, useEffect } from "react";
import { Controller }          from "react-hook-form";
import CreatableSelect         from "react-select/creatable";
import {
	Label,
	FormGroup,
} from "reactstrap";

import { deepAccessObj, reactSelectStyles } from "Helpers";
import "./FormCreatable.scss";

const FormCreatable = ({
	name,
	size,
	label,
	errors,
	hookForm,
	className,
	placeholder = "Seleccione una opcin",
	rules={
		required : {
			value   : true,
			message : "Este campo no puede estar vaco.",
		},
	},
	isClearable = true,
	options = [],
	newOptionId,
	...rest
}) => {
	if (newOptionId === undefined) {
		console.log("Creable witout id, add id props on", name);
	}

	const [state, setState] = useState({
		isLoading : false,
		options   : options,
	});

	const createOption = (label) => ({
		label,
		value : newOptionId !== undefined ? newOptionId : label.toLowerCase().replace(/\W/g, ""),
	});

	const handleCreate = (onChange) => (inputValue) => {
		setState({ isLoading : true });

		setTimeout(() => {
		  const { options } = state;

		  const optionsWithOutCreableOption = options.filter(option => option.value !== newOptionId);

		  const newOption = createOption(inputValue);
		  setState({
				isLoading : false,
				options   : [...optionsWithOutCreableOption, newOption],
		  });
		  onChange(newOption);
		}, 700);
	};

	useEffect(() => setState({ ...state, options}), [options]);

	return (
		<FormGroup  id="FormCreatable" className={className}>
			{ label && (
				<Label>{label}</Label>
			)}
			{
				hookForm ?
					<Controller
						name={name}
						control={hookForm.control}
						rules={rules}
						render={({ field: { onChange, value }, formState : { errors : controllerErrors } },) => (
							<>
								<CreatableSelect
									{...rest}
									options={state.options}
									isDisabled={state.isLoading}
									isLoading={state.isLoading}
									isClearable
									defaultOptions
									value={value}
									onChange={onChange}
									placeholder={placeholder}
									className={"react-select"}
									classNamePrefix="select"
									styles={reactSelectStyles(deepAccessObj(name, controllerErrors), size)}
									menuPortalTarget={document.body}
									onCreateOption={handleCreate(onChange)}
								/>
								{ !!deepAccessObj(name, controllerErrors) && (
									<span className="text-error mb-1">{ deepAccessObj(name, controllerErrors)?.message }</span>
								)}
							</>
						)}
					/>
					:
					<>
						<CreatableSelect
							{...rest}
							isClearable
							defaultOptions
							options={state.options}
							placeholder={placeholder}
							className="react-select"
							classNamePrefix="select"
							styles={ reactSelectStyles(deepAccessObj(name, errors), size) }
						/>
						{ !!deepAccessObj(name, errors) && (
							<span className="text-error mb-1">{ deepAccessObj(name, errors)?.message }</span>
						)}
					</>
			}
		</FormGroup>
	);
};

export default FormCreatable;
