import React             from "react";
import { Button, Table } from "reactstrap";
import { Trash, X }      from "react-feather";

import {
	getAgeF,
	getVrNet,
	productSetOfValues,
	getFResultTwoFactors,
} from "Helpers/formulas";

import apprasialOptions                                 from "Resources/data/Appraisal";
import { FormInputGroup, HelpToolTip, InputMaskNumber } from "Components/Global";
import TdInput                                          from "Components/Appraisal/TdInput";
import TdWatcherHOC                                     from "Components/Appraisal/TdWatcherHOC";

const { conservationOptions }  = apprasialOptions;

const TableAddElm = ({
	delegations : {
		elements,
		hookForm,
		removeRow,
		descOptions,
		appendNewRow,
		mountOtherFields,
		handleDescChange,
	},
}) => {
	return (
		<div className="mb-3">
			<div className="row align-items-end mb-1">
				<h5 className="col-md-8">Elementos adicionales (instalaciones especiales, obras complementarias y elementos accesorios)</h5>
				<InputMaskNumber
					bsSize="sm"
					className="col-md-4"
					name="undivided"
					label="Indiviso:"
					hookForm={hookForm}
					rules={{ required : false }}
				/>
			</div>
			<Table striped bordered responsive>
				<thead>
					<tr>
						<th colSpan="1" rowSpan="3" className="text-center align-middle">Clave</th>
						<th colSpan="1" rowSpan="3" className="text-center align-middle w-200">Descripcin</th>
						<th colSpan="1" rowSpan="3" className="text-center align-middle">Unidad</th>
						<th colSpan="1" rowSpan="3" className="text-center align-middle">Cant.</th>
						<th colSpan="1" rowSpan="3" className="text-center align-middle">VR nuevo</th>
						<th colSpan="6" rowSpan="1" className="text-center align-middle">Factores</th>
						<th colSpan="1" rowSpan="3" className="text-center align-middle">
							<span>F. Re. </span>
							<HelpToolTip target="help-add-elm-age-f-re">
								<p>Factor Resultante = (Factor Edad) x (Factor Conservacion) </p>
							</HelpToolTip>
						</th>
						<th colSpan="1" rowSpan="3" className="text-center align-middle">
							<span>V.R.Neto </span>
							<HelpToolTip target="help-add-elm-age-vr-net">
								<p>V.R. Neto = (V.R. Nuevo) x (Factor Resultante)</p>
							</HelpToolTip>
						</th>
						<th colSpan="1" rowSpan="3" className="text-center align-middle">
							<span>Valor Parcial Areas Comunes </span>
							<HelpToolTip target="help-add-elm-age-vr-partial">
								<p>V.R. Parcial = (Cantidad) x (V.R. Neto)</p>
							</HelpToolTip>
						</th>
						<th colSpan="1" rowSpan="3" className="text-center align-middle"><Trash size={14} /></th>
					</tr>
					<tr>
						<th colSpan="3" rowSpan="1" scope="col" className="text-center">Demrito por Edad</th>
						<th colSpan="3" rowSpan="2" scope="col" className="text-center align-middle w-200">Conservacin</th>
					</tr>
					<tr>
						<th colSpan="1" rowSpan="1" className="text-center" scope="col">Edad</th>
						<th colSpan="1" rowSpan="1" className="text-center" scope="col">VUT</th>
						<th colSpan="1" rowSpan="1" className="text-center" scope="col">
							<span>Factor Edad</span>
							<HelpToolTip target="help-add-elm-age-factor">
								<p>Demerito = (0.1) VUT + (0.9) (VUT - Edad)</p>
								<p>No puede ser menor de .60</p>
							</HelpToolTip>
						</th>
					</tr>
				</thead>
				<tbody>
					{
						elements.map( (comunArea, index) => (
							<tr key={comunArea.id}>
								<TdInput hookForm={hookForm} name={`elements.${index}.key`} type="text" />
								<TdWatcherHOC
									hookForm={hookForm}
									options={descOptions}
									name={`elements.${index}.desc`}
									callback={handleDescChange(index)}
								>
									{
										mountOtherFields?.[index] &&
										<FormInputGroup
											bsSize="sm"
											className="w-100 mt-1"
											name={`elements.${index}.desc_other`}
											inputGroupClassName="mb-0"
											register={hookForm.register}
											errors={hookForm.formState.errors}
											options={{
												required         : true,
												shouldUnregister : true,
											}}
										/>
									}
								</TdWatcherHOC>
								<TdInput hookForm={hookForm} name={`elements.${index}.unit`} type="text" />
								<TdInput hookForm={hookForm} name={`elements.${index}.amount`} />
								<TdInput hookForm={hookForm} name={`elements.${index}.vr`} />
								<TdInput hookForm={hookForm} name={`elements.${index}.age_demerit`} />
								<TdInput hookForm={hookForm} name={`elements.${index}.vut`} />
								<TdWatcherHOC
									name={`elements.${index}.age_factor`}
									deps={[
										`elements.${index}.vut`,
										`elements.${index}.age_demerit`,
									]}
									formula={getAgeF}
								/>
								<TdInput hookForm={hookForm} name={`elements.${index}.conservation`} tdProps={{ colSpan : "3" }} options={conservationOptions} />
								<TdWatcherHOC
									name={`elements.${index}.f_re`}
									deps={[
										`elements.${index}.age_factor`,
										`elements.${index}.conservation`,
									]}
									formula={getFResultTwoFactors}
								/>
								<TdWatcherHOC
									name={`elements.${index}.net_ver`}
									deps={[
										`elements.${index}.vr`,
										`elements.${index}.f_re`,
									]}
									formula={getVrNet}
								/>
								<TdWatcherHOC
									isArrayArgs={true}
									name={`elements.${index}.partial_value`}
									deps={[
										`elements.${index}.amount`,
										`elements.${index}.net_ver`,
									]}
									formula={productSetOfValues}
								/>
								<td className="text-center align-middle">
									<Button.Ripple
										color="flat-danger"
										onClick={removeRow(index)}
										className="btn-icon rounded-circle"
									>
										<X size={14} className="color-danger" />
									</Button.Ripple>
								</td>
							</tr>
						))
					}
					<tr>
						<td colSpan="13" align="end">Valor Total de Elementos Adicionales:</td>
						<TdInput hookForm={hookForm} name="total_val_element" disabled />
						<td></td>
					</tr>
				</tbody>
			</Table>
			<Button
				size="sm"
				type="button"
				color="primary"
				className="mt-1"
				onClick={appendNewRow}
			>
				Agregar Elemento
			</Button>
		</div>
	);
};

export default TableAddElm;
