import { useFormContext } from "react-hook-form";

import { InputMaskNumber } from "Components/Global";
import Form                from "Components/Appraisal/Form";

function CostForm({ onSubmit, isSubmitingData }) {

	const hookForm = useFormContext();

	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<InputMaskNumber
				name="cost"
				bsSize="sm"
				label="Costo Final del Avalo (Capturado):"
				hookForm={hookForm}
				rules={{ required : false }}
			/>
		</Form>
	);
}

export default CostForm;
