import { useState }     from "react";
import PropTypes        from "prop-types";
import { Plus, Search } from "react-feather";
import {
	Input,
	Button,
	InputGroup,
	DropdownItem,
	DropdownMenu,
	ButtonDropdown,
	InputGroupText,
	DropdownToggle,
} from "reactstrap";


import "./HeaderPage.scss";
import { isValidArray } from "Helpers";

const HeaderPage = ({
	title,
	showBtn,
	btnLabel,
	btnOptions,
	handleSearch,
	handleOnclick,
	showInputSearch,
}) => {

	const [dropdownOpen, setDropdownOpen] = useState(false);

	const toggleDropdown = () => {
	  setDropdownOpen(!dropdownOpen);
	};

	return (
		<div id="HeaderPage">
			<div className="header-container">

				<h1>{title}</h1>

				<div className="gruop">
					{
						showInputSearch &&
						<div className="inpt-wrapper">
							<InputGroup className="input-group-merge">
								<Input
									placeholder="Buscar..."
									onChange={(e) => {
										handleSearch(e.target.value);
									// setValue(e.target.value);
									}}
								/>
								<InputGroupText>
									<Search size={14} />
								</InputGroupText>
							</InputGroup>
						</div>
					}
					{
						showBtn && (
							<>
								{

									isValidArray(btnOptions) ?
										<ButtonDropdown isOpen={dropdownOpen} toggle={toggleDropdown}>
											<DropdownToggle caret color="primary">
												{btnLabel}
											</DropdownToggle>
											<DropdownMenu>
												{
													btnOptions.map((item) => (
														<DropdownItem
															key={item.label}
															tag="a"
															onClick={handleOnclick(item.value)}
														>
															{item.label}
														</DropdownItem>
													))
												}
											</DropdownMenu>
										</ButtonDropdown>
										:
										<Button.Ripple
											block
											color="primary"
											onClick={handleOnclick}
										>
											{btnLabel}
											<Plus size={16} />
										</Button.Ripple>
								}
							</>
						)
					}
				</div>
			</div>
			<hr />
		</div>
	);
};
HeaderPage.propTypes = {
	title           : PropTypes.string,
	btnOptions      : PropTypes.array,
	btnLabel        : PropTypes.string,
	showBtn         : PropTypes.bool,
	showInputSearch : PropTypes.bool,
	handleSearch    : PropTypes.func,
	handleOnclick   : PropTypes.func,
};

HeaderPage.defaultProps = {
	title           : "",
	btnLabel        : "",
	showBtn         : true,
	showInputSearch : true,
	btnOptions      : [],
	handleSearch    : () => {},
	handleOnclick   : () => {},
};

export default HeaderPage;
