
import { MapFormInpts }                           from "Components/Global";
import { setOnlyPositiveNumber }                  from "Helpers";
import { settlementTypeOptions, roadTypeOptions } from "Resources/data/RequestForm";

import Form from "../Form";

const GeoForm = ({
	delegations : {
		onSubmit,
		hookForm,
		localities,
		getStates,
		municipalities,
		isSubmitingData,
	},
}) => {
	const addresTypeOptions = [
		{
			label : "Domicilio Urbano (Vialidad)",
			value : 1,
		},
		{
			label : "Domicilio Rural (Vialidad)",
			value : 2,
		},
		{
			label : "Vias De Comunicacin - Carretera",
			value : 3,
		},
		{
			label : "Vias De Comunicacin - Camino",
			value : 4,
		},
	];

	const inptsAddresInfo = [
		{
			label   : "Tipo de Domicilio:",
			name    : "type_address",
			options : addresTypeOptions,
			rules   : { required : false },
		},
		{
			label   : "Estado:",
			name    : "state",
			options : getStates(),
			rules   : { required : false },
		},
		{
			label   : "Municipio:",
			name    : "municipality",
			options : municipalities,
			rules   : { required : false },
		},
		{
			label   : "Localidad:",
			name    : "localities",
			options : localities,
			rules   : { required : false },
		},
		{
			label      : "Cdigo Postal:",
			name       : "zip_code",
			maxLength  : 5,
			onKeyPress : setOnlyPositiveNumber,
			options    : {
				required : {
					value   : true,
					message : "Este campo es requerido",
				},
				minLength : {
					value   : 5,
					message : "El cdigo postal debe de tener 5 carcteres",
				},
			 },
		},
		{
			label   : "Tipo de Asentamiento:",
			name    : "settlement_type",
			options : settlementTypeOptions,
			rules   : { required : false },
		},
		{
			label   : "Nombre de Asentamiento:",
			name    : "settlement_name",
			options : { required : false },
		},
		{
			label   : "Nombre de Vialidad (Urbano/Rural):",
			name    : "viality_name",
			options : { required : false },
		},
		{
			label   : "Tipo de Vialidad (Urbano/Rural):",
			name    : "viality_type",
			options : roadTypeOptions,
			rules   : { required : false },
		},
	];

	const inptsExtNum =  [
		{
			label   : "Nmero:",
			name    : "num_ext.number",
			options : { required : false },
			type    : "number",
		},
		{
			label   : "Alfanumrico:",
			name    : "num_ext.alphanumeric",
			options : { required : false },
		},
		{
			label   : "Anterior:",
			name    : "num_ext.previous",
			options : { required : false },
		},
	];

	const inptsInNum =  [
		{
			label   : "Nmero:",
			name    : "num_int.number",
			options : { required : false },
			type    : "number",
		},
		{
			label   : "Alfanumrico:",
			name    : "num_int.alphanumeric",
			options : { required : false },
		},
	];

	const inptsRest = [
		{
			label   : "Descripcin Ubicacin:",
			name    : "description_location",
			options : { required : false },
			type    : "textarea",
		},
	];

	const inptsReferences = [
		{
			label   : "Tipo de Vialidad:",
			name    : "viality_references.0.viality_type",
			options : roadTypeOptions,
			rules   : { required : false },
		},
		{
			label   : "Clave:",
			name    : "viality_references.0.clave",
			options : { required : false },
		},
		{
			label   : "Nombre:",
			name    : "viality_references.0.name",
			type    : "textarea",
			options : { required : false },
		},
		{
			label   : "Tipo de Vialidad:",
			name    : "viality_references.1.viality_type",
			options : roadTypeOptions,
			rules   : { required : false },
		},
		{
			label   : "Clave:",
			name    : "viality_references.1.clave",
			options : { required : false },
		},
		{
			label   : "Nombre:",
			name    : "viality_references.1.name",
			type    : "textarea",
			options : { required : false },
		},
		{
			label   : "Tipo de Vialidad:",
			name    : "viality_references.2.viality_type",
			options : roadTypeOptions,
			rules   : { required : false },
		},
		{
			label   : "Clave:",
			name    : "viality_references.2.clave",
			options : { required : false },
		},
		{
			label   : "Nombre:",
			name    : "viality_references.2.name",
			type    : "textarea",
			options : { required : false },
		},

	];
	return (
		<Form onSubmit={onSubmit} isSubmitingData={isSubmitingData}>
			<p className="mb-1">
				Se llenan los datos de esta seccin con base en la informacin de la solicitud del avalo.
				Es necesario completar la informacin manualmente de acuerdo a los criterios del perito.
			</p>
			<p className="mb-1">
				El Estado y Municipio tienes informacin por defecto por lo cual se recomienda no modificarlos, pero si hay algn error que cause que las opcines de localidades no se cargen, pueden ser cambiados.
			</p>
			<div className="row">
				<MapFormInpts className="col-md-6" inpts={inptsAddresInfo} hookForm={hookForm} />
			</div>
			<div className="row">
				<MapFormInpts className="col-md-4" inpts={inptsExtNum} hookForm={hookForm}  title="Nmero Exterior:" />
			</div>
			<div className="row">
				<MapFormInpts className="col-md-4" inpts={inptsInNum} hookForm={hookForm}  title="Nmero Interior:" />
			</div>
			<MapFormInpts inpts={inptsRest} hookForm={hookForm} />
			<div className="row">
				<MapFormInpts className="col-md-4" inpts={inptsReferences} hookForm={hookForm} title="Vialidades de Referencia" />
			</div>
		</Form>
	);
};

export default GeoForm;
