import { useFormContext } from "react-hook-form";

import {
	getNetRate,
	getDeductions,
	getAnualIncomeNet,
	simpleSubtraction,
} from "Helpers/formulas";

import { HelpToolTip, InputMaskNumber } from "Components/Global";
import InptWatcherHOC                   from "Components/Appraisal/InptWatcherHOC";
import PhysicAndBusinessApproachResult  from "../PhysicAndBusinessApproachResult";

const PBRates = ({
	delegations : {
		handleSetValue,
	},
}) => {

	const hookForm = useFormContext();

	return (
		<div className="row">
			<PhysicAndBusinessApproachResult />
			<div className="col-md-6">
				<h5 className="text-center my-1">Tasa en Base al Valor Fsico</h5>
				<div className="d-flex">
					<InptWatcherHOC
						label="Renta Mensual"
						className="w-100"
						name="physic_rate.income_monthly"
						callback={handleSetValue}
						deps={["gross_mensual_rent"]}
					/>
					<HelpToolTip target="physic_rate-income-monthly">
						Renta Mensual = Sumatoria de Rentas Mensuales
					</HelpToolTip>
				</div>
				<div className="d-flex">
					<InptWatcherHOC
						label="Deducciones"
						className="w-100"
						name="physic_rate.deduction"
						formula={getDeductions}
						deps={["physic_rate.income_monthly", "rounded_porcentage"]}
					/>
					<HelpToolTip target="physic_rate-deduction">
						Deducciones = (Renta Mensual) x (Tabla Deducciones [En numeros redondos]) % )
					</HelpToolTip>
				</div>
				<div className="d-flex">
					<InptWatcherHOC
						label="Renta Neta Mensual"
						className="w-100"
						name="physic_rate.income_net_monthly"
						formula={simpleSubtraction}
						deps={["gross_mensual_rent", "physic_rate.deduction"]}
					/>
					<HelpToolTip target="physic_rate-income_net_monthly">
						Renta Neta Mensual = Renta Mensual Bruta - Deducciones
					</HelpToolTip>
				</div>
				<div className="d-flex">
					<InptWatcherHOC
						label="Renta Neta Anual"
						className="w-100"
						name="physic_rate.income_net_anual"
						formula={getAnualIncomeNet}
						deps={["physic_rate.income_net_monthly"]}
					/>
					<HelpToolTip target="physic_rate-income_net_anual">
						Renta Neta Anual = (Renta Neta Mensual) x (12)
					</HelpToolTip>
				</div>
				<div className="d-flex">
					<InputMaskNumber
						disabled
						bsSize="sm"
						className="w-100"
						label="Valor Fsico o Directo"
						name="physic_rate.physic_value"
						hookForm={hookForm}
						rules={{ required : false }}
					/>
					<HelpToolTip target="physic_rate-physic_value">
						Valor Fisico o Directo = Resultado del Enfoque Fsico
					</HelpToolTip>
				</div>
				<div className="d-flex">
					<InptWatcherHOC
						icon={<span style={{ fontSize : "10px" }}>%</span>}
						label="Tasa Neta"
						className="w-100"
						name="physic_rate.net_rate"
						formula={getNetRate}
						deps={["physic_rate.income_net_anual", "physic_rate.physic_value"]}
					/>
					<HelpToolTip target="physic_rate-net_rate">
						Tasa Neta = (Renta Neta Anual) / (Valor Fisico o Directo)
					</HelpToolTip>
				</div>
			</div>
			<div className="row col-md-6">
				<h5 className="text-center my-1">Tasa en Base Al Valor de Mercado</h5>
				<div className="d-flex">
					<InptWatcherHOC
						label="Renta Unitaria de Mercado"
						className="w-100"
						name="business_rate.unit_icome"
						callback={handleSetValue}
						deps={["homologation_average"]}
					/>
					<HelpToolTip target="business_rate-unit_icome">
						Renta unitaria de Mercado = Promedio Homologacion
					</HelpToolTip>
				</div>
				<div className="d-flex">
					<InptWatcherHOC
						label="Deducciones"
						className="w-100"
						name="business_rate.deduction"
						formula={getDeductions}
						deps={["business_rate.unit_icome", "rounded_porcentage"]}
					/>
					<HelpToolTip target="business_rate-deduction">
						Deducciones = (Renta unitaria de Mercado) x (Tabla Deducciones [En numeros redondos]) % )
					</HelpToolTip>
				</div>
				<div className="d-flex">
					<InptWatcherHOC
						label="Renta Neta Mensual"
						className="w-100"
						name="business_rate.income_net_monthly"
						formula={simpleSubtraction}
						deps={["business_rate.unit_icome", "business_rate.deduction"]}
					/>
					<HelpToolTip target="business_rate-income_net_monthly">
						Renta Neta Mensual = Renta unitaria de Mercado - Deducciones
					</HelpToolTip>
				</div>
				<div className="d-flex">
					<InptWatcherHOC
						label="Renta Neta Anual"
						className="w-100"
						name="business_rate.income_net_anual"
						formula={getAnualIncomeNet}
						deps={["business_rate.income_net_monthly"]}
					/>
					<HelpToolTip target="business_rate-income_net_anual">
						Renta Neta Anual = (Renta Neta Mensual) x (12)
					</HelpToolTip>
				</div>
				<div className="d-flex">
					<InputMaskNumber
						disabled
						bsSize="sm"
						className="w-100"
						label="Valor Unitario de Mercado"
						name="business_rate.bussiness_value"
						hookForm={hookForm}
						rules={{ required : false }}
					/>
					<HelpToolTip target="business_rate-bussiness_value">
						Valor unitario de Mercado = Monto Unitario Aplicable (Enfoque de Mercado)
					</HelpToolTip>
				</div>
				<div className="d-flex">
					<InptWatcherHOC
						icon={<span style={{ fontSize : "10px" }}>%</span>}
						label="Tasa Neta"
						className="w-100"
						name="business_rate.net_rate"
						formula={getNetRate}
						deps={["business_rate.income_net_anual", "business_rate.bussiness_value"]}
					/>
					<HelpToolTip target="business_rate-net_rate">
						Tasa Neta = (Renta Neta Anual) / (Valor unitario de Mercado)
					</HelpToolTip>
				</div>
			</div>
		</div>
	);
};

export default PBRates;
