import { useSelector, shallowEqual } from "react-redux";

//Own Imports
import { ac } from "Helpers";

const useCan = () => {
	const { _authSlice : {  user } } = useSelector(state => state, shallowEqual);

	const can = (action, resource, attribute) => {
		try {
			const permission = ac.can(user.role)[action](resource);

			if (attribute) {
				const found = permission.attributes.findIndex(item => item === attribute);
				return found !== -1;
			}

			return permission;
		} catch (err) {
			console.error("[VerticalNavMenuItems.canRead] ", err);

			return { granted : false };
		}
	};

	return [can, user.role];
};

export default useCan;
