import { connect } from "react-redux";
import { useForm } from "react-hook-form";

//Our Imports
import { ModalSlice }         from "Store/Slices";
import bindAll                from "Helpers/bindAll";
import { useHandlerMutation } from "Helpers/Hooks";
import ReturnStatus           from "./ReturnStatusModal.jsx";

const ReturnStatusContainerModal = ({
	id,
	newStatus,
	modalSliceActions,
}) => {

	const { handleSubmit, ...hookForm } = useForm();

	const handleCloseModal = () => modalSliceActions.closeModal("returnStatus");

	const [ patchRequest, { isLoading } ] = useHandlerMutation({
		successCallback : handleCloseModal,
		toastName       : "statusPetition",
	});

	const onSubmit = (event, ...args) => {
		event.preventDefault();

		handleSubmit( (data) => {
			const method       = "PATCH";
			const dataToSubmit = {
				status : newStatus,
				...data,
			};

			patchRequest({
				method,
				data   : dataToSubmit,
				module : `petitions/${id}/status`,
				tags   : ["petitions", "petition_details"],
			});
		})(...args);
	};

	return (
		<ReturnStatus
			delegations={{
				hookForm,
				onSubmit,
				isLoading,
				handleCloseModal,
			}}
		/>
	);
};

const mapStateToProps = ({ _modalSlice }) => ({
	id        : _modalSlice?.props?.returnStatus?.id        ?? "",
	newStatus : _modalSlice?.props?.returnStatus?.newStatus ?? "",
});

const mapDispatchToProps = bindAll({
	ModalSliceActions : ModalSlice.actions,
});

export default connect(mapStateToProps, mapDispatchToProps)(ReturnStatusContainerModal);
