import { useFormContext } from "react-hook-form";
import StatusApproach     from "./StatusApproach";

const StatusApproachContainer = ({ approachStatus  }) => {

	const hookForm = useFormContext();

	return (
		<StatusApproach
			delegations={{
				hookForm,
				approachStatus,
			}}
		/>
	);
};

export default StatusApproachContainer;
