import { useFieldArray, useFormContext, useWatch } from "react-hook-form";

import ComparableProperty from "./ComparableProperty";

const ComparablePropertyContainer = () => {

	const hookForm = useFormContext();

	const { fields : compPropertyFields } = useFieldArray({ name : "comparable_property" });

	const approachStatus    = useWatch({ name : "approach_status" });
	const investigationType = useWatch({ name : "investigation_type" });

	return (
		<ComparableProperty
			delegations={{
				hookForm,
				approachStatus,
				investigationType,
				compPropertyFields,
			}}
		/>
	);
};

export default ComparablePropertyContainer;
