import { useColumnAverage }              from "Helpers/Hooks";
import { useFieldArray, useFormContext } from "react-hook-form";
import TableCUS                          from "./TableCUS";

const TableCUSContainer = () => {

	const hookForm = useFormContext();

	const { setValue } = hookForm;

	const { fields : cusFields } = useFieldArray({ name : "cus"});

	const setFactorValues = () => cusFields.forEach((_, index) => {
		setValue(`cus.${index}.efficiency_factor`, 1);
	});

	useColumnAverage({
		length       : 6,
		arrayKey     : "cus",
		fieldKey     : "unit_val",
		name         : "cus_average",
		isAbsAverage : true,
	});

	return (
		<TableCUS
			delegations={{
				hookForm,
				cusFields,
				setFactorValues,
			}}
		 />
	);
};

export default TableCUSContainer;
