import { connect } from "react-redux";

import { bindAll }      from "Helpers";
import { ModalSlice }   from "Store/Slices";
import UploadFileModal  from "./UploadFileModal";
import { useFormState } from "react-hook-form";
import { useSendFiles } from "Helpers/Hooks";
import { useParams }    from "react-router-dom";

const UploadFileModalContainer = ({ modalSliceActions, hookForm }) => {

	const { id : appraiseId } = useParams();

	const [ handleSubmitFiles, handleRemoveFiles, { isLoading : isLoadingFiles } ] = useSendFiles({
		module : "appraise",
		tags   : ["appraisals"],
	});

	const { control, handleSubmit } = hookForm;

	const { dirtyFields } = useFormState({ control });

	const isDirtyFiles = () => {
		const isDirtySomeFile =  dirtyFields?.private_boundarie || dirtyFields?.land_boundaries;
		return isDirtySomeFile;
	};

	const toggleModal = async () => {
		modalSliceActions.closeModal("uploadFileModal");
	};

	const onSubmit = handleSubmit(async (files) => {
		if (!isDirtyFiles()) return;

		await handleRemoveFiles();
		await handleSubmitFiles(files, appraiseId);

		toggleModal();
	});

	return (
		<UploadFileModal
			delegations={{
				onSubmit,
				hookForm,
				toggleModal,
				isLoading : isLoadingFiles,
			}}
		 />
	);
};

const mapDispatchToProps = bindAll({
	ModalSliceActions : ModalSlice.actions,
});

export default connect(null, mapDispatchToProps)(UploadFileModalContainer);
