import { Menu as MenuI }   from "react-feather";
import { Link, useParams } from "react-router-dom";

// import { Link, /*Prompt,*/ useParams }                            from "react-router-dom";

import { Card, Collapse, Button, Breadcrumb, BreadcrumbItem } from "reactstrap";
import { useEffect, useState }                                from "react";

import { genericApi } from "Store/api/genericApi";
//Our Components
import Menu from "../Menu";
import "./HeaderPage.scss";


const HeaderPage = ({
	delegations : {
		isDirty,
		isOpenMenu,
		toggleMenu,
		isSubmitingData,
		getCurrentSection,
		handleSaveBtnClick,
	},
}) => {
	const { id } = useParams();

	const [appraise, setAppraise] = useState({
		key_sofia : id,
	});

	const { data, isFetching } = genericApi.useGetDataQuery({
		params : {
			select_fields : ["key_sofia"],
		},
		module : `appraisals/${id}`,
	});

	useEffect(() => {
		if (!isFetching) {
			const { appraise } = data;
			setAppraise(appraise);
		}
	}, [isFetching]);

	return (
		<div id="HeaderPage">
			{/* <Prompt
				when={isDirty}
				message="Ests seguro que quieres salir sin guardar los cambios?"
			/> */}
			<div className="row p-1 gy-1">
				<div className="col-md-8">
					<div className="d-flex align-items-center">
						<Button.Ripple className="btn-icon " color="primary" onClick={toggleMenu}>
							<MenuI size={15} />
						</Button.Ripple>
						<Breadcrumb className="mx-1">
							<BreadcrumbItem>
								<Link to="/app/appraise/records"> Avalos </Link>
							</BreadcrumbItem>
							<BreadcrumbItem onClick={toggleMenu} className="pointer-cursor">
								<span>
									{
										appraise.key_sofia
									}
								</span>
							</BreadcrumbItem>
							<BreadcrumbItem active>
								<span> {getCurrentSection()} </span>
							</BreadcrumbItem>
						</Breadcrumb>
					</div>
				</div>
				<Button.Ripple
					color="primary"
					className="col-md-4"
					onClick={handleSaveBtnClick}
					disabled={!isDirty || isSubmitingData}
				>
					Guardar e ir a la siguiente pgina
				</Button.Ripple>
			</div>
			<Collapse isOpen={isOpenMenu}>
				<Card className="mb-2">
					<Menu id={id} />
				</Card>
			</Collapse>
		</div>
	);
};

export default HeaderPage;
