import { useFormContext } from "react-hook-form";
import TableInvesSelects  from "./TableInvesSelects";

const TableInvesSelectsContainer = ({ index }) => {

	const hookForm = useFormContext();

	return (
		<TableInvesSelects
			delegations={{
				index,
				hookForm,
			}}
		/>
	);
};

export default TableInvesSelectsContainer;
