import { useColumnAverage }              from "Helpers/Hooks";
import { useFieldArray, useFormContext } from "react-hook-form";

import UnitValueLand from "./UnitValueLand";

const UnitValueLandContainer = () => {

	const hookForm = useFormContext();

	const { control } = hookForm;

	const { fields : unitValueLandFields } = useFieldArray({
		control,
		name : "unit_value_land",
	});

	useColumnAverage({
		length   : 6,
		arrayKey : "unit_value_land",
		fieldKey : "invest_comp.unit_price",
		name     : "invest_comp_average",
	});

	return (
		<UnitValueLand
			delegations={{
				hookForm,
				unitValueLandFields,
			}}
		/>
	);
};

export default UnitValueLandContainer;
